import { BaseAd } from "./BaseAd";

class WXAd extends BaseAd {
    private bannerAd: wx.BannerAd = null;
    private videoAd: wx.RewardedVideoAd = null;
    private videoSuccess: Function = null;
    private videoFail: Function = null;
    private nativeAd = null;
    private nativeSuccess: Function = null;
    private nativeFail: Function = null;

    constructor() {
        super("wechat");
    }

    private getUserInfo(success?: (res: wx.UserInfo) => void, fail?: () => void) {
        wx.getSetting({
            success: () => {
                wx.getUserInfo({
                    success: (res) => {
                        success && success(res.userInfo);
                    },
                    fail: () => {
                        fail && fail();
                    }
                })
            },
            fail: () => {
                let sysInfo = wx.getSystemInfoSync();
                let width = sysInfo.screenWidth;
                let height = sysInfo.screenHeight;
                let button = wx.createUserInfoButton({
                    type: "text",
                    text: "",
                    style: {
                        left: 0,
                        top: 0,
                        width: width,
                        height: height,
                        lineHeight: height,
                        backgroundColor: '#00000080',
                        color: '#ffffff',
                        textAlign: 'center',
                        fontSize: 16,
                        borderRadius: 4
                    }
                });

                button.onTap((res) => {
                    if (res.userInfo) {
                        button.destroy();
                        success && success(res.userInfo);
                    } else {
                        fail && fail();
                    }
                })
            }
        });
    }

    login(success?: Function, fail?: Function) {
        wx.login({
            success: (res) => {
                success && success({
                    appid: wx.getAccountInfoSync().miniProgram.appId,
                    platform: this.platform,
                    code: res.code
                })
                // this.getUserInfo((userInfo) => {
                //         success && success({
                //             nickname: userInfo.nickName,
                //             avatar: userInfo.avatarUrl,
                //             gender: userInfo.gender,
                //             appid: wx.getAccountInfoSync().miniProgram.appId,
                //             platform: "wechat",
                //             code: res.code
                //         });
                //     }, () => {
                //         fail && fail();
                //     }
                // )
            },
            fail: () => {
                fail && fail();
            }
        });
    }

    share() {

    }

    requestSubscribeMessage(success?: Function, fail?: Function) {
        if (!this.subscribe_templates || !this.subscribe_templates.length) {
            fail && fail();
            return;
        }

        // wx.requestSubscribeMessage({
        //     tmplIds: this.subscribe_templates,
        //     success: function(res) {

        //     },
        //     fail: function(res) {

        //     }
        // })
    }

    createBannerAd() {
        if (!this.banner_id) {
            return;
        }

        if (!this.bannerAd) {
            let sysInfo = wx.getSystemInfoSync();
            this.bannerAd = wx.createBannerAd({
                adUnitId: this.banner_id,
                style: {
                    width: Math.min((sysInfo.pixelRatio * 300), sysInfo.screenWidth),
                }
            });

            this.bannerAd.onResize((size) => {
                this.bannerAd.style.top = sysInfo.screenHeight - size.height;
                this.bannerAd.style.left = sysInfo.screenWidth / 2 - size.width / 2;
            })

            this.bannerAd.show();
            this.bannerAd.onLoad(() => {
                console.log("Banner广告加载成功");
            });
            this.bannerAd.onError(() => {
                console.log("Banner广告加载失败");
            });
        }
    }

    destroyBannerAd() {
        if (this.bannerAd) {
            this.bannerAd.hide();
            this.bannerAd.destroy();
            this.bannerAd = null;
        }
    }

    createVideoAd(success?: Function, fail?: Function) {
        if (!this.video_id) {
            success && success();
            return;
        }

        if (!this.videoAd) {
            this.videoAd = wx.createRewardedVideoAd({
                adUnitId: this.video_id
            });

            this.videoAd.onLoad(() => {
                console.log("激励视频广告加载成功");
            });

            this.videoAd.onError(() => {
                console.log("激励视频广告加载失败");
                this.videoFail && this.videoFail();
            })

            this.videoAd.onClose((res) => {
                if (res && res.isEnded) {
                    console.log("激励视频正常播放结束");
                    this.videoSuccess && this.videoSuccess();
                } else {
                    console.log("激励视频播放中途推出");
                    this.videoFail && this.videoFail();
                }
            })
        }

        this.videoSuccess = success;
        this.videoFail = fail;

        this.videoAd.show()
        .catch(() => {
            this.videoAd.load()
            .then(() => {
                this.videoAd.show();
            })
        })
    }

    createInterstitialAd(callback?: Function) {
        if (!this.interstitial_id) {
            callback && callback();
            return;
        }
        
        let interstitialAd = wx.createInterstitialAd({
            adUnitId: this.interstitial_id
        });
        interstitialAd.show();
        interstitialAd.onLoad(() => {
            console.log('插屏广告加载成功');
        });
        interstitialAd.onError(() => {
            console.log('插屏广告加载失败');
        });
        interstitialAd.onClose(() => {
            console.log('插屏广告关闭');
            callback && callback();
        })
    }
    
    createNativeAd(success?: Function, fail?: Function) {
        if (!this.native_id) {
            success && success();
            return;
        }
    }
    
    reportAdShow(id: string) {

    }
    
    reportAdClick(id: string) {

    }

    vibrateShort() {
        wx.vibrateShort();
    }

    vibrateLong() {
        wx.vibrateLong();
    }
}

export const wxAd: WXAd = new WXAd();