    protected _focusmatchins:MatchInsModel[]=[]
    get focusmatchins()
    {
        return this._focusmatchins
    }
    protected _blinds:{[blind_id:number]:BlindModel}={}
    
	setMatchInsM(value:MatchInsModel)
	{
        let index = this._allmatchins.findIndex((mim)=>mim.id==value.id)
        if(index>=0)
        {
            this._allmatchins[index]=value
        }
        if(value.match)
        {
            return
        }
        for(let i=0;i<this._allmatchins.length;++i)
        {
            if(this._allmatchins[i].id==value.id)
            {
                for(let j=0;j<this._allmatches.length;++j)
                {
                    let m = this._allmatches[j]
                    if(m.id==value.match_id)
                    {
                        value.match=m
                        break
                    }
                }
                break
            }
        }
	}
    trySignup(ins_id:string)
    {
        let matchIns = this.getMatchInsById(ins_id)
        if(!matchIns||!matchIns.match)
        {
            return false
        }
        let mm = matchIns.match
        let need_coin = 0
        let need_item:ItemItem=null
        let need_item_count=0
        for(let i=0;i<mm.signup_cost.length;++i)
        {
            let item = mm.signup_cost[i]
            if(item.id==-99)
            {
                need_coin=item.count
            }
            else
            {
                need_item=GItemCfg.getItemById(item.id)
                need_item_count=item.count
            }
        }
        //优先扣道具
        if(need_item)
        {
            let bagItems = GItemCfg.bagItems
            if(bagItems)
            {
                let allneeds:BagItemItem[]=[]
                for(let i=0;i<bagItems.length;++i)
                {
                    if(bagItems[i].item_id==need_item.id)
                    {
                        allneeds.push(bagItems[i])
                    }
                }
                //try
                let tryItems=allneeds.concat()
                let tryNeedCount=need_item_count
                while(tryNeedCount>0&&tryItems.length>0)
                {
                    let item = tryItems.shift()
                    if(tryNeedCount<=item.count)
                    {
                        tryNeedCount=0
                        break
                    }
                    else
                    {
                        tryNeedCount-=item.count
                    }
                }
                need_coin=tryNeedCount*need_item.coin
                if(GUserMgr.self_user.coin+GUserMgr.self_user.reward_coin<need_coin)
                {
                    return false
                }
            }
        }
        //-99金币
        if(need_coin>GUserMgr.self_user.coin+GUserMgr.self_user.reward_coin)
        {
            return false
        }
        return true
    }
    getSelfPlayer(ins_id:string)
    {
        let mim = this.getMatchInsById(ins_id)
        return mim?.players[GUserMgr.self_user_id]
    }
    getPlayers(ins_id:string)
    {
        let mim = this.getMatchInsById(ins_id)
        return mim?.players
    }
    setPlayers(ins_id:string,value:{[user_id:number]:MatchPlayer})
