import { GHelper } from "./Helper";
import { GNativeTool } from "./NativeTool";

export enum EShareTo
{
    Friend=0,
    Group,
    Favorites
}
export enum EMediaType
{
    Text=0,
    Image,
    Page,
    Music,
    Vedio
}

export enum EWeiXinCode
{
    OK=0,
    ErrorNoWeiXin=1,
    ErrorNullAPI=2,
    ErrorGetAccessToken=3,
    ErrorGetUserInfo=4,
    ErrorCancle=5,
    ErrorDenyByWeixin=6
}
export class WeiXinLoginModel
{
    code=null
    wxcode=null
    accessToken=""
    refreshToken=""
    openId=""
    roleName=""
    avatar=""
    sex=-1
    unionId=""
}
export class WeiXinPayModel
{
    partnerId=""
    prepayId=""
    packageValue=""
    nonceStr=""
    timeStamp=""
    sign=""
}
/*
    分享的类型	需要的参数
    文本	shareTo、mediaType、text
    图片	shareTo、mediaType、imagePath、thumbImage（或 thumbSize）
    页面	shareTo、mediaType、thumbImage（或 imagePath、thumbSize）、url、title、text
    音乐	shareTo、mediaType、thumbImage（或 imagePath、thumbSize）、url、title、text
    视频	shareTo、mediaType、thumbImage（或 imagePath、thumbSize）、url、title、text
*/
export class WeiXinShareModel
{
    shareTo=-1//分享的目标，0 聊天窗口，1 朋友圈，2 收藏
    mediaType=-1//分享类型，0 文本，1 图片，2 页面，3 音乐，4 视频
    url=""//音乐、视频或网页的链接
    imagePath=""//图片路径（本地图片，如"/sdcard/test.png"）
    title=""//分享的标题
    text=""//分享的内容
}
export let GWXTool:WeiXinTool=null
class WeiXinTool
{
    /**
     * 微信的appid，通过开发者账号生成的
     */
    protected _app_id = null
    get appId()
    {
        if(!this._app_id)
        {
            if(cc.sys.os == cc.sys.OS_ANDROID) 
            {
                this._app_id = jsb.reflection.callStaticMethod(this._wx_plugin, "getAppId",this._return_one_string)
            }
            else if(cc.sys.os == cc.sys.OS_IOS) 
            {
                this._app_id = jsb.reflection.callStaticMethod("WechatLogin", "getAppId")
            }
        }
        return this._app_id
    }
    /**
     * 微信的app secret，通过开发者账号生成的
     */
    protected _app_idsecret = null
    get secret()
    {
        if(!this._app_idsecret)
        {
            if(cc.sys.os == cc.sys.OS_ANDROID) 
            {
                this._app_idsecret = jsb.reflection.callStaticMethod(this._wx_plugin, "getSecret",this._return_one_string)
            }
            else if(cc.sys.os == cc.sys.OS_IOS) 
            {
                this._app_idsecret = jsb.reflection.callStaticMethod("WechatLogin", "getSecret")
            }
        }
        return this._app_idsecret
    }
    protected _wx_plugin         = "com/eryi/casino/wxapi/WXPlugin"
    protected _pay_activity      = "com/eryi/casino/wxapi/WXPayEntryActivity"

    protected _one_string_param  = "(Ljava/lang/String;)V"
    protected _two_string_params = "(Ljava/lang/String;Ljava/lang/String;)V"
    protected _return_one_string = "()Ljava/lang/String;"
    protected _return_one_bool = "()Z"
    protected _login_cb:Function=null
    protected _share_cb:Function=null
    protected _pay_cb:Function=null

    //是否支持微信调用（有可能微信没安装）
    protected _is_support=null
    get isSupport():boolean
    {
        if(this._is_support==null)
        {
            if(cc.sys.isBrowser)
            {
                //方便测试
                return false
            }
            if(cc.sys.os == cc.sys.OS_ANDROID) 
            {
                this._is_support = jsb.reflection.callStaticMethod(this._wx_plugin, "isSupport",this._return_one_bool)
            }
            else if(cc.sys.os == cc.sys.OS_IOS) 
            {
                this._is_support = jsb.reflection.callStaticMethod("WechatLogin", "isSupport")
            }
        }
        //ios有可能出错
        return true
        //return this._is_support
    }
    constructor()
    {
        
    }
    
    login(fun:Function)
    {
        if(cc.sys.isBrowser)
        {
            return
        }
        GHelper.showRunningTip()
        this._login_cb = fun
        if(cc.sys.os == cc.sys.OS_ANDROID) 
        {
            let fun_name = this._convertCallBackName(this._on_login_callback.bind(this),"wx_login")
            jsb.reflection.callStaticMethod(this._wx_plugin, "login", this._one_string_param, fun_name)
        }
        else if(cc.sys.os == cc.sys.OS_IOS) 
        {
            let fun_name = this._convertCallBackName(this._on_login_callback.bind(this),"wx_login")
            jsb.reflection.callStaticMethod("WechatLogin", "setJsCallback:", fun_name)
            jsb.reflection.callStaticMethod("WechatLogin", "login")
        }
    }
    protected _on_login_callback(userInfo)
    {
        userInfo.code = parseInt(userInfo.code)
        if(userInfo.sex)
        {
            userInfo.sex = parseInt(userInfo.sex)
        }

        if(userInfo.code)
        {
            let err_info = ""
            switch(userInfo.code)
            {
                case EWeiXinCode.ErrorNoWeiXin:
                {
                    err_info="没有安装微信"
                    break
                }
                case EWeiXinCode.ErrorNullAPI:
                {
                    err_info="没找到可调用的API"
                    break
                }
                case EWeiXinCode.ErrorGetAccessToken:
                {
                    err_info="获取AccessToken的时候出错"
                    break
                }
                case EWeiXinCode.ErrorGetUserInfo:
                {
                    err_info="获取UserInfo的时候出错"
                    break
                }
                case EWeiXinCode.ErrorCancle:
                {
                    err_info="取消微信登陆"
                    break
                }
                case EWeiXinCode.ErrorDenyByWeixin:
                {
                    err_info="微信验证失败"
                    break
                }
                default:
                {
                    err_info="微信登陆失败"
                    break
                }
            }
            if(err_info.length>0)
            {
                GNativeTool.log("weixin",err_info);
                GHelper.showTip(err_info)
                GHelper.clearRunningTip()
            }
        }
        else
        {
            if(userInfo.wxcode)
            {
                //让客户端直接用code登陆
                GHelper.clearRunningTip()
                if(this._login_cb)
                {
                    this._login_cb(userInfo)
                }
                //this._getAccessToken(userInfo.wxcode)
            }
            else
            {
                GHelper.clearRunningTip()
                if(this._login_cb)
                {
                    this._login_cb(userInfo)
                }
            }
        }
    }

    share(wxsm:WeiXinShareModel, onCallFunc) 
    {
        if(cc.sys.isBrowser)
        {
            return
        }
        this._share_cb = onCallFunc

        if(cc.sys.os == cc.sys.OS_ANDROID)
        {
            let str_params = JSON.stringify(wxsm)
            let fun_name = this._convertCallBackName(this._on_share_callback.bind(this),"wx_share")
            jsb.reflection.callStaticMethod(this._wx_plugin, "share", this._two_string_params,str_params,fun_name)
        } 
        else if(cc.sys.os == cc.sys.OS_IOS)
        {
            if(wxsm.mediaType!=EMediaType.Image)
            {
                wxsm.imagePath = null
                delete wxsm.imagePath
            }
            if(wxsm.mediaType!=EMediaType.Page)
            {
                wxsm.url = null
                delete wxsm.url 
            }
            let str_params = JSON.stringify(wxsm)
            let fun_name = this._convertCallBackName(this._on_share_callback.bind(this),"wx_share")
            jsb.reflection.callStaticMethod("WechatShare", "setJsCallback:", fun_name)
            jsb.reflection.callStaticMethod("WechatShare", "share:",str_params)
        }
    }
    protected _on_share_callback(params)
    {
        try
        {

        }
        catch(e)
        {

        }
    }
    pay(wxpm:WeiXinPayModel,onCallFunc)
    {
        this._pay_cb = onCallFunc
        var str_params = JSON.stringify(wxpm)
        if(cc.sys.os == cc.sys.OS_ANDROID)
        {
            let listener = this._convertCallBackName(this._on_pay_callback.bind(this), "wx_pay")
            jsb.reflection.callStaticMethod(this._wx_plugin, "openShop", this._two_string_params, str_params, listener)
        }
    }
    protected _on_pay_callback(params)
    {
        let result = params
        if(cc.sys.os == cc.sys.OS_ANDROID)
        {
            if(result.code != 998)
            {
                if (result.code == 1) 
                {
                    //微信不存在
                }
                if (result.code == 2) 
                {
                    //微信api不存在
                } 
                if (result.code == 3) 
                { 
                    //取消
                }
                if (result.code == 4) 
                { 
                    //失败
                }
            } 
            else 
            {
                // 充值成功
                this._pay_cb()
            }
        }
    }
    protected _getAccessToken(wxcode)
    {
        let url = "https://api.weixin.qq.com/sns/oauth2/access_token?appid="
        url += this.appId + "&secret=" + this.secret + "&code=" + wxcode + "&grant_type=authorization_code"
        let xhr = new XMLHttpRequest()
        xhr.onreadystatechange = ()=>
        {
            if (xhr.readyState == 4 && (xhr.status >= 200 && xhr.status < 400)) 
            {
                let json = xhr.response
                if(json.errmsg)
                {
                    GHelper.showTip(json.errmsg)
                    GHelper.clearRunningTip() 
                }
                else
                {
                    this._getUserInfo(json.access_token, json.refresh_token, json.openid)
                }
            }
            else
            {
                GHelper.showTip("获取accessToken失败!")
                GHelper.clearRunningTip()
            }
        }
        xhr.responseType = "json"
        xhr.open("GET", url, true)
        xhr.send()
    }
    protected _refreshAccessToken(refreshToken) 
    {
        let url="https://api.weixin.qq.com/sns/oauth2/refresh_token?appid="
        url=url+this.appId
        url=url+"&grant_type=refresh_token"
        url=url+"&refresh_token="+refreshToken
        let xhr = new XMLHttpRequest()
        xhr.onreadystatechange = ()=>
        {
            if (xhr.readyState == 4 && (xhr.status >= 200 && xhr.status < 400)) 
            {
                let json = xhr.response
                this._getUserInfo(json.access_token, refreshToken, json.openid)
            }
            else
            {
                GHelper.showTip("获取accessToken失败!")
            }
        }
        xhr.responseType = "json"
        xhr.open("GET", url, true)
        xhr.send()
	}
	
	protected _checkAccessToken(accessToken,openId)
	{
        let url="https://api.weixin.qq.com/sns/auth?access_token=";
        url=url+accessToken
        url=url+"&openid="+openId
        let xhr = new XMLHttpRequest()
        xhr.onreadystatechange = ()=>
        {
            if (xhr.readyState == 4 && (xhr.status >= 200 && xhr.status < 400)) 
            {
                let json = xhr.response
                let errorCode=json.errcode
            }
        }
        xhr.responseType = "json"
        xhr.open("GET", url, true)
        xhr.send()
	}
	
    protected _getUserInfo(accessToken,refreshToken,openId) 
    {
        let url = "https://api.weixin.qq.com/sns/userinfo?access_token="
        url = url + accessToken + "&openid=" + openId
        let xhr = new XMLHttpRequest()
        xhr.onreadystatechange = ()=>
        {
            GHelper.clearRunningTip()
            if (xhr.readyState == 4 && (xhr.status >= 200 && xhr.status < 400)) 
            {
                let json = xhr.response
                if(json.errmsg)
                {
                    GHelper.showTip(json.errmsg)
                    GHelper.clearRunningTip() 
                }
                else
                {
                    let wxlm=new WeiXinLoginModel()
                    wxlm.code=0
                    wxlm.accessToken = accessToken
                    wxlm.refreshToken = refreshToken
                    wxlm.openId = openId
                    wxlm.unionId = json.unionid
                    wxlm.roleName = json.nickname
                    wxlm.avatar = json.headimgurl
                    wxlm.sex = json.sex
                    if(this._login_cb)
                    {
                        this._login_cb(wxlm)
                    }
                }
            }
            else
            {
                GHelper.showTip("获取userInfo失败!")
            }
        }
        xhr.responseType = "json"
        xhr.open("GET", url, true)
        xhr.send()
	}
    protected _convertCallBackName(fun, name) 
    {
        var funName = ""
        if (name)
        {
            console[name] = function(params)
            {
                fun(params)
            }
            funName = "console." + name
        } 
        else 
        {
            (<any>console).nativeToJsCallback = function(params)
            {
                fun(params)
            }
            funName = "console.nativeToJsCallback"
        }
        return funName
    }
}
GWXTool = new WeiXinTool()