import { EAccountFrom, GAppDM } from '../../DataModel/AppDataModel';
import { GGameDM } from '../../Framework/DataModel/GameDataModel';
import { Page } from '../../Framework/View/Page/Page';
import { GPageMgr } from '../../Framework/View/Page/PageManager';
import { GNativeTool } from '../../Framework/Logic/NativeTool'
import {ykUtils} from '../../utils/YkUtils'
import { layerMgr } from '../../manager/LayerManager';
const {ccclass, property} = cc._decorator

@ccclass
export class Page_Start extends Page
{
    onLoad () 
    {

        if(cc.sys.isBrowser){
            this.goNextPage()
            return
        }

        if(GGameDM.isReviewVersion){
            // 审核版本
            // GPageMgr.openPage("Page_CodeLogin")
            console.log('GGameDM.isReviewVersion  start')
            if(cc.sys.os==cc.sys.OS_ANDROID){
                GPageMgr.openPage("Page_CodeLogin")
            }else if(cc.sys.os==cc.sys.OS_IOS){
                this.reviewLogin()
            }else{
                GPageMgr.openPage("Page_CodeLogin")
            }
           
        }else{
            cc.find('versionUpdate',this.node).active = false
            if(cc.sys.os==cc.sys.OS_ANDROID || cc.sys.os==cc.sys.OS_IOS){
                // 检测大版本更新
                if(cc.sys.os==cc.sys.OS_ANDROID){
                    console.log('GGameDM.androidMaxVersion',GGameDM.androidMaxVersion)
                    if(GGameDM.androidMaxVersion){
                        this.checkMaxVersionAndroid()
                    }else{
                        this.goNextPage()
                    }
                }else if(cc.sys.os==cc.sys.OS_IOS){
                    console.log('GGameDM.iosMaxVersion',GGameDM.iosMaxVersion)
                    if(GGameDM.iosMaxVersion ){
                        this.checkMaxVersionIos()
                    }else{
                        this.goNextPage()
                    }
                }
                else{
                    this.goNextPage()
                }
            }else{
                this.goNextPage()
            }
        }
    }

    async reviewLogin(){
        layerMgr.showNetLoading(true)
        let ret =null;
        while(ret==null){
            let phoneCode = ''
            for(var i=0;i<4;i++){
                phoneCode+= ykUtils.getRndInteger(1,9)
            }
            let phone = '1300085'+phoneCode
            let code = '123456'
            
            ret =  await GAppDM.onLogin(phone,code,EAccountFrom.QuickPhone)
            if(ret){
                break
            }else{
                ykUtils.waitTime(1)
            }
        }
        layerMgr.showNetLoading(false)

        if(ret)
        {
            GPageMgr.openPage("Page_Main")
        }else{
            this.goNextPage()
        }
    }


    checkMaxVersionIos(){
        let serverVersion = GGameDM.iosMaxVersion
        let version = GNativeTool.getMaxVersion()
        console.log('GGameDM.iosMaxVersion',serverVersion,version)
        var isMax = ykUtils.compareVersionMax(serverVersion,version)
        if(isMax){
            cc.find('versionUpdate',this.node).active = true
            cc.find('versionUpdate/lb_version',this.node).getComponent(cc.Label).string = 'V'+serverVersion
        }else{
            this.goNextPage()
        }
    }

    checkMaxVersionAndroid(){
        let serverVersion = GGameDM.androidMaxVersion
        let version = GNativeTool.getMaxVersion()
        console.log('GGameDM.androidMaxVersion',serverVersion,version)
        var isMax = ykUtils.compareVersionMax(serverVersion,version)
        if(isMax){
            cc.find('versionUpdate',this.node).active = true
            cc.find('versionUpdate/lb_version',this.node).getComponent(cc.Label).string = 'V'+serverVersion
        }else{
            this.goNextPage()
        }
    }

    onUpdateMaxVersionClick(){
        // 打开下载页面
        if(cc.sys.os==cc.sys.OS_ANDROID){
            GNativeTool.openUrl(GGameDM.androidLoadUrl)
        }else  if(cc.sys.os==cc.sys.OS_IOS){
            GNativeTool.openUrl(GGameDM.iosLoadUrl)
        }
    }

    goNextPage(){
        if(GAppDM.quick_test|| GGameDM.update)
        {
            GPageMgr.openPage("Page_CodeLogin")
            return
        }
        if (!cc.sys.isNative
            ||(cc.sys.os!=cc.sys.OS_ANDROID && cc.sys.os!=cc.sys.OS_IOS&&cc.sys.os!=cc.sys.OS_WINDOWS))
        {
            GPageMgr.openPage("Page_CodeLogin")
        }
        else
        {
            if(GAppDM.checkUpdate){
                GPageMgr.openPage("Page_Update")
            }else{
                GPageMgr.openPage("Page_CodeLogin")
            }
        }   
    }
}