import {GHelper} from "../Logic/Helper"

const {ccclass, property} = cc._decorator

@ccclass
export class AlphaReverseTimerLabel extends cc.Component{
    _nums: {sp:cc.Sprite,str:string}[]
    _root:cc.Node
    @property({})
    selectedFont:number= 10
    @property({})
    num_spacing:number= -9
    _interval_handler:number
    _seconds:number= 0
    get seconds() 
    {
        return this._seconds
    }
    set seconds(value) 
    {
        if(this._seconds===value)
        {
            return
        }
        this._seconds = value
        if(this._isOnLoadCalled)//有尺寸表示已经onload过了现在立刻刷新，，否则等onload去处理就好
        {
            this._init(this._getNumStr())
        }
        if(this._interval_handler)//表示正在倒计时的过程中修改了数据所以要重新run
        {
            this.run()
        }
    }
    end_callback:any//时间为0的时候的回调
    _init_callback:any
    static create(seconds?:number,font_num?:number)
    {
        let rtl = <AlphaReverseTimerLabel>GHelper.createNode(AlphaReverseTimerLabel)
        rtl._seconds = seconds || 0
        rtl.selectedFont = font_num || 10
        return rtl
    }
    _getNumStr(num?:number):string
    {
        if(typeof num==="undefined")
        {
            num = this._seconds
        }
        let m:any = Math.floor(num/60)
        let s:any = num%60
        if(m<10)
        {
            m = "0"+m
        }
        if(s<10)
        {
            s = "0"+s
        }
        return m+"."+s
    }
    // use this for initialization
    onLoad() 
    {
        this._root = new cc.Node()
        this._root.parent = this.node
        this._init(this._getNumStr(0))
        this._changeTo(this._seconds)
    }
    _init(numStr)
    {
        this._root.removeAllChildren()
        let length = numStr.length
        let pre_path = "Image/Num/" + this.selectedFont + "_"
        let x = 0
        this._nums = []
        for(let i = 0; i<length; ++i)
        {
            let sp = GHelper.createSprite(pre_path+numStr[i])
            this._nums.push({sp:sp,str:numStr[i]})
            sp.node.parent = this._root
            if(i==0)
            {
                sp.node.x=x
                x = sp.node.width/2+this.num_spacing
            }
            else
            {
                sp.node.x=x+sp.node.width/2
                x += sp.node.width + this.num_spacing 
            }
        }
        if(this._init_callback)
        {
            this._init_callback()
        }
    }
    stop()
    {
        if(this._interval_handler)
        {
            clearInterval(this._interval_handler)
            this._interval_handler = null
        }
    }
    run()
    {
        if(this._isOnLoadCalled)
        {
            this._run()
        }
        else
        {
            this._init_callback = this._run.bind(this)
        }
    }
    _run()
    {
        this._init_callback = null
        let self = this
        let startTime = GHelper.getLocalTime()
        let endTime = startTime+this._seconds*1000
        if(this._interval_handler)
        {
            clearInterval(this._interval_handler)
            this._interval_handler = null
        }
        if(this._seconds<=0)
        {
            return
        }
        this._interval_handler = setInterval(function(){
            let curTime = GHelper.getLocalTime()
            let curNum = Math.floor((endTime-curTime)/1000)
            curNum = curNum>0?curNum:0
            if(curNum!=self._seconds)
            {
                self._changeTo(curNum)
                self._seconds = curNum
            }
            if(curNum<=0)
            {
                if(self.end_callback)
                {
                    if(self._interval_handler)
                    {
                        clearInterval(self._interval_handler)
                        self._interval_handler = null
                    }
                    self.end_callback()
                }
            }
        },1000)
    }
    _changeTo(num)
    {
        let numStr = this._getNumStr(num)
        let length = numStr.length
        for(let i = 0; i<length; ++i)
        {
            this._changeOne(numStr,i)
        }
    }
    _changeOne(numStr,i)
    {
        let str_num = numStr[i]
        let pre = this._nums[i]
        if(pre.str != str_num)
        {
            let pre_path = "Image/Num/" + this.selectedFont + "_"
            let sp = GHelper.createSprite(pre_path+str_num)
            this._nums[i] = {sp:sp,str:str_num}
            sp.node.parent = this._root
            sp.node.opacity = 0
            sp.node.x = pre.sp.node.x
            sp.node.runAction(cc.fadeTo(0.3,255))
            pre.sp.node.runAction(cc.sequence(
                                    cc.fadeTo(0.3,0),
                                    cc.removeSelf())
                                    )
        }
    }
    onDestroy()
    {
        if(this._interval_handler)
        {
            clearInterval(this._interval_handler)
            this._interval_handler = null
        }
    }
}