import { GGameDM } from "../../Framework/DataModel/GameDataModel";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GHallHttp } from "../../Net/HallHttp";
import { GAdSer } from "../../Service/AdService";
import { GMatchSer } from "../../Service/MatchService";
import { GNewsSer } from "../../Service/NewsService";
import { GMsgCenter } from "../../utils/MessageCenter";
import Widget_HomeAdsItem from "../Widget/Widget_HomeAdsItem";
import Widget_HomeNewsItem from "../Widget/Widget_HomeNewsItem";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_Home extends Page {

    @property(cc.PageView)
    pv_ads: cc.PageView = null;

    @property(cc.ScrollView)
    sv_news: cc.ScrollView = null;

    @property(cc.Prefab)
    pre_newsitem: cc.Prefab = null

    @property(cc.Prefab)
    pre_iditem: cc.Prefab = null

    @property(cc.Button)
    btn_exchange: cc.Button = null

    start() {
        if(GGameDM.isReviewVersion)
        {
            this.btn_exchange.node.parent.active=false
            this.sv_news.node.height+=this.btn_exchange.node.parent.height
        }
        this.initNews()
        this.initAds()
        GMatchSer.init()
        this.checkIsNew()
    }

    protected onEnable(): void {
        console.log('onEnable')
        this.checkIsNew()
    }

    async checkIsNew(){
        let res = await GHallHttp.request("getfollwers")
        if(res.newbies&&res.newbies.length>0){
            cc.find('layout_menu/btn_invitee/new1',this.node).active = true
        }else{
            cc.find('layout_menu/btn_invitee/new1',this.node).active = false
        }
    }

    async initNews()
    {
        await GNewsSer.init()
        let news = GNewsSer.news
        news.sort((a,b)=>
        {
            return b.create_time-a.create_time
        })
        for(let i=0;i<news.length;++i)
        {
            let item = cc.instantiate(this.pre_newsitem)
            item.parent = this.sv_news.content
            let sc = item.getComponent(Widget_HomeNewsItem)
            sc.init(news[i])
        }
        this.sv_news.scrollToTop()
    }
    async initAds()
    {
        await GAdSer.init()
        let ads = GAdSer.ads
        ads.sort((a,b)=>
        {
            return a.create_time-b.create_time
        })
        for(let i=0;i<ads.length;++i)
        {
            let item = cc.instantiate(this.pre_iditem)
            this.pv_ads.addPage(item)
            let sc = item.getComponent(Widget_HomeAdsItem)
            sc.init(ads[i])
        }
        this.pv_ads.scrollToLeft()
    }
    onClickSignupMatch() {
        GPageMgr.openPage("Page_MatchSignup")
    }

    onClickFocusMatch() {
        GPageMgr.openPage("Page_MatchFocus")
    }

    onClickExchange() {
        GMsgCenter.send("toexchange")
    }

    onClickInvitee() {
   
        GPageMgr.openPage("Page_Invitee",()=>{
            this.checkIsNew()
        })
    }

    onClickSign() {
        // GPageMgr.openPage("Page_Sign")
        if(GGameDM.isServerUpdate){
            GPageMgr.openPage("Page_Maintain");
            return 
        }
        GPageMgr.openPage("Page_NeiYe")
    }

    onClickInvite(){
        // GPageMgr.openPage("Page_Sign")
        // GPageMgr.openPage("Page_Invite_Friend")
    }
}
