import { GMatchSer, MatchInsModel, MatchModel } from "../../Service/MatchService";
import { GMsgCenter } from "../../utils/MessageCenter";

import { ClubModel ,GClubSer} from "../../Service/ClubService";
const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_CupItem extends cc.Component {

    @property(cc.Sprite)
    sp_icon: cc.Sprite = null

    @property(cc.Label)
    lbl_clubname: cc.Label = null

    @property(cc.Label)
    lbl_areaname: cc.Label = null

    private curData = null

    // @property(cc.Button)
    // btn_join: cc.Button = null

    onLoad()
    {
        
    }
    protected onDestroy(): void {
    }
    Btn_Enter_Click(event)
    {
        GMsgCenter.send("detail",  this.curData[1])
    }

    setdata(jsonData) {
        // this.lbl_clubname.string = jsonData.name
        this.curData = jsonData
        let clubId = this.curData[0]

        this.setClubData(clubId)
    }

    async setClubData(clubId){
        let clubData:ClubModel = await GClubSer.getClubDataById(clubId)
        if(clubData==null){
            this.lbl_clubname.string = '未查询到此俱乐部'
            this.lbl_areaname.string = 'xx'
        }else{
            this.lbl_clubname.string = clubData.title
            this.lbl_areaname.string = clubData.city
        }
    }

}
