import {Page} from "../View/Page/Page"
import {GPageMgr} from "../View/Page/PageManager"

const {ccclass, property} = cc._decorator

@ccclass
export class JoyStick extends cc.Component
{
    @property({type:cc.Node})
    node_bg: cc.Node=null

    @property({type:cc.Node})
    node_tip: cc.Node=null
    _radius:number=0
    _battle_page:Page
    _fun_callback:Function
    onLoad()
    {
        this._battle_page = GPageMgr.getPage("Page_Battle")
        this._radius = this.node_tip.x
        this.node_tip.active = false
        this.node_bg.on(cc.Node.EventType.TOUCH_START, this.onTouchEvent, this)
        this.node_bg.on(cc.Node.EventType.TOUCH_MOVE,this.onTouchEvent,this)
        this.node_bg.on(cc.Node.EventType.TOUCH_END, this.onTouchEvent, this)
        this.node_bg.on(cc.Node.EventType.TOUCH_CANCEL, this.onTouchEvent, this)
    }
    onTouchEvent(eventType)
    {
        let location = this.node_bg.convertToNodeSpace(eventType.touch.getLocation())
        location.x-=this.node_bg.width/2
        location.y-=this.node_bg.height/2
        let toward = cc.v2(location.x,location.y).normalize()
        let pos:cc.Vec2
        let angle = 0
        if(location.x==0)
        {
            if(location.y>0)
            {
                pos.x = 0
                pos.y = 1
                angle = 90
            }
            else
            {
                pos.x = 0
                pos.y = -1
                angle = -90
            }
        }
        else
        {
            pos.x = toward.x*this._radius
            pos.y = toward.y*this._radius
            angle = -Math.atan2(location.y,location.x) *180/Math.PI
        }
        this.node_tip.angle = angle
        this.node_tip.x = pos.x
        this.node_tip.y = pos.y
        if(eventType.type == "touchstart")
        {
            this.node_tip.active = true
        }
        else if(eventType.type == "touchmove")
        {
            if(this._fun_callback)
            {
                this._fun_callback("move",toward)
            }
        }
        else if(eventType.type == "touchend")
        {
            this.node_tip.active = false
            if(this._fun_callback)
            {
                this._fun_callback("stop")
            }
        }
        else if(eventType.type == "touchcancel")
        {
            this.node_tip.active = false
            if(this._fun_callback)
            {
                this._fun_callback("stop")
            }
        }
        return true
    }
    registerCallBack(fun:Function)
    {
        this._fun_callback = fun
    }
}