import { core } from "../../Framework/Core/Base";
import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { AddressItem, GUserMgr } from "../../Logic/UserManager";
import { GHallHttp } from "../../Net/HallHttp";
import Page_Address from "./Page_Address";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_EditAddress extends Page {

    @property(cc.EditBox)
    editReceiver: cc.EditBox = null;

    @property(cc.EditBox)
    editPhone: cc.EditBox = null;

    @property(cc.EditBox)
    editAddress: cc.EditBox = null;

    @property(cc.Button)
    btn_remove: cc.Button = null;

    @property(cc.Button)
    btn_confirm: cc.Button = null;
    protected _address:AddressItem=null
    onLoad() {
        this.btn_remove.node.active = false
        this.btn_confirm.node.active = false
    }
    onOpen(address:AddressItem)
    {
        this.btn_confirm.node.active = true
        if(!address)
        {
            this.btn_confirm.getComponentInChildren(cc.Label).string="添加新地址"
            return
        }
        this.btn_remove.node.active = true
        this._address=core.clone(address)
        this.editReceiver.string = this._address.name
        this.editPhone.string = this._address.phone
        this.editAddress.string = this._address.address
    }
    onEditReceiver() {
        this.editReceiver.string = this.editReceiver.string.trim()
    }

    onEditPhone() {
        this.editPhone.string = this.editPhone.string.trim()
    }

    onEditAddress() {
        this.editAddress.string = this.editAddress.string.trim()
    }

    async Btn_Adress_OnClick() {
        if(this._address)
        {
            this._address.name=this.editReceiver.string
            this._address.phone=this.editPhone.string
            this._address.address=this.editAddress.string
            let res = await GHallHttp.request("updateaddressinfo",{address:this._address})
            if(res.errcode)
            {
                GHelper.showTip(res.errcode.des)
                return
            }
            GUserMgr.addOneUser(res.user)
        }
        else
        {
            let address = new AddressItem()
            address.name=this.editReceiver.string
            address.phone=this.editPhone.string
            address.address=this.editAddress.string
            address.create_time=Date.now()
            let res = await GHallHttp.request("addaddressinfo",{address:address})
            if(res.errcode)
            {
                GHelper.showTip(res.errcode.des)
                return
            }
            GUserMgr.addOneUser(res.user)
        }
        let page = GPageMgr.getPage("Page_Address") as Page_Address
        page?.refresh()
        GPageMgr.closePage("Page_EditAddress")
    }
    Btn_Return_OnClick(event)
    {
        GPageMgr.closePage("Page_EditAddress")
    }
    async Btn_Remove_OnClick(event)
    {
        let res = await GHallHttp.request("removeaddressinfo",{create_time:this._address.create_time})
        if(res.errcode)
        {
            GHelper.showTip(res.errcode.des)
            return
        }
        GUserMgr.addOneUser(res.user)
        let page = GPageMgr.getPage("Page_Address") as Page_Address
        page?.refresh()
        GPageMgr.closePage("Page_EditAddress")
    }
}
