import { GWXTool } from "../Logic/WeiXinTool";

export enum EGameState
{
    Login=0,
    Logining,
    Entering,
    Main,
    OX,
}

export let GGameDM:GameDataModel = null


class GameDataModel
{
    isLAN = false//是否是局域网
    isDev = false//是否开发模式
    update=false

  

    protected _pre_state = EGameState.Entering//前个状态
    protected _state = EGameState.Entering//游戏状态
    get state()
    {
        return this._state
    }

    protected _match_is_verification:any= [];
    get match_is_verification(){
       return   this._match_is_verification;
    }
    set match_is_verification(value:any){
        this._match_is_verification = value
    }

    protected _pass_word:any= "";
    get pass_word(){
       return   this._pass_word;
    }
    set pass_word(value:any){
        this._pass_word = value
    }


    protected _server_list:any= [];
    get server_list(){
       return   this._server_list;
    }
    set server_list(value:any){
        this._server_list = value
    }


    set state(value)
    {
        this._pre_state = this._state
        this._state = value
    }
    protected _androidMaxVersion= null
    get androidMaxVersion(){
       return   this._androidMaxVersion
    }
    set androidMaxVersion(value:string){
        this._androidMaxVersion = value
    }


    protected _isServerUpdate= null
    get isServerUpdate(){
       return   this._isServerUpdate
    }
    set isServerUpdate(value:string){
        this._isServerUpdate = value
    }

    protected _iosMaxVersion= null
    get iosMaxVersion(){
       return   this._iosMaxVersion
    }
    set iosMaxVersion(value:string){
        this._iosMaxVersion = value
    }

    protected _iosLoadUrl=''
    get iosLoadUrl(){
        return   this._iosLoadUrl
     }
     set iosLoadUrl(value:string){
         this._iosLoadUrl = value
     }


    protected _androidLoadUrl=''
    get androidLoadUrl(){
        return   this._androidLoadUrl
     }
     set androidLoadUrl(value:string){
         this._androidLoadUrl = value
     }

    //和服务器的时间差
    protected _diff_server_time=100000
    get diffServerTime()
    {
        return this._diff_server_time
    }
    set diffServerTime(value)
    {
        this._diff_server_time = value
    }
    get serverTime()
    {
        let dt = Date.now()+this._diff_server_time
        return dt
    }
    //最近心跳延迟时间
    protected _delay_time=0
    get delayTime()
    {
        return this._delay_time
    }
    set delayTime(value)
    {
        this._delay_time = value
    }
    protected _authtype=-1
    get authType()
    {
        return this._authtype
    }
    set authType(at)
    {
        this._authtype = at
    }
    // protected _version="1.4.755"//当前版本号
    protected _version="1.3.34"//当前版本号
    get version()
    {
        return this._version
    }
    set version(vs)
    {
        this._version = vs
    }
    protected _review_version="1.3.34"//当前评审版本号
    set review_version(vs)
    {
        this._review_version = vs
    }
    get isReviewVersion()
    {
        console.log('this._version',this._version,this._review_version)
        // 检测android 审核
        // if(cc.sys.os==cc.sys.OS_ANDROID){
        //     if(this._review_version == '1.3.32'){
        //         return true
        //     }
        // }

        // return true

        if(cc.sys.os!=cc.sys.OS_IOS)
        {
            return false
        }
        return this._version == this._review_version
    }
    protected _auth:{host:string,port:number}=null
    get auth()
    {
        return this._auth
    }
    set auth(value:{host:string,port:number})
    {
        this._auth=value
    }
    protected _res_url=""
    get resUrl()
    {
        return this._res_url
    }
    set resUrl(value)
    {
        this._res_url=value
    }
    restoreGameState()
    {
        this._state = this._pre_state
    }
    isState(state:EGameState)
    {
        return this._state == state
    }


    protected _arrayInvite
    get arrayInvite(){
        return this._arrayInvite
    }
    set arrayInvite(data){
        this._arrayInvite = data
    }

    protected _arrayInviteNei
    get arrayInviteNei(){
        return this._arrayInviteNei
    }
    set arrayInviteNei(data){
        this._arrayInviteNei = data
    }
    
    
}
GGameDM = new GameDataModel()