import { GAppDM } from "../../DataModel/AppDataModel";
import { GGameDM } from "../../Framework/DataModel/GameDataModel";
import { Ajust } from "../../Framework/Extention/Ajust";
import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../Logic/UserManager";
import { layerMgr } from "../../manager/LayerManager";
import { resMgr } from "../../manager/ResourceManager";
import { GMsgCenter } from "../../utils/MessageCenter";
import { EBagItemState, EItemType, GItemCfg } from "../../Service/ItemService";
import { ykUtils } from "../../utils/YkUtils";
const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_My extends Page {

    @property(cc.Sprite)
    imgAvatar: cc.Sprite = null;

    @property(cc.Label)
    labelNickname: cc.Label = null;

    @property(cc.Label)
    labelUid: cc.Label = null;

    @property(cc.Sprite)
    imgAuth: cc.Sprite = null;

    @property(cc.Label)
    labelScore: cc.Label = null;

    @property(cc.Label)
    labelCoin: cc.Label = null;


    @property(cc.Label)
    labelMoney: cc.Label = null;

    @property(cc.Label)
    labelTicket: cc.Label = null;

    @property(cc.Label)
    lbl_version: cc.Label = null

    @property(cc.Node)
    node_func: cc.Node = null

    @property(cc.Label)
    label_checkAuth: cc.Label = null
    onLoad() {
        GMsgCenter.register("updateUserInfo", this.updateUserInfo.bind(this), this);
        GMsgCenter.register("changelogo", this.changelogo.bind(this), this)
        GMsgCenter.register("VerificationSuccess", this.verify.bind(this), this)
        let user = GUserMgr.self_user
        let suffix: string = "image/head/" + user.logo;
        let path = `${suffix}`;

        resMgr.loadImage(path, (res) => {
            cc.isValid(this.imgAvatar) && (this.imgAvatar.getComponent(cc.Sprite).spriteFrame = res);
        })
    }

    changelogo(num) {
        let suffix: string = "image/head/" + num;
        let path = `${suffix}`;

        resMgr.loadImage(path, (res) => {
            cc.isValid(this.imgAvatar) && (this.imgAvatar.getComponent(cc.Sprite).spriteFrame = res);
        })
    }

    start() {
        if(GGameDM.isReviewVersion)
        {
            this.node_func.active=false
        }
        // let user = GUserMgr.self_user
        // this.labelNickname.string = user.nickname;
        // this.labelUid.string = user.id+""
        // this.labelScore.string = (user.coin||0)+""
        // this.labelMoney.string = (user.reward_coin||0)+""
        // // this.labelTicket.string = (user.ticket||0)+""
        // let auth = !ykUtils.isEmpty(user.nickname)  && !ykUtils.isEmpty(user.idcard_no) ? 1 : 0;
        // GHelper.createSpriteFrame("View/Page/Res/main/person/img_auth_" + auth, (sf) => {
        //     cc.isValid(this.imgAuth) && (this.imgAuth.spriteFrame = sf)
        // })
        // this.lbl_version.string=GGameDM.version//+this.getComponentInChildren(Ajust).info

        this.updateUserInfo()
      
    }


    protected onDestroy(): void {
        GMsgCenter.unregister("updateUserInfo", this);
        GMsgCenter.unregister("changeHead", this);
        GMsgCenter.unregister("VerificationSuccess", this);
    }

    public updateUserInfo() {
        let user = GUserMgr.self_user
        this.labelNickname.string = user.nickname
        this.labelScore.string = (user.coin||0)+""
        this.labelMoney.string = (user.reward_coin||0)+""
        this.labelUid.string = user.id+""

        // 这个值有可能为空
        if(user.video_coin == null){
            user.video_coin = 0
        }
        this.labelCoin.string =user.video_coin+''

        let auth = !ykUtils.isEmpty(user.nickname)  && !ykUtils.isEmpty(user.idcard_no) ? 1 : 0;
        GHelper.createSpriteFrame("View/Page/Res/main/person/img_auth_" + auth, (sf) => {
            cc.isValid(this.imgAuth) && (this.imgAuth.spriteFrame = sf)
        })
        this.lbl_version.string=GGameDM.version
        if(auth){
            this.label_checkAuth.string = '已认证'
            // cc.find('layout_middle/btn_sign',this.node).getComponent(cc.Button).interactable = false
        }else{
            this.label_checkAuth.string = '未认证'
        }

         // 初始化我的卡券数量
        let dataList = []
        let bagItems = GItemCfg.bagItems
        bagItems.forEach(element => {
            if(element.state==EBagItemState.None){
                dataList.push(element)
            }
        });
        this.labelTicket.string = dataList.length+""

    }

    protected onEnable(): void {
        // let user = GUserMgr.self_user
        // this.labelScore.string = (user.coin||0)+""
        // this.labelMoney.string = (user.money||0)+""
        // // this.labelTicket.string = (user.ticket||0)+""
        // this.labelMoney.string = (user.reward_coin||0)+""
        this.refreshUser()
       
    }

    async refreshUser(){
        await GUserMgr.refreshSelfUser()
        this.updateUserInfo()
    }   

    onClickPerson() {
        // let node = GHelper.screenShot(null);
        // node.setPosition(50, 0);
        // node.parent = this.node;
        if(GGameDM.isReviewVersion)
        {
            return
        }
        let user = GUserMgr.self_user
        let auth = user.nickname && user.idcard_no ? 1 : 0;
        //auth = 1;
        if (auth==1) {
            return;
        }

        GPageMgr.openPage("Page_Person")
    }

    onClickNotice() {
        GPageMgr.openPage("Page_Notice")
    }

    onClickSetting() {
        GPageMgr.openPage("Page_Setting")
    }

    onClickInvite() {
        // GPageMgr.openPage("Page_Invite")
        GPageMgr.openPage("Page_NeiYe")
    }

    onClickSign() {
        let user = GUserMgr.self_user
        let auth = !ykUtils.isEmpty(user.nickname)  &&  !ykUtils.isEmpty(user.idcard_no)
        if(!auth){
            GPageMgr.openPage("Page_IDCard")
        }else
        {
            GHelper.showTip('已实名认证')
        }
    }

    onClickCharge() {
        GPageMgr.openPage("Page_Charge")
    }

    onClickReceivingAddress() {
        GPageMgr.openPage("Page_Address")
    }

    onClickMyPrize() {
        GPageMgr.openPage("Page_MyPrize")
    }

    onClickCustomerService() {
        GPageMgr.openPage("Page_CustomerService")
    }

    onClickInvitationCode() {
        GPageMgr.openPage("Page_Bind")
    }

    verify() {
        this.updateUserInfo()
    }
}
