
class ResourceManager {

    constructor() {
    }

    public preload(bundle: string, onProgress?: (finish, total, item) => void, onComplete?: (err, items) => void) {
        cc.assetManager.loadBundle(String(bundle), (err, res) => {
            if (!err) {
                res.preloadDir("", onProgress, onComplete)
            }
        })
    }

    private load(bundle: string, path: number | string, type: typeof cc.Asset, success: Function, fail: Function) {
        cc.assetManager.loadBundle(String(bundle), (err, res) => {
            if (err) {
                console.log("load bundle fail", err);
                fail && fail();
                return;
            }

            res.load(String(path), type, (err, res) => {
                if (err) {
                    fail && fail();
                    return;
                }
                success && success(res);
            })
        })
    }

    private loadRes(path: string, type: typeof cc.Asset, success?: Function, fail?: Function) {
        cc.resources.load(path, type, (err, res) => {
            if (err) {
                fail && fail();
                return;
            }
            success && success(res);
        })
    }

    public loadImage(path: string, success: (res: cc.SpriteFrame) => void, fail?: () => void) {
        this.loadRes(path, cc.SpriteFrame, success, fail);
    }

    public loadCard(type: number, id: number, success: (res: cc.SpriteFrame) => void, fail?: () => void) {
        this.loadRes("image/card/" + type + "/" + id, cc.SpriteFrame, success, fail);
    }

    public loadProp(id: number, success: (res: cc.SpriteFrame) => void, fail?: () => void) {
        this.loadRes("image/prop/" + id, cc.SpriteFrame, success, fail);
    }

    public loadGoods(id: number, success: (res: cc.SpriteFrame) => void, fail?: () => void) {
        this.load("goods", id, cc.SpriteFrame, success, fail);
    }
    
    public loadAudio(path: string, success: (res: cc.AudioClip) => void, fail?: () => void) {
        this.load("audio", path, cc.AudioClip, success, fail);
    }

    public loadPrefab(path: string, success: (res: cc.Prefab) => void, fail?: () => void) {
        this.load("prefab", path, cc.Prefab, success, fail);
    }
}

export const resMgr: ResourceManager = new ResourceManager();