import { ItemInfo, PropConfig } from "../data/data";
import { resMgr } from "./ResourceManager";

import { GPageMgr } from "../Framework/View/Page/PageManager";
export let  layerMgr: LayerManager = null


class LayerManager {
    private layers: { [key: string]: cc.Node } = {};
    private loading: boolean = false;
    private beginTimeOut = null
    private closeTimeOut = null
    private qzCloseTimeOut = null //强制清理

    constructor() {
    }

    loadPrefab(name: string, callback?: (node: cc.Node) => void) {
        resMgr.loadPrefab(name, (res) => {
            let node = this.layers[name];

            if (!cc.isValid(node)) {
                node = cc.instantiate(res);
                this.layers[name] = node;
            }

            callback && callback(node);
        })
    }

    showPersist(name: string, ...args) {
        console.log("show persist: " + name);
        this.loadPrefab(name, (node) => {
            node.parent =  cc.director.getScene();
            node.active = true;
            node.setSiblingIndex(-1);
            node.emit("init", ...args);

            if (!cc.game.isPersistRootNode(node)) {
                cc.game.addPersistRootNode(node);
            }
        })
    }

    showLayer(name: string, ...args) {
        console.log("show layer: " + name);
        this.loadPrefab(name, (node) => {
            node.parent = cc.Canvas.instance.node;
            node.active = true;
            node.setSiblingIndex(-1);
            node.emit("init", ...args);
        })
    }

    showLoading() {
        this.loading = true;
        this.loadPrefab("loading", (node) => {
            node.parent = cc.Canvas.instance.node;
            node.active = this.loading;
            node.setSiblingIndex(-1);
        })
    }

    hideLoading() {
        this.loading = false;
        this.layers["loading"] && (this.layers["loading"].active = this.loading);
    }



    private loadingIdx = 0
    private beginTime = 0



    showNetLoading(isShow,showLabel:string='',timeOut = 5000){
        console.log('showNetLoading',isShow)
        

        var clearQzgb = ()=>{
            if(this.qzCloseTimeOut){
                clearTimeout(this.qzCloseTimeOut)
                this.qzCloseTimeOut = null
            }
        }

        var qzGbTimeOut = ()=>{
            this.qzCloseTimeOut = setTimeout(()=>{
                clearQzgb()
                GPageMgr.closePage('Page_Loading')
                console.log('showNetLoading close Page 4')
            },timeOut)
        }

       
        // 0.5秒未响应 开始弹出
        if(isShow){
            // this.loadingIdx +=1
    
            if(!this.beginTimeOut){
                this.beginTimeOut = setTimeout(async ()=>{
                    this.beginTime = new Date().getTime()
                    if(this.closeTimeOut){
                        // 说明界面还在显示
                        clearTimeout(this.closeTimeOut)
                        this.closeTimeOut = null
                        this.beginTimeOut = null
                    }else{
                        console.log('showNetLoading open page')
                        let page = await GPageMgr.openPage('Page_Loading')
                        page['node']['zIndex'] = 1000 
                        cc.find("netlabel",page['node']).getComponent(cc.Label).string = showLabel
                        if(this.beginTimeOut == null){
                            GPageMgr.closePage('Page_Loading')
                            console.log('showNetLoading close Page 0')
                        }
                        this.beginTimeOut = null
                    }
                    clearQzgb()
                    //五秒后强制关闭
                    qzGbTimeOut()
                },500)
            }
        }else{
            clearQzgb()
            if(this.beginTimeOut){
                clearTimeout(this.beginTimeOut)
                this.beginTimeOut = null
                console.log('showNetLoading close Page 1')
                GPageMgr.closePage('Page_Loading')
        
            }else{
                let endTime = new Date().getTime()
                if(endTime-this.beginTime>0.3*1000){
                    console.log('showNetLoading close Page 2')
                    GPageMgr.closePage('Page_Loading')
                }else{
                    this.closeTimeOut = setTimeout(()=>{
                        console.log('showNetLoading close Page 3')
                        GPageMgr.closePage('Page_Loading')
                        this.closeTimeOut = null
                    },300)
                }
          
            }
        }
    }


    showToast(toast: string | string[]) {
        this.showPersist("toast", toast);
    }

    showTips(tips: string, buttons?: {label: string, video?, callback?}[]) {
        this.showLayer("tips_layer", tips, buttons);
    }

    showReward(rewards: ItemInfo[]) {
        // this.showLayer(LAYER.REWARD_LAYER, rewards);
        // let toasts = configMgr.getItemsDesc(rewards)
        // this.showToast(toasts);
    }

    showPropGet(id: number, count: number, pos: cc.Vec3, callback?: Function) {
        //this.showLayer(LAYER.PROP_GET, id, count, pos, callback);
    }

    showPropCost(id: number, count: number, pos: cc.Vec3, callback?: Function) {
        //this.showLayer(LAYER.PROP_COST, id, count, pos, callback);
    }

    showRewardProp(id: number, count: number, posStart: cc.Vec3, posEnd: cc.Vec3) {
        //this.showLayer(LAYER.REWARD_PROP, id, count, posStart, posEnd);
    }

    showNotEnough(id: number, count: number) {
        // let config = configMgr.getConfig(PropConfig, id);
        // this.showToast(config.name + "数量不足");
        // this.showLayer(LAYER.SHOP_LAYER);
        //this.showLayer(LAYER.PROP_FILL, id, count);
    }

    showWebview(url: string) {
        //this.showLayer(LAYER.WEBVIEW_LAYER, url);
    }

    showVideo(url: string) {
        //this.showLayer(LAYER.VIDEO_LAYER, url);
    }
}

layerMgr = new LayerManager();