import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { ykUtils } from "../../utils/YkUtils";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_CustomerService extends Page {

    @property(cc.Label)
    labelWechat: cc.Label = null;

    @property(cc.Sprite)
    imgQrcode: cc.Sprite = null;
    
    start() {
        this.initKefu()
    }

    async initKefu(){
        let sp = await ykUtils.loadNetSpriteFrame(ykUtils.getKefu())
        if(sp){
            cc.find('content/layout/kf',this.node).getComponent(cc.Sprite).spriteFrame = sp
        }
    }
    Btn_Close_OnClick(event)
    {
        GPageMgr.closePage("Page_CustomerService")
    }
}
