import Card from "../game/play/Card";
import { ECardType, ETexasAction } from "../../Config/_ini_";
import { ETexasSeatState, TexasRecordSeatInfo } from "../../DataModel/TexasTableDataModel";
import { GUserMgr } from "../../Logic/UserManager";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ReViewItem extends cc.Component {

    @property(cc.Label)
    yourname: cc.Label = null;

    @property(cc.Label)
    cardtype: cc.Label = null;

    @property(cc.Node)
    card0: cc.Node = null;

    @property(cc.Node)
    card1: cc.Node = null;

    @property(cc.Node)
    card2: cc.Node = null;

    @property(cc.Node)
    card3: cc.Node = null;

    @property(cc.Node)
    card4: cc.Node = null;

    @property(cc.Node)
    card5: cc.Node = null;

    @property(cc.Node)
    card6: cc.Node = null;

    @property(cc.Label)
    reward: cc.Label = null;

    cards: cc.Node[]= [];

    @property([cc.Node])
    actions: cc.Node[]=[];

    onLoad() {
        this.cards.push(this.card0);
        this.cards.push(this.card1);
        this.cards.push(this.card2);
        this.cards.push(this.card3);
        this.cards.push(this.card4);
        this.cards.push(this.card5);
        this.cards.push(this.card6);
    }

    setdata(jsonData) {
        let userrecord:TexasRecordSeatInfo=jsonData.userrecord
        this.yourname.string = userrecord.playerinfo.nickname
        if(userrecord.showcards!=3)
        {
            userrecord.cardtype=ECardType.None
        }
        switch (userrecord.cardtype) {
            case ECardType.None:
                this.cardtype.string = ""
                break;
            case ECardType.High:
                this.cardtype.string = "高牌"
                break;
            case ECardType.OnePair:
                this.cardtype.string = "一对"
                break;
            case ECardType.TwoPair:
                this.cardtype.string = "两对"
                break;
            case ECardType.Tribble:
                this.cardtype.string = "三条"
                break;
            case ECardType.Flush:
                this.cardtype.string = "顺子"
                break;
            case ECardType.Straight:
                this.cardtype.string = "同花"
                break;
            case ECardType.FullHouse:
                this.cardtype.string = "葫芦"
                break;
            case ECardType.Quadruple:
                this.cardtype.string = "四条"
                break;
            case ECardType.StraightFlush:
                this.cardtype.string = "同花顺"
                break;
            case ECardType.RoyalFlush:
                this.cardtype.string = "皇家同花顺"
                break;

            default:
                break;
        }

        // export enum ETexasAction {
        //     None = 0,
        //     Raise,
        //     Call,
        //     Allin,
        //     Check,
        //     Fold
        // }

        for (let index = 0; index < this.actions.length; index++) {
            const element = this.actions[index];
            element.getComponent(cc.Label).string = ""
        }

        for (let index = 0; index < userrecord.actions.length; index++) {
            const element = userrecord.actions[index].action;
            if (element == ETexasAction.None) {
                break
            }
            else
            {
                if (element == ETexasAction.Check) {
                    this.actions[index].getComponent(cc.Label).string = "看牌";
                }
                else if (element == ETexasAction.Call) {
                    this.actions[index].getComponent(cc.Label).string = "跟注" + " " + userrecord.actions[index].bet;
                }
                else if (element == ETexasAction.Fold) {
                    this.actions[index].getComponent(cc.Label).string = "弃牌";
                }
                else if (element == ETexasAction.Raise) {
                    this.actions[index].getComponent(cc.Label).string = "加注" + " " + userrecord.actions[index].bet;
                }
                else if (element == ETexasAction.Allin) {
                    this.actions[index].getComponent(cc.Label).string = "" + " " + userrecord.actions[index].bet;
                }
            }
        }

        if (userrecord.dtpoint > 0) {
            this.reward.string = "+" + userrecord.dtpoint;
            this.reward.node.color = new cc.Color().fromHEX("#EC4F30")
        }
        else
        {
            this.reward.string = userrecord.dtpoint+"";
            this.reward.node.color = new cc.Color().fromHEX("#EC9630")
        }
        let isself = GUserMgr.self_user_id==userrecord.playerinfo.id

        for (let i = 0; i < 7; i++) {
            this.cards[i].getComponent(Card).clearContent()
            this.cards[i].getComponent(Card).setFace(false)
            
            this.cards[i].getComponent(Card).updateCardBack1();
        }
        let show_all = (userrecord.state!=ETexasSeatState.Fold)
        // //手牌
        if (userrecord.cards[0]!=null&&(userrecord.showcards==1 || userrecord.showcards== 3 ||isself/*||userrecord.negative*/)) {
            this.card0.getComponent(Card).setFace(true)
            this.card0.getComponent(Card).setReviewContent(userrecord.cards[0])
        }

        if (userrecord.cards[1]!=null&&(userrecord.showcards==2 || userrecord.showcards== 3 ||isself/*||userrecord.negative*/)) {
            this.card1.getComponent(Card).setFace(true)
            this.card1.getComponent(Card).setReviewContent(userrecord.cards[1])
        }
        let acion_len = userrecord.actions.length
        if (jsonData.publiccards[0]!=null&&(acion_len>=2||show_all)) {
            this.card2.getComponent(Card).setFace(true)
            this.card2.getComponent(Card).setReviewContent(jsonData.publiccards[0])
        }

        if (jsonData.publiccards[1]!=null&&(acion_len>=2||show_all)) {
            this.card3.getComponent(Card).setFace(true)
            this.card3.getComponent(Card).setReviewContent(jsonData.publiccards[1])
        }

        if (jsonData.publiccards[2]!=null&&(acion_len>=2||show_all)) {
            this.card4.getComponent(Card).setFace(true)
            this.card4.getComponent(Card).setReviewContent(jsonData.publiccards[2])
        }

        if (jsonData.publiccards[3]!=null&&(acion_len>=3||show_all)) {
            this.card5.getComponent(Card).setFace(true)
            this.card5.getComponent(Card).setReviewContent(jsonData.publiccards[3])
        }

        if (jsonData.publiccards[4]!=null&&(acion_len>=4||show_all)) {
            this.card6.getComponent(Card).setFace(true)
            this.card6.getComponent(Card).setReviewContent(jsonData.publiccards[4])
        }
    }
}
