import { GAudioTool } from './../Logic/AudioTool';
import { GHelper } from './../Logic/Helper';
import { GEmotionDataModel } from './../DataModel/EmotionDataModel';
export class MutualEmotion extends cc.Component
{
    onLoad()
    {

    }
    fly(index,from,to)
    {
        from = this.node.parent.convertToNodeSpace(from)
        to = this.node.parent.convertToNodeSpace(to)
        this.node.position = from
        let data = GEmotionDataModel.Mutual[index]
        let fly = data.fly
        let ani = data.ani
        let fly_node = GHelper.createSprite(fly,(sp)=>
        {
            let actions=
            [
                cc.moveTo(0.3,to),
                cc.callFunc(()=>
                {
                    let ani_obj = GHelper.createAnimation(ani,()=>
                    {
                        this.node.removeFromParent()
                    })
                    ani_obj.node.parent = this.node
                    ani_obj.node.x=40
                    ani_obj.node.y=40
                    sp.node.active = false
                })
            ]
            this.node.runAction(cc.sequence(actions))
        })
        fly_node.node.parent = this.node

        this.node.runAction(cc.sequence(
            cc.delayTime(data.audio_delay),
            cc.callFunc(()=>
            {
                GAudioTool.playVoice(data.audio)
            })
        ))
    }
}