const {ccclass, property} = cc._decorator;

import {ykUtils} from '../../utils/YkUtils'


@ccclass
export default class Widget_ClubShaiXuanItem extends cc.Component {

    @property(cc.Node)
    maxDirect: cc.Node = null

    @property(cc.Node)
    city: cc.Node = null
    
    private cityNodes:cc.Node[] = []
    private delegate = null
    public isOpen = false

    onLoad()
    {
        
    }

    setDelegate(delegate){
        this.delegate = delegate
    }
    setData(direct,citys){
        cc.find('lb_directname',this.maxDirect).getComponent(cc.Label).string = direct
        ykUtils.addClickEventListener(this.node,this.maxDirect.getComponent(cc.Button),'Widget_ClubShaiXuanItem','onDirectClick',direct)
        this.cityNodes.push(this.city)
        for(let i=0;i< citys.length-1;i++){
            let node = cc.instantiate(this.city)
            this.cityNodes.push(node)
            this.node.addChild(node)
        }

        for(var k in citys){
            cc.find('lb_cityName', this.cityNodes[k]).getComponent(cc.Label).string = citys[k]
            // 绑定点击事件
            ykUtils.addClickEventListener(this.node,this.cityNodes[k].getComponent(cc.Button),'Widget_ClubShaiXuanItem','onCityClick',citys[k])
        }
    }


    setIsOpen(isOpen){
        if(isOpen){
            this.setShowGray(this.maxDirect,true) 
        }else{
            this.setShowGray(this.maxDirect,true) 
        }

        this.cityNodes.forEach((node)=>{
            node.active = isOpen
            if(isOpen== false){
                this.setShowGray(node,false)
            }
        })
    }

    setCityOpen(city){
        this.cityNodes.forEach((nn)=>{
            if(cc.find('lb_cityName', nn).getComponent(cc.Label).string==city){
                this.setShowGray(nn,true)
            }else{
                this.setShowGray(nn,false)
            }
        })
    }

    onDirectClick(event,eventData){
        this.delegate.onDirectChoice(this.node)
    }
    onCityClick(event,eventData){
        // console.log(eventData)
        this.setCityOpen(eventData)
        this.delegate.onCityChoice(this.node,eventData)
    }

    setShowGray(nn,isShowGray){
        cc.find('back_gray',nn).active = isShowGray
        cc.find('back_black',nn).active = !isShowGray
        
    }

  
}