import { GAppDM } from "../../DataModel/AppDataModel";
import { GHelper } from "../../Framework/Logic/Helper";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GNetGameTexas } from "../../Net/NetGameTexas";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_Logout extends cc.Component {

    onLoad(){
        
    }
    onClickConfirm() {
        GNetGameTexas.close()
        GHelper.clearData()
        GPageMgr.closeAll()
        
        if(GAppDM.isShowServerList)
        {
            GPageMgr.openPage("Page_ServerList");
        }
        else
        {
            GPageMgr.openPage("Page_CodeLogin")
        }
    }

    onClickCancel() {
        GPageMgr.closePage("Page_Logout");
    }
}
