import { core } from "../../Framework/Core/Base";
import { GHelper } from "../../Framework/Logic/Helper";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { NewsModel } from "../../Service/NewsService";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_HomeNewsItem extends cc.Component {

    @property(cc.Label)
    labelTitle: cc.Label = null

    @property(cc.Label)
    labelTime: cc.Label = null

    @property(cc.Label)
    labelType: cc.Label = null

    @property(cc.Sprite)
    imgIcon: cc.Sprite = null

    protected _news:NewsModel = null

    init(news:NewsModel) {
        this._news=news
        this.labelTitle.string = news.title
        if(this._news.subtitle)
        {
            this.labelTime.string = this._news.subtitle
        }
        else
        {
            let str =""
            let dt = new Date(this._news.create_time)
            let cdt = new Date()
            if(cdt.getFullYear()!=dt.getFullYear())
            {
                str = core.format(dt,"YYYYY-M-D HH:mm")
            }
            else if(cdt.getMonth()==dt.getMonth()&&cdt.getDate()==dt.getDate())
            {
                str = core.format(dt,"HH:mm")
            }
            else
            {
                str = core.format(dt,"M-D HH:mm")
            }
            this.labelTime.string = str
        }
        this.labelType.string = this._news.type
        GHelper.createSpriteFrame(this._news.icon,(sf)=>
        {
            this.imgIcon.spriteFrame=sf
        })
    }

    Btn_Self_OnClick(event) {
        if(this._news.url.startsWith("http"))
        {
            GPageMgr.openPage("Page_WebView",this._news.url)
        }
    }
}
