import { GAppDM } from "../../DataModel/AppDataModel";
import { core } from "../../Framework/Core/Base";
import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GAccountHttp } from "../../Net/AccountHttp";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ResetPasswordLayer extends Page {

    @property(cc.EditBox)
    editPhone: cc.EditBox = null
    @property(cc.Label)
    lbl_phonetip:cc.Label = null

    @property(cc.EditBox)
    editCode: cc.EditBox = null

    @property(cc.EditBox)
    editPassword: cc.EditBox = null

    @property(cc.EditBox)
    editPasswordConfirm: cc.EditBox = null

    @property(cc.Label)
    btnCode: cc.Label = null;

    start() {
        this.lbl_phonetip.node.active=false
        this.editPassword.node.parent.getChildByName("tips").active=false
        this.editPasswordConfirm.node.parent.getChildByName("tips").active=false
    }

    async onClickGetCode() {
        let phone = this.editPhone.string;
        if (!core.isPhoneNo(phone)) {
            GHelper.showTip("请输入正确的手机号")
            return
        }
        let remain = GAppDM.last_get_code ? GAppDM.last_get_code + 60000 - Date.now() : 0
        if (remain > 0) {
            return
        }
        let res = await GAccountHttp.request("getphonecode",{phone:phone,why:"resetpassword"})
        if(res.errcode)
        {
            GHelper.showTip(res.errcode.des)
        }
        else
        {
            GHelper.showTip("验证码获取成功,请注意查收")
            GAppDM.last_get_code=Date.now()
        }
    }
    protected update(dt: number){
        let remain = GAppDM.last_get_code ? GAppDM.last_get_code + 60000 - Date.now() : 0
        if (remain < 0) {
            remain = 0
        }
        remain = Math.floor(remain / 1000)
        this.btnCode.string = "获取验证码" + (remain ? `(${remain})` : "")
    }

    async onClickResetPassword() {
        let phone = this.editPhone.string;
        let code = this.editCode.string;
        let password = this.editPassword.string;
        let password_confirm = this.editPasswordConfirm.string;

        if (phone.length != 11) {
            GHelper.showTip("请输入11位手机号");
            return;
        }

        if (!code) {
            GHelper.showTip("请输入验证码");
            return;
        }

        if (!password || !password_confirm) {
            GHelper.showTip("请输入密码");
            return;
        }

        if (password != password_confirm) {
            GHelper.showTip("两次输入密码不一致");
            return;
        }

        if (password.length < 6) {
            GHelper.showTip("您输入的密码长度过短，请重新输入");
            return;
        }
        let res = await GAccountHttp.request("resetpassword",{key:phone,code:code,password:password,from:4})
        if(res.errcode)
        {
            GHelper.showTip(res.errcode.des)
        }
        else
        {
            GHelper.showTip("密码重置成功")
            GPageMgr.openPage("Page_AccountLogin")
        }
    }

    async onEditPhone() {
        this.editPhone.string = this.editPhone.string.replace(/[^0-9]/ig, "");
        if (!core.isPhoneNo(this.editPhone.string)) {
            this.lbl_phonetip.string = "请输入正确的手机号码"
            this.lbl_phonetip.node.active=true
            return;
        }
        let res = await GAccountHttp.request("checkphoneexist",{phone:this.editPhone.string})
        if(res.errcode)
        {
            this.lbl_phonetip.string = "手机号码不存在"
            this.lbl_phonetip.node.active=false
        }
        else
        {
            this.lbl_phonetip.node.active=true
        }
    }

    onEditCode() {
        this.editCode.string = this.editCode.string.replace(/[^0-9]/ig, "");
    }

    onEditPassword() {
        this.editPassword.string = this.editPassword.string.trim();
        this.editPassword.node.parent.getChildByName("tips").active = this.editPassword.string.length < 6 || this.editPassword.string.length > 20;
    }

    onEditPasswordConfirm() {
        this.editPasswordConfirm.string = this.editPasswordConfirm.string.trim();
        this.editPasswordConfirm.node.parent.getChildByName("tips").active = this.editPasswordConfirm.string && this.editPassword.string != this.editPasswordConfirm.string;
    }
    Btn_Close_OnClick(event)
    {
        GPageMgr.closePage("Page_ResetPassword")
    }
}
