class ShopTool
{
    protected _unHandlePI:ProductInfo=null
    protected _pay_cb=null

    protected _st_plugin            = "com/eryi/casino/stpay/STPayPlugin"

    protected _one_string_param  = "(Ljava/lang/String;)V"
    protected _two_string_params = "(Ljava/lang/String;Ljava/lang/String;)V"

    get productInfo()
    {
        return this._unHandlePI
    }
    set productInfo(value)
    {
        this._unHandlePI = value
    }
    constructor()
    {
        if(cc.sys.os == cc.sys.OS_ANDROID) 
        {
            //let ret = jsb.reflection.callStaticMethod(this._st_plugin,"init","(Ljava/lang/String;)Z",JSON.stringify(STInitData))
        }
        else if(cc.sys.os == cc.sys.OS_IOS) 
        {
        }
    }
    pay(order_id:string,money:number,from:EOrderFrom,product_id:string,cb?)
    {
        if(this._unHandlePI)
        {
            return false
        }
        if(from==EOrderFrom.Ios)
        {
            this._pay_cb = cb
            this._unHandlePI=new ProductInfo()
            this._unHandlePI.user_id = GUserMgr.self_user_id
            this._unHandlePI.order_id = order_id
            this._unHandlePI.product_id = product_id
            this._unHandlePI.from = from
            GHelper.showRunningTip()
            let params = 
            {
                id:product_id//服务器标识的商品id
            }
            let listener = this._convertCallBackName(this._on_pay_callback.bind(this), "ios_pay")
            jsb.reflection.callStaticMethod("AppShop", "setJsCallback:", listener)
            let pd = GOrderSer.getProduct(product_id)
            jsb.reflection.callStaticMethod("AppShop", "openAppShop:params:", pd.key, JSON.stringify(params))
        }
        else if(from==EOrderFrom.Google)
        {
            let pd = GOrderSer.getProduct(product_id)
            GGoogleTool.buy(pd.key,order_id,cb)
        }
        else if(from==EOrderFrom.Android_Sft)
        {
            let dm = new STPayDataModel()
            dm.OrderAmount = money+""
            dm.OrderNo = order_id
            dm.ProductId = product_id
            dm.ProductName = "钻石"
            dm.ProductDesc = "钻石好好多！"
            let str = JSON.stringify(dm)
            let ret = false
            ret = jsb.reflection.callStaticMethod(this._st_plugin,"pay","(Ljava/lang/String;)Z",str)
        }
        return true
    }

    protected _on_pay_callback(params)
    {
        GHelper.clearRunningTip()
        let result = params
        let state = params.errorCode
        this.clearPay()
        this._unHandlePI.transactionIdentifier = params.transactionIdentifier
        this._unHandlePI.receipt = params.receipt
        if(this._pay_cb)
        {
            this._pay_cb(state == 998,this._unHandlePI)
        }
        this._unHandlePI = null
    }

    clearPay()
    {
        jsb.reflection.callStaticMethod("AppShop", "clearPayQueu","1");
    } 

    protected _convertCallBackName(fun, name) 
    {
        var funName = ""
        if (name)
        {
            console[name] = function(params)
            {
                fun(params)
            }
            funName = "console." + name
        } 
        else 
        {
            (<any>console).nativeToJsCallback = function(params)
            {
                fun(params)
            }
            funName = "console.nativeToJsCallback"
        }
        return funName
    }
}
GShopTool = new ShopTool()