import { EAccountFrom, GAppDM } from "../../DataModel/AppDataModel";
import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../Logic/UserManager";
import { GAccountHttp } from "../../Net/AccountHttp";
import { GHallHttp } from "../../Net/HallHttp";
import { GNetGameTexas } from "../../Net/NetGameTexas";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_AccountLogin extends Page {

    @property(cc.EditBox)
    editAccount: cc.EditBox = null;

    @property(cc.EditBox)
    editPassword: cc.EditBox = null;

    @property(cc.Toggle)
    toggleAgree: cc.Toggle = null;

    start() {
        this.toggleAgree.check();
        GPageMgr.closePage("Page_ResetPassword")
    }

    async onClickLogin() {
        if (!this.toggleAgree.isChecked) {
            GHelper.showTip("请勾选同意用户协议和隐私条款");
            return;
        }

        let account = this.editAccount.string
        let password = this.editPassword.string
        let ret = await GAppDM.onLogin(account,password,EAccountFrom.Phone)
        if(ret)
        {
            GPageMgr.openPage("Page_Main")
        }
    }

    onClickRegister() {
        GPageMgr.closePage("Page_AccountLogin")
        GPageMgr.openPage("Page_Register")
    }

    onClickCode() {
        GPageMgr.closePage("Page_AccountLogin")
        GPageMgr.openPage("Page_CodeLogin")
    }

    onClickResetPassword() {
        GPageMgr.closePage("Page_AccountLogin")
        GPageMgr.openPage("Page_ResetPassword")
    }

    onEditAccount() {
        this.editAccount.string = this.editAccount.string.trim();
    }

    onEditPassword() {
        this.editPassword.string = this.editPassword.string.trim();
    }
}
