import { GMsgCenter } from "../../utils/MessageCenter"
// import  SingleBase from '../../Base/SingleBase'

export let GNativeTool:NativeTool = null
class NativeTool extends cc.Object
{
    protected _native_plugin = "com/eryi/casino/utils/NativePlugin"
    protected _one_string_param  = "(Ljava/lang/String;)V"
    protected _two_string_params = "(Ljava/lang/String;Ljava/lang/String;)V"
    protected _return_one_string = "()Ljava/lang/String;"

    protected _return_boolean  = "()Z"

    protected _empty_params = "()V"
    protected _voice_callback = null
    protected _battery_info_callback = null
    constructor()
    {
        super()
        //this._registerBatterCallback()
        GMsgCenter.register("identify_success", ()=>
        {
            this._flag=-1
            console.log("rlsb identify_success")
        }, this)
        GMsgCenter.register("identify_failed", ()=>
        {
            this._flag=-1
            console.log("rlsb identify_failed")
        }, this)
    }
    log(tag:string,msg?:string)
    {
        if(!msg)
        {
            msg = tag
            tag = "nativetool"
        }
        if(cc.sys.isNative && cc.sys.os == cc.sys.OS_ANDROID)
        {
            jsb.reflection.callStaticMethod(this._native_plugin, "log",this._two_string_params,tag,msg)
        }
        else if(cc.sys.isNative && cc.sys.os == cc.sys.OS_IOS)
        {
            jsb.reflection.callStaticMethod("NativePlugin", "log:",msg)
        }
    }
    protected _registerBatterCallback()
    {
        if(cc.sys.isNative && cc.sys.os == cc.sys.OS_ANDROID)
        {
            let fun_name = this._convertCallBackName(this.onBatteryCallback.bind(this),"nt_")
            jsb.reflection.callStaticMethod(this._native_plugin, "registerBatteryListener",this._one_string_param,fun_name)
        }
    }
    setBatteryCallback(cb)
    {
        this._battery_info_callback = cb
    }
    onBatteryCallback(info)
    {
        if(!this._battery_info_callback)
        {
            return
        }
        if(info.isCharging=="true")
        {
            info.isCharging = true
        }
        else
        {
            info.isCharging = false
        }
        this._battery_info_callback(info)
    }
    getBatteryStatus()
    {
        if(cc.sys.isNative && cc.sys.os == cc.sys.OS_ANDROID)
        {
            jsb.reflection.callStaticMethod(this._native_plugin, "getBatteryStatus",this._empty_params)
        }
    }
    copyString(str)
    {
        str = str+""
        if(cc.sys.isNative && cc.sys.os == cc.sys.OS_ANDROID)
        {
            jsb.reflection.callStaticMethod(this._native_plugin, "copyString",this._two_string_params,"成功复制",str)
        }
        else if(cc.sys.isNative && cc.sys.os == cc.sys.OS_IOS)
        {
            jsb.reflection.callStaticMethod("NativePlugin", "copyString:",str)
        }
    }
    getUDID()
    {
        var udId = ""
        if(cc.sys.isNative && cc.sys.os == cc.sys.OS_ANDROID) 
        {
            udId = jsb.reflection.callStaticMethod(this._native_plugin, "getUDID",this._return_one_string)
        } 
        else if(cc.sys.isNative&&cc.sys.os == cc.sys.OS_IOS) 
        {
            udId = jsb.reflection.callStaticMethod("NativePlugin", "getUDID","")
        } 
        else 
        {
            udId = "1233210"
        }
        return udId
    }
    beginVoiceRecord(callback)
    {
        this._voice_callback = callback
        if(cc.sys.os == cc.sys.OS_ANDROID)
        {
            let fun_name = this._convertCallBackName(this._on_record_callback.bind(this),"nt_record")
            jsb.reflection.callStaticMethod(this._native_plugin, "beginRecord",this._one_string_param,fun_name)
        } 
        else if(cc.sys.os == cc.sys.OS_IOS) 
        {
            let fun_name = this._convertCallBackName(this._on_record_callback.bind(this),"nt_record")
            jsb.reflection.callStaticMethod("VoiceRecord", "registerScriptCallback:",fun_name)
            jsb.reflection.callStaticMethod("VoiceRecord", "beginRecord","")
        }
    }
        
    endVoiceRecord()
    {
        if(cc.sys.os == cc.sys.OS_ANDROID)
        {
            jsb.reflection.callStaticMethod(this._native_plugin, "endRecord",this._empty_params)
        }
        else if(cc.sys.os == cc.sys.OS_IOS)
        {
            jsb.reflection.callStaticMethod("VoiceRecord", "endRecord","")
        }
    }
    
    cancelRecord()
    {
        if(cc.sys.os == cc.sys.OS_ANDROID)
        {
            jsb.reflection.callStaticMethod(this._native_plugin, "cancelRecord",this._empty_params)
        }
        else if(cc.sys.os == cc.sys.OS_IOS)
        {
            jsb.reflection.callStaticMethod("VoiceRecord", "cancelRecord","")
        }
    }
    
    protected _on_record_callback(result)
    {
        if(result.code=="2")
        {
            if(this._voice_callback)
            {
                this._voice_callback(result.url)
            }
        }
        else
        {
            if(this._voice_callback)
            {
                this._voice_callback()
            }
        }
    }
    protected _convertCallBackName(fun, name) 
    {
        var funName = ""
        if (name)
        {
            console[name] = function(params)
            {
                fun(params)
            }
            funName = "console." + name
        } 
        else 
        {
            (<any>console).nativeToJsCallback = function(params)
            {
                fun(params)
            }
            funName = "console.nativeToJsCallback"
        }
        return funName
    }
    protected _flag=-1
    protected _preslsb_time=-1
    
    callRlsb(name:string,no:string,user_id:string)
    {
        console.log("rlsb flag="+this._flag)
        if(this._flag!=-1)
        {
            //return
        }
        let now = Date.now()
        //两秒之内只能调一次，不用flag是因为不做不到成败
        if(now-this._preslsb_time<2000)
        {
            return
        }
        this._preslsb_time=now
        //调用sdk人脸识别
        if (!cc.sys.isNative) {
            return
        }
        this._flag=1
        if (cc.sys.os==cc.sys.OS_IOS) {
            jsb.reflection.callStaticMethod("AppController", "CallCameraWithName:andCardID:andUserID:", name, no, user_id+"");
        }
        else if (cc.sys.os==cc.sys.OS_ANDROID) {
            jsb.reflection.callStaticMethod("org/cocos2dx/javascript/AppActivity", "CallCamera", "(Ljava/lang/String;Ljava/lang/String;Ljava/lang/String;)V", name, no, user_id+"");
        }
    }

    getMaxVersion(){
        if (cc.sys.os==cc.sys.OS_ANDROID) {
            return jsb.reflection.callStaticMethod("org/cocos2dx/javascript/AppActivity", "getMaxVersion", this._return_one_string);
        }else  if (cc.sys.os==cc.sys.OS_IOS) {
            return jsb.reflection.callStaticMethod("AppController", "getMaxVersion");
        }
    }

    openUrl(url:string){
        if (cc.sys.os==cc.sys.OS_ANDROID) {
            jsb.reflection.callStaticMethod("org/cocos2dx/javascript/AppActivity", "openUrl", this._one_string_param,url);
        }else if(cc.sys.os==cc.sys.OS_IOS) {
            return jsb.reflection.callStaticMethod("AppController", "openUrl:",url);
        }
    }

    doBackClose(){
        if (cc.sys.os==cc.sys.OS_ANDROID) {
            jsb.reflection.callStaticMethod("org/cocos2dx/javascript/AppActivity", "doBackClose", this._empty_params);
        }
    }

    checkLocationPremissionIsOpen(){
        if (cc.sys.os==cc.sys.OS_ANDROID) {
            return jsb.reflection.callStaticMethod("org/cocos2dx/javascript/AppActivity", "checkLocationPremissionIsOpen", this._return_boolean);
        }
    }

    reqLocationPremission(){
        if (cc.sys.os==cc.sys.OS_ANDROID) {
            jsb.reflection.callStaticMethod("org/cocos2dx/javascript/AppActivity", "reqLocationPremission", this._empty_params);
        }
    }
 


}
GNativeTool = new NativeTool()