import { GTexasTableDM } from "../../../DataModel/TexasTableDataModel";
import tableView from "../../../Framework/TableView/tableView";
import { Page } from "../../../Framework/View/Page/Page";
import { GPageMgr } from "../../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../../Logic/UserManager";
import { GHallHttp } from "../../../Net/HallHttp";
import { GMatchSer, MatchPlayer } from "../../../Service/MatchService";
// import ListView from "../../../utils/ListView";
import { GMsgCenter } from "../../../utils/MessageCenter";
import { utils } from "../../../utils/Utils";
import Page_Game from "../../Page/Page_Game";
import Page_MatchInfo from "../../Page/Page_MatchInfo";
import Widget_ScoreItem from "../../Widget/Widget_ScoreItem";

const {ccclass, property} = cc._decorator;

@ccclass
export default class NodeMatchScore extends Page {

    // @property(cc.ScrollView)
    // scrollview: cc.ScrollView = null;

    @property(cc.Node)
    layout: cc.Node = null;

    // @property(cc.Node)
    // item: cc.Node = null;

    @property(tableView)
    tableview :tableView = null;

    onLoad()
    {
        GMsgCenter.register("getplayers",this.init.bind(this),this)
        GMsgCenter.register("upgradeblind",this.init.bind(this),this)
        GMsgCenter.register("refresh_score", this.refresh.bind(this),this.node)
    }



    start() {
        // this.refresh()
    }
    
    onEnable(){
        this.refresh()
    }

    protected onDestroy(): void {
        GMsgCenter.unregister("getplayers",this)
        GMsgCenter.unregister("upgradeblind",this)
        GMsgCenter.unregister("refresh_score", this.node)
    }

    private isGetNext = false
    refresh(){
        if(this.isGetNext){
            return
        }
        this.isGetNext = true

        this.tableview.clear()
        let page = GPageMgr.getPage("Page_Game") as Page_Game
        if(!page)
        {
            this.isGetNext = false
            return
        }
        let table_id = page.tableId
        let tableinfo = GTexasTableDM.getTableInfo(table_id)
        let matchInsM = GMatchSer.getMatchInsById(tableinfo.match_insid)
        // if(matchInsM.rankplayers == null ||matchInsM.rankplayers.length<=0){
        matchInsM.rankplayers = []
        GHallHttp.request("GetMatchPlayers",{ins_id:tableinfo.match_insid}).then((res)=>
        {
            let players:MatchPlayer[] = matchInsM.rankplayers = res.players||[]
            matchInsM.player_num=res.player_num
            if(tableinfo.total_player_num<matchInsM.player_num)
            {
                tableinfo.total_player_num=matchInsM.player_num
            }
            GMsgCenter.send("getplayers")
            if(cc.isValid(this))
            {
                this.init()
            }
        })
        // }else{
        //     this.init()
        // }
    }

    init() {
        this.isGetNext = false
        //this.layout.removeAllChildren()
        let page = GPageMgr.getPage("Page_Game") as Page_Game
        if(!page)
        {
            return
        }
        // 刷新详情
        let matchInfoPage = GPageMgr.getPage("Page_MatchInfo") as Page_MatchInfo;
        if(matchInfoPage)
        {
            let gage_MatchInfo = matchInfoPage.getComponent("Page_MatchInfo") as  Page_MatchInfo
            gage_MatchInfo.matchInfo.getComponent("Widget_MatchInfo").onRefresh();
        }
        
        let table_id = page.tableId
        let tableinfo = GTexasTableDM.getTableInfo(table_id)

        let matchins = GMatchSer.getMatchInsById(tableinfo.match_insid)
        if(!matchins||!matchins.match)
        {
            return
        }
        
        let players= matchins.rankplayers||[]
        players.sort((p1,p2)=>
        {
            if(p1.point!=p2.point)
            {
                return p2.point-p1.point
            }
            return p1.cur_rank-p2.cur_rank
        })
        let big_blind=tableinfo.big_blind
        if(big_blind<=0)
        {
            let index = tableinfo.blind_index-1
            if (index<0)
            index = 0
            let bm = GMatchSer.getBlindModel(matchins.match.blind_id)
            if(!bm||!bm.items[index])
            {
                return
            }
            big_blind=bm.items[index].big_blind
            if(!big_blind)
            {
                return
            }
        }
        if(big_blind<=0) // 未开赛 记分 显示 负数  修复 
        {
            big_blind = 1;
        }
        let dataList = []
        for (const key in players) {
            dataList.push({type: 0, jsonData: players[key], i: key, big_blind: big_blind})
        }

        

        this.tableview.delegate =this
        this.tableview.init(dataList)
        this.tableview.node.on("scroll-to-bottom", this.onSVEventScrollToBottom, this)

        // this.scrollview.getComponent(ListView).init(this.bindcallfunc.bind(this), this.getItemType.bind(this))
        // this.scrollview.getComponent(ListView).notifyDataSetChanged(dataList)
        // this.scrollview.getComponent(ListView).bindScrollEventTarget(this)

        // for(let i=0;i<players.length;++i)
        // {
        //     let p = players[i]
        //     let node = cc.instantiate(this.item)
        //     this.layout.addChild(node)
        //     if(p.user_id==GUserMgr.self_user_id)
        //     {
        //         node.getChildByName('img_shadow').active=true
        //     }
        //     else
        //     {
        //         node.getChildByName('img_shadow').active=false
        //     }
        //     node.getChildByName('label_rank').getComponent(cc.Label).string = (i + 1)+""
        //     node.getChildByName('label_score').getComponent(cc.Label).string = utils.formatNumber(p.point)
        //     node.getChildByName('label_blind').getComponent(cc.Label).string = (p.point/big_blind).toFixed(2) + 'BB'
        //     node.getChildByName('label_nickname').getComponent(cc.Label).string = p.nickname
        // }
    }

   
    onSVEventScrollToBottom() {
        console.log('onSVEventScrollToBottom')
        if(this.isGetNext){
            return
        }
        this.isGetNext = true
        let page = GPageMgr.getPage("Page_Game") as Page_Game
        if(!page)
        {
            return
        }
        let table_id = page.tableId
        let tableinfo = GTexasTableDM.getTableInfo(table_id)
        let matchInsM = GMatchSer.getMatchInsById(tableinfo.match_insid)

        if( matchInsM.rankplayers == null || matchInsM.rankplayers.length == 0){
            this.isGetNext = false
            return 
        }

        let hadSaved = matchInsM.rankplayers.length;

        let big_blind=tableinfo.big_blind




        // GHallHttp.request("GetMatchPlayers",{ins_id:tableinfo.match_insid}).then((res)=>
        // {
        //     let players:MatchPlayer[] = matchInsM.rankplayers = res.players||[]
        //     matchInsM.player_num=res.player_num
        //     if(tableinfo.total_player_num<matchInsM.player_num)
        //     {
        //         tableinfo.total_player_num=matchInsM.player_num
        //     }
        //     cc.error("发22res=",res)
        //     GMsgCenter.send("getplayers")
        //     if(cc.isValid(this))
        //     {
        //         this.init()
        //     }
        // })


        GHallHttp.request("GetMatchPlayers",{ins_id:tableinfo.match_insid, skip: hadSaved}).then(async (res)=>
        {
            if(!res.players || res.players.length<=0)
            {
                this.isGetNext =false
                return;
            }else
            {

            }
            matchInsM.rankplayers = matchInsM.rankplayers.concat(res.players || []);
            matchInsM.player_num=res.player_num
            // GMsgCenter.send("getplayers")
            console.log('onSVEventScrollToBottom',matchInsM.rankplayers.length)

            // this.tableview.init(matchInsM.rankplayers)

           var lastOffset = this.tableview.getScrollOffset()

           if(big_blind<=0) // 未开赛 记分 显示 负数  修复 
           {
               big_blind = 1;
           }

            let dataList = []
            for (const key in matchInsM.rankplayers) {
                dataList.push({type: 0, jsonData: matchInsM.rankplayers[key], i: key, big_blind: big_blind})
            }
            await this.tableview.init(dataList)
            this.tableview.scrollToOffset(lastOffset) 
            this.isGetNext =false
        })
    }

    public getItemType(data) {
        return 0
    }

    public bindcallfunc(node: cc.Node, info, i) {
        node.getComponent(Widget_ScoreItem).setdata(info)
    }
}
