import { GUserMgr } from "../Logic/UserManager";
import { EPrepareTimeOutType, EFeeType, ETableState } from "../Config/_ini_";
import { ETexasSeatState } from "./TexasTableDataModel";

export class _ECardSeatState
{
    Waiting=0
    Preparing=1
    Playing=2
    Leaving=3
}
export let ECardSeatState=new _ECardSeatState()

export class CardRoomInfo
{
    _id:string = ""
    game_name:string = ""
    level:number = 0
    base:number = 0
    min:number = 0
    max:number = -1
    user_num:number = 0
}

export enum ETableType
{
    Friend=0,//自建
    Score,//竞技点
    ClubFriend,//俱乐部自建
}


export class PlayerInfo
{
    id=-1           //用户id
    nickname=""     //名称
    logo=""         //头像
    sex=-1          //性别
    ip=""           //登陆ip
    with_point=0    //带入分数
    point=0         //当前分数
    crystal=0
    is_robot=0
}
export class CardSeatInfo
{
    playerinfo:PlayerInfo=null
    state=ECardSeatState.Waiting
    index=-1        //座位号
    dt_point=0      //当前局分数
    auto=false
    agree_dismiss=null
}

export class CardRecordSeatInfo
{
    playerinfo:PlayerInfo=null
    index=-1        //座位号
    table_id=-1
    dtpoint=0
}
export class CardCreateTableDataModel
{
    /**
     * 房间类型
     */
    table_type:ETableType=-1
    /**
     * 游戏分类
     */
    game_type=-1
    /**
     * 底分
     */
    base_point:number=1
    /**
     * 最小金币携带量
     */
    min_point:number=-1
    /**
     * 最多人数
     */
    max_user:number=-1
    /**
     * 最低人数
     */
    min_user:number=-1
    /**
     * 局数
     */
    total_num:number=-1
    /**
     * 付费方式
     */
    fee_type:EFeeType=-1
    /**
     * 是否可以中途加入
     */
    can_enter_after:boolean = false
    /**
     * 是否可以中途离开
     */
    can_leave:boolean = false
    /**
     * 是否可以随时离开，包括正在玩耍还没结束当前局
     */
    can_any_leave:boolean = false
    /**
     * 是否需要记录结束时候的总结战绩，通常只有再单纯固定局数的玩法中为true其他都为false
     */
    need_end_record:boolean = false
    /**
     * 是否自动准备
     */
    auto_prepare:boolean = false
    /**
     * 自动坐下模式不可支持旁观
     * 非自动坐下模式可支持旁观
     */
    auto_sitdown:boolean = false   
    /**
     * 超时准备的处理方式
     */
    prepare_timeout_type = EPrepareTimeOutType.None
    /**
     * 是否随机座位
     */
    random_seat:boolean=true
    /**
     * 是否携带金币
     */
    carry_coin:boolean=false
    /**
     * 虚拟金币（非携带模式使用）
     */
    virtual_coin:number = -1
}
export class CardTableInfo<TSeatInfo extends CardSeatInfo>
{
    ctdm:CardCreateTableDataModel = null
    id = -1
    room_id = ""
    room_level = -1
    club_id = -1
    state = -1
    create_user_id = -1
    is_dismissing = false
    apply_dismiss_user_id = -1
    cur_num = -1                        //当前局数
    dismiss_timeout = -1
    timeout = -1
    seats:{[user_id:number]:TSeatInfo} = {}
    audience = new Map<number,{id:number,logo:string,nickname:string}>()
}

export class CardRecordTableInfo<TRecordSeatInfo extends CardRecordSeatInfo>
{
    userrecords:{[user_id:number]:TRecordSeatInfo}={}
    table_id:number=-1
    create_time:number=-1
    create_user_id=-1
    create_user_nickname=""
    create_user_logo=""
    ctdm:CardCreateTableDataModel=null
    total_num:number=-1
	cur_num:number=-1
    begin_time:number=-1
    end_time:number=-1
}

export class CardTableDataModel<TSeatInfo extends CardSeatInfo,TTableInfo extends CardTableInfo<TSeatInfo>,TRecordSeatInfo extends CardRecordSeatInfo,TRecordTableInfo extends CardRecordTableInfo<TRecordSeatInfo>>
{
    /**
     * room_id,RoomModel
     */
    protected _room_infos:Map<number,CardRoomInfo>=null
    get roomInfos()
    {
        return this._room_infos
    }
    set roomInfos(value)
    {
        this._room_infos=value
    }
    protected _cur_room_id:number=-1
    get curRoomId()
    {
        return this._cur_room_id
    }
    set curRoomId(value)
    {
        this._cur_room_id=value
    }
    get curRoomInfo():CardRoomInfo
    {
        return this._room_infos[this._cur_room_id]
    }
    protected _table_infos:{[table_id:number]:TTableInfo}={}
    getTableInfo(table_id:number)
    {
        return this._table_infos[table_id]
    }

    protected _battlerecords:{[table_id:number]:TRecordTableInfo[]}={}
    get battleRecords()
    {
        return this._battlerecords
    }
    addBattleRecords(value:TRecordTableInfo[])
    {
        if(!value||value.length<=0)
        {
            return
        }
        let table_id=value[0].table_id
        this._battlerecords[table_id]=this._battlerecords[table_id]||[]
        this._battlerecords[table_id]=this._battlerecords[table_id].concat(value)
    }
    getUserIdList(table_id:number)
    {
        let user_id_list=[]
        let ti = this._table_infos[table_id]
        if(!ti)
        {
            return user_id_list
        }
        for(let user_id in ti.seats)
        {
            if(ti.seats[user_id])
            {
                user_id_list.push(user_id)
            }
        }
        return user_id_list
    }
    getSeatByUserId(table_id:number,user_id:number)
    {
        let ti = this._table_infos[table_id]
        if(!ti||!ti.seats)
		{
			return null
        }
        return ti.seats[user_id]
    }
    getSeatByIndex(table_id:number,index:number)
	{
        let ti = this._table_infos[table_id]
        if(!ti||!ti.seats)
        {
            return null
        }
        for(let user_id in ti.seats)
		{
            let st = ti.seats[user_id] as TSeatInfo
            if(st&&st.index==index)
            {
                return st
            }
		}
		return null
	}
    init(table_info:TTableInfo)
    {
        this._table_infos[table_info.id] = table_info
        for(let user_id in table_info.seats)
        {
            let seat = table_info.seats[user_id]
            seat.dt_point = Math.abs(seat.dt_point||0)
        }
        this._refreshToUserMgr(table_info)
    }
    protected _refreshToUserMgr(table_info:TTableInfo)
    {
        for(let user_id in table_info.seats)
        {
            let seat = table_info.seats[user_id] as TSeatInfo
            this._addOneUserBySeat(seat)
        }
    }
    protected _addOneUserBySeat(seat:TSeatInfo)
    {
        let one = 
        {
            id:seat.playerinfo.id,
            nickname:seat.playerinfo.nickname,
            sex:seat.playerinfo.sex,
            logo:seat.playerinfo.logo,
            ip:seat.playerinfo.ip,
        }
        GUserMgr.addOneUser(<any>one)
    }
    onEnter(table_id:number,seatinfo:TSeatInfo)
	{
        let ti = this._table_infos[table_id]
		ti.seats[seatinfo.playerinfo.id] = seatinfo
		let one = 
		{
			id:seatinfo.playerinfo.id,
			nickname:seatinfo.playerinfo.nickname,
			logo:seatinfo.playerinfo.logo,
			sex:seatinfo.playerinfo.sex,
			ip:seatinfo.playerinfo.ip,
		}
		GUserMgr.addOneUser(<any>one)
	}
    //有玩家进来
    
    
    onSitdown(table_id:number,seat:TSeatInfo)
    {
        let ti = this._table_infos[table_id]
        if(!ti)
        {
            return
        }
        ti.seats[seat.playerinfo.id] = seat
        this._refreshToUserMgr(ti)
    }
    onStandup(table_id:number,user_id:number)
    {
        let tableinfo = this.getTableInfo(table_id)
        if(!tableinfo)
        {
            return
        }
        delete tableinfo.seats[user_id]
    }
    //有玩家托管
    onAuto(table_id:number,user_id:number,auto:boolean)
    {
        let seat = this.getSeatByUserId(table_id,user_id)
        seat.auto = auto
    }
    onAgree(table_id:number,user_id:number,agree_dismiss)
    {
        let seat = this.getSeatByUserId(table_id,user_id)
        seat.agree_dismiss = agree_dismiss
    }
    onApplyDismiss(table_id:number,user_id:number,deadline:number)
    {
        this.onAgree(table_id,user_id,true)
        this._table_infos[table_id].apply_dismiss_user_id = user_id
        this._table_infos[table_id].timeout = deadline   
    }
    onPrepare(table_id:number,user_id:number)
    {
        let seat = this.getSeatByUserId(table_id,user_id)
        seat.state = ECardSeatState.Preparing
    }
    onResult(record:TRecordTableInfo)
    {
        let ti = this._table_infos[record.table_id]
        if(!ti)
        {
            return
        }
        ++ti.cur_num
        ti.state=ETableState.End
        this._battlerecords[record.table_id]=this._battlerecords[record.table_id]||[]
		this._battlerecords[record.table_id].push(record)
        for(let user_id in record.userrecords)
        {
            let one = record.userrecords[user_id]
            let seat = this.getSeatByUserId(record.table_id,one.playerinfo.id)
			seat.playerinfo.point = one.playerinfo.point
		}
    }
    //有玩家离开
    onLeave(table_id:number,user_id:number)
    {
        delete this._table_infos[table_id].seats[user_id]
    }
}