import { GGameDM } from "../../Framework/DataModel/GameDataModel";
import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr, PageManager } from "../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../Logic/UserManager";
import MultiPlatform from "../../platform/MultiPlatform";
import { ykUtils } from "../../utils/YkUtils";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_NeiYe extends Page {
    @property(cc.Node)
    shareContent: cc.Node = null;
    @property(cc.Node)
    inviteDeminCode: cc.Node = null;

    @property(cc.Label)
    labelInvite: cc.Label = null;

    @property(cc.Label)
    labelInviteCode: cc.Label = null;

    doClose(){
        // console.log(this.name)
        GPageMgr.closePage('Page_NeiYe')
    }

    
    onLoad(): void {
        let allHb = GGameDM.arrayInvite
        this.loadInviteCode()

        this.labelInvite.string = GUserMgr.self_user.nickname+'邀请您加入ZDPK'

        allHb.forEach(async (element) => {
            let gg = await ykUtils.loadNetSpriteFrame(ykUtils.getPosterNet(element))
            if(gg){
                let nn = cc.instantiate(cc.find('smallhb',this.node))
                nn.getComponent(cc.Sprite).spriteFrame = gg
                nn.active = true
                nn.y = 0
                nn.setContentSize(511,910)
                // nn.children[0].getComponent(cc.Label).string = GUserMgr.self_user.id+''
                cc.find('content/pageview',this.node).getComponent(cc.PageView).addPage(nn)
            }
        });
      
        this.labelInviteCode.string = '邀请码:'+GUserMgr.self_user.id+''
    }

    async loadInviteCode(){
        let code =  await ykUtils.loadNetSpriteFrame(ykUtils.PATH.png_kefuCode)
        if(code){
            let size = this.inviteDeminCode.getContentSize()
            this.inviteDeminCode.getComponent(cc.Sprite).spriteFrame = code
            this.inviteDeminCode.setContentSize(size)
        }
    }

    onShareFriend()
    {
        MultiPlatform.getInstance().capScreenShareWx(1,this.node.getChildByName("content"),null);
    }
    onShareFriendCircle()
    {   
        MultiPlatform.getInstance().capScreenShareWx(2,this.node.getChildByName("content"),null);
    }

    onSaveClick(){

        MultiPlatform.getInstance().capScreen(this.node.getChildByName("content"),(isSucces)=>
        {
            GHelper.showTip(isSucces?'保存成功':'保存失败',2,cc.v2(0,630));
        });
    }
}