import { GTexasTableDM } from "../../../DataModel/TexasTableDataModel";
import { Page } from "../../../Framework/View/Page/Page";
import { GPageMgr } from "../../../Framework/View/Page/PageManager";
import { GItemCfg } from "../../../Service/ItemService";
import { GMatchSer, MultiDynamicReward } from "../../../Service/MatchService";
import { GMsgCenter } from "../../../utils/MessageCenter";
import { utils } from "../../../utils/Utils";
import Page_Game from "../../Page/Page_Game";

const {ccclass, property} = cc._decorator;

@ccclass
export default class NodeMatchInfo extends Page {

    @property(cc.Label)
    labelStartTime: cc.Label = null;

    @property(cc.Label)
    labelCost: cc.Label = null;

    @property(cc.Label)
    labelPlayerMin: cc.Label = null;

    @property(cc.Label)
    labelReward: cc.Label = null;

    @property(cc.Label)
    labelChips: cc.Label = null;

    @property(cc.Label)
    labelSignup: cc.Label = null;

    @property(cc.Label)
    labelReentry: cc.Label = null;

    @property(cc.Label)
    labelDesc: cc.Label = null;
    onLoad()
    {
        GMsgCenter.register("getplayers",this.init.bind(this),this)
    }
    protected onDestroy() {
        GMsgCenter.unregister("getplayers",this)
    }
    protected onEnable()
    {
        this.init()
    }

    
    init(){
        let page = GPageMgr.getPage("Page_Game") as Page_Game
        if(!page)
        {
            return
        }
        let table_id = page.tableId
        let tableinfo = GTexasTableDM.getTableInfo(table_id)
        let matchInsM = GMatchSer.getMatchInsById(tableinfo.match_insid)
        let matchM = matchInsM.match
        if (!matchM) {
            return
        }

        this.labelStartTime.string = utils.formatDate(new Date(matchInsM.start_time));
        let func=(info)=>
        {
            if(info.id==-99)
            {
                return info.count+"游戏点"
            }
            let item = GItemCfg.getItemById(info.id)
            if(!item)
            {
                return ""
            }
            return item.name+"x"+info.count
        }
        let str=""
        for(let i=0;i<matchM.signup_cost.length;++i)
        {
            str+=func(matchM.signup_cost[i])
            if(i<matchM.signup_cost.length-1)
            {
                str+="/"
            }
        }
        this.labelCost.string = str
        this.labelPlayerMin.string = matchM.min_user + "人起开始比赛"
        let mtLen = page.getComponent('Page_Game').getRewardsLen();
        this.labelReward.string = (mtLen?mtLen:matchM.reward.length) + "人"
        this.labelChips.string = utils.formatNumber(matchM.coin, 0)
        this.labelSignup.string = matchInsM.player_num + '人'
        this.labelReentry.string = `前${matchM.reenter_blind_index||0}个盲注级别内可再报名${matchM.reenter_count}次`
        this.labelDesc.string = matchM.des
    }
}
