import { GGameDM } from "../../Framework/DataModel/GameDataModel";
import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr, PageManager } from "../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../Logic/UserManager";
import { ykUtils } from "../../utils/YkUtils";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_Invite_Friend extends Page {
    @property(cc.Node)
    nn_page: cc.Node = null;

    @property(cc.Node)
    content: cc.Node = null;


    onLoad(){
        this.initPage()
    }

    async initPage(){
        // let pngs = ['hb1','hb2','hb3']
        // for(var i=0;i<3;i++){
        //     let paths = 'image/invite/'+pngs[i]
        //     let spFrame = await ykUtils.loadResourse(paths,cc.SpriteFrame) as cc.SpriteFrame
        //     let page = cc.instantiate(this.nn_page)
        //     page.getComponent(cc.Sprite).spriteFrame = spFrame

        //     page.active = true
        //     page.y = 0
        //     page.setContentSize(567,1010)
        //     // this.content.addChild(page)
          
        //     cc.find('pageview',this.node).getComponent(cc.PageView).addPage(page)
          
        // }
        
        let allHb = GGameDM.arrayInviteNei
        allHb.forEach(async (element) => {
            let gg = await ykUtils.loadNetSpriteFrame(ykUtils.getPosterNet(element))
            let nn = cc.instantiate(this.nn_page)
            nn.getComponent(cc.Sprite).spriteFrame = gg
            nn.active = true
            nn.y = 0
            cc.find('pageview',this.node).getComponent(cc.PageView).addPage(nn)
        })
    }

    onShareFriend(){
        GHelper.wxShareShot(1, this.content.getParent());
    }
    onShareFriendCircle(){
        GHelper.wxShareShot(2, this.content.getParent());
    }

    doClose(){
        GPageMgr.closePage('Page_Invite_Friend')
    }

}