import { GHelper } from "../../../Framework/Logic/Helper";
import { GNativeTool } from "../../../Framework/Logic/NativeTool";
import { Page } from "../../../Framework/View/Page/Page";
import { GPageMgr } from "../../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../../Logic/UserManager";
import { GHallHttp } from "../../../Net/HallHttp";
import { GMsgCenter } from "../../../utils/MessageCenter";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_IDCard extends Page {
    
    @property(cc.EditBox)
    editboxIdcardName: cc.EditBox = null;

    @property(cc.EditBox)
    editboxIdcardNumber: cc.EditBox = null;

    @property(cc.Node)
    layoutTop: cc.Node = null;

    @property(cc.Node)
    layoutMiddle: cc.Node = null;

    @property(cc.Button)
    btnNext: cc.Button = null;

    @property(cc.Label)
    label_name1: cc.Label = null;
    @property(cc.Label)
    label_number: cc.Label = null;

    private step: number = 0;

    onLoad() {
        GPage_IDCard = this

        GMsgCenter.register('identify_success',this.VerificationSuccess.bind(this),this)
        this.onShow()
    }

    onShow() {
        if (GUserMgr.self_user.idcard_name && GUserMgr.self_user.idcard_no) {
            this.step = 3
        } else {
            this.step = 1
        }
        this.initTable()
    }

    initTable() {
        this.btnNext.node.active = this.step < 3;
        for (let i = 0; i < 3; i++) {
            this.layoutTop.children[i].active = this.step == i + 1;
            this.layoutMiddle.children[i].active = this.step == i + 1;
        }
    }

    onEditIdcardName() {
        this.editboxIdcardName.string = this.editboxIdcardName.string.trim();
    }

    onEditIdcardNumber() {
        this.editboxIdcardNumber.string = this.editboxIdcardNumber.string;
    }

    onBtnBack() {
        GPageMgr.closePage("Page_IDCard")
    }

    /**
     * 身份证号码校验
     * 身份证号码为15位或者18位，15位时全为数字，18位前17位为数字，最后一位是校验位，可能为数字或字符X
     */
    checkIdCard(idCard){
        var reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
        if(reg.test(idCard)){
            return true;
        }else{
            return false;
        }
    }

    async onClickNext() {

        // this.VerificationSuccess();
        // return;
        if (this.checkIdCard(this.editboxIdcardNumber.string)) {
            // ------------------
            let res = await GHallHttp.request("CheckIdCardNo", {idcard_no: '@'+this.editboxIdcardNumber.string})
            if(res.id!=null){
                // console.log(res)
                GHelper.showTip('该身份证已经绑定了其他账号')
                return
            }
            // ------------------
            GNativeTool.callRlsb(this.editboxIdcardName.string, this.editboxIdcardNumber.string, GUserMgr.self_user_id.toString())
        }
        else{
            GHelper.showTip("请输入正确的身份证号码");
        }
    }

    protected onDestroy(): void {
        GMsgCenter.unregister("identify_success", this)
    }

    VerificationSuccess() {
        //验证成功
        GHelper.showTip("验证成功");
        GPageMgr.closePage("Page_IDCard")
        GPageMgr.closePage("Page_Person")
        // this.step = 3
        // this.initTable();
        // GHallHttp.request("getuser").then((rs)=>
        // {
        //     GUserMgr.addOneUser(rs.user);
        //     GMsgCenter.send("updateuserinfo");
        // })

        let name = this.editboxIdcardName.string;
        let id = this.editboxIdcardNumber.string;
        GHallHttp.request("changeid", {nickname: name, idcard_no: "@"+id}).then((rs)=>
        {
            GUserMgr.addOneUser(rs.user);
            GMsgCenter.send("updateuserinfo");
            GMsgCenter.send("VerificationSuccess");
        })

        // this.label_name1.string = GUserMgr.self_user.idcard_name
        // this.label_number.string = GUserMgr.self_user.idcard_no
    }
}

let GPage_IDCard:Page_IDCard = null;

