import { GAudioTool } from './../Logic/AudioTool';
import { GHelper } from "../Logic/Helper";
import { GResTool } from "../Logic/ResTool";

const {ccclass, property} = cc._decorator

@ccclass
export class FlyCoins extends cc.Component
{
    protected _sf:cc.SpriteFrame = null
    onLoad()
    {
    }
    flyTo(time:number,coin_num,pos:cc.Vec2)
    {
        GHelper.createSpriteFrame("Common/Image/Other/icon_coin",(sf)=>
        {
            if(sf == null){
                return
            }

            this._sf = sf
            let coins = new Array<cc.Sprite>()
            for(let i=0;i<coin_num;++i)
            {
                let sp = GHelper.createSprite(cc.Sprite)
                sp.spriteFrame = this._sf
                coins.push(sp)
                sp.node.parent = this.node
            }
            let x = pos.x-this.node.x
            let y = pos.y-this.node.y
            for(let i=0;i<coins.length;++i)
            {
                let sp = coins[i]
                let actions = []
                actions.push(cc.delayTime(0.08*i))
                actions.push(cc.callFunc(()=>
                {
                    GAudioTool.playSound("Common/Audio/Effect/coins_fly")
                }))
                actions.push(cc.moveTo(time,cc.v2(x,y)))
                actions.push(cc.fadeOut(0.3))
                let action = cc.sequence(actions)
                sp.node.runAction(action)
            }
            let actions = []
            actions.push(cc.delayTime(coins.length*0.08+(0.3+time)))
            actions.push(cc.callFunc(()=>
            {
                this.node.removeFromParent()
            }))
            let action = cc.sequence(actions)
            this.node.runAction(action)
        })
    }
}