import { GAlipay } from "../../Framework/View/Page/Alipay";
import { GOrderSer, ProductItem } from "../../Service/OrderService";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_ProductItem extends cc.Component {
    @property(cc.Label)
    lbl_name: cc.Label = null

    @property(cc.Label)
    lbl_money: cc.Label = null

    @property(cc.Label)
    lbl_score: cc.Label = null

    @property(cc.Button)
    btn_item: cc.Button = null

    item: ProductItem = null;
    id: number = null;

    onLoad() {
        this.btn_item.node.on('click', this.Btn_Click, this)
    }
    
    Btn_Click(event)
    {
        GAlipay.alipayEvent(this.id);
    }

    setdata(jsonData) {
        this.item = jsonData.product;
        this.id = jsonData.product.id;
        this.lbl_money.string = jsonData.product.money
        this.lbl_name.string = jsonData.product.title
        this.lbl_score.string = "赠送：" + jsonData.product.coin + "游戏点"
    }
}
