
import { Page } from "../../Framework/View/Page/Page";
import { EMatchState } from "../../Config/_ini_";
import { GTexasTableDM } from "../../DataModel/TexasTableDataModel";

import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GMatchSer } from "../../Service/MatchService";
import { utils } from "../../utils/Utils";
import { EBagItemState, GItemCfg } from "../../Service/ItemService";
import { GUserMgr } from "../../Logic/UserManager";
import { GGameDM } from "../../Framework/DataModel/GameDataModel";
import { ykUtils } from "../../utils/YkUtils";
import Widget_SignUpBar from "../Widget/Widget_SignUpBar";
const {ccclass, property} = cc._decorator;
@ccclass
export default class Page_MatchPay extends Page {

    
    
    @property(cc.Label)
    title:cc.Label = null;

    @property(Widget_SignUpBar)
    widget_SignUpBar:Widget_SignUpBar = null;

    @property(cc.Node)
    upLayout:cc.Node = null;

    @property(cc.Label)
    lb_lackCoin:cc.Label = null;

    @property(cc.Node)
    zhifulabel:cc.Node = null;

    @property(cc.Node)
    downLayout:cc.Node = null;

    private datas:any = null
    private coinUse:{ coin:number,
        reward_coin:number,
        notEnoughNum:number}= null
    onLoad(): void {
        
    }
    onOpen(datas)
    {
        this.datas =datas
        let tableinfo = GTexasTableDM.getTableInfo(datas.table_id)
        if(!tableinfo)
        {
            this.node.active=false
            return
        }
        let user =GUserMgr.self_user

        let matchInsM = GMatchSer.getMatchInsById(tableinfo.match_insid)
        let signup_cost = matchInsM.match.signup_cost
        this.initTitle(signup_cost)
        //  取出报名基准
        let needCost = 0   //报名实际需要
        let useItem = []   //所有物品
        let itemCoin = 0   //物品使用价值

        for(let i=0;i<signup_cost.length;++i)
         {
            let item = signup_cost[i]
            if(item.id == -99){
                needCost = item.count
            }else{
                useItem.push({item_id:item.id,count: item.count, itemcfg:GItemCfg.getItemById(item.id),_canUse:0,canUse:0,use:0})
            }
         }
  
        useItem.sort((a,b)=>{
            a.itemcfg.use_weight = a.itemcfg.use_weight ||0
            b.itemcfg.use_weight = b.itemcfg.use_weight ||0

            return b.itemcfg.use_weight - a.itemcfg.use_weight
        })


        let checkIsEnough = (uItems,idx,isNeedReturn = false)=>{
            // ==== 只组合后面的

            // 调换排序
            if(idx <uItems.length){
                let item = uItems.splice(idx,1)[0]
                if(item){
                   uItems.unshift(item) 
                }
            }
           


            let ceItemCoin = 0
            for(var i =0;i<uItems.length;i++){
                let ceItem = uItems[i]
                let ceItemNum =ceItem.canUse

                for(let m=0;m<ceItemNum;m++){
                    if(ceItemCoin<needCost){
                        let rc = ceItemCoin+ceItem.itemcfg.coin
                        if(rc>needCost){
                            // 什么都不做
                        }else{
                            ceItem.canUse = ceItem.canUse-1
                            ceItem.use = ceItem.use+1
                            ceItemCoin = rc
                            if(ceItemCoin == needCost){   
                                break
                            }
                        }
                        
                    }
                }
            }

            let leftCoin = needCost - ceItemCoin
            let allCoin =  user.coin+user.reward_coin

            if(leftCoin>allCoin){
                if(isNeedReturn){
                    itemCoin  = ceItemCoin
                    return uItems
                }else{
                    return
                }
                
            }
            itemCoin  = ceItemCoin
            return uItems
        }

        
        let haveItems = GItemCfg.bagItems
        // 计算用户的物品数量
        haveItems.forEach((element)=>{
            for(var i = 0;i<useItem.length;i++){
                if(useItem[i].item_id == element.item_id && element.give_check !=0){
                    useItem[i].canUse +=1
                    useItem[i]._canUse +=1
                    break
                }
            }
        })
        // // 计算使用费用
        // for(let i=0;i<useItem.length;i++){
        //     // 计算价值的累加
        //    let citem = useItem[i]
        //    let citemNum = citem.canUse
        //    for(let m=0;m<citemNum;m++){
        //         if(itemCoin<needCost){
        //             let rc = itemCoin+citem.itemcfg.coin
        //             if(itemCoin>needCost){
        //                 // 什么都不做
        //             }else{
        //                 citem.canUse = citem.canUse-1
        //                 citem.use = citem.use+1
        //                 itemCoin = rc
        //                 if(itemCoin == needCost){   
        //                     break
        //                 }
        //             }
        //         }
        //    }
        // }
        let checkRes
        for(var i=0;i<useItem.length;i++){
            // 开始计算报名
            checkRes = checkIsEnough(ykUtils.clone(useItem),i)
            if(checkRes){
                break
            }
        }

        
        if(checkRes == null){
            useItem = checkIsEnough(ykUtils.clone(useItem),0,true)
        }else{
            useItem = checkRes
        }

        useItem.sort((a,b)=>{
            a.itemcfg.use_weight = a.itemcfg.use_weight ||0
            b.itemcfg.use_weight = b.itemcfg.use_weight ||0

            return b.itemcfg.use_weight - a.itemcfg.use_weight
        })

        let leftCoin = needCost - itemCoin
        let allCoin =  user.coin+user.reward_coin


        let notEnoughNum = 0

        if(leftCoin>allCoin){
            //   费用不足
            notEnoughNum = leftCoin - allCoin
        }

        //====================适配界面
        this.initView(signup_cost,useItem,needCost,itemCoin)

      
    }
    initView(signup_cost, useItem: any[],needCost,itemCoin) {
        let user = GUserMgr.self_user
        let createUseView = (itemCfg,haveNum,useNum)=>{
            let nView = cc.instantiate(this.widget_SignUpBar.node)
            nView.active = true
            let sc = nView.getComponent(Widget_SignUpBar)
            sc.setItem(itemCfg)
            sc.setNum(haveNum,useNum)
            this.upLayout.addChild(nView)
        }   
        for(var v of useItem){
            createUseView(v.itemcfg,v._canUse,v.use)
        }
        let leftCoin = needCost - itemCoin
        let allCoin =  user.coin+user.reward_coin

        this.coinUse={
            coin :0,
            reward_coin:0,
            notEnoughNum:0
        }
        let coinUse = this.coinUse
        if(leftCoin>allCoin){
            coinUse.notEnoughNum = leftCoin - allCoin
        }


        if( coinUse.notEnoughNum>0){
            coinUse.coin = user.coin
            coinUse.reward_coin = user.reward_coin
        }else{
            if(leftCoin>user.coin){
                coinUse.coin = user.coin
                coinUse.reward_coin = leftCoin - user.coin
            }else{
                coinUse.coin = leftCoin
            }
        }
        createUseView('coin',user.coin,coinUse.coin)
        createUseView('rewardCoin',user.reward_coin,coinUse.reward_coin)
        // =============================初始化说明

        if(coinUse.notEnoughNum>0){
            this.lb_lackCoin.node.active = true
            this.lb_lackCoin.string = '不足'+coinUse.notEnoughNum.toString()+'游戏点'
        }else{
            this.lb_lackCoin.node.active = false
        }

        let createZhifuLabel =(name,num)=>{
            let view =cc.instantiate(this.zhifulabel)
            view.active = true
            this.downLayout.addChild(view)
            view.getComponent(cc.Label).string = name+'支付'+num+'游戏点'
        }

        for(var v of useItem){
            if(v.use>0){
                createZhifuLabel(v.itemcfg.name,v.use*v.itemcfg.coin)
            }
        }
        if(coinUse.coin>0){
            createZhifuLabel('游戏点',coinUse.coin)
        }
        if(coinUse.reward_coin>0){
            createZhifuLabel('欢乐点',coinUse.reward_coin)
        } 
    }

    initTitle(signup_cost){
        let str="参赛资格:"
        let func=(info)=>
        {
            if(info.id==-99)
            {
                return info.count+"游戏点/欢乐点"
            }
            let item = GItemCfg.getItemById(info.id)
            if(!item)
            {
                return ""
            }
            return item.name+"x"+info.count
        }

        for(let i=0;i<signup_cost.length;++i)
        {
            str+=func(signup_cost[i])
            if(i<signup_cost.length-1)
            {
                str+="或"
            }
        }
        this.title.string = str;
    }


    onCheckClick(){
        if(!(this.coinUse.notEnoughNum>0)){
            if(this.datas){
                this.datas.callBack()
            }
        }else{
            // 打开 充值页面
            GPageMgr.openPage("Page_Charge")
            // GPageMgr.getPage("Page_main")
        }

        GPageMgr.closePage("Page_MatchPay")
    }
    
    onCloseClick(){
        GPageMgr.closePage("Page_MatchPay")
    }

}