import viewCell from "../../Framework/TableView/viewCell";
import { GUserMgr } from "../../Logic/UserManager";
import { utils } from "../../utils/Utils";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_ScoreItem extends viewCell {
    @property(cc.Label)
    lbl_name: cc.Label = null

    @property(cc.Label)
    lbl_blind: cc.Label = null

    @property(cc.Label)
    lbl_rank: cc.Label = null

    @property(cc.Label)
    lbl_score: cc.Label = null;


 
    init(index: number, data?: any) {
        this.setdata(data)
    }


    setdata(p) {
        // console.log(p)
        if(p.jsonDataid==GUserMgr.self_user_id)
        {
            this.node.getChildByName('img_shadow').active=true
        }
        else
        {
            this.node.getChildByName('img_shadow').active=false
        }

        this.node.getChildByName('label_rank').getComponent(cc.Label).string = (Number(p.i) + 1)+""
        this.node.getChildByName('label_score').getComponent(cc.Label).string = utils.formatNumber(p.jsonData.pre_point)
        this.node.getChildByName('label_blind').getComponent(cc.Label).string = (p.jsonData.pre_point/p.big_blind).toFixed(2) + ' BB'
        this.node.getChildByName('label_nickname').getComponent(cc.Label).string = p.jsonData.nickname

        if (p.jsonData.point<=0) {
            this.node.getChildByName('label_rank').color = new cc.Color().fromHEX("#CCCCCC");
            this.node.getChildByName('label_score').color = new cc.Color().fromHEX("#CCCCCC");
            this.node.getChildByName('label_blind').color = new cc.Color().fromHEX("#CCCCCC");
            this.node.getChildByName('label_nickname').color = new cc.Color().fromHEX("#CCCCCC");
        }
        else
        {
            this.node.getChildByName('label_rank').color = new cc.Color().fromHEX("#383838");
            this.node.getChildByName('label_score').color = new cc.Color().fromHEX("#383838");
            this.node.getChildByName('label_blind').color = new cc.Color().fromHEX("#383838");
            this.node.getChildByName('label_nickname').color = new cc.Color().fromHEX("#383838");
        }
    }
}
