import { ucAd } from "../sdk/UCAd";
import { wxAd } from "../sdk/WXAd";
import { ttAd } from "../sdk/TTAd";
import { vivoAd } from "../sdk/VivoAd";
import { BaseAd, testAd } from "../sdk/BaseAd";
import { oppoAd } from "../sdk/OppoAd";
import { qqAd } from "../sdk/QQAd";

class SdkManager {
    private ad: BaseAd = null;
    private vibrate_on: boolean = true;

    constructor() {
        this.init();
    }

    init() {
        this.ad = testAd;
        switch(cc.sys.platform) {
            case cc.sys.MOBILE_BROWSER: {
                if (cc.sys.browserType == cc.sys.BROWSER_TYPE_UC) {
                    this.ad = ucAd;
                }
                break;
            }
            case cc.sys.WECHAT_GAME: {
                this.ad = wxAd;
                break;
            }
            case cc.sys.QQ_PLAY: {
                this.ad = qqAd;
                break;
            }
            case cc.sys.HUAWEI_GAME: {
                break;
            }
            case cc.sys.XIAOMI_GAME: {
                break;
            }
            case cc.sys.OPPO_GAME: {
                this.ad = oppoAd;
                break;
            }
            case cc.sys.VIVO_GAME: {
                this.ad = vivoAd;
                break;
            }
            case cc.sys.BYTEDANCE_GAME: {
                this.ad = ttAd;
                break;
            }
        }
    }

    getAd() {
        return this.ad;
    }

    getPlatform() {
        return this.ad ? this.ad.getPlatform() : "unknown";
    }

    getNetworkMode() {
        return true;
    }

    checkVideoAd(callback?) {
        callback && callback();
    };

    checkInterstitialAd(callback?) {
        callback && callback();
    };

    checkNativeAd(callback?) {
        callback && callback();
    };

    hasLogin() {
        return true;
    }

    hasShare() {
        return true;
    }

    hasVideoAd() {
        return true;
    }

    hasInterstitialAd() {
        return true;
    }

    hasNativeAd() {
        return false;
    }

    hasRanking() {
        return true;
    }

    login(success?: Function, fail?: Function) {
        this.ad ? this.ad.login(success, fail) : fail && fail();
    }

    share(success?: Function, fail?: Function) {
        this.ad ? this.ad.share(success, fail) : fail && fail();
    }

    showBannerAd(success?: Function, fail?: Function) {
        this.ad ? this.ad.createBannerAd() : fail && fail();
    }

    hideBannerAd(success?: Function, fail?: Function) {
        this.ad ? this.ad.destroyBannerAd() : fail && fail();
    }

    showVideoAd(success?: Function, fail?: Function) {
        this.ad ? this.ad.createVideoAd(success, fail) : fail && fail();
    }

    showInterstitialAd(success?, fail?) {
        this.ad ? this.ad.createInterstitialAd(success) : fail && fail();
    }

    showNativeAd(success?: Function, fail?: Function) {
        this.ad ? this.ad.createNativeAd(success, fail) : fail && fail();
    }

    reportAdShow(id: string) {
        this.ad && this.ad.reportAdShow(id);
    }

    reportAdClick(id: string) {
        this.ad && this.ad.reportAdClick(id);
    }
}

export const sdkMgr = new SdkManager();