import {GHelper} from "../Logic/Helper"

const {ccclass, property} = cc._decorator;

export let GTipRunning:TipRunning=null

@ccclass
export class TipRunning extends cc.Component{
    protected _time:number = -1
    protected _bg:cc.Sprite = null
    protected _run:cc.Sprite = null
    // use this for initialization
    onLoad() 
    {
        GTipRunning = this
        this.node.active = false

        this.node.width = cc.winSize.width
        this.node.height = cc.winSize.height
        this.node.setPosition(cc.winSize.width/2,cc.winSize.height/2)
        this._bg = GHelper.createSprite("Common/Image/Other/running_bg")
        this._bg.node.parent = this.node
        this._run = GHelper.createSprite("Common/Image/Other/running")
        this._run.node.parent = this.node
        let action =cc.repeatForever(cc.rotateBy(1,150))
        this._run.node.runAction(action)
    }
}