
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_WebView extends Page {

    @property(cc.WebView)
    webview: cc.WebView = null

    @property(cc.Node)
    webNode: cc.Node = null

    onOpen(url: string) {
        this.webview.url = url
    }

    resizeWibHeight(){
        this.webview.node.height = this.webview.node.height - 130
    }

    Btn_Close_OnClick(event)
    {
        GPageMgr.closePage("Page_WebView")
    }

    onClose(){
        console.log('Page_WebView onClose')
    }
}
