import { TaskConfig, TASK_TYPE } from "../../../data/data";
import { GHelper } from "../../../Framework/Logic/Helper";
import { GPageMgr } from "../../../Framework/View/Page/PageManager";
import { GUserMgr, TaskStatus } from "../../../Logic/UserManager";
import { resMgr } from "../../../manager/ResourceManager";
import { GHallHttp } from "../../../Net/HallHttp";
import Page_Main from "../../Page/Page_Main";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemTask extends cc.Component {

    @property(cc.Sprite)
    imgIcon: cc.Sprite = null;

    @property(cc.Label)
    labelName: cc.Label = null;

    @property(cc.Label)
    labelDesc: cc.Label = null;

    @property(cc.Button)
    btnGoto: cc.Button = null;

    @property(cc.Label)
    labelGoto: cc.Label = null;

    @property(cc.Button)
    btnGet: cc.Button = null;

    @property(cc.Label)
    labelReward: cc.Label = null;

    @property(cc.String)
    taskname=""

    start() {
        this.refresh()
    }

    refresh()
    {
        let selfuser = GUserMgr.self_user
        let task:TaskStatus = null
        if(selfuser.taskstatus)
        {
            task = selfuser.taskstatus[this.taskname]
        }
        if(!task||!task.ticket||task.ticket<=0)
        {
            this.btnGet.node.active = false
            this.btnGoto.node.active = true    
        }
        else
        {
            this.btnGet.node.active = true
            this.btnGoto.node.active = false
        }
    }
    onClickGoto() {
        if(this.taskname=="bind")
        {
            GPageMgr.openPage("Page_Bind",this.refresh.bind(this))
        }
        else if(this.taskname=="daymatch")
        {
            GPageMgr.closePage("Page_Sign")
            let page = <Page_Main>GPageMgr.getPage("Page_Main")
            if(page)
            {
                page.onToggleMatch()
            }
        }
        else if(this.taskname=="bebinded")
        {
            GPageMgr.openPage("Page_Invite")
        }
    }

    async onClickGet() {
        let res = await GHallHttp.request("gettaskticket",{task:this.taskname})
        if(res.errcode)
        {
            GHelper.showTip(res.errcode.des)
            return
        }
        GUserMgr.addOneUser(res.user)
        let ticket = res.ticket
        GHelper.showTip("恭喜您获得实物参赛券x"+ticket)

        GUserMgr.self_user.taskstatus[this.taskname].ticket=0
        
        this.refresh()
    }
}
