import { GHallHttp } from "../Net/HallHttp"
import { MatchItemInfo } from "../Service/MatchService"
import { GMsgCenter } from "../utils/MessageCenter"

/**
 * 记录用户的参赛信息和情况
 */
 export class UserMatchInfo
 {
     //比赛实例id
     ins_id=""
     //报名时间
     signup_time=0
     //报名费用
     sigup:MatchItemInfo=null
     //名词
     rank=-1
     //获得的奖励
     reward:MatchItemInfo=null
 }

export class TaskStatus
{
    //最近达成时间
    latest_time=0
    ticket=0//好友绑定赠送的票
    ticket_club=0//俱乐部绑定赠送的票
}
export class AddressItem
{
    name=""
    phone=""
    address=""
    create_time=-1
}
/**
 * 用户信息模块
 */
export class UserModel
{
    _id:string = ''
    id:number=-1
    account_id:number=-1
    nickname:string=""
    sex:number=-1
    logo:string=""
    state:number=0
    role_group:number=4
    role:number=0
    phone:string=""
    wechat:string=""
    qq:string=""
    email:string=""
    about:string=""
    pre_user_id:number=-1
    exp:number = 0
    level:number = 1//等级
    vip_exp:number = 0
    vip_level:number = 0//等级
    is_robot:number = 0
    create_time:number = 0
    coin=0//默认0
    reward_coin=0
    money=0//钱包
    ticket=0
    video_coin = 0
    clubs = {}

    addresses:AddressItem[]=[]
    address_time=0
    
    idcard_name=""
    idcard_no=""
    idcard_time=0

    sign_count=0
    sign_time=0
    
    matchinfos:{[ins_id:string]:UserMatchInfo}={} // 用户比赛信息 
    focusmatches:string[]=[] // 关注赛事

    taskstatus:{[name:string]:TaskStatus}={}

    // 是否是分销商
    is_distributor = 0
    //分成记录
    divide_all = 0
    divide_money = 0
    divide_record:{user_id:number,create_time:number,money:number,divide:number}[] = []

}

export let GUserMgr:UserManager = null
class UserManager
{
    protected _self_user_id=-1
    protected _users={}//角色信息
    init(self_user_id)
    {
        this._self_user_id = self_user_id
    }
    get self_user_id()
    {
        return this._self_user_id
    }
    get self_user()
    {
        return this.getUserByUserId(this._self_user_id)
    }
    //添加一个角色信息
    addOneUser(user:UserModel)
    {
        if(!user)
        {
            return
        }
        user.reward_coin=user.reward_coin||0
        let r = this._users[user.id]
        if(!r)
        {
            this._users[user.id] = user
        }
        else
        {
            for(let k in user)   
            {
                if(user[k]==undefined)
                {
                    continue
                }
                r[k] = user[k]
            }
        }
    }
    getUserByUserId(user_id):UserModel
    {
        return this._users[user_id]
    }
    clear()
    {
        this._users = { }
    }


    async refreshSelfUser(){
       let info = await GHallHttp.request("getuser")
       this.addOneUser(info.user)
       GMsgCenter.send("updateuserinfo")
       GMsgCenter.send("coinchanged")

    }
}

GUserMgr = new UserManager()