import {GHelper} from "../Logic/Helper"

const {ccclass, property} = cc._decorator

@ccclass
export class ReverseTimerLabel extends cc.Component
{
    _nums: {top_one:cc.Sprite,bottom_one:cc.Sprite,str:string}[]
    _root:cc.Node
    _num: number=0
    _forceLen: number=-1
    _max_num: number=9
    
    @property()
    selectedFont: number=10
    
    @property()
    num_spacing: number=6
    _interval_handler:number=0
    _seconds:number=0
    _sp_rect:any
    get seconds() 
    {
        return this._seconds
    }
    set seconds(value) 
    {
        if(this._seconds===value)
        {
            return
        }
        this._seconds = value
        if(this._interval_handler)//表示正在倒计时的过程中修改了数据所以要重新run
        {
            this.run()
        }
    }
    end_callback:Function//时间为0的时候的回调
    _init_callback:Function
    static create(num=0,font_num=10,forcelen=-1)
    {
        let rtl = <ReverseTimerLabel>GHelper.createNode(ReverseTimerLabel)
        rtl._num = num
        rtl._forceLen = forcelen
        rtl.selectedFont = font_num
        rtl._seconds = num
        return rtl
    }

    _getNumStr():string
    {
        let numStr = this._num+""
        //numStr = numStr.replace(":",".")
        let length = numStr.length
        if(this._forceLen > 0 && length < this._forceLen)
        {
            for(let i = 1; this._forceLen - length;++i)
            {
                numStr = "0" + numStr
            }
            length = numStr.length
        }
        return numStr
    }
    // use this for initialization
    onLoad() 
    {
        let self = this
        this._root = new cc.Node()
        this._root.parent = this.node
        
        let str_one = "Image/Num/" + this.selectedFont + "_0"
        GHelper.createSprite(str_one, function(sp)
        {
            let rect = sp.spriteFrame.getRect()
            self._init(self._getNumStr(),rect)
        })
    }
    _init(numStr,r)
    {
        this._sp_rect = r
        let length = numStr.length
        this._nums = []
        for(let i = length-1; i>=0; --i)
        {
            let x =(i - 1) * r.width -(i - 1) * this.num_spacing
            let str_num = numStr[i]
            let tb = this._createOne(str_num,x)
            this._nums[i] = {
                top_one : tb.top_one,
                bottom_one : tb.bottom_one,
                str : str_num
            }
        }
        if(this._init_callback)
        {
            this._init_callback()
        }
    }
    _createOne(str_num,x)
    {
        let str_one = "Image/Num/" + this.selectedFont + "_" + str_num
        let r = this._sp_rect
        let top_one = GHelper.createSprite(str_one, function(sp)
        {
            let rect = new cc.Rect(0,0,r.width,r.height/2)
            sp.spriteFrame.setRect(rect)
            sp.node.width = r.width
            sp.node.height = r.height/2
        },null,"_top")
        top_one.node.x = x
        top_one.node.setAnchorPoint(0.5, 0)
        let bottom_one = GHelper.createSprite(str_one, function(sp)
        {
            let rect = new cc.Rect(0,r.height/2,r.width,r.height/2)
            sp.spriteFrame.setRect(rect)
            sp.node.width = r.width
            sp.node.height = r.height/2
        },null,"_bottom")
        bottom_one.node.x = x
        bottom_one.node.setAnchorPoint(0.5, 1)

        top_one.node.parent = this._root
        bottom_one.node.parent = this._root

        return {top_one:top_one,bottom_one:bottom_one,str:str_num}
    }
    stop()
    {
        if(this._interval_handler)
        {
            clearInterval(this._interval_handler)
            this._interval_handler = null
        }
    }
    run()
    {
        if(this._sp_rect)
        {
            this._run()
        }
        else
        {
            this._init_callback = this._run.bind(this)
        }
    }
    _run()
    {
        this._init_callback = null
        let self = this
        let startTime = GHelper.getLocalTime()
        let endTime = startTime+this._seconds*1000
        if(this._interval_handler)
        {
            clearInterval(this._interval_handler)
            this._interval_handler = null
        }
        if(this._seconds<=0)
        {
            return
        }
        this._interval_handler = setInterval(function(){
            let curTime = GHelper.getLocalTime()
            let curNum = Math.floor((endTime-curTime)/1000)
            curNum = curNum>0?curNum:0
            if(curNum!=self._seconds)
            {
                self._changeTo(curNum)
                self._seconds = curNum
            }
            if(curNum<=0)
            {
                if(self.end_callback)
                {
                    if(self._interval_handler)
                    {
                        clearInterval(self._interval_handler)
                        self._interval_handler = null
                    }
                    self.end_callback()
                }
            }
        },1000)
    }
    _changeTo(num)
    {
        let numStr = num+""
        let length = numStr.length
        if(this._forceLen > 0 && length < this._forceLen)
        {
            for(let i = 0; i<this._forceLen - length;++i)
            {
                numStr = "0" + numStr
            }
            length = numStr.length
        }
        for(let i = length-1; i>=0; --i)
        {
            this._changeOne(numStr,i)
        }
    }
    _changeOne(numStr,i)
    {
        let r = this._sp_rect
        let str_num = numStr[i]
        let pre = this._nums[i]
        if(pre.str != str_num)
        {
            let x =(i - 1) * r.width -(i - 1) * this.num_spacing
            let tb = this._createOne(str_num,x)

            tb.bottom_one.node.scaleY = 0
            let pto = pre.top_one
            let pbo = pre.bottom_one
            
            pre.top_one = tb.top_one
            pre.bottom_one = tb.bottom_one
            pre.str = tb.str

            let action = cc.sequence(
                cc.scaleTo(0.3, 1, 0),
                cc.callFunc( function()
                {
                    let newAction = cc.sequence(
                        cc.scaleTo(0.2, 1, 1),
                        cc.callFunc( function()
                        {
                            pbo.node.removeFromParent()
                        } )
                    )
                    tb.bottom_one.node.runAction(newAction)
                } ),
                cc.removeSelf()
            )
            tb.top_one.node.zIndex = pto.node.zIndex - 1000
            pto.node.runAction(action)
        }
    }
    onDestroy()
    {
        if(this._interval_handler)
        {
            clearInterval(this._interval_handler)
            this._interval_handler = null
        }
    }
}