import { GHelper } from "../Logic/Helper";
import { GPageMgr } from "../View/Page/PageManager";

const {ccclass, property} = cc._decorator;

enum EAjustPosType
{
    None,
    Left,
    Top,
    Right,
    Bottom,
    LeftTop,
    RightTop,
    RightBottom,
    LeftBottom
}

enum EAjustScaleType
{
    None,
    Width,
    Height,
    All,
    DtHeight
}

@ccclass
export class Ajust extends cc.Component
{
    protected _scale=-1
    get scale()
    {
        if(this._scale!=-1)
        {
            return this._scale
        }
        //获取当前游戏窗口大小
        let frameSize = cc.view.getFrameSize()
        //设计分辨率
        let designSize = cc.view.getDesignResolutionSize()
        let sw=designSize.width/frameSize.width
        //let sh=designSize.height/frameSize.height
        this._scale=sw//Math.max(sw,sh)
        return this._scale
    }
    protected _dtw=-1
    get dtWidth()
    {
        if(this._dtw!=-1)
        {
            return this._dtw
        }
        //获取当前游戏窗口大小
        let frameSize = cc.view.getFrameSize()
        //设计分辨率
        let designSize = cc.view.getDesignResolutionSize()
        this._dtw = Math.abs(frameSize.width*this.scale - designSize.width)
        return this._dtw
    }
    protected _dth=-1
    get dtHeight()
    {
        if(this._dth!=-1)
        {
            return this._dth
        }
        //获取当前游戏窗口大小
        let frameSize = cc.view.getFrameSize()
        //设计分辨率
        let designSize = cc.view.getDesignResolutionSize()
        this._dth = frameSize.height*this.scale - designSize.height
        return this._dth
    }
    get lh()
    {
        //获取当前游戏窗口大小
        let frameSize = cc.view.getFrameSize()
        let safeSize = cc.sys.getSafeAreaRect()
        let visibleSize = cc.view.getVisibleSize()
        if(safeSize.size.equals(visibleSize))
        {
            return {h:0,w:0}
        }
        let lh={h:(frameSize.height*this.scale-safeSize.height),w:(frameSize.width*this.scale-safeSize.width)}
        if(cc.sys.os==cc.sys.OS_IOS)
        {
            lh={h:Math.abs(safeSize.y),w:Math.abs(safeSize.x)}
        }
        return lh
    }
    get info()
    {
        //获取当前游戏窗口大小
        let frameSize = cc.view.getFrameSize()
        let ratio = cc.view.getDevicePixelRatio()
        //设计分辨率
        let designSize = cc.view.getDesignResolutionSize()
        let safeSize = cc.sys.getSafeAreaRect()
        let visibleSize = cc.view.getVisibleSize()
        let winSize = cc.winSize
        let data = {
            scale:this.scale,
            lb:this.lh,
            frameSize,
            designSize,
            safeSize,
            visibleSize,
            winSize,
            ratio
        }
        return JSON.stringify(data)
    }
    @property({type:cc.Enum(EAjustPosType)})
    type=EAjustPosType.None
    @property({type:cc.Enum(EAjustScaleType)})
    scaleType=EAjustScaleType.None
    onLoad()
    {
        let dty_ipad = (1-GPageMgr.node.scale)*1344/2
        let lh = this.lh
        switch(this.type)
        {
            case EAjustPosType.Left:
            {
                this.node.x-=this.dtWidth/2
                break
            }
            case EAjustPosType.Top:
            {
                this.node.y+=this.dtHeight/2-lh.h+dty_ipad
                break
            }
            case EAjustPosType.Right:
            {
                this.node.x+=this.dtWidth/2
                break
            }
            case EAjustPosType.Bottom:
            {
                this.node.y-=this.dtHeight/2+dty_ipad
                break
            }
            case EAjustPosType.LeftTop:
            {
                this.node.x-=this.dtWidth/2
                this.node.y+=this.dtHeight/2-lh.h+dty_ipad
                break
            }
            case EAjustPosType.RightTop:
            {
                this.node.x+=this.dtWidth/2
                this.node.y+=this.dtHeight/2-lh.h+dty_ipad
                break
            }
            case EAjustPosType.RightBottom:
            {
                this.node.x+=this.dtWidth/2
                this.node.y-=this.dtHeight/2+dty_ipad
                break
            }
            case EAjustPosType.LeftBottom:
            {
                this.node.x-=this.dtWidth/2
                this.node.y-=this.dtHeight/2+dty_ipad
                break
            }
        }
        //获取当前游戏窗口大小
        let frameSize = cc.view.getFrameSize()
        //设计分辨率
        let designSize = cc.view.getDesignResolutionSize()
        switch(this.scaleType)
        {
            case EAjustScaleType.Width:
            {
                let s = designSize.width/(frameSize.width*this.scale)
                this.node.width/=s
                break
            }
            case EAjustScaleType.Height:
            {
                let s = designSize.height/(frameSize.height*this.scale)
                this.node.height/=s
                break
            }
            case EAjustScaleType.All:
            {
                let sw = designSize.width/(frameSize.width*this.scale)
                let sh = designSize.height/(frameSize.height*this.scale)
                this.node.height/=Math.min(sw,sh)
                this.node.width/=Math.min(sw,sh)
                this.node.height+=dty_ipad*2
                break
            }
            case EAjustScaleType.DtHeight:
                {
                    this.node.height+=this.dtHeight-lh.h+dty_ipad*2
                }
        }
    }
}
