import { GGameDM } from "../../Framework/DataModel/GameDataModel";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../Logic/UserManager";
import { GMsgCenter } from "../../utils/MessageCenter";

import { GNativeTool } from "../../Framework/Logic/NativeTool"

import { layerMgr } from "../../manager/LayerManager"
import { GaoDeMapTool } from "../../Framework/Logic/GaoDeMapTool";
import { GHelper } from "../../Framework/Logic/Helper";
import { GHallHttp } from "../../Net/HallHttp";
import { ykUtils } from "../../utils/YkUtils";
import { GItemCfg } from "../../Service/ItemService";
import { GAppDM } from "../../DataModel/AppDataModel";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Page_Main extends Page {

    @property(cc.Node)
    nodeHome: cc.Node = null;

    @property(cc.Node)
    nodeMatch: cc.Node = null;

    @property(cc.Node)
    nodeClub: cc.Node = null;

    @property(cc.Node)
    nodePerson: cc.Node = null;

    @property(cc.ToggleContainer)
    toggles: cc.ToggleContainer = null;

    @property(cc.Node)
    nodeHead: cc.Node = null;

    start() 
    {
        //预打开这个界面
        GPageMgr.openPage("Page_MatchInfo")
        GPageMgr.closePage("Page_CodeLogin")
        GPageMgr.closePage("Page_AccountLogin")
        if(GGameDM.isServerUpdate){
            GPageMgr.openPage("Page_Maintain");
            return 
        }

        if(!GGameDM.isReviewVersion){
            GPageMgr.openPage("Page_NeiYe")
        }
       
        this.toggles.toggleItems[0].check();
        this.onToggleHome();
        GMsgCenter.register("toexchange",this.showNodeMatch.bind(this),this)
        if(GGameDM.isReviewVersion)
        {
            this.toggles.toggleItems[2].node.active=false
            this.toggles.node.getComponent(cc.Layout).spacingX=180
            this.toggles.toggleItems[1].getComponentInChildren(cc.Label).string="购票"
            // this.toggles.node.children[0].getChildByName('mark').getChildByName('label').getComponentInChildren(cc.Label).string="购票"
        
            cc.find('toggle_match/mark/label',this.toggles.node).getComponent(cc.Label).string = "购票"
        }
        if(cc.sys.isNative)
        {
            if (!GUserMgr.self_user.idcard_no&&!GGameDM.isReviewVersion && GAppDM.account_server.host!="123.60.12.239") {
                GPageMgr.openPage("Page_IDCard");
            }
        }

        GaoDeMapTool.getLocation(true)
        this.initClubView()

        ykUtils.getServerTime()

    }

   



    async initClubView(){
        var chs = cc.find('layout_center',this.node).children
        for(var v of chs){
            if(v.name == 'Page_Home'){
                v.active = true
            }else{
                v.active = false
            }
        }

     
    }
    protected onDestroy(): void {
        GMsgCenter.unregister("toexchange",this)
    }
    onToggleHome() {
        // this.nodeHome.active = true
        // this.nodeMatch.active = false
        // this.nodePerson.active = false
        // this.nodeHead.active = false
        // this.hideClubAll()
        this.changeNode('Page_Home')
    }

    onToggleMatch() {
        // this.nodeHome.active = false
        // this.nodeMatch.active = true
        // this.nodePerson.active = false
        // this.nodeHead.active = false
        // this.hideClubAll()
        this.changeNode('Page_MatchList')
    }

    onToggleLive() {
        this.nodeHome.active = false
        this.nodeMatch.active = false
        this.nodePerson.active = false
        this.nodeHead.active = false
        this.nodeClub.active = false
    }

    onToggleClub() {
        // this.nodeHome.active = false
        // this.nodeMatch.active = false
        // this.nodeClub.active = true
        // this.nodePerson.active = false
        // this.nodeHead.active = false;
        this.changeNode('node_club')
    }

    onTogglePerson() {
        // this.nodeHome.active = false
        // this.nodeMatch.active = false
        // this.nodeHead.active = false
        // this.nodePerson.active = true
        // this.hideClubAll()
        GItemCfg.init();
        this.changeNode('Page_My')
    }

    changeNode(name){
        var layout_center = cc.find('layout_center',this.node).children
        layout_center.forEach(element => {
            if(element.name == name){
                element.active = true
                element.getComponent('Page')&&element.getComponent('Page')['refreshData']&&element.getComponent('Page')['refreshData']()
            }else{
                element.active = false
            }
        });
    }


    showNodeHome(...args) {
        this.toggles.toggleItems[0].check();
        this.onToggleHome();
        this.nodeHome.emit("init", ...args);
    }

    showNodeMatch(...args) {
        this.toggles.toggleItems[1].check();
        this.onToggleMatch();
    }

    showNodeClub(...args) {
        this.toggles.toggleItems[2].check()
        this.onToggleLive()
    }

    showNodePerson(...args) {
        this.toggles.toggleItems[3].check()
        this.onTogglePerson()
    }

    onClickAvatar() {
        this.nodeHead.active = true;
    }
    // 
    faceCheckTest(){
        GNativeTool.callRlsb(GUserMgr.self_user.nickname, GUserMgr.self_user.idcard_no.toString(), GUserMgr.self_user_id.toString())
    }

    shareTest(){
        GHelper.wxShareShot(2, this.node);
    }
}
