// 专门用来计算奖励

import { core } from "../../Framework/Core/Base"
import { GItemCfg } from "../../Service/ItemService"
import { MatchInsModel, MatchItemInfo, MultiDynamicReward } from "../../Service/MatchService"

export let GTexasRewardTool:TexasMatchReward = null

export interface RewardCoinDetail{
    lastRewardCoin:number,
    allRewardCoin:number
}



class TexasMatchReward
{
    getRewardAll(matchnIns:MatchInsModel,mrs:MultiDynamicReward[]):MatchItemInfo[]{
        // 判断奖励类型
        let rewards:MatchItemInfo[] = null
        switch(matchnIns.match.dynamic_reward){
            case 0:
                //固定奖励
                rewards = this.getImmobilizationReward(matchnIns)
                break
            case 1:
                //基础动态奖励
                rewards = this.getBaseActiveReward(matchnIns)
                break
            case 2:
                // 高级动态奖励
                rewards = this.getAdvanceActiveReward(matchnIns,mrs)
                break
            case 3:
                rewards = this.getAdvanceActiveCoinReward(matchnIns,mrs)
                // 高级积分动态奖励
                break
            case 4:
                 // 纯积分动态奖励
                rewards = this.getAdvanceActiveOnlyCoinReward(matchnIns,mrs)
                break
        }

        return rewards
    }

    
    // 固定奖励 (不理会破保)
    getImmobilizationReward(matchnIns:MatchInsModel){
        return matchnIns.match.reward
    }
    // 基础动态奖励(破保后所有的奖励都放在下一位)
    getBaseActiveReward(matchnIns:MatchInsModel){
        let rewards = matchnIns.match.reward
        let oneCost = this.getOneCost(matchnIns)

        let lastReward = rewards[rewards.length-1]

        let rewardDetail:RewardCoinDetail = this.getRewardCoinDetail(rewards)

        // 进行破保计算

        // 所有积分
        let total_coin = matchnIns.player_num*oneCost
        let dtcoin = total_coin - rewardDetail.allRewardCoin

      
        if(dtcoin<=0){
            return core.clone(rewards) 
        }
        // ==================累加到最后的奖励
        
        let newRewards = this.getNewRewards_acc(dtcoin,rewardDetail,lastReward)
        let res = rewards.concat(newRewards)
        return res
    }

    // 获得高级动态奖励的奖励
    getAdvanceActiveReward(matchnIns:MatchInsModel,mrs:MultiDynamicReward[]){
        let rewards = matchnIns.match.reward
        let oneCost = this.getOneCost(matchnIns)

        let lastReward = rewards[rewards.length-1]
        let rewardDetail:RewardCoinDetail = this.getRewardCoinDetail(rewards)

        // 进行破保计算

        // 所有积分
        let total_coin = matchnIns.player_num*oneCost
        let dtcoin = total_coin - rewardDetail.allRewardCoin

        let needAddCoinNum = matchnIns.match.gg_reward_begin_coin_num || 0
        let realLeft = dtcoin

        if(needAddCoinNum>0){
            realLeft = dtcoin - needAddCoinNum
            if(dtcoin<=needAddCoinNum){
                // 积分不足报名
                dtcoin = needAddCoinNum
            }
        }
        if(dtcoin<=0){
            return core.clone(rewards) 
        }
        let peopleOff = matchnIns.match.gg_reward_begin_people_num||0
        let newRewards = this.getNewRewards_advance(dtcoin,rewardDetail,lastReward,oneCost,mrs,realLeft,peopleOff)
        let res = rewards.concat(newRewards)
        return res
    }

    // 高级积分动态奖励 
    getAdvanceActiveCoinReward(matchnIns:MatchInsModel,mrs:MultiDynamicReward[]){
        let rewards = matchnIns.match.reward
        let oneCost = this.getOneCost(matchnIns)

        let lastReward = rewards[rewards.length-1]
        let rewardDetail:RewardCoinDetail = this.getRewardCoinDetail(rewards)

        // 进行破保计算

        // 所有积分
        let total_coin = matchnIns.player_num*oneCost
        let dtcoin = total_coin - rewardDetail.allRewardCoin

        let needAddCoinNum = matchnIns.match.gg_reward_begin_coin_num || 0
        let realLeft = dtcoin
        if(needAddCoinNum>0){
            realLeft = dtcoin - needAddCoinNum
            if(dtcoin<=needAddCoinNum){
                // 积分不足报名
                dtcoin = needAddCoinNum
            }
        }
        if(dtcoin<=0){
            return core.clone(rewards) 
        }
        let peopleOff = matchnIns.match.gg_reward_begin_people_num||0
        let newRewards = this.getNewRewards_advance(dtcoin,rewardDetail,lastReward,oneCost,mrs,realLeft,peopleOff,true)
        let res = rewards.concat(newRewards)
        return res
    }

    // 纯积分动态奖励
    getAdvanceActiveOnlyCoinReward(matchnIns:MatchInsModel,mrs:MultiDynamicReward[]){
        let rewards = matchnIns.match.reward
        let oneCost = this.getOneCost(matchnIns)

        let lastReward = rewards[rewards.length-1]
        let rewardDetail:RewardCoinDetail = this.getRewardCoinDetail(rewards)

        let total_coin = matchnIns.player_num*oneCost
        if(total_coin<rewardDetail.lastRewardCoin){
            total_coin = rewardDetail.lastRewardCoin
        }

        let peopleOff = matchnIns.match.gg_reward_begin_people_num||0
        let newRewards = this.getNewRewards_advance(total_coin,null,null,
            oneCost,mrs,total_coin,peopleOff,true)

        return newRewards
    }



    // 高级动态奖励 realLeft真正破保后的遗留  peopleOff 奖励人数的偏移  
    getNewRewards_advance(dtcoin:number,
        rewardDetail:RewardCoinDetail,
        lastReward:MatchItemInfo,
        oneCost:number,
        mrs:MultiDynamicReward[],
        realLeft:number,
        peopleOff=0,         //奖励开始的偏移量
        onlayCoin = false //是否只给积分
        ):MatchItemInfo[]{
        // 动态分配所有的奖励
        
        let left_signup_num = 1
        // 先找到奖励区间
        if(realLeft<=0){
            // 只计算真正遗留下的可以计算奖励人数的竞技点
            left_signup_num = 1
        }else{
            left_signup_num = Math.floor(realLeft/oneCost)||1
        }
        left_signup_num = left_signup_num+peopleOff

        let mr:MultiDynamicReward = null
        for(let i=0;i<mrs.length;++i)
        {
            let temp = mrs[i]
            if(left_signup_num>=temp.min&&left_signup_num<=temp.max)
            {
                mr = temp
                break
            }
        }
        if(!mr)
        {
            mr = mrs[mrs.length-1]
        }

        let newRewad = []
        let outReward = 0 //溢出奖励 
        for(var v of mr.rate){
            let rew:MatchItemInfo = null
            let rateReward = Math.floor(dtcoin*v/100)||1

            // 溢出奖励+给下一名
            if(outReward>0){
                rateReward = rateReward+outReward
                outReward = 0
            }
            //=====================================================================

            if(lastReward&&rewardDetail && rateReward>= rewardDetail.lastRewardCoin){
                if(onlayCoin){
                    rew = new MatchItemInfo()
                    rew.id=-99
                    rew.count=rewardDetail.lastRewardCoin
                }else{
                    rew = core.clone(lastReward)
                }
                outReward = rateReward -rewardDetail.lastRewardCoin
            }else{
                rew = new MatchItemInfo()
                rew.id=-99
                rew.count=rateReward
            }
            newRewad.push(rew)
        }
        return newRewad
    }


    // 累加最后一位奖励
    getNewRewards_acc(dtcoin:number,rewardDetail:RewardCoinDetail,lastReward:MatchItemInfo):MatchItemInfo[]{
        let num = Math.floor(dtcoin/rewardDetail.lastRewardCoin)
        let left = dtcoin%rewardDetail.lastRewardCoin
        
        let newRewad = []
        for(var i=0;i<num;i++){
            newRewad.push(core.clone(lastReward))
        }
        if(left>0){
            // 剩余的奖励 通过奖励点发放
            let rwOne=new MatchItemInfo()
            rwOne.id=-99
            rwOne.count=left
            newRewad.push(rwOne)
        }

        return  newRewad
    }

    

    // 获取一次报名所需
    getOneCost(matchnIns:MatchInsModel){
        let costs=matchnIns.match.signup_cost
        let coin = 0
        for(let key in costs)
        {
            if(costs[key].id==-99)
            {
                coin=costs[key].count
                break
            }
        }
        return coin
    }
    // 获取最后一名的奖励额度 以及所有奖励的额度
    getRewardCoinDetail(rewards:MatchItemInfo[]):RewardCoinDetail{
        let res:RewardCoinDetail = {
            lastRewardCoin:0,
            allRewardCoin:0
        }

        for(var k in rewards){
            let dcoin = 0
            if(rewards[k].id==-99)
            {
                dcoin=rewards[k].count
            }
            else
            {
                let id = rewards[k].id
                let item = GItemCfg.getItemById(id)
                dcoin=item.coin*rewards[k].count
            }


            res.lastRewardCoin=dcoin
            res.allRewardCoin+=dcoin
        }

        return res
    }


}
GTexasRewardTool = new TexasMatchReward()