export let GLocalizationDataModel={
    _cfg:{},
    _curLan:"cn",
    _noTip:"Key Not Found!",
    init:function(lan:string,all)
    {
        this.add(all)
        this._curLan = lan
    },
    add:function(add)
    {
        let self = this
        for(let key in add)
        {
            self._cfg[key] = add[key]
        }
    },
    getStringByKey:function(key):string
    {
        let item = this._cfg[key]
        if(!item)
        {
            return this._noTip
        }
        return item[this._curLan]
    },
    getTipStr:function():string
    {
        return this._noTip
    }
}