import { core } from "../../Framework/Core/Base";
import { GGameDM } from "../../Framework/DataModel/GameDataModel";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../Logic/UserManager";
import { GHallHttp } from "../../Net/HallHttp";
import Widget_InviteeItem, { FollowerItem } from "../Widget/Widget_InviteeItem";
const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_Invitee extends Page {
    
    @property(cc.Button)
    btn_invite: cc.Button = null

    @property(cc.Label)
    labelMoney: cc.Label = null

    @property(cc.ScrollView)
    scrollview: cc.ScrollView = null

    @property(cc.Prefab)
    pf_item: cc.Prefab = null
    private cb = null
    start() {
        if(GGameDM.isReviewVersion)
        {
            this.labelMoney.node.parent.active=false
            this.btn_invite.node.active=false
        }
        this.init()
    }

    onOpen(cb){
        this.cb = cb
    }
    onClose(){
        this.cb&&this.cb()
    }

    async init() {
        if(GGameDM.isReviewVersion)
        {
            return
        }
        this.labelMoney.string = (GUserMgr.self_user.ticket?GUserMgr.self_user.ticket:0) + " 张"
        let res = await GHallHttp.request("getfollwers")
        let followers:FollowerItem[]=res?.followers
        if(!followers)
        {
            return
        }
        for(let i=0;i<followers.length;++i)
        {
            let f = followers[i]
            let node = cc.instantiate(this.pf_item)
            this.scrollview.content.addChild(node)
            let sc = node.getComponent(Widget_InviteeItem)

            if(res.newbies&&res.newbies.indexOf(f.id)!=-1){
                f.isNew = true
            }
            sc.init(f)
        }
        this.scrollview.scrollToTop()

        GHallHttp.request("readbebindedticket")
        this.initFenxiao()
    }

    // 分销系统初始化
    initFenxiao(){
        let fenXiao = cc.find('content/fenxiao',this.node)
        let user = GUserMgr.self_user
        if(user.is_distributor!=1){
            fenXiao.active = false
            return
        }
        fenXiao.active = true

        user.divide_all =  user.divide_all || 0

        let allGet = user.divide_all*100
        let curDate = Date.now()
        let today = core.format(curDate,"YYYY-MM-DD")
        let divideDetail = user.divide_record||[]
        let todayGet = 0
        divideDetail.forEach((element)=>{
            if(core.format(element.create_time,"YYYY-MM-DD")==today){
                todayGet+=element.divide
            }
        })
        todayGet = todayGet*100

        cc.find('lb_reward1',fenXiao).getComponent(cc.Label).string = '推广奖励: '+allGet
        cc.find('lb_reward2',fenXiao).getComponent(cc.Label).string = '推广奖励(今日)：'+todayGet
    }

    onClickInvite() {
        GPageMgr.openPage("Page_Invite")
    }

    onClickWithdraw() {
        
    }
    Btn_Close_OnClick(event)
    {
        GPageMgr.closePage("Page_Invitee")
    }
}
