import { GGameDM } from "../../Framework/DataModel/GameDataModel";
import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GHallHttp } from "../../Net/HallHttp";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_Setting extends Page {

    @property(cc.Node)
    node_match:cc.Node=null
    start() {
        if(GGameDM.isReviewVersion)
        {
            this.node_match.active=false
        }
    }

    onClickChangePhoneNumber() {
        GPageMgr.openPage("Page_ChangePhone")
    }

    onClickChangePassword() {
        GPageMgr.openPage("Page_ResetPassword")
    }

    onClickTerm() {
        //GPageMgr.openPage("Page_Term")
        GPageMgr.openPage("Page_WebView","http://www.zdpoker.com/protocol/user")
    }

    onClickStatement() {
        GPageMgr.openPage("Page_Statement")
    }

    onClickAbout() {
        //GPageMgr.openPage("Page_About")
        GPageMgr.openPage("Page_WebView","http://www.zdpoker.com/protocol/privacy")
    }

    onClickLogout() {
        GPageMgr.openPage("Page_Logout")
    }

    onClickDeleteAccount() {
        GPageMgr.openPage("Page_Tips",{
            tips:"确定要注销帐号吗",
            buttons:[
                {
                    label:"确定",
                    callback:async ()=>
                    {
                        let res = await GHallHttp.request("removeAccount",{})
                        if(res.errcode)
                        {
                            GHelper.showTip(res.errcode.des)
                            return
                        }
                        else
                        {
                            GHelper.showTip("注销帐号成功")
                            GPageMgr.openPage("Page_Logout")
                        }
                    }
                },
                {
                    label:"取消",
                    callback:()=>
                    {

                    }
                }
            ]
        })
    }

    Btn_Close_OnClick(event)
    {
        GPageMgr.closePage("Page_Setting")
    }
}
