import { GPageMgr } from "../../Framework/View/Page/PageManager";

const {ccclass, property} = cc._decorator;

export class ButtonInfo {
    label: string;
    callback?: Function;
}

@ccclass
export default class Page_Tips extends cc.Component {

    @property(cc.Button)
    btnClose: cc.Button = null;

    @property(cc.Label)
    labelTips: cc.Label = null;

    @property(cc.Node)
    layoutButton: cc.Node = null;

    @property([cc.Node])
    itemButtons: cc.Node[] = [];

    private tips: string = "";
    private buttons: ButtonInfo[] = [];

    onOpen(info:{tips: string, buttons: ButtonInfo[]}) {
        this.tips = info.tips
        this.buttons = info.buttons || []
        
        this.initUI();
    }

    initUI() {
        this.labelTips.string = this.tips || "";

        this.btnClose.interactable = !this.buttons.length;

        this.layoutButton.removeAllChildren();
        for (let i = 0; i < this.buttons.length; i++) {
            let info = this.buttons[i];
            let node = cc.instantiate(this.itemButtons[i % this.itemButtons.length]);
            this.layoutButton.addChild(node);

            node.getChildByName("label").getComponent(cc.Label).string = info.label;

            node.on(cc.Node.EventType.TOUCH_END, async () => {
                if(info.callback)
                {
                    await info.callback()
                }
                this.onClickClose();
            })
        }
    }

    onClickClose() {
        GPageMgr.closePage("Page_Tips")
    }
}
