export class core {
    static isObject(param):boolean
    {
        return typeof (param) === "object"
    }
    static isArray(param):boolean
    {
        return param instanceof Array
    }
    static isNumber(param):boolean
    {
        return !isNaN(param)
    }
    static isNaN(param):boolean
    {
        return isNaN(param)
    }
    static isFunction(param):boolean
    {
        return typeof (param) === "function"
    }
    static isString(param):boolean
    {
        return typeof (param) === "string"
    }
    static random(min?:number, max?:number):number
    {
        if(min==undefined)
        {
            return Math.random()
        }
        else if(!max)
        {
            max = min
            min = 0
        }
        return Math.floor(Math.random() * (max - min + 1) + min)
    }
    static clone(obj)
    {
        if (typeof obj==="undefined")
        {
            return null
        }
        if (core.isArray(obj))
        {
            let newArray = new Array()
            for (let i = 0, length = obj.length; i < length; ++i)
            {
                newArray[i] = this.clone(obj[i])
            }
            return newArray
        }
        if (core.isObject(obj))
        {
            let newObj = new Object()
            for (let i in obj)
            {
                newObj[i] = this.clone(obj[i])
            }
            return newObj
        }
        return obj
    }
    //合并但是不改变dest
    static merge(dest, src)
    {
        let t = core.clone(dest)
        if (!src)
        {
            return t
        }
        if (core.isArray(src))
        {
            t = t || []
            let tlen = t.length
            for (let i = 0, len = src.length; i < len; ++i)
            {
                let value = src[i]
                if (core.isFunction(value))
                {
                    continue
                }
                if (core.isArray(value) || core.isObject(value))
                {
                    if (i < tlen)
                    {
                        t[i] = core.merge(null, value)
                    }
                    else
                    {
                        t.push(core.merge(null, value))
                    }

                }
                else
                {
                    if (i < tlen)
                    {
                        t[i] = value
                    }
                    else
                    {
                        t.push(value)
                    }
                }
            }
        }
        else if (core.isObject(src))
        {
            t = t || {}
            for (let key in src)
            {
                let value = src[key]
                if (core.isFunction(value))
                {
                    continue
                }
                if (core.isArray(value) || core.isObject(value))
                {
                    t[key] = core.merge(null, value)
                }
                else
                {
                    t[key] = value
                }
            }
        }
        else
        {
            t = src
        }
        return t
    }
    //只是取出template里面已经有的部分
    static getExit(template, src)
    {
        if (!src || !template)
        {
            return {}
        }
        let t = {}
        for (let key in src)
        {
            if (template[key]===undefined)
            {
                continue
            }
            let value = src[key]
            if (core.isFunction(value))
            {
                continue
            }
            if (core.isArray(value) || core.isObject(value))
            {
                t[key] = core.getExit(template[key], value)
            }
            else
            {
                t[key] = value
            }
        }
        return t
    }
    static dump(data)
    {
        let strSpace = "    "
        let _dump = function(data, intent)
        {
            let startIntentStr = ""
            for (let i = 0; i < intent; ++i)
            {
                startIntentStr += strSpace
            }
            let contentStr = ""
            if (core.isArray(data))
            {
                contentStr += "\n" + startIntentStr + "[\n"
                for (let i = 0, length = data.length; i < length; ++i)
                {
                    if (!core.isArray(data[i]) && !core.isObject(data[i]) && !core.isFunction(data[i]))
                    {
                        contentStr += startIntentStr + strSpace + _dump(data[i], intent + 1) + ",\n"
                    }
                    else
                    {
                        contentStr += startIntentStr + _dump(data[i], intent + 1) + ",\n"
                    }
                }
                contentStr += startIntentStr + "]"
            }
            else if (core.isObject(data))
            {
                contentStr += startIntentStr + "{\n"
                for (let key in data)
                {
                    contentStr += startIntentStr + strSpace + key + ":" + _dump(data[key], intent + 1) + ",\n"
                }
                contentStr += startIntentStr + "}"
            }
            else if (!core.isFunction(data))
            {
                if (core.isString(data))
                {
                    return "\"" + data + "\""
                }
                return data
            }
            if (contentStr == "")
            {
                return ""
            }
            return contentStr
        }
        console.log(_dump(data, 0))
    }

    static toArray(table):any[]
    {
        let array = []
        for (let key in table)
        {
            if (core.isFunction(table[key]))
            {
                continue
            }
            array.push(key)
            array.push(table[key])
        }
        return array
    }

    static foreach(items, callback)
    {
        if (!items || !callback)
        {
            return
        }
        if (core.isNumber(items))
        {
            for (let i = 0; i < items; ++i)
            {
                let ret = callback(i)
                if(ret)
                {
                    return
                }
            }
        }
        else if (core.isString(items))
        {
            for (let i = 0, length = items.length; i < length; i++)
            {
                let ret = callback(i,items.charAt(i))
                if(ret)
                {
                    return
                }
            }
        }
        else if (core.isArray(items))
        {
            for (let i = 0, length = items.length; i < length; ++i)
            {
                let ret = callback(i,items[i])
                if(ret)
                {
                    return
                }
            }
        }
        else if (core.isObject(items))
        {
            for (let key in items)
            {
                let ret = callback(key,items[key])
                if(ret)
                {
                    return
                }
            }
        }
        return
    }

    static getLength(items):number
    {
        if(core.isArray(items))
        {
            return items.length
        }
        if(core.isObject)
        {
            let len = 0
            core.foreach(items,function()
            {
                ++len
            })
            return len
        }
        return 0
    }

    static find(items, callback)
    {
        if (!items || !callback)
        {
            return
        }
        if (core.isArray(items))
        {
            for (let i = 0, length = items.length; i < length; ++i)
            {
                if (callback(items[i]) === true)
                {
                    return items[i]
                }
            }
        }
        else if (core.isObject(items))
        {
            for (let key in items)
            {
                if (callback(items[key]) === true)
                {
                    return items[key]
                }
            }
        }
    }

    static indexOf(items, value)
    {
        if (!items || !value)
        {
            return -1
        }
        if (core.isArray(items))
        {
            for (let i = 0, length = items.length; i < length; ++i)
            {
                if (items[i] === value)
                {
                    return i
                }
            }
        }
        else if (core.isObject(items))
        {
            for (let key in items)
            {
                if (items[key] === value)
                {
                    return key
                }
            }
        }
        return -1
    }
    static include = function(objs, key) 
    {
        let r = false
        core.foreach(objs,function(k,obj)
        {
            if(k==key)
            {
                r = true
                return true
            }
        })
        return r
    }

    static removeAt(arr:any[],index:number)
    {
        let len = arr.length
        if(isNaN(index)||index>=len||index<0)
        {
            return arr
        }
        for(let i=0;i<len-1;++i)
        {
            if(i<index)
            {
                continue
            }
            else
            {
                arr[i] = arr[i+1]
            }
        }
        arr.length-=1
        return arr
    }
    
    static char2buf(str)
    {
        let out = new ArrayBuffer(str.length*2)
        let u16a= new Uint16Array(out)
        let strs = str.split("")
        for(let i =0 ; i<strs.length;i++)
        {
            u16a[i]=strs[i].charCodeAt()
        }
        return out
    }

    static array2arraybuffer(array) 
    {
        let b = new ArrayBuffer(array.length)
        let v = new DataView(b, 0)
        for (let i = 0; i < array.length; i++) 
        {
            v.setUint8(i, array[i])
        }
        return b
    }

    static arraybuffer2array(buffer) 
    {
        let v = new DataView(buffer, 0)
        let a = new Array()
        for (let i = 0; i < v.byteLength; i++)
        {
            a[i] = v.getUint8(i)
        }
        return a
    }

    static firstCharUpCase(str)
    {
        let reg = /\b(\w)|\s(\w)/g 
        str = str.toLowerCase() 
        return str.replace(reg,function(m){return m.toUpperCase()}) 
    } 

    static convertIntToUInt8Array(num):number[]
    {
        let c2 = num%256
        let c1 = Math.floor(num/256)
        
        return [c1,c2]
    }

    static convertUInt8ArrayToInt(array):number
    {
        if(array.length!=2)
        {
            return 0
        }
        return array[0]*256+array[1]
    }
    //从一个数组里面按照下标和长度的方式去获取另一个数组，至少返回空数组
    //包含start
    static getArrayFromArray(data,start,l?):any[]
    {
        let len = data.length
        start = start || 0//默认最开始
        l = l || len//默认到最后
        if(start>=len)
        {
            return []
        }
        let arr = []
        for(let i = start ;i<start+l&&i<len;++i)
        {
            arr.push(data[i])
        }
        return arr
    }

    static blobToArrayBuffer(data,callback)
    {
        if(cc.sys.isNative)
        {
            callback(data)
            return
        }
        let fileReader = new FileReader()
        fileReader.onload  = function(progressEvent) 
        {
            if(callback)
            {
                callback(this.result)
            }
        }
        fileReader.readAsArrayBuffer(data)
    }

    static format(src,formatStr)   
    {   
        if(core.isString(src))
        {
            let args = Array.prototype.slice.call(arguments, 1)
            return src.replace(/\{(\d+)\}/g, function(m, i)
            {
                return args[i]
            })
        }
        else
        {
            if(core.isNumber(src))
            {
                src=new Date(src)
            }
            let str = formatStr
            let Week = ['日','一','二','三','四','五','六']
            let Y=src.getFullYear()
            str=str.replace(/yyyy|YYYY/,Y)
            str=str.replace(/yy|YY/,( Y % 100)>9?(Y % 100).toString():'0' + (Y % 100))
        
            let M=src.getMonth()+1
            str=str.replace(/MM/,M>9?M:'0' + M)
            str=str.replace(/M/g,M)
        
            str=str.replace(/w|W/g,Week[src.getDay()])
        
            let D=src.getDate()
            str=str.replace(/dd|DD/,D>9?D.toString():'0' + D)
            str=str.replace(/d|D/g,D)
        
            let H=src.getHours()
            str=str.replace(/hh|HH/,H>9?H.toString():'0' + H)
            str=str.replace(/h|H/g,H)
            let m=src.getMinutes()
            str=str.replace(/mm/,m>9?m.toString():'0' + m)
            str=str.replace(/m/g,m)
        
            let S=src.getSeconds()
            str=str.replace(/ss|SS/,S>9?S.toString():'0' + S)
            str=str.replace(/s|S/g,S)
        
            return str
        }
    }
    static fuzzy(str:string)
    {
        if(!str)
        {
            return str
        }
        if(str.length<=1)
        {
            return str
        }
        if(str.length==2)
        {
            return str[0]+"*"
        }
        let first = str[0]
        let last = str[str.length-1]
        let newStr = first
        for(let i=0;i<str.length-2;++i)
        {
            newStr+="*"
        }
        newStr+=last
        return newStr
    }
    static trim(str:string)
    {
        return str.replace(/(^\s*)|(\s*$)/g, "")
    }
        /**
     * 判断是否是手机号
     * @param phone 手机号
     */
    static isPhoneNo(phone:string)
    {
        if(!phone)
        {
            return false
        }
        // let reg = /^1[3|4|5|7|8][0-9]{9}$/ //验证规则
        // let flag = reg.test(phone) //true
        // return flag
        return /^1[3-9]\d{9}$/.test(phone)
    }
    static isVirtualNo(phone:string)
    {
        let flag = false
        if(phone.indexOf("167")==0
            ||phone.indexOf("170")==0
            ||phone.indexOf("171")==0)
        {
            flag = true
        }
        return flag
    }
    static toLatestTime(time:number)
    {
        let dt = new Date(time)
        let now = new Date()
        let dtm=now.getTime()-dt.getTime()
        let one_day=24*60*60*1000
        if(dtm<=7*one_day)
        {
            if(dtm>one_day)
            {
                let d = Math.floor(dtm/one_day)
                return d+"天前"
            }
            let one_hour=1*60*60*1000
            if(dtm>one_hour)
            {
                let h = Math.floor(dtm/one_hour)
                return h+"小时前"
            }
            let one_m=1*60*1000
            if(dtm>one_m)
            {
                let m = Math.floor(dtm/one_m)
                return m+"分前"
            }
            let one_s=1*1000
            let s = Math.floor(dtm/one_s)
            return s+"秒前"
        }
        if(dt.getFullYear()!=now.getFullYear())
        {
            return core.format(dt,"YYYY-MM-DD HH:mm")
        }
        if(dt.getMonth()!=now.getMonth()
            ||dt.getDate()!=now.getDate())
        {
            return core.format(dt,"MM-DD HH:mm")
        }
        return core.format(dt,"HH:mm")
    }
    static toSimpleTime(time:number)
    {
        let dt = new Date(time)
        let now = new Date()
        if(dt.getFullYear()!=now.getFullYear())
        {
            return core.format(dt,"YYYY-MM-DD HH:mm")
        }
        if(dt.getMonth()!=now.getMonth()
            ||dt.getDate()!=now.getDate())
        {
            return core.format(dt,"MM-DD HH:mm")
        }
        return core.format(dt,"HH:mm")
    }
    static getStartTime(time?:number)
    {
        time = time || Date.now()
        let dt = new Date(time)
        time-=dt.getHours()*60*60*1000+dt.getMinutes()*60*1000+dt.getSeconds()*1000+dt.getMilliseconds()
        return time
    }
}
Number.prototype.toLocaleString=function (locales?: string | string[], options?: Intl.NumberFormatOptions)
{
    var parts = this.toString().split(".")
    parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    return parts.join(".")
}