import  SingleBase from '../../Base/SingleBase'
import { GNativeTool } from "../../Framework/Logic/NativeTool";
import { GMsgCenter } from "../../utils/MessageCenter";
import { GUserMgr } from "../../Logic/UserManager";
import { GHelper } from "../../Framework/Logic/Helper";
import { ykUtils } from '../../utils/YkUtils';
import { GAppDM } from '../../DataModel/AppDataModel';
import { GGameDM } from '../DataModel/GameDataModel';


 class FaceCheckTool extends SingleBase{

    // private lastCheckFaceTime = 0
    private isVerifiOpen = true
    private isNeedRefreshCheck = false
    public lastCheckTime = 0
    initOnCreate(){
        console.log('face open')
        // 注册消息 
        window["VerificationSuccess"] = async ()=> {
            setTimeout(()=>{
                GMsgCenter.send("identify_success")
            },200)
          
            setTimeout(()=>
            {
                // GHelper.showTip("验证成功")
            },500)
            if(this.isNeedRefreshCheck){
                this.isNeedRefreshCheck = false
                this.lastCheckTime = await ykUtils.getServerTime()
                ykUtils.saveData('FACE_TIME',this.lastCheckTime)
            }
            
        }

        window["VerificationFailed"] = ()=> {
            GMsgCenter.send("identify_failed")
            setTimeout(()=>
            {
                // GHelper.showTip("验证失败")
            },2500)
        }
        //验证 窗口关闭
        window["VerificationWindowsClosed"] = ()=> {
            console.log('VerificationWindowsClosed')
            GMsgCenter.send("identify_window_close")

            this.isVerifiOpen = false
        }
    }


    async checkFace(nickname,idcard_no,self_user_id){
        if(cc.sys.isBrowser){
            window["VerificationSuccess"]()
            return
        }
        if(GAppDM.account_server.host=="123.60.12.239")
        {
            window["VerificationSuccess"]()
            return
        }
        if(GGameDM.match_is_verification.indexOf(GUserMgr.self_user_id.toString())!=-1)
        {
            window["VerificationSuccess"]()
            return
        }
    
        console.log('lastCheckTime ===>',this.lastCheckTime)
        if(this.lastCheckTime==0 || this.lastCheckTime==null){
            let saveTime = ykUtils.getData('FACE_TIME')
            if(saveTime!=null){
                this.lastCheckTime = Number(saveTime)
            }
        }

        console.log('lastCheckTime2 ===>',this.lastCheckTime)
        if(this.lastCheckTime==0||this.lastCheckTime==null){
            this.isNeedRefreshCheck = true
            GNativeTool.callRlsb(nickname, idcard_no, self_user_id)
        }else{
            let curTime =await ykUtils.getServerTime()
            if((curTime-this.lastCheckTime)/1000>2*60*60){
                // 超时
                this.isNeedRefreshCheck = true
                GNativeTool.callRlsb(nickname, idcard_no, self_user_id)
            }else{
                window["VerificationSuccess"]()
            }
        }
    }

}

export let faceCheckTool = FaceCheckTool.Instance()