import { EMatchState } from "../../Config/_ini_";
import { GTexasTableDM } from "../../DataModel/TexasTableDataModel";
import { GHelper } from "../../Framework/Logic/Helper";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GHallHttp } from "../../Net/HallHttp";
import { GMatchSer, MatchPlayer } from "../../Service/MatchService";
import { GMsgCenter } from "../../utils/MessageCenter"
import Page_Game from "../Page/Page_Game";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_MatchInfo extends cc.Component {

    @property(cc.Label)
    lbl_title: cc.Label = null

    @property(cc.Label)
    lbl_time: cc.Label = null

    @property(cc.Label)
    lbl_level: cc.Label = null

    @property(cc.Label)
    lbl_curnum: cc.Label = null

    @property(cc.Label)
    lbl_prizenum: cc.Label = null

    @property(cc.Label)
    lbl_blind: cc.Label = null

    @property(cc.Label)
    lbl_ante: cc.Label = null

    @property(cc.Label)
    lbl_nextblind: cc.Label = null

    @property(cc.Label)
    lbl_signendtime: cc.Label = null

    @property(cc.Label)
    lbl_avcoin: cc.Label = null

    @property(cc.Label)
    lbl_totalcoin: cc.Label = null

    onLoad()
    {
        GMsgCenter.register("upgradeblind",this.onRefresh.bind(this),this)
    }
    protected onDestroy(): void {
        GMsgCenter.unregister("upgradeblind",this)
    }
    onEnable(){
        let page = GPageMgr.getPage("Page_Game") as Page_Game
        if(!page)
        {
            return
        }
        // let table_id = page.tableId
        // let tableinfo = GTexasTableDM.getTableInfo(table_id)
        // GHallHttp.request("GetMatchPlayers",{ins_id:tableinfo.match_insid}).then((res)=>
        // {
        //     let matchInsM = GMatchSer.getMatchInsById(tableinfo.match_insid)
        //     let players:MatchPlayer[] = matchInsM.rankplayers = res.players||[]
        //     matchInsM.player_num=res.player_num
        //     if(tableinfo.total_player_num<matchInsM.player_num)
        //     {
        //         tableinfo.total_player_num=matchInsM.player_num
        //     }
        //     GMsgCenter.send("getplayers")
        //     if(cc.isValid(this))
        //     {
        //         this.onRefresh()
        //     }
        // })
        
        this.onRefresh()
    }
    protected onDisable(): void {
        this.unscheduleAllCallbacks()
    }
    onRefresh()
    {
        this.unscheduleAllCallbacks()
        let page = GPageMgr.getPage("Page_Game") as Page_Game
        if(!page)
        {
            return
        }
        let table_id = page.tableId
        let tableinfo = GTexasTableDM.getTableInfo(table_id)
        let matchInsM = GMatchSer.getMatchInsById(tableinfo.match_insid)
        let matchM = matchInsM.match
        if (!matchM) {
            return
        }

        this.lbl_title.string=matchM.name
        let blindItem = GMatchSer.getBlindItem(matchM.blind_id,matchInsM.blind_index)
        if(blindItem.level<0)
        {
            blindItem = GMatchSer.getBlindItem(matchM.blind_id,matchInsM.blind_index-1)
        }
        this.lbl_level.string=blindItem.level+""
        matchInsM.rankplayers=matchInsM.rankplayers||[]
        this.lbl_curnum.string=tableinfo.cur_player_num+"/"+tableinfo.total_player_num

      

        let rewardsLen = page.getComponent('Page_Game').getRewardsLen();
        this.lbl_prizenum.string=(rewardsLen?rewardsLen:matchM.reward.length)+""
        this.lbl_blind.string=blindItem.small_blind+"/"+blindItem.big_blind
        this.lbl_ante.string=blindItem.ante+""
        let nextBlindItem = GMatchSer.getBlindItem(matchM.blind_id,matchInsM.blind_index+1)||blindItem
        if(nextBlindItem.level<0)
        {
            nextBlindItem = GMatchSer.getBlindItem(matchM.blind_id,matchInsM.blind_index+1+1)
        }
        this.lbl_nextblind.string=nextBlindItem.small_blind+"/"+nextBlindItem.big_blind
        
        
        let total = tableinfo.total_player_num*matchM.coin
        let playsers = matchInsM.rankplayers||[]
        if(total==0||playsers.length<=0)
        {
            this.lbl_avcoin.string="-"
            this.lbl_totalcoin.string="-"
        }
        else
        {
            if(tableinfo.cur_player_num>0)
            {
                this.lbl_avcoin.string=Math.floor(total/tableinfo.cur_player_num).toString();
            }
            else
            {
                this.lbl_avcoin.string=matchM.coin+""
            }
            this.lbl_totalcoin.string=total+""
        }
        this.schedule(this.onUpdate.bind(this),0.5)
        this.onUpdate()
    }
    onUpdate()
    {
        let page = GPageMgr.getPage("Page_Game") as Page_Game
        if(!page)
        {
            return
        }
        let table_id = page.tableId
        let tableinfo = GTexasTableDM.getTableInfo(table_id)
        let matchInsM = GMatchSer.getMatchInsById(tableinfo.match_insid)
        let matchM = matchInsM.match
        let bm = GMatchSer.getBlindModel(matchM.blind_id)

        let dttime = tableinfo.blind_deadline-GHelper.getServerTime()
        if(dttime<0)
        {
            dttime=0
            // let mtd = Math.abs( GHelper.getServerTime()-matchInsM.start_time);
            // if(mtd<1000)
            // {
            //     if(GPageMgr.isPageOpened("Page_MatchInfo"))
            //     {
            //         GPageMgr.getPage("Page_MatchInfo").getComponent("Page_MatchInfo").hide();
            //     }
            // }
        }
        dttime=Math.floor(dttime/1000)
        let m = Math.floor(dttime/60)
        let s = dttime%60
        let str=m+":"
        if(m<10)
        {
            str="0"+str
        }
        if(s>=10)
        {
            str+=s
        }
        else
        {
            str+="0"+s

        }
        this.lbl_time.string=str

        let time = matchInsM.start_time
        for(let i=0;i<matchM.reenter_blind_index;++i)
        {
            time += bm.items[i].seconds*1000
        }
        let now = GHelper.getServerTime()
        let seconds=Math.floor((time-now)/1000)
        if(seconds<0)
        {
            seconds=0
        }
        m = Math.floor(seconds/60)
        s = seconds%60
        str=m+":"
        if(m<10)
        {
            str="0"+str
        }
        if(s>=10)
        {
            str+=s
        }
        else
        {
            str+="0"+s
        }

        this.lbl_signendtime.string=str
        if (str=="00:00") {
            this.lbl_signendtime.node.parent.getComponent(cc.Label).string = "截止报名"
            this.lbl_signendtime.node.parent.color = new cc.Color().fromHEX("#D43030")
            this.lbl_signendtime.node.active = false;
        }
    }
}