export enum MATERIAL {
    SPRITE = "2d-sprite",
    GRAY_SPRITE = "2d-gray-sprite",
    UNLIT = "unlit",
    SPINE = "2d-spine",
    CLEAR = "clear-stencil"
}

class MaterialManager {
    constructor() {
    }

    getMaterial(name: MATERIAL): cc.Material {
        switch(name) {
            case MATERIAL.SPRITE:
            case MATERIAL.GRAY_SPRITE:
            case MATERIAL.UNLIT:
            case MATERIAL.SPINE:
            case MATERIAL.CLEAR: {
                return cc.Material.getBuiltinMaterial(name);
            }
        }
    }

    setGray(node: cc.Node | cc.Sprite, gray: boolean = true) {
        if (node instanceof cc.Sprite) {
            node.setMaterial(0, matMgr.getMaterial(gray ? MATERIAL.GRAY_SPRITE : MATERIAL.SPRITE));
        } else if (node instanceof cc.Node) {
            let sprites = node.getComponentsInChildren(cc.Sprite);
            for (let sprite of sprites) {
                sprite.setMaterial(0, matMgr.getMaterial(gray ? MATERIAL.GRAY_SPRITE : MATERIAL.SPRITE));
            }
        }
    }

    initVertices(sprite: cc.Sprite, count_x: number = 2, count_y: number = 2) {
        let uv = sprite.spriteFrame["uv"];
        let rect = sprite.spriteFrame.getRect();
        let vertices = {
            nu: [],
            nv: [],
            triangles: [],
            x: [],
            y: []
        }

        let points = [cc.v2(uv[0], uv[1]), cc.v2(uv[2], uv[3]), cc.v2(uv[4], uv[5]), cc.v2(uv[6], uv[7])];
        let vector_x = points[1].sub(points[0]);
        let vector_y = points[2].sub(points[0]);
        for (let y = 0; y < count_y; y++) {
            for (let x = 0; x < count_x; x++) {
                let offset_x = x / (count_x - 1);
                let offset_y = y / (count_y - 1);
                let pos = points[0].add(vector_x.mul(offset_x)).add(vector_y.mul(offset_y));

                vertices.nu.push(pos.x);
                vertices.nv.push(pos.y);
                vertices.x.push(offset_x * rect.width);
                vertices.y.push((1 - offset_y) * rect.height);
            }
        }

        for (let x = 0; x < count_x - 1; x++) {
            for (let y = 0; y < count_y - 1; y++) {
                let p0 = x + y * count_x;
                let p1 = p0 + 1;
                let p2 = p0 + count_x;
                let p3 = p2 + 1;
                vertices.triangles.push(p0, p1, p2);
                vertices.triangles.push(p1, p2, p3);
            }
        }

        sprite.type = cc.Sprite.Type.MESH;
        (sprite as any).spriteFrame.vertices = vertices;
        (sprite as any).setVertsDirty();
    }

    updateVerticesX() {
        
    }
}

export const matMgr: MaterialManager = new MaterialManager();