export class TeachConfig {
    id: number;
    type: TEACH_TYPE;
    layer: string;
    node: string[];
    tips: string;
    event: string;
    next: number;
    check: {[key: string]: number};

    constructor(data?) {
        data = data || {};
        this.id = Number(data.id || 0);
        this.type = Number(data.type || 0);
        this.layer = String(data.layer || "");
        this.node = data.node ? data.node.split(',') : [];
        this.tips = String(data.tips || "").replace(/\\n/g, "\n");
        this.event = String(data.event || "");
        this.next = Number(data.next || 0);
        this.check = parseObject(data.check);
    }
}

