export class MatchConfig {
    id: number;
    type: number;
    name: string;
    desc: string;
    icon: string;
    start_time: number;
    end_time: number;
    cycle_day: number[];
    cycle_time: {hour: number, minute: number}[];
    entry_cost: ItemInfo[];
    reentry_cost: ItemInfo[];
    reentry_count: number;
    reentry_blind: number;
    player_min: number;
    player_max: number;
    blind_id: number;
    chips: number;
    reward: ItemInfo[];

    constructor(data?) {
        data = data || {};
        this.id = Number(data.id || 0);
        this.type = Number(data.type || 0);
        this.name = String(data.name || "");
        this.desc = String(data.desc || "");
        this.icon = String(data.icon || "");
        this.start_time = new Date(data.start_time || 0).getTime();
        this.end_time = new Date(data.end_time || 0).getTime();
        this.cycle_day = data.cycle_day ? data.cycle_day.split(',').map(Number) : [];
        this.cycle_time = data.cycle_time ? data.cycle_time.split(',').map(parseTime) : [];
        this.entry_cost = parseItems(data.entry_cost);
        this.reentry_cost = parseItems(data.reentry_cost);
        this.reentry_count = Number(data.reentry_count || 0);
        this.reentry_blind = Number(data.reentry_blind || 0);
        this.player_min = Number(data.player_min || 0);
        this.player_max = Number(data.player_max || 0);
        this.blind_id = Number(data.blind_id || 0);
        this.chips = Number(data.chips || 0);
        this.reward = parseItems(data.reward);
    }
}

