

import { LocationModel } from '../../model/LocationModel';
import { ykUtils } from '../../utils/YkUtils';
import { GGameDM } from '../DataModel/GameDataModel';

// 高德地图
export class GaoDeMapTool{
    public static locationRes:LocationModel = {code:-1,errorMsg:'未初始化'}
    public static timeOutCb = null

   

    public static async getLocation(force=false): Promise<LocationModel>{

        var promise = new Promise<LocationModel>(resolve => {
            if(cc.sys.isBrowser || GGameDM.isReviewVersion){
                this.locationRes = {code:1,longitude:115.7,latitude:117.4,errorMsg:''}
                resolve(GaoDeMapTool.locationRes);
                return 
            }


            


            // 检测存储
            console.log('检测存储',force,GaoDeMapTool.locationRes.code)
            if(!force&&GaoDeMapTool.locationRes.code==-1){
                let data = ykUtils.getData('LOCATIONH_RES')
                console.log('存储数据',data)
                if(data){
                    GaoDeMapTool.locationRes = JSON.parse(data)
                    console.error('地址返回 存储的',GaoDeMapTool.locationRes)
                    // 需要检测时效
                    resolve(GaoDeMapTool.locationRes);

                    return
                }
            }

            if((!force&&GaoDeMapTool.locationRes.code>0)||cc.sys.isBrowser){

                cc.error('地址返回 已有的',GaoDeMapTool.locationRes.code)
                cc.error('地址返回 已有的',GaoDeMapTool.locationRes.errorMsg)
                resolve(GaoDeMapTool.locationRes);
                return 
            }



            cc['yk']['locationCb'] = async (res)=>{
                cc['yk']['locationCb'] = null
                console.log('定位回调: ',res)
                
                GaoDeMapTool.locationRes = JSON.parse(res)
                GaoDeMapTool.locationRes.getTime = await ykUtils.getServerTime()
                if( GaoDeMapTool.locationRes.code==1){
                    ykUtils.saveData('LOCATIONH_RES',JSON.stringify(GaoDeMapTool.locationRes))
                }
                resolve(GaoDeMapTool.locationRes);
                console.log('地址返回 请求的')
            }

            if(cc.sys.os == cc.sys.OS_ANDROID) 
            {
                jsb.reflection.callStaticMethod("org/cocos2dx/javascript/AppActivity", "getLocation","()V")
            }
            else if(cc.sys.os == cc.sys.OS_IOS) 
            {
                //   jsb.reflection.callStaticMethod("WechatLogin", "getAppId")
                console.log('定位',"getLocation")
                jsb.reflection.callStaticMethod("AppController", "getLocation");
            }
        })
        return promise;
    }

    // 手机如果没有获取到
    public static asyncGetLoaction(){

    }
}
















