import { GGameDM } from './../DataModel/GameDataModel';
import {core} from "../Core/Base"
import {EProtoType,GProtoFactory} from "./ProtoFilter/ProtoFactory"
import {IProtoFilter} from "./ProtoFilter/IProtoFilter"
import {GHelper} from "../Logic/Helper"
import { GToSvrLog } from '../Logic/ToServerLog';
import { layerMgr } from '../../manager/LayerManager';
import { GPageMgr } from '../View/Page/PageManager';

export class SimpleWebSocket
{
    protected _wb_client:WebSocket
    protected _host:string=""
    protected _port:number=0
    protected _cacheMsg = new Array<any>()
    protected _protoType:EProtoType=EProtoType.Json
    protected _protoFilter:IProtoFilter
    protected _need_close:boolean=false
    protected _heart_beat:boolean=false
    protected _is_open:boolean=false

    protected outTime = 5000
    get isOpen()
    {
        return this._is_open
    }
    protected _heart_beat_handler:number=0
    protected _cmd_cb_objs={}
    protected _rcv_heart_beat = true
    protected _rec_handler = null
    protected _send_heart_beat_time=0
    protected _cmd_callbacks={}//"cmd":{fun:fun,is_only}//一个cmd同时只能有一个，is_only代表是否仅仅该回调，
    protected _protocol=null

    protected _heart_timeout = 5
    protected lastChckTime = null

    init(host:string,port:number)
    {
        this._host = host || this._host
        this._port = port || this._port
        if(!this._protoFilter)
        {
            this._protoFilter = GProtoFactory.createFilter(this._protoType)
        }
    }

    connect ()
    {
        if(this._is_open)
        {
            return
        }
        this._need_close = false
        this._is_open = false
        //this._host="127.0.0.1"
        let url = "ws://" + this._host + ":" + this._port

        cc.log("Trying to connect to server : " + url)
        if(this._wb_client)
        {
            this._wb_client.onopen = null
            this._wb_client.onmessage = null
            this._wb_client.onerror = null
            this._wb_client.onclose = null
            this._wb_client.close()
            this._wb_client=null
        }
        if(this._protocol)
        {
            this._wb_client = new WebSocket(url+"/",[this._protocol])
        }
        else
        {
            this._wb_client = new WebSocket(url+"/")
        }
        this._wb_client.onopen = this.onOpen.bind(this)
        this._wb_client.onmessage = this.onMessage.bind(this)
        this._wb_client.onerror = this.onError.bind(this)
        this._wb_client.onclose = this.onClose.bind(this)
        if(this._wb_client.readyState==3)
        {
            this.onClose()
        }
    }
    onOpen (e?)
    {
        this._rcv_heart_beat = true
        this._is_open = true

        layerMgr.showNetLoading(false)
        cc.log("success to connect to " + this._host + ":" + this._port)
        this._onCacheMsg()
        this._heart_beat_handler = setInterval(()=>
        {
            this.lastChckTime = Date.now()+1000
            if(this._heart_beat)
            {
                this.send_heartbeat()
            }
        },2000)
    }
    _onCacheMsg()
    {
        core.foreach(this._cacheMsg,(k,data)=>
        {
            this.send(data)
        })
        this._cacheMsg=[]
    }
    onError (e?)
    {
        cc.log("simplewebsocket onerror!")
        cc.log(e)
        this.onClose()
    }
    onClose (e?):boolean
    {

        this._wb_client.onopen = null
        this._wb_client.onmessage = null
        this._wb_client.onerror = null
        this._wb_client.onclose = null

        if(this._heart_beat_handler)
        {
            clearInterval(this._heart_beat_handler)
            this._heart_beat_handler = null
        }
        
        
        if(this._rec_handler)
        {
            return true
        }
        this._is_open = false
       
        cc.log("connection is closed：host="+this._host+" port="+this._port+" this._need_close="+this._need_close)
        
        if(!this._need_close)
        {
            layerMgr.showNetLoading(true,'正在重连...')
            this._rec_handler = setTimeout(()=>
            {
                this._rec_handler = null
                if(!this._need_close)
                {
                    
                    this.connect()
                }
            },1000)
            return true
        }
        return false
    }
    registerCmdCallbackObj(key,cmd_cb_obj)
    {
        this._cmd_cb_objs[key] = cmd_cb_obj
    }
    registerCmdCallback(cmd,fun,is_only?)
    {
        this._cmd_callbacks[cmd] = {fun:fun,is_only:is_only}
    }
    getGamekeys()
    {
        let keys = []
        for(let key in this._cmd_cb_objs)
        {
            keys.push(key)
        }
        return keys
    }
    onMessage (e)
    {
        //web平台自己调试无需发送到服务器
        if(!cc.sys.isBrowser)
        {
            try
            {
                this._protoFilter.decode(e.data,this._onMessage.bind(this))
            }
            catch(err)
            {
                if(err&&err.stack)
                {
                    GToSvrLog.log({stack:err.stack,data:e.data})
                }
                else if(err)
                {
                    GToSvrLog.log({err,data:e.data})
                }
            }
        }
        else
        {
            this._protoFilter.decode(e.data,this._onMessage.bind(this))
        }
    }
    protected _onMessage(data)
    {
        if(!data)//异常消息
        {
            return
        }

        if (data && data.cmd)
        {
            if(data.servertime)
            {
                let dt = data.servertime - Date.now()
                if(Math.abs(dt)<Math.abs(GGameDM.diffServerTime))
                {
                    GGameDM.diffServerTime = dt
                }
            }
            data.cmd = data.cmd.toLowerCase()
            let cmd:string = data.cmd
            let _index = data.cmd.indexOf("_")
            let prefix = null
            if(_index>0)
            {
                prefix = cmd.substring(0,_index)
            }
            if(prefix)
            {
                let obj = this._cmd_cb_objs[prefix]
                if(obj)
                {
                    obj.onMessage(data)
                    return
                }
            }
            if(cmd.indexOf("heartbeat")<0)
            {
                let strData = JSON.stringify(data)
                cc.log(core.format(new Date(),"mm:hh:ss  ")+"receive:"+strData)
            }
            else
            {
                let strData = JSON.stringify(data)
                // cc.log(core.format(new Date(),"mm:hh:ss  ")+"receive:"+strData)
                cmd="heartbeat"
            }
            if ( !data.errcode )
            {
                if(this["receive_" + cmd])
                {
                    this["receive_" + cmd](data)
                }
                else
                {
                    cc.log(cmd + " has no handler!")
                }
            }
            else
            {
                if (this["receive_" + cmd + "_failed"])
                {
                    this["receive_" + cmd + "_failed"](data)
                }
                else if(data.errcode.id>0)
                {
                    if(cmd=="texas_login")
                    {
                        GHelper.showTip(data.errcode.des+"请重新登陆...")
                        GHelper.clearData()
                        GPageMgr.closeAll()
                        GPageMgr.openPage("Page_CodeLogin")
                    }else
                    {
                        GHelper.showTip(data.errcode.des)
                    }
                }
            }
        }
    }
    send (data)
    {
        if (this._wb_client&&this._is_open)
        {
            //if(data.cmd!="heartbeat")
            {
                let strData = JSON.stringify(data)
                
                if(data.cmd != 'heartbeat'){
                    cc.log(core.format(new Date(),"mm:hh:ss  ")+"send:"+strData)
                }
            }
            let msg = this._protoFilter.encode(data)
            this._wb_client.send(msg)
        }
        else
        {
            this._cacheMsg.push(data)
        }
    }
    close()
    {   
        this._need_close = true
        if(!this._wb_client)
        {
            return
        }
        this._wb_client.close()
    }
    
    // 手动关闭
    closeInHide()
    {   
        if(!this._wb_client)
        {
            return
        }
        this._wb_client.close&&this._wb_client.close()
    }


    protected _close_and_rc()
    {
        // this.close()

        // 重置状态

        this._wb_client.close()
    }
    // update(dt)
    // {
    //     if(dt>5000)
    //     {
    //         this._close_and_rc()
    //     }
    // }
    openHeartBeat()
    {
        this._heart_beat = true
    }
    /*
    //心跳包请求
    message HeartReq {
        optional Version version = 1;	
    }
    */
    send_heartbeat()
    {
        if( this.lastChckTime==null){
            this.lastChckTime = Date.now()
        }

        let xtTime = (Date.now() - this.lastChckTime)/1000
        if( xtTime> this._heart_timeout)
        {
            console.log('心跳断线',xtTime)
            if(this._heart_beat_handler)
            {
                clearInterval(this._heart_beat_handler)
                this._heart_beat_handler = null
            }

           
            this._close_and_rc()
            return
        }
         // this._rcv_heart_beat = false

         this.send({cmd:"heartbeat"})
         this._send_heart_beat_time = GHelper.getLocalTime()
    }
    /*
    //心跳包响应
    message HeartRes {
        optional int32 errcode = 1; //错误原因 0表示成功
        optional string errcodedes = 2; //错误描述
        optional int32 servertime = 3;  //服务器时间
    }
    */
    receive_heartbeat(jsonData)
    {

        this.lastChckTime = Date.now()
    }
}