export let GItemCfg:ItemConfig=null
class ItemConfig
{
    protected _items:ItemItem[]=[]
    get items()
    {
        return this._items
    }
    getItemById(item_id:number)
    {
        for(let i=0;i<this._items.length;++i)
        {
            let item = this._items[i]
            if(item.id==item_id)
            {
                return item
            }
        }
        return null
    }

    protected _exitems:BagItemItem[]=[]
    get exitems()
    {
        return this._exitems
    }
    // getexItemById(item_id:number)
    // {
    //     for(let i=0;i<this._exitems.length;++i)
    //     {
    //         let item = this._exitems[i]
    //         if(item.id==item_id)
    //         {
    //             return item
    //         }
    //     }
    //     return null
    // }

    protected _bagitems:BagItemItem[]=[]
    get bagItems()
    {
        return this._bagitems
    }
    getBagItemById(item_id:number):BagItemItem
    {
        if(!this._bagitems)
        {
            return null
        }
        for(let i=0;i<this._bagitems.length;++i)
        {
            if(this._bagitems[i].item_id==item_id)
            {
                return this._bagitems[i]
            }
        }
        return null
    }

    removeById(id: string) {
        if(!this._bagitems)
        {
            return null
        }
        for(let i=0;i<this._bagitems.length;++i)
        {
            if(this._bagitems[i].id==id)
            {
                this._bagitems.splice(i, 1);
                break
            }
        }
    }

    get exItems() {
        return this._exitems;
    }

    async init()
    {
        let rs = await GHallHttp.request("bagitems")
        this._items=rs.items||{}
        this._bagitems=rs.bagitems||[]
        this._exitems=rs.exitems||[]
    }
}
GItemCfg=new ItemConfig()