import { GHelper } from "../Framework/Logic/Helper";
import { GUserMgr } from "../Logic/UserManager";
import { GAccountHttp } from "../Net/AccountHttp";
import { GHallHttp } from "../Net/HallHttp";
import { GNetGameTexas } from "../Net/NetGameTexas";
import { GOrderHttp } from "../Net/OrderHttp";
import { GItemCfg } from "../Service/ItemService";
import { GMatchSer, MatchItemInfo } from "../Service/MatchService";
import { GOrderSer } from "../Service/OrderService";

export enum EAccountFrom
{
    Guest=0,
    OpenSocial,
    WeChat,
    QQ,
    Phone,
    Email,
    Name,
    QuickPhone,
    Apple,
    Google
}
export class AccountModel
{
    id: number;
    phone: string;
    email: string;
    name: string;
    password: string;
    unionid: string;
    openid: string;
    create_time: number;
    create_ip: string;
    login_time: number;
    login_ip: string;
    from: number;
    state: number;
}
class AppDataModel
{
    account:AccountModel=null
    token=""
    account_server={host:"192.168.0.106",port:6001}
    hall_server={host:"192.168.0.106",port:6001}
    texas_server={host:"192.168.0.106",port:6101}
    texas_servers=[{host:"192.168.0.106",port:6101},{host:"192.168.0.106",port:6101}]
    //最近获取验证码的时间
    last_get_code=0
    signs:MatchItemInfo[]=[]
    quick_test=false
    auto_login=true
    update = true

    isShowServerList = false;
    checkUpdate = true  // 是否检测更新


    getItemDesc(mii:MatchItemInfo)
    {
        if(mii.id==-99)
        {
            return "获得" + mii.count+"游戏点"
        }
        let item=GItemCfg.getItemById(mii.id)
        return "获得" + mii.count+"x"+item.name
    }
    async onLogin(unionid:string,openid:string,from:EAccountFrom)
    {
        return new Promise(async (resolve,reject)=>
        {
            let msg = {unionid,openid,from}
            let res = await GAccountHttp.request("thirdlogin",msg)
            if(res.errcode)
            {
                GHelper.showTip(JSON.stringify(res.errcode))
                resolve(false)
                return
            }
            cc.sys.localStorage.setItem("login_data",JSON.stringify(msg))
            this.account=res.account
            this.hall_server=res.hall
            let allhall:any[]=res.allhall
            if(allhall&&allhall.length>0)
            {
                let index = this.account.id%allhall.length
                this.hall_server=allhall[index]
            }
            this.texas_server=res.texas
            this.texas_servers=res.alltexas
            this.token=res.token
            GHallHttp.init("logic",this.hall_server.host,this.hall_server.port)
            GOrderHttp.init("order",this.hall_server.host,this.hall_server.port)
            res = await GHallHttp.request("login",{account_id:this.account.id,token:this.token})
            GUserMgr.addOneUser(res.user)
            GUserMgr.init(res.user.id)
            GHallHttp.session_id=res.session_id
            GOrderHttp.session_id=res.session_id
            let i=4
            let _tryEnd=()=>
            {
                --i
                if(i==0)
                {
                    resolve(true)
                }
            }
            
            GMatchSer.init().then(()=>
            {
                _tryEnd()
            })
            GItemCfg.init().then(()=>
            {
                _tryEnd()
            })
            GHallHttp.request("getsignconfig").then((rs)=>
            {
                this.signs = rs.signs
                _tryEnd()
            })
            GOrderSer.init().then(()=>
            {
                _tryEnd()
            })
        })
    }
}

export let GAppDM=new AppDataModel()