import {core} from "../../Core/Base"
import {IProtoFilter} from "./IProtoFilter"

export class JsonProtoFilter extends IProtoFilter
{
    init(callback:Function)
    {
        if(callback)
        {
            callback(true)
        }
    }
    encode(data)
    {
        if(!core.isString(data))
        {
            data = JSON.stringify(data) 
        }
        return data
    }
    decode(data,callback)
    {
        if(core.isString(data))
        {
            data = JSON.parse(data)
        }
        if(callback)
        {
            callback(data)
        } 
    }
}