import {GSettingDM} from "../DataModel/SettingDataModel"
import { GHelper } from "./Helper";
export let GAudioTool:AudioTool=null

 
class AudioTool  
{
    protected _musicId=0
    protected _m_v=1
    protected _s_v=1
    protected _v_v=1
    protected _lock=0
    protected _cur_music = null

    playMusic(path,loop=true)
    {
        if(!path)
        {
            return
        }
        this._cur_music = path
        if(!GSettingDM.musicOn)
        {
            return
        }
        if(loop === undefined)
        {
            loop = true
        }
        this.stopMusic()
        cc.loader.loadRes(path, cc.AudioClip, (err, clip)=>{
            if(err)
            {
                console.log("err =" + JSON.stringify(err))
                return
            }
            this._musicId = cc.audioEngine.play(clip, loop, this._m_v)
        })
    }
    resumeMusic()
    {
        this.playMusic(this._cur_music)
    }
    setMusicVolume(v)
    {
        this._m_v = v
        if(this._musicId)
        {
            cc.audioEngine.setVolume(this._musicId,v)
        }
    }
    lockMusic()
    {
        ++this._lock
        if(this._lock>0)
        {
            this.setMusicVolume(0)
        }
    }
    unlockMusic()
    {
        --this._lock
        if(this._lock<=0)
        {
            this.setMusicVolume(this._m_v)
        }
    }
    setSoundVolume(s)
    {
        this._s_v = s
    }
    resetEffect()
    {
        cc.audioEngine.stopAllEffects();
        this._allIn_id =-1;
    }

    _allIn_id = -1;

    stopAllIn()
    {
        cc.audioEngine.stopEffect( this._allIn_id)
        this._allIn_id  = -1;
    }
    playEffect(path,isAllin?)
    {
        if(!GSettingDM.soundOn)
        {
            return
        }
        let self = this;
        cc.loader.loadRes(path, cc.AudioClip, (err, clip)=>{
            if(err)
            {
                console.log("err =" + JSON.stringify(err))
                return
            }
            var audioID = cc.audioEngine.playEffect(clip,false)
            if(isAllin)
            {
                self._allIn_id = audioID;
            }
        })
    }
    playSound(path,fun?)
    {
        //path = cc.url.raw("resources/"+path)
        if(!GSettingDM.soundOn)
        {
            return
        }
        //return cc.audioEngine.play(path,false,this._s_v)
        cc.loader.loadRes(path, cc.AudioClip, (err, clip)=>{
            if(err)
            {
                console.log("err =" + JSON.stringify(err))
                return
            }
            var audioID = cc.audioEngine.play(clip, false, this._s_v)
            if(fun)
            {
                fun(audioID)
            }
        })
    }
    stopSound(id)
    {
        if(!id)
        {
            return
        }
        cc.audioEngine.stop(id)
    }
    setVoiceVolume(s)
    {
        this._v_v = s
    }
    playVoice(path:string)
    {
        if(!GSettingDM.voiceOn)
        {
            return
        }
        if(path.indexOf("http")<0)
        {
            //path = cc.url.raw("resources/"+path)
            //return cc.audioEngine.play(path,false,this._v_v)
            cc.loader.loadRes(path, cc.AudioClip, (err, clip)=>{
                if(err)
                {
                    console.log("err =" + JSON.stringify(err))
                    return
                }
                var audioID = cc.audioEngine.play(clip, false, this._v_v)
                return audioID
            })
        }
        else
        {
            //var name = path.split(".com/")[1] + ".wav"
            var name = path.split(".com/")[1] + ""
            var voice_path = jsb.fileUtils.getWritablePath() + name
            if (!jsb.fileUtils.isFileExist(voice_path))
            {    
                GHelper.httpRequest(path, (xhr)=>
                {
                    var tmp_data = new Uint8Array(xhr.response)          
                    jsb.fileUtils.writeDataToFile(tmp_data, voice_path)
                    //return cc.audioEngine.play(voice_path,false,this._v_v)
                    cc.loader.loadRes(voice_path, cc.AudioClip, (err, clip)=>{
                        if(err)
                        {
                            console.log("err =" + JSON.stringify(err))
                            return
                        }
                        var audioID = cc.audioEngine.play(clip, false, this._v_v)
                        return audioID
                    })
                })
            }
            else
            {
                //return cc.audioEngine.play(voice_path,false,this._v_v)
                cc.loader.loadRes(voice_path, cc.AudioClip, (err, clip)=>{
                    if(err)
                    {
                        console.log("err =" + JSON.stringify(err))
                        return
                    }
                    var audioID = cc.audioEngine.play(clip, false, this._v_v)
                    return audioID
                })
            }
        }
    }    
    stopMusic()
    {
        cc.audioEngine.stop(this._musicId)
    }
    stopAllSound()
    {
        cc.audioEngine.stopAll()
    }
}
GAudioTool = new AudioTool()