/**
 * CocosCreator's global Event/Message Center.
 */
export let GNotificationCenter:NotificationCenter=null
class NotificationCenter {
    private eventTarget: cc.EventTarget = new cc.EventTarget();

    /**
     * Listen to a notification
     * @param name 
     * @param callback 
     */
    public on(name: string, callback: Function, target?: any) {
        this.eventTarget.on(name, callback, target);
    }

    /**
     * Dispatch a notification
     * @param name 
     */
    public emit(name: string, arg1?: any, arg2?: any, arg3?: any, arg4?: any, arg5?: any) {
        this.eventTarget.emit(name, arg1, arg2, arg3, arg4, arg5);
    }

    /**
     * Cancel listen
     * @param name 
     */
    public off(name: string, callback?: Function, target?: any) {
        this.eventTarget.off(name, callback, target);
    }
}
GNotificationCenter=new NotificationCenter()

