import { resMgr } from "../../../manager/ResourceManager";

const {ccclass, property} = cc._decorator;

export class CardInfo {
    id: number = 0;
    x: number = 0;
    y: number = 0;
    scale: number = 1;
    mark: number = 0;
    select: boolean = false;
    delay: number = 0;
}

@ccclass
export default class ItemCard extends cc.Component {

    @property(cc.Sprite)
    imgCard: cc.Sprite = null;

    @property(cc.Sprite)
    imgLight: cc.Sprite = null;

    @property(cc.Node)
    imgShadow: cc.Node = null;

    @property(cc.Node)
    imgCardBack: cc.Node = null;

    public info: CardInfo = null;

    init(info) {
        if (this.info != info) {
            this.info = info;
        } else {
            this.initTable();
        }
    }

    update (dt) {
        if (!this.info) {
            return;
        }

        this.imgShadow.opacity = this.info.select ? 128 : 0;

        if (this.info.delay > 0) {
            this.info.delay -= dt;
            if (this.info.delay <= 0) {
                this.info.delay = 0;
            }
            return;
        }
    }

    initTable() {
        if (!this.info) {
            return;
        }

        this.imgCardBack.active = this.info.id == 0;
        resMgr.loadImage("image/card/" + this.info.id, (res) => {
            cc.isValid(this.imgCard) && (this.imgCard.spriteFrame = res);
        })
    }
}
