import { GHelper } from "../Logic/Helper";
import { core } from "../Core/Base";

export class CoinRain extends cc.Component
{
    protected isSpine = true
    protected _spinePath = "Platform/View/Page/Res/Common/spine/goldcoin2"
    protected trackIndex = 0
    protected _is_rain = true
    set isRain(value)
    {
        this._is_rain = value
    }
    protected _fly_up_height = 640
    set flyUpHeight(value)
    {
        this._fly_up_height = value
    }
    protected _fly_down_height = -60
    set flyDownHeight(value)
    {
        this._fly_down_height = value
    }
	_amin:cc.Component = null
    protected _start_posy = 0
    set startPosY(value)
    {
        this._start_posy=value
    }
    protected lPosX = 0
    protected rPosX = 0
    protected _scale = 1.0
    set rainScale(value)
    {
        this._scale = value
    }
	// rain密度
    protected _rain_level = 1	//返回0——1
    set rainLevel(value)
    {
        this._rain_level = value
    }
    protected _time = 1.5
    set rainSpeedTime(value)
    {
        this._time = value
    }
    constructor()
    {
        super()
    }
    onLoad()
    {
    }


    onDestory()
    {
        if(this._amin)
        {
            this._amin.destroy()
            this._amin = null
        }
    }

    startRain()
    {
        let action = cc.sequence(cc.callFunc(this.upDateCoin.bind(this))
                                    ,cc.delayTime(0.03))
        this.node.runAction(cc.repeatForever(action))
    }


    stopRain()
    {
        this.node.stopAllActions()
    }

    // spin 动画为金币的雨
    setRainCoinSpine()
    {
        this.isSpine = true
        this._spinePath = "Platform/View/Page/Res/Common/spine/goldcoin2"
        this.trackIndex = 10
        this._start_posy = 720 + 100
        this.lPosX = 0
        this.rPosX = 1280
    }

    setRainCoin()
    {
        this._amin = GHelper.createAnimation("Platform/View/Page/Res/Common/coinGold.plist",cc.WrapMode.Loop,0.03,()=>
        {
            return false
        })

        this.startPosY = 720 + 60
        this.lPosX = 0
        this.rPosX = 1280
    }


    upDateCoin(deltaTime)
    {
        if(!this._amin && !this.isSpine)
        {
            return
        }

        // 过滤雨大小(密度)
        let rainLevel = core.random(1, 1000)/1000
        if(rainLevel > this.rainLevel)
        {
            return
        }
        let x = 0
        let bezier = []
        if(this.isRain)
        {
            x = core.random() * (this.rPosX - this.lPosX) + this.lPosX
            bezier.push(cc.v2(x, this._start_posy))
            bezier.push(cc.v2(x, this._start_posy))
            bezier.push(cc.v2(x, -160))
        }
        else
        {
            x =  core.random() * (this.rPosX - this.lPosX + 200) + this.lPosX - 100
            bezier.push(cc.v2(640, 0))
            let midx = (x - 640)/2 + 640
            bezier.push(cc.v2(midx, this._fly_up_height + core.random(0, 200)))
            bezier.push(cc.v2(x, this._fly_down_height))
        }

        let coin = GHelper.createNode(cc.Sprite)
        coin.node.position = bezier[0]
        coin.node.scale = this._scale
        coin.node.setParent(this.node)
        if(this.isSpine)
        {
            cc.loader.loadRes(this._spinePath, sp.SkeletonData, (err,skeleton_data)=>
            {
                let skeleton = GHelper.createNode(sp.Skeleton)
                skeleton.skeletonData = skeleton_data
                skeleton.animation = "animation"
                skeleton.loop = true
                skeleton.premultipliedAlpha = false
                skeleton.node.setParent(coin.node)
            })
        }
        else
        {
            this._amin.node.setParent(coin.node)
        }
        coin.node.runAction(cc.sequence(cc.bezierTo(this._time, bezier), cc.removeSelf()))
    }
}