import { eventMgr } from "../../../manager/EventManager";
import { resMgr } from "../../../manager/ResourceManager";
import { GSettingDM } from "../../../Framework/DataModel/SettingDataModel";

const {ccclass, property} = cc._decorator;

// 14,2-13,其中14位A，而0、1、2、3 = 黑、红、梅、方
// 例如{0,14}代表黑桃A  52
export class CardInfo {
    id: number = 0;
    x: number = 0;
    y: number = 0;
    scale: number = 1;
    mark: number = 0;
    select: boolean = false;
    delay: number = 0;
}

@ccclass
export default class Card extends cc.Component {

    // @property(cc.Sprite)
    // imgCard: cc.Sprite = null;

    @property(cc.Sprite)
    imgLight: cc.Sprite = null;

    @property(cc.Node)
    imgShadow: cc.Node = null;

    @property(cc.Node)
    imgBack: cc.Node = null;

    public info: CardInfo = new CardInfo();
    
    @property(cc.Node) rootNode: cc.Node = null;
    @property(cc.Node) img_cardFace: cc.Node = null;
    @property(cc.Sprite) img_num: cc.Sprite = null;
    @property(cc.Node) img_shadow: cc.Node = null;

    private _bBonny: boolean = false;
    private _dealPos: cc.Vec2 = cc.v2(0, 0);
    private _dealRotate: number = 0;
    private _dealTime: number = 0;
    private _hasContent: boolean = false;

    setColor(color:cc.Color)
    {
        if(!this.img_num)
        {
            return
        }
        this.img_num.node.color=color
    }

    onLoad() {
        this.node.active = true;
        this.img_cardFace.active = false;

        this.imgBack.active = true;
        this.imgShadow.active = false;
        this.imgLight.node.active = false;

        this.updateCardBack()
    }

    // 重置牌值（默认显示牌背）
    public reset() {
        this._hasContent = false;
        //cv.resMgr.setSpriteFrame(this.img_num.node, this._getCardBgResPath());
        let imgpath = this._getCardBgResPath();
        resMgr.loadImage(imgpath, (res) => {
            cc.isValid(this.img_num) && (this.img_num.spriteFrame = res);
        })
    }

    setCardBg(index: number) {
        let suffix: string = "image/game/card/img_card_back" + index.toString();
        let path = `${suffix}`;

        resMgr.loadImage(path, (res) => {
            cc.isValid(this.imgBack) && (this.imgBack.getComponent(cc.Sprite).spriteFrame = res);
        })
    }

    _getCardBgResPath() {
        let str = cc.sys.localStorage.getItem("setting_cardbg_style") || "1"
        let suffix: string = "image/game/card/img_card_back" + str;
        let path = `${suffix}`;

        return path;
    }

    updateCardBack(): void {
        let path: string = this._getCardBgResPath();
        resMgr.loadImage(path, (res) => {
            cc.isValid(this.imgBack) && (this.imgBack.getComponent(cc.Sprite).spriteFrame = res);
        })
    }

    updateCardBack1(): void {
        let path: string = "image/game/card/img_card_back1";
        resMgr.loadImage(path, (res) => {
            cc.isValid(this.imgBack) && (this.imgBack.getComponent(cc.Sprite).spriteFrame = res);
        })
    }

    setContent(id: number): void {
        //this.imgBack.active = id == 0;
        this.info.id = id;
        this.setColor(cc.Color.WHITE)
        resMgr.loadCard(Number(GSettingDM.card_style), id, (res) => {
            cc.isValid(this.img_num) && (this.img_num.spriteFrame = res);
            this._hasContent = true;
        });
        // resMgr.loadImage("image/card/1/" + id, (res) => {
        //     cc.isValid(this.img_num) && (this.img_num.spriteFrame = res);
        //     this._hasContent = true;
        // })
    }

    setReviewContent(id: number): void {
        //this.imgBack.active = id == 0;
        this.info.id = id;
        this.setColor(cc.Color.WHITE)
        resMgr.loadCard(5, id, (res) => {
            cc.isValid(this.img_num) && (this.img_num.spriteFrame = res);
            this._hasContent = true;
        });
        // resMgr.loadImage("image/card/1/" + id, (res) => {
        //     cc.isValid(this.img_num) && (this.img_num.spriteFrame = res);
        //     this._hasContent = true;
        // })
    }

 


    clearContent(): void {
        // let path: string = `${this._card_back_folder_path}Pb_02`;
        // cv.resMgr.setSpriteFrame(this.img_num.node, path);
        this.info.id = 0
        this.img_num.spriteFrame=null
        // this.imgBack.active = this.info.id == 0;

        this._hasContent = false;
        this.doStopAllActions()
    }



 
    /**
     * 当前牌是否设置了牌内容
     */
    hasContent(): boolean {
        return this._hasContent;
    }

    /**
    * 设置坐标(把传入的坐标转换为该节点坐标系坐标)
    */
    setDealPos(pos: cc.Vec2): void {
        this.node.convertToNodeSpaceAR(pos, this._dealPos);
    }

    getDealPos() {
        return this._dealPos;
    }
    /**
     * 重置节点坐标,旋转角度
     */
    resetPos(): void {
        this.rootNode.setPosition(cc.v2(0, 0));
        this.rootNode.angle = 0;
    }

    /**
     * 设置旋转角度
     */
    setDealRotate(rotate: number): void {
        this._dealRotate = rotate;
    }

    deal(delay: number) {
        this.node.active = true;
        this.imgBack.runAction(cc.show());
        this._onDeal(delay)
    }

    private resetPosTimeOut = null
    private _onDeal(delay: number): void {
        this.node.parent.opacity=255;
        this.node.parent.scale = 1;
        this.node.opacity=255

        this.rootNode.stopAllActions()

        this.rootNode.setPosition(this._dealPos);
        let resetPos = cc.callFunc(this.resetCardPos.bind(this));
        this.rootNode.runAction(cc.sequence(cc.delayTime(delay),cc.moveTo(0.3, new cc.Vec2(0, 0)), resetPos))
    }

    public resetCardPos(): void {
        this.rootNode.setPosition(0, 0)
    }

 
    /**
    * 设置透明度  发发看之前为透明，现在改为不透明
    */
    setOp(op: boolean): void {
        this._bBonny = op;
        this.node.opacity = op ? 80 : 255;
        // this.img_num.node.opacity = op ? 80 : 255;
        // this.img_back.node.opacity = op ? 80 : 255;
        // this.img_num.node.opacity = op ? 80 : 255;
    }

    isBonny(): boolean {
        return this._bBonny;
    }

    
    turn(delay: number, showAction: boolean = false): void {
        this._realTurn(showAction,delay)
    }   

    doStopAllActions(){
        this.node.stopAllActions()
        this.imgBack.stopAllActions()
        this.rootNode.stopAllActions()
        this.img_cardFace.stopAllActions()
        // this.rootNode.setPosition(new cc.Vec2(0,0))
        this.resetCardPos()

    }

    public isFace() {
        return this.img_cardFace.active = true;
    }

    private _realTurn(showAction: boolean,dely:number): void {
        //if (this.isFace()) return;

        if (showAction) {
            this.img_cardFace.active = true;
            this.img_cardFace.runAction(cc.sequence(cc.delayTime(dely),cc.scaleTo(0.13, 0.01, 1), cc.scaleTo(0.13, 1, 1)));
            let turnCallBack = cc.callFunc(this.hide_Img_back.bind(this));
            this.imgBack.runAction(cc.sequence(cc.delayTime(dely),cc.scaleTo(0.13, 0.01, 1), turnCallBack));
        } else {
            this.setFace(true);
        }
    }

    /**
    * 设置是否显示牌面
    * @param bActive true:显示牌正面, false:显示牌背面
    */
    setFace(bActive: boolean): void {
        this.doStopAllActions()
        this.img_cardFace.active = bActive;
        this.img_cardFace.scaleX = 1;

        this.imgBack.active = !bActive;
        this.imgBack.scaleX = 1;

        this.img_cardFace.angle = 0;
        this.imgBack.angle = 0;

        this.resetCardPos()
    }

    setGary(b: boolean) {
        this.img_shadow.active = b;
    }

    public hide_Img_back(): void {
        this.imgBack.active = false;
        this.imgBack.scaleX = 1;
    }

    init(info) {
        if (this.info != info) {
            this.info = info;
        } else {
            this.initTable();
        }
    }

    update (dt) {
        if (!this.info) {
            return;
        }

        this.imgShadow.opacity = this.info.select ? 128 : 0;

        if (this.info.delay > 0) {
            this.info.delay -= dt;
            if (this.info.delay <= 0) {
                this.info.delay = 0;
            }
            return;
        }
    }

    onDisable() {
        eventMgr.removeListener(this);
    }

    initTable() {
        if (!this.info) {
            return;
        }

        this.imgBack.active = this.info.id == 0;
        // resMgr.loadImage("image/card/1" + this.info.id, (res) => {
        //     cc.isValid(this.img_num) && (this.img_num.spriteFrame = res);
        // })

        resMgr.loadCard(Number(GSettingDM.card_style), this.info.id, (res) => {
            cc.isValid(this.img_num) && (this.img_num.spriteFrame = res);
            this._hasContent = true;
        });
    }
}
