import { GMatchSer ,MatchModel} from "../../Service/MatchService";
import { GMsgCenter } from "../../utils/MessageCenter";
import { core } from "../../Framework/Core/Base";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_CupDetailItem extends cc.Component {
    @property(cc.Label)
    lbl_title: cc.Label = null

    @property(cc.Label)
    lbl_date: cc.Label = null

    @property(cc.Label)
    lbl_time: cc.Label = null

    onLoad()
    {
        
    }
    protected onDestroy(): void {
    }
    Btn_Detail_Click(event)
    {
        // GMsgCenter.send("detail")
    }
    setdata(jsonData:MatchModel) {
        // cc.log("ab")
        console.log("detail Data",JSON.stringify(jsonData))
        
        this.lbl_title.string = jsonData.des

        let dt = new Date(Number(jsonData.match_starttime))

        let str = core.format(dt,"HH:mm")
        let str2 = core.format(dt,"M-D")

        this.lbl_date = str
        
        this.lbl_time = str2
    }
}
