    private aft2 = [20,25,33,40,50,60,66,75,100,120,150,200,250,300,350,400,450,500,999]

    refreshQuickRaise(choiceNum?)
    {

        GSettingDM.updateQuickRaisePre();
        let pre=GSettingDM.quick_raise_pre||[2,2.5,2.8,3]
        let aft = ykUtils.clone(this.aft1)

        if(choiceNum!=null){
            this.lbl_curs[choiceNum].node.parent.getComponent(cc.Toggle).check()
        }
        
        if(this.tg_pre.isChecked)
        {
            //pre.sort((a,b)=>a-b)
            for(let i=0;i<this.lbl_curs.length;++i)
            {

                if(pre[i] == null){
                    this.lbl_curs[i].node.parent.active = false
                    continue
                }
                this.lbl_curs[i].node.parent.active = true

                if(pre[i]!=999)
                {
                    this.lbl_curs[i].string=pre[i]+"X"
                }
                else
                {
                    this.lbl_curs[i].string="All In"
                }
                let index = aft.indexOf(pre[i])
                if(index>=0)
                {
                    aft.splice(index,1)
                }
            }
            for(let i=0;i<this.lbl_chooses.length&&i<aft.length;++i)
            {
                if(aft[i]!=999)
                {
                    this.lbl_chooses[i].string=aft[i]+"X"
                }
                else
                {
                    this.lbl_chooses[i].string="All In"
                }
                this.lbl_chooses[i].node.parent.active=true
            }
            for(let i=aft.length;i<this.lbl_chooses.length;++i)
            {
                this.lbl_chooses[i].node.parent.active=false
            }
        }
        else
        {    
            pre = GSettingDM.quick_raise_aft||[33,50,60,100]
            aft = ykUtils.clone(this.aft2)
            //pre.sort((a,b)=>a-b)
            for(let i=0;i<this.lbl_curs.length;++i)
            {
                if(pre[i] == null){
                    this.lbl_curs[i].node.parent.active = false
                    continue
                }
                this.lbl_curs[i].node.parent.active = true


                if(pre[i]!=999)
                {
                    this.lbl_curs[i].string=pre[i]+"%"
                }
                else
                {
                    this.lbl_curs[i].string="All In"
                }
                let index = aft.indexOf(pre[i])
                if(index>=0)
                {
                    aft.splice(index,1)
                }
            }
            for(let i=0;i<this.lbl_chooses.length&&i<aft.length;++i)
            {
                if(aft[i]!=999)
                {
                    this.lbl_chooses[i].string=aft[i]+"%"
                }
                else
                {
                    this.lbl_chooses[i].string="All In"
                }
                this.lbl_chooses[i].node.parent.active=true
            }
            for(let i=aft.length;i<this.lbl_chooses.length;++i)
            {
                this.lbl_chooses[i].node.parent.active=false
            }
        }
    }
