    getRewardsLen()
    {

        let page = GPageMgr.getPage("Page_Game") as Page_Game
        if(!page)
        {
            return
        }
        let table_id = page.tableId
        let tableinfo = GTexasTableDM.getTableInfo(table_id)
        let matchInsM = GMatchSer.getMatchInsById(tableinfo.match_insid)
        let matchM = matchInsM.match
        if (!matchM) {
            return 0;
        }
    
        let rewards = matchM.reward
        if(matchM.dynamic_reward>0)
        {
            rewards=rewards.concat()
            let coin = 0
            for(let i=0;i<matchM.signup_cost.length;++i)
            {
                if(matchM.signup_cost[i].id==-99)
                {
                    coin=matchM.signup_cost[i].count
                    break
                }
            }
            let total_coin = matchInsM.player_num*coin
            let now_coin = 0
            let last_coin=0
            for (let i = 0; i < rewards.length; i++)
            {
                let rd = rewards[i]
                let dcoin=0
                if(rd.id==-99)
                {
                    dcoin=rd.count
                }
                else
                {
                    let item = GItemCfg.getItemById(rd.id)
            
                    if(item==null)
                    {
                        GHelper.showTip("比赛奖励不存在id="+rd.id)
                        return;
                    }
                    dcoin=item.coin*rd.count
                }
                now_coin+=dcoin
                last_coin=dcoin
            }
            let dtcoin = total_coin-now_coin
            if(dtcoin>0)
            {
                let num = Math.floor(dtcoin/last_coin)
                let left = dtcoin%last_coin
                for(let i=0;i<num;++i)
                {
                    rewards.push(rewards[rewards.length-1])
                }
                if(left>0)
                {
                    if(matchM.dynamic_reward==1)
                    {
                        rewards.push({id:-99,count:left})
                    }
                    else
                    {
                        let signup_coin = coin
                        //1 剩下手数
                        let left_signup_num=Math.floor(left/signup_coin)||1
                        //2 计算出属于哪个区间，病找出这个区间配置
                        let mutireward = GMatchSer.mutireward
                        let mr:MultiDynamicReward = null
                        for(let i=0;i<mutireward.length;++i)
                        {
                            let temp = mutireward[i]
                            if(left_signup_num>=temp.min&&left_signup_num<temp.max)
                            {
                                mr = temp
                                break
                            }
                        }
                        if(!mr)
                        {
                            mr = mutireward[mutireward.length-1]
                        }
                        for(let i=0;i<mr.rate.length;++i)
                        {
                            rewards.push({id:-99,count:Math.floor(left*mr.rate[i]/100)||1})
                        }
                    }
                }
            }
        }
        
        return rewards.length;
    }

