    match_result(jsonData) {
        GUserMgr.refreshSelfUser()
        if(this._table_id!=jsonData.table_id)
        {
            return
        }

        console.log(jsonData)
        this.initGameShare();
        this.nodePoster.active = true;
        this.nodePoster.getChildByName('poster').active = false;
        this.nodePoster.getChildByName('posterno').active = false;
        

        let tableinfo = GTexasTableDM.getTableInfo(this._table_id)
        let mim = GMatchSer.getMatchInsById(tableinfo.match_insid)
        this.matchName.string = mim.match.name
        
        //请求更新背包
        GItemCfg.init();
        if (jsonData.reward != null) {
            this.nodePoster.getChildByName('poster').active = true
            let poster = this.nodePoster.getChildByName('poster').getChildByName('poster');
            poster.active = true;
            cc.find('nameLayout/label_name',poster).getComponent(cc.Label).string = GUserMgr.self_user.nickname;

            let map : HashMap<number, string> = new HashMap();
            map.add(1, "一");
            map.add(2, "二");
            map.add(3, "三");
            map.add(4, "四");
            map.add(5, "五");
            map.add(6, "六");
            map.add(7, "七");
            map.add(8, "八");
            map.add(9, "九");
            map.add(10, "十");

            let suffix: string = "image/prize/" + jsonData.rank;
            let path = `${suffix}`;
    
            resMgr.loadImage(path, (res) => {
                cc.isValid(poster.getChildByName('img_reward')) && (poster.getChildByName('img_reward').getComponent(cc.Sprite).spriteFrame = res);
            })

            if (jsonData.rank > 10) {
                
                cc.find('nameLayout/label_rank',poster).getComponent(cc.Label).string = "第" + jsonData.rank + "名";
            }
            else
            {
                let ch = map.get(jsonData.rank);
                cc.find('nameLayout/label_rank',poster).getComponent(cc.Label).string = "第" + ch + "名";
            }
     

            let matchitem = jsonData.reward;
            if (matchitem.id == -99) {
                poster.getChildByName('label_prize').getComponent(cc.Label).string = '欢乐点' + "X" + matchitem.count
                poster.getChildByName('label_tips').getComponent(cc.Label).string = "恭喜获得第" + jsonData.rank + "名，奖励已发送到您卡包"
            }
            else
            {
                let itemcfg = GItemCfg.getItemById(matchitem.id)
                poster.getChildByName('label_prize').getComponent(cc.Label).string = itemcfg.name + "(" + matchitem.count + ")"
                poster.getChildByName('label_tips').getComponent(cc.Label).string = "恭喜获得第" + jsonData.rank + "名，" + itemcfg.name + "已发送到您卡包"
            }

            let rankImageStr = jsonData.rank<=2?("rewardHb"+jsonData.rank):"rewardHb3";
            resMgr.loadImage(rankImageStr, (res) => {
                let bg = this.nodePoster.getChildByName('poster').getChildByName("poster");
                cc.isValid(bg) && (bg.getComponent(cc.Sprite).spriteFrame = res);
            })
        }
        else
        {
            // 补丁 报名显示
            let cansignup = GMatchSer.canSignup(this._table_id)
            this.node.getChildByName("Page_Table").getComponent("Page_Table").reshSignUp(cansignup);
           
            // 处理淘汰随机背景
            this.nodePoster.getChildByName('posterno').active = true;
            let random = Math.floor (Math.random()*3) + 1
            this.fail_share_invite_codeLab.node.color = random==3?cc.Color.WHITE:cc.Color.BLACK;
            let bgno = "img_fail_share" + random;
            resMgr.loadImage(bgno, (res) => {
                let bg = this.nodePoster.getChildByName('posterno').getChildByName('nobg');
                cc.isValid(bg) && (bg.getComponent(cc.Sprite).spriteFrame = res);
            })
        }
    }

 
