    onClickShare(event: cc.Event.EventCustom, data: string) {
        // let channel = {
        //     "wechat": "分享微信",
        //     "friend": "分享朋友圈",
        //     "qq": "分享QQ",
        //     "weibo": "分享微博",
        //     "url": "分享链接"
        // }
        // GHelper.showTip(channel[data]);
        let posterno = this.node.getChildByName('node_poster').getChildByName("posterno");
        let poster = this.node.getChildByName('node_poster').getChildByName("poster");
        let tempNode = posterno.active?posterno:poster;
        // let displayNode = this.nodePoster.getChildByName('posterno').active == true ? this.nodePoster.getChildByName('posterno') : this.nodePoster.getChildByName('poster').getChildByName('poster');
        if (data=="wechat") {
            MultiPlatform.getInstance().capScreenShareWx(1,tempNode,null);
        }
        else if (data=="friend")
        {
            MultiPlatform.getInstance().capScreenShareWx(2,tempNode,null);
        }else if(data=="save")
        {
            MultiPlatform.getInstance().capScreen(tempNode,(isSucces)=>
            {
                GHelper.showTip(isSucces?'保存成功':'保存失败',2,cc.v2(0,630));
            });
        }
    }
    _tryIpad()
    {
        let safeSize = cc.sys.getSafeAreaRect()
        let r_h = safeSize.height/safeSize.width*750
        let ratio = r_h/1334
        if(ratio<1)
        {
            GPageMgr.node.scale=ratio
        }
    }

