import { GTexasTableDM } from "../../../DataModel/TexasTableDataModel";
import { GPageMgr } from "../../../Framework/View/Page/PageManager";
import { GItemCfg } from "../../../Service/ItemService";
import { GMatchSer, MultiDynamicReward } from "../../../Service/MatchService";
import Page_Game from "../../Page/Page_Game";
import { GTexasRewardTool } from "../TexasRewardTool";

const {ccclass, property} = cc._decorator;

@ccclass
export default class NodeMatchReward extends cc.Component {

    @property(cc.Node)
    layout: cc.Node = null

    @property(cc.Node)
    item: cc.Node = null

    @property(cc.Label)
    labelTotal: cc.Label = null

    onLoad() {
        this.init()
    }
    protected onEnable(): void {
        this.init()
    }
    init() {
        let page = GPageMgr.getPage("Page_Game") as Page_Game
        if(!page)
        {
            return
        }
        let table_id = page.tableId
        let tableinfo = GTexasTableDM.getTableInfo(table_id)
        let matchInsM = GMatchSer.getMatchInsById(tableinfo.match_insid)
        let matchM = matchInsM.match
        if (!matchM) {
            return
        }
        
        let score = 0
        this.layout.removeAllChildren()
       
       
        let mrs:MultiDynamicReward[]=GMatchSer.mutireward
        if(matchInsM.match.dynamic_reward == 4){
            mrs = GMatchSer.mutireward2
        }
        let rewards = GTexasRewardTool.getRewardAll(matchInsM,mrs)


        for (let i = 0; i < rewards.length; i++) {
            let node = cc.instantiate(this.item)
            this.layout.addChild(node)

            let item = rewards[i]
            node.getChildByName("label_rank").getComponent(cc.Label).string = String(i + 1);
            

            if (item.id == -99) {
                score += item.count
                node.getChildByName("label_reward").getComponent(cc.Label).string = '欢乐点' + "X" + item.count
            }
            else
            {
                let itemcfg = GItemCfg.getItemById(item.id)
                node.getChildByName("label_reward").getComponent(cc.Label).string = itemcfg.name + "X" + item.count
            }
        }
        this.labelTotal.string = "总欢乐点：" + score
    }
}
