import { GHallHttp } from "../Net/HallHttp"

export class AdModel
{
    id=0
    title=""
    icon=""
    url=""
    info=""
    create_time=-1
}
class AdService
{
    protected _ads:AdModel[]=[]
    get ads()
    {
        return this._ads
    }
    async init()
    {
        let res = await GHallHttp.request("allads")
        this._ads=res.ads||[]
        return this._ads
    }
}
export let GAdSer=new AdService()