import { GAppDM } from "../../DataModel/AppDataModel";
import { core } from "../../Framework/Core/Base";
import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { eventMgr } from "../../manager/EventManager";
import { resMgr } from "../../manager/ResourceManager";
import { GAccountHttp } from "../../Net/AccountHttp";
import { GMsgCenter } from "../../utils/MessageCenter";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_Register extends Page {

    @property(cc.EditBox)
    editNickname: cc.EditBox = null;

    @property(cc.EditBox)
    editPassword: cc.EditBox = null;

    @property(cc.EditBox)
    editPasswordConfirm: cc.EditBox = null;

    @property(cc.EditBox)
    editPhone: cc.EditBox = null;

    @property(cc.Label)
    lbl_phonetip: cc.Label = null;

    @property(cc.EditBox)
    editCode: cc.EditBox = null;

    @property(cc.EditBox)
    editInviter: cc.EditBox = null;

    @property(cc.Label)
    lbl_invitertip: cc.Label = null;

    @property(cc.Label)
    btnCode: cc.Label = null;

    @property(cc.Button)
    btnHead: cc.Button = null;

    @property(cc.Node)
    headNode: cc.Node = null;

    @property(cc.Sprite)
    imgAvatar: cc.Sprite = null;

    @property([cc.Node])
    heads = new Array<cc.Node>()

    random: number = 6;

    onLoad() {
        this.headNode.active = false;
        //随机一个头像
        this.random = this.getRandomInt(1, 16);
        let suffix: string = "image/head/" + this.random;
        let path = `${suffix}`;

        resMgr.loadImage(path, (res) => {
            cc.isValid(this.imgAvatar) && (this.imgAvatar.getComponent(cc.Sprite).spriteFrame = res);
        })
    }

    start() {
        // this.editNickname.node.parent.getChildByName("tips").active = false;
        this.editPassword.node.parent.getChildByName("tips").active = false;
        this.editPasswordConfirm.node.parent.getChildByName("tips").active = false;
        this.lbl_phonetip.node.active = false
        this.lbl_invitertip.node.active = false
    }

    protected update(dt: number){
        let remain = GAppDM.last_get_code ? GAppDM.last_get_code + 60000 - Date.now() : 0
        if (remain < 0) {
            remain = 0
        }
        remain = Math.floor(remain / 1000)
        this.btnCode.string = "获取验证码" + (remain ? `(${remain})` : "")
    }

    onClickLogin() {
        GPageMgr.closePage("Page_Register")
    }

    async onClickGetCode() {
        let phone = this.editPhone.string
        if (!core.isPhoneNo(phone)) {
            GHelper.showTip("请输入正确的手机号")
            return
        }
        let remain = GAppDM.last_get_code ? GAppDM.last_get_code + 60000 - Date.now() : 0
        if (remain > 0) {
            return
        }
        let res = await GAccountHttp.request("getphonecode",{phone:phone,why:"register"})
        if(res.errcode)
        {
            GHelper.showTip(JSON.stringify(res.errcode))
        }
        else
        {
            GHelper.showTip("验证码获取成功，请注意查收")
            GAppDM.last_get_code=Date.now()
        }
    }

    async onClickRegister() {
        // let nickname = this.editNickname.string;
        let password = this.editPassword.string;
        let password_confirm = this.editPasswordConfirm.string;
        let phone = this.editPhone.string;
        let code = this.editCode.string;
        let inviter = this.editInviter.string;

        if (!core.isPhoneNo(phone)) {
            GHelper.showTip("请输入正确的手机号");
            return;
        }

        if (!code) {
            GHelper.showTip("请输入验证码");
            return;
        }

        if (!password || !password_confirm) {
            GHelper.showTip("请输入密码");
            return;
        }

        if (password != password_confirm) {
            GHelper.showTip("两次输入密码不一致");
            return;
        }

        if (password.length < 6) {
            GHelper.showTip("您输入的密码长度过短，请重新输入");
            return;
        }
        let res = await GAccountHttp.request("register",{
            key:phone+"",
            code:code,
            password:password,
            from:4,
            invite_code:inviter,
            extra:
            {
                nickname:"",
                logo:this.random.toString(),
                sex:1
            }
        })
        if(res.errcode)
        {
            GHelper.showTip(res.errcode.des)
            return
        }
        GHelper.showTip("恭喜你注册成功")
        GPageMgr.closePage("Page_Register")
    }

    onEditPassword() {
        this.editPassword.string = this.editPassword.string.trim();
        this.editPassword.node.parent.getChildByName("tips").active = this.editPassword.string && this.editPassword.string.length < 6 || this.editPassword.string.length > 20;
    }

    onEditPasswordConfirm() {
        this.editPasswordConfirm.string = this.editPasswordConfirm.string.trim();
        this.editPasswordConfirm.node.parent.getChildByName("tips").active = this.editPasswordConfirm.string && this.editPassword.string != this.editPasswordConfirm.string;
    }

    async Edit_Phone_OnEnd() {
        this.editPhone.string = this.editPhone.string.replace(/[^0-9]/ig, "");
        if (!core.isPhoneNo(this.editPhone.string)) {
            this.lbl_phonetip.string = "请输入正确的手机号码"
            this.lbl_phonetip.node.active=true
            return;
        }
        let res = await GAccountHttp.request("checkphoneexist",{phone:this.editPhone.string})
        if(res.errcode)
        {
            this.lbl_phonetip.string = res.errcode.des
            this.lbl_phonetip.node.active=true
        }
        else
        {
            this.lbl_phonetip.node.active=false
        }
    }

    onEditCode() {
        this.editCode.string = this.editCode.string.replace(/[^0-9]/ig, "");
    }

    async Edit_Inviter_OnEnd() {
        this.editInviter.string = this.editInviter.string.replace(/[^0-9]/ig, "");
        if (this.editInviter.string) {
            let res = await GAccountHttp.request("checkinviteexist",{pre_user_id:this.editInviter.string})
            if(res.errcode)
            {
                this.lbl_invitertip.string = res.errcode.des
                this.lbl_invitertip.node.active=true
            }
            else
            {
                this.lbl_invitertip.node.active=false
            }
        } else {
            this.lbl_invitertip.node.active=false
        }
    }

    clickHeadCallback(selfTg) {
        let name = selfTg.node.name
        this.random = Number(name.replace("head", ""))
        for (let index = 0; index < this.heads.length; index++) {
            const element = this.heads[index]
            element.getComponent(cc.Toggle).uncheck()
        }

        selfTg.check()
    }

    onClickClose() {
        this.headNode.active = false
        let suffix: string = "image/head/" + this.random;
        let path = `${suffix}`;

        resMgr.loadImage(path, (res) => {
            cc.isValid(this.imgAvatar) && (this.imgAvatar.getComponent(cc.Sprite).spriteFrame = res);
        })
    }

    onClickHead() {
        this.headNode.active = true;
        //之前随机的头像带上勾选框
        for (let index = 0; index < this.heads.length; index++) {
            const element = this.heads[index]
            element.on('toggle', this.clickHeadCallback.bind(this), this);
            if (this.random-1 == index) {
                element.getComponent(cc.Toggle).check()
            }
            else
            {
                element.getComponent(cc.Toggle).uncheck()
            }
        }
    }

    getRandomInt(min, max) {
        return Math.floor(Math.random()*(max - min)) + min
    }
}
