import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../Logic/UserManager";
import { layerMgr } from "../../manager/LayerManager";
import { GOrderSer } from "../../Service/OrderService";
import ListView from "../../utils/ListView";
import Widget_ProductItem from "../Widget/Widget_ProductItem";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_Charge extends Page {
    
    @property(cc.Label)
    labelScore: cc.Label = null;

    @property(cc.ScrollView)
    sv_score: cc.ScrollView = null;

    start() {
        this.labelScore.string = GUserMgr.self_user.coin+""
        this.initProducts()
    }

    onClickCharge(event: cc.Event.EventCustom, data: string) {
        let id = Number(data);
        GPageMgr.openPage("Page_ChargeConfirm",id)
    }
    
    Btn_Close_OnClick(event)
    {
        GPageMgr.closePage("Page_Charge")
    }

    initProducts() {
        let products = GOrderSer.products;
        let dataList = []
        for (const key in products) {
            dataList.push({type: 0, product: products[key]})
        }

        this.sv_score.getComponent(ListView).init(this.bindcallfunc.bind(this), this.getItemType.bind(this));
        this.sv_score.getComponent(ListView).notifyDataSetChanged(dataList)
    }

    public getItemType() {
        return 0
    }

    public bindcallfunc(node: cc.Node, info, i) {
        node.getComponent(Widget_ProductItem).setdata(info)
    }
}
