import { EMatchState } from "../../Config/_ini_";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../Logic/UserManager";
import { GMatchSer } from "../../Service/MatchService";
import Widget_MatchListItem from "../Widget/Widget_MatchListItem";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_MatchFocus extends Page {

    @property(cc.ScrollView)
    sv_list: cc.ScrollView = null;

    @property(cc.Prefab)
    pf_item: cc.Prefab = null;

    start() {
        this.init()
    }
    init()
    {
        let focusmatchins = GMatchSer.focusmatchins
        if(!focusmatchins||focusmatchins.length==0)
        {
            return
        }
        for(let i=0;i<focusmatchins.length;++i)
        {
            let insm = focusmatchins[i]
            
            if(!insm.match)
            {
                insm.match=GMatchSer.getMatchById(insm.match_id)
                if(!insm.match)
                {
                    continue
                }
            }
            if(insm.state==EMatchState.Cancel)
            {
                continue
            }
            if(insm.state==EMatchState.End)
            {
                continue
            }
            if(insm.blind_index>=insm.match.reenter_blind_index)
            {
                continue
            }
            let node = cc.instantiate(this.pf_item)
            this.sv_list.content.addChild(node)
            let sc = node.getComponent(Widget_MatchListItem)
            sc.init(insm)
        }
    }
    Btn_Close_OnClick(event)
    {
        GPageMgr.closePage("Page_MatchFocus")
    }
}
