import { BaseAd } from "./BaseAd";

class UCAd extends BaseAd {
    private bannerAd = null;

    constructor() {
        super("uc");
    }

    private getUserInfo(success?: Function, fail?: Function) {
        uc.getSetting({
            success: () => {
                uc.getUserInfo({
                    success: (res) => {
                        success && success(res.userInfo);
                    },
                    fail: () => {
                        fail && fail();
                    }
                })
            },
            fail: () => {
                uc.authorize({
                    scope: "userInfo",
                    success: (data) => {
                        uc.getUserInfo({
                            success: (res) => {
                                success && success(res.userInfo);
                            },
                            fail: () => {
                                fail && fail();
                            }
                        })
                    },
                    fail: () => {
                        fail && fail();
                    }
                })
            }
        });
    }

    login(success?: Function, fail?: Function) {
        uc.login({
            success: (res) => {
                this.getUserInfo((userInfo) => {
                        success && success({
                            nickname: userInfo.nickname,
                            avatar: userInfo.avatar_url,
                            gender: userInfo.gender,
                            platform: this.platform,
                            code: res.code
                        });
                    }, () => {
                        fail && fail();
                    }
                )
            },
            fail: () => {
                fail && fail();
            }
        });
    }

    share() {
        uc.shareAppMessage({
            query: "",//key=value&key=value
            target: "wechat",//wechat、qq
            success: (res) => {

            },
            fail: (err) => {

            }
        })
    }

    createBannerAd() {
        if (this.bannerAd == null) {
            let sysInfo = uc.getSystemInfoSync();
            if (typeof sysInfo == "string") {
                sysInfo = JSON.parse(sysInfo);
            }
            this.bannerAd = uc.createBannerAd({
                style: {
                    width: Math.min((sysInfo.pixelRatio * 250), sysInfo.screenWidth),
                    height: 0,  // 高度自适应
                    gravity: 7  // 底部居中
                }
            });
            this.bannerAd.show();
            this.bannerAd.onLoad(() => {
                console.log("Banner广告加载成功");
            });
            this.bannerAd.onError(() => {
                console.log("Banner广告加载失败");
            });
        }
    }

    destroyBannerAd() {
        if (this.bannerAd != null) {
            this.bannerAd.hide();
            this.bannerAd.destroy();
            this.bannerAd = null;
        }
    }

    createVideoAd(success?: Function, fail?: Function) {
        let videoAd = uc.createRewardVideoAd();
        videoAd.onLoad(() => {
            console.log("激励视频广告加载成功");
            videoAd.show();
        });
        videoAd.onError(() => {
            console.log("激励视频广告加载失败");
            fail && fail();
        });
        videoAd.onClose((res) => {
            if (res && res.isEnded) {
                console.log("激励视频正常播放结束");
                success && success();
            } else {
                console.log("激励视频播放中途推出");
                fail && fail();
            }
        })
    }

    createInterstitialAd(callback?: Function) {
        let interstitialAd = uc.createInterstitialAd();
        interstitialAd.show();
        interstitialAd.onLoad(() => {
            console.log('插屏广告加载成功');
        });
        interstitialAd.onError(() => {
            console.log('插屏广告加载失败');
        });
        interstitialAd.onClose(() => {
            console.log('插屏广告关闭');
            callback && callback();
        })
    }
    
    createNativeAd(success?: Function, fail?: Function) {
        success && success();
    }
    
    reportAdShow(id: string) {

    }
    
    reportAdClick(id: string) {

    }
}

export const ucAd: UCAd = new UCAd();