import { GGameDM } from "../../Framework/DataModel/GameDataModel";
import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import ListView from "../../utils/ListView";
import { GMsgCenter } from "../../utils/MessageCenter";
import Widget_ClubItem from "../Widget/Widget_ClubItem";
import Widget_CupItem from "../Widget/Widget_CupItem";
import Widget_CitySearchCupItem from "../Widget/Widget_CitySearchCupItem";

import { ClubModel ,GClubSer} from "../../Service/ClubService";
import { GMatchSer, MatchInsModel, MatchModel } from "../../Service/MatchService";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_Club extends Page {

    @property(cc.Label)
    area: cc.Label = null;

    @property(cc.EditBox)
    edit: cc.EditBox = null;

    @property(cc.ToggleContainer)
    toggles: cc.ToggleContainer = null;

    @property(cc.Node)
    nodeClub: cc.Node = null;

    @property(cc.Node)
    nodeCup: cc.Node = null;

    @property(cc.Node)
    nodeSearch: cc.Node = null;

    @property(cc.Node)
    filtrate:cc.Node = null;

    @property(cc.Node)
    nodeTogs:cc.Node = null;

    @property(cc.Node)
    inputLabel:cc.Node = null;

    @property(cc.Node)
    nodeCitySearch:cc.Node = null;

    private filtrateCity:string = ''

    private cubAll:any[] = []

    protected _allMatchData:{matches:MatchModel[],matchins:MatchInsModel[]}={matches:null,matchins:null}

    onLoad() {
        GMsgCenter.register("detail", this.detailInfo.bind(this), this);
        GMsgCenter.register("detailBack", this.detailBack.bind(this), this);

        // GMsgCenter.register("club_join", this.doClubJoin.bind(this), this);
        GMsgCenter.register("club_detail", this.doClubDetail.bind(this), this);

        GMsgCenter.register("filtrate_club_search", this.filtrateSearch.bind(this), this);
    }

    refreshData(){
        console.log('refreshData')
        // 重置状态
        // this.resetSearch()
        this.onClickEditCancel()
        this.toggles.toggleItems[0].check()
        this.detailBack(false)
        this.onToggleClub()
        // 
    
    }


    async doClubDetail(data:ClubModel){
       let page = await GPageMgr.openPage("Page_AddClub",data) as Page
       page.setDelegate(this)
    }
    
    detailInfo(detailData) {
        let detail = this.node.parent.getChildByName("node_cupdetail");
        let x = detail.getPosition().x;
        detail.setPosition(detail.getPosition().x + cc.winSize.width, detail.getPosition().y);
        detail.active = true;
        this.node.runAction(cc.moveTo(0.2, this.node.x - cc.winSize.width, this.node.y));
        detail.runAction(cc.moveTo(0.2, 0, detail.y));
 
        detail.getComponent('Page_CupDetail').initDetailData(detailData,this.allClubData.get(detailData[0].club_id))
    }

    
    detailBack(needAnimal) {
        console.log('needAnimal',needAnimal)
        let animalTime = 0.2
        if(needAnimal == false){
            animalTime = 0
        }
        let detail = this.node.parent.getChildByName("node_cupdetail");
        this.node.runAction(cc.moveTo(animalTime, 0, this.node.y));
        detail.runAction(
            cc.sequence(cc.moveTo(animalTime, cc.winSize.width, detail.y),
            cc.callFunc(()=>{
                detail.active = false
            }))
            );
    }


    public getItemType1(data) {
        return 0
    }

    public getItemType2(data) {
        return 0
    }
   
    public getItemType3(data) {
        return 0
    }

    public getItemType4(data) {
        return 0
    }

    public bindcallfunc1(node: cc.Node, info, i) {
        node.getComponent(Widget_ClubItem).setdata(info)
    }

    public bindcallfunc2(node: cc.Node, info, i) {
        node.getComponent(Widget_CupItem).setdata(info)
    }

    public bindcallfunc3(node: cc.Node, info, i) {
        node.getComponent(Widget_ClubItem).setdata(info)
    }

    public bindcallfunc4(node: cc.Node, info, i) {
        node.getComponent(Widget_CitySearchCupItem).setdata(info)
    }

    allClubData:any =null;
    async start() {
        this.allClubData = new Map();
        let allClub  = await GClubSer.init()
        this.nodeClub.getComponent(ListView).init(this.bindcallfunc1.bind(this), this.getItemType1.bind(this));
        this.refreshClubAll(allClub)
        for(var i=0;i<allClub.length;i++)
        {
            this.allClubData.set(allClub[i]['id'],allClub[i]['title'] )
        }
        // dataList.push({type: 0, clubdata: {}})
        // dataList.push({type: 0, clubdata: {}})
        // dataList.push({type: 0, clubdata: {}})

      
        // let cupList  = []
        // cupList.push({type: 0, cupdata: {}})
        // cupList.push({type: 0, cupdata: {}})
        // cupList.push({type: 0, cupdata: {}})

        
        // this.nodeCup.getComponent(ListView).notifyDataSetChanged(cupList)

        // let searchList  = []
        // searchList.push({type: 0, searchdata: {}})
        // searchList.push({type: 0, searchdata: {}})
        // searchList.push({type: 0, searchdata: {}})

        this.nodeSearch.getComponent(ListView).init(this.bindcallfunc3.bind(this), this.getItemType3.bind(this));
        // this.nodeSearch.getComponent(ListView).notifyDataSetChanged(searchList)
        this.nodeCitySearch.getComponent(ListView).init(this.bindcallfunc4.bind(this), this.getItemType4.bind(this));

        this.toggles.toggleItems[0].check()
        this.onToggleClub()
        this.initPageCub()
    }

    // 刷新赛事
    async initPageCub()
    {
        this.nodeCup.getComponent(ListView).init(this.bindcallfunc2.bind(this), this.getItemType2.bind(this));
        this._allMatchData = await GMatchSer.getAllMatches(false)

        let resDd = {}
        for(var v of this._allMatchData.matches){
            if(v.club_id>0 && v.match_starttime>Date.now()){
                if(!resDd[v.club_id.toString()]){
                    resDd[v.club_id.toString()] = []
                }

                resDd[v.club_id.toString()].push(v)
            }
        }
        this.cubAll = []
        
        for(var k in resDd){
            this.cubAll.push([k,resDd[k]])
        }


        this.nodeCup.getComponent(ListView).notifyDataSetChanged( this.cubAll)

    }
    
    async refreshClubAll(allClub){
       
        let dataList = []
        for(var k in allClub){
            dataList.push({type: k, clubdata: allClub[k]})
        }
        
        this.nodeClub.getComponent(ListView).notifyDataSetChanged(dataList)
    }


    protected onDestroy(): void {
        GMsgCenter.unregister("detail", this)
        GMsgCenter.unregister("detailBack", this)
        // GMsgCenter.unregister("club_join",this);
        GMsgCenter.unregister("club_detail", this);
        // GMsgCenter.unregister("refresh_clubAll", this);
        GMsgCenter.unregister("filtrate_club_search",  this);
    }

    

    resetSearch(){
        if(!this.filtrate.active){
            cc.find('inputLayout/back_long',this.node).active = false
            cc.find('inputLayout/back_short',this.node).active = true
            this.nodeClub.getComponent(ListView).notifyDataRefresh()
            // this.edit.node.x = 59
            this.edit.node.children[0].x= 77
        }
    }

    onToggleClub() {
        this.nodeClub.active = true;
        this.nodeCup.active = false;
        this.nodeSearch.active = false;
        this.nodeTogs.active = true  
    }

    onToggleCup() {
        this.nodeClub.active = false;
        this.nodeCup.active = true;
        this.nodeSearch.active = false;
        this.nodeTogs.active = true
    }

    onClickEditCancel(){
        this.edit.string = ''
        this.filtrate.active = false
        this.filtrateCity = ''
        if(this.toggles.toggleItems[0].isChecked){
            this.onToggleClub()
        }else{
            this.onToggleCup()
        }

        this.resetSearch()
    }

    onClickSearch() {
        let str = this.edit.string
        cc.log(str)
    }

    onEditBegin() {
        cc.log("begin")
        cc.find('inputLayout/back_long',this.node).active = true
        cc.find('inputLayout/back_short',this.node).active = false
        // this.edit.node.x = -60.928
        this.edit.node.children[0].x= -220
    }

    onEditChanged() {
        cc.log("changed")
        let str = this.edit.string
      
    }

    onClubEditClick(){

    }

    onCLubFiltrateClick(){
        // this.nodeClub.getComponent(ListView).notifyDataRefresh()
        this.filtrate.active = true
        this.onEditBegin()
    }
s

    onEditEnd() {
        let str = this.edit.string
        if (str.length==0) {
            if(this.toggles.toggleItems[0].isChecked){
                this.onToggleClub()
            }else{
                this.onToggleCup()
            }
            
            this.nodeSearch.active = false;
        }
        else
        {
            this.nodeTogs.active = false;
            this.nodeClub.active = false;
            this.nodeCup.active = false;
            this.nodeSearch.active = true;
           
            // cc.find('inputLayout/back_long',this.node).active = true
            // cc.find('inputLayout/back_short',this.node).active = false

            this.searchClub(str)
        }
        cc.log(str)
        cc.log("end")
    }

    async searchClub(title:string){

        let searchResultListView =   this.nodeSearch
        if(this.filtrate.active){
            searchResultListView =   this.nodeCitySearch
        }

        searchResultListView.getComponent(ListView).notifyDataSetChanged([])
        // let res = await GClubSer.searchClub({city:this.filtrateCity||'',title:title})

        let res = await GClubSer.searchClub({city:'',title:title})
        if(!res.clublist){
            return
        }
        let searchList  = []    
        for(var k in res.clublist){
            searchList.push({type: 0, clubdata: res.clublist[k]})
        }
        console.log(searchList.length)
        searchResultListView.getComponent(ListView).notifyDataSetChanged(searchList)
    }

    filtrateSearch (city){
        this.filtrateCity = city
        this.searchClub(this.edit.string)
    }

    onEditReturn() {
        cc.log("return")
    }
}
