import Widget_DemainCode from "../Widget/Widget_DemainCode";
import { Page } from "../../Framework/View/Page/Page";
import { BagItemItem, GItemCfg } from "../../Service/ItemService";
import { GUserMgr } from "../../Logic/UserManager";
import { MD5 } from "../../utils/MD5";
import { GBase64 } from "../../Framework/Core/Base64";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { ClubModel ,GClubSer} from "../../Service/ClubService";
import Widget_NumberFrame from "../Widget/Widget_NumberFrame";
import { GMsgCenter } from "../../utils/MessageCenter";
import { GGameDM } from "../../Framework/DataModel/GameDataModel";
import { GHelper } from "../../Framework/Logic/Helper";
import { GHallHttp } from "../../Net/HallHttp";
import { ykUtils } from "../../utils/YkUtils";
import { layerMgr } from "../../manager/LayerManager";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_UseItemByCode extends Page {
    @property(Widget_DemainCode)
    widget_DemainCode:Widget_DemainCode = null
    @property(Widget_NumberFrame)
    numberFrame = null

    private _item
    private hxData
    private xpInterval

    start() {
    }
    onLoad(): void {
        this.numberFrame.setOnEditEndCb(()=>{
            this.onRefreshNumber()
        })
    }

    async onOpen(item:BagItemItem)
    {   
        // 生成此张票的核销信息
      
        
        // 加密
        // let keyMm = 'ffddccvvaa%?'

        // let sign = new MD5().hex_md5(userId+id+date+keyMm) 
        // data['sign'] = sign

        // console.log(data)
        this._item = item
        this.numberFrame.setMax(item.count)


        let itemConf = GItemCfg.getItemById(this._item.item_id)
        let club = itemConf.verifi_club
        if(club<-1){
            cc.find('content/lb_club',this.node).getComponent(cc.Label).string ="(所有俱乐部)"
        }else if(club && club>0){
           let clubData =  await GClubSer.getClubDataById(club)
           cc.find('content/lb_club',this.node).getComponent(cc.Label).string = clubData.title
        }

        this.onRefreshNumber()
    }


    async onRefreshNumber(){
        let userId = GUserMgr.self_user._id
        let id = this._item.id
        let date = Date.now()
        let itemId = this._item.item_id
        let data ={}
        if(this._item.count == 1){
       
            data = {
                userId:userId,
                id:id,
                date:date,
                type:1
            }
      
        }else{
            data = {
                userId:userId,
                itemId:itemId,
                id:id,
                num:this.numberFrame.getCurNum(),
                date:date,
                type:2
            }
        }

        this.hxData = data

        this.widget_DemainCode.generateDeminCode(JSON.stringify(data))
        let itemConf = GItemCfg.getItemById(this._item.item_id)
        cc.find('content/lb_name',this.node).getComponent(cc.Label).string = itemConf.name+'x'+this.numberFrame.getCurNum()
    
        // console.log(JSON.stringify(data))
        // 3s 检测一次
        this.clearXpInterval()
        this.xpInterval = setInterval(()=>{
            this.onCheckXiaoPiao()
        },2000)
    }

    onCancel(){
        GPageMgr.closePage("Page_UseItemByCode")
    }
    
    clearXpInterval(){
        if(this.xpInterval){
            clearInterval(this.xpInterval)
        }
        this.xpInterval = null
    }


    protected onDestroy(): void {
        this.clearXpInterval()
    }


    // 检测销票 信息
    async onCheckXiaoPiao(){
        if(this.hxData = null){
            this.clearXpInterval
            return
        }
        // 刷新背包
        // let bagItems = GItemCfg.bagItems
        // GMsgCenter.send('update_myprize',{tag:'Page_MyPrizeDetail',item_id:this._item.item_id})
        // await GItemCfg.init()
        // let bagItems = GItemCfg.bagItems
        // var isHave = false
        // bagItems.forEach((items)=>{
        //     if(items.id == this._item.id
        //         &&items.item_id == this._item.item_id
        //         &&items.create_time == this._item.create_time){
        //             isHave = true
        //         }
        // })
        let tp = this._item.type

        let res = await GHallHttp.request('checkIsBagItemsHaveItem',{id:this._item.id})
        // 防止销毁后执行
        if(!cc.isValid(this)){
            return
        }
        if(!res.isHave){
            GHelper.showTip('扫码成功')
            this.clearXpInterval()
            // ==================等待1s后进行刷新

            if (this.hxData.type == 2){
                 // 显示等待 
                 layerMgr.showNetLoading(true,'销票中...')
                 await ykUtils.waitTime(2)
                 layerMgr.showNetLoading(false)
            }
          

            GMsgCenter.send('update_myprize',{tag:'Page_MyPrizeDetail',item_id:this._item.item_id})
            GPageMgr.closePage("Page_UseItemByCode")
        }
    }
}