import { GHelper } from "../Logic/Helper";

export class SimpleWebServer
{
    protected _host=""
    protected _port=0
    protected _ctr=""
    session_id:string=null
    init(ctr:string,host:string,port:number)
    {
        this._host = host
        this._port = port
        this._ctr = ctr
    }
    protected _getUrl(action:string,ctr?:string)
    {
        ctr = ctr||this._ctr
        let url = "http://"+this._host+":"+this._port+"/"+ctr+"/"+action
        return url
    }
    protected async _request(action:string,msg?:any,ctr?:string)
    {
        msg=msg||{}
        if(this.session_id&&action!="GetTime")
        {
            msg.session_id=this.session_id
        }
        let url = this._getUrl(action,ctr)

        let rs = await GHelper.httpPost(url,msg)
        console.log(JSON.stringify(rs))
        return rs
    }
    async request(action:string,msg?:any,ctr?:string)
    {
        return await this._request(action,msg,ctr)
    }
}