import { EMatchState } from "../../Config/_ini_";
import { GGameDM } from "../../Framework/DataModel/GameDataModel";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GMatchSer } from "../../Service/MatchService";
import Widget_MatchListItemEd from "../Widget/Widget_MatchListItemEd";
import Widget_MatchListItemIng from "../Widget/Widget_MatchListItemIng";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_MatchSignup extends Page {

    @property(cc.ToggleContainer)
    toggles: cc.ToggleContainer = null

    @property(cc.ScrollView)
    sv_ing:cc.ScrollView=null

    @property(cc.ScrollView)
    sv_ed:cc.ScrollView=null

    @property(cc.Prefab)
    pf_ing:cc.Prefab=null

    @property(cc.Prefab)
    pf_ed:cc.Prefab=null

    start() {
        this.onToggleMatchBegin()
        this.sv_ing.node.active=true
        this.sv_ed.node.active=false
        this.init()
    }

    async init() {
        if(GGameDM.isReviewVersion)
        {
            return
        }
        await GMatchSer.initMyMatches()
        let all = GMatchSer.myMatchInfos||{}
        for(let key in all)
        {
            let umi = all[key]
            if(!umi.match)
            {
                continue
            }
            if(umi.match.state==EMatchState.Playing)
            {
                let node = cc.instantiate(this.pf_ing)
                this.sv_ing.content.addChild(node)
                let sc = node.getComponent(Widget_MatchListItemIng)
                sc.init(umi)
            }
            else if(umi.match.state==EMatchState.End)
            {
                let node = cc.instantiate(this.pf_ed)
                this.sv_ed.content.addChild(node)
                let sc = node.getComponent(Widget_MatchListItemEd)
                sc.init(umi)
            }
        }
    }

    onToggleMatchBegin() {
        this.sv_ing.node.active=true
        this.sv_ed.node.active=false
        this.toggles.toggleItems[0].node.getChildByName("bg").active=false
        this.toggles.toggleItems[1].node.getChildByName("bg").active=true
    }

    onToggleMatchEnd() {
        this.sv_ing.node.active=false
        this.sv_ed.node.active=true
        this.toggles.toggleItems[0].node.getChildByName("bg").active=true
        this.toggles.toggleItems[1].node.getChildByName("bg").active=false
    }
    Btn_Close_OnClick(event)
    {
        GPageMgr.closePage("Page_MatchSignup")
    }
}
