import { RoomInfo } from "../../../data/data";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemRoom extends cc.Component {
    
    @property(cc.Label)
    labelTitle: cc.Label = null;

    @property(cc.Label)
    labelScore: cc.Label = null;

    @property(cc.Label)
    labelTime: cc.Label = null;

    @property(cc.Label)
    labelCount: cc.Label = null;

    private info: RoomInfo = null;

    init(info: RoomInfo) {
        this.info = info;
        this.refreshUI();
    }

    update (dt) {
    }

    refreshUI() {
        if (!this.info) {
            return;
        }

        this.labelTitle.string = String(this.info.config.name);
        this.labelScore.string = String(this.info.config.entry_cost);
        this.labelCount.string = this.info.count_play + "/" + this.info.count_match;
        let date = new Date(this.info.config.start_time);
        let hour = date.getHours();
        let minute = date.getMinutes();
        this.labelTime.string = (hour < 10 ? '0' + hour : hour) + ":" + (minute < 10 ? '0' + minute : minute);
    }

    onClickSelect() {
        // gameCtrl.joinRoom(this.info.id, (msg) => {
        //     if (msg) {
        //         gameCtrl.room_id = this.info.id;
        //         sceneMgr.toGameScene();
        //     }
        // });
    }
}
