import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { AddressItem, GUserMgr } from "../../Logic/UserManager";
import { GHallHttp } from "../../Net/HallHttp";
import { BagItemItem, EBagItemState, GItemCfg } from "../../Service/ItemService";
import { GMsgCenter } from "../../utils/MessageCenter";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_Exchange extends Page {
    @property(cc.Node)
    node_chooseaddress:cc.Node=null

    @property(cc.Node)
    node_addressinfo:cc.Node=null

    @property(cc.Label)
    lbl_address:cc.Label=null
    @property(cc.Label)
    lbl_name_phone:cc.Label=null

    @property(cc.Sprite)
    sp_icon:cc.Sprite=null

    @property(cc.Label)
    lbl_itemname:cc.Label=null
    @property(cc.Label)
    lbl_itemcount:cc.Label=null

    protected _item:BagItemItem=null
    protected _count=1
    protected _address:AddressItem=null
    onLoad()
    {
        GMsgCenter.register("address_changed",this.init.bind(this),this)
        this.init()
    }
    onDestroy()
    {
        GMsgCenter.unregister("address_changed",this)
    }
    init()
    {
        let address_time = GUserMgr.self_user.address_time||0
        if(address_time<=0)
        {
            this.node_addressinfo.active=false
            this.node_chooseaddress.active=true
            return
        }
        let addresses = GUserMgr.self_user.addresses||[]
        for(let i=0;i<addresses.length;++i)
        {
            if(addresses[i].create_time==address_time)
            {
                this._address=addresses[i]
                break
            }
        }
        if(!this._address)
        {
            this.node_addressinfo.active=false
            this.node_chooseaddress.active=true
        }
        else
        {
            this.lbl_name_phone.string = this._address.name+" "+this._address.phone
            this.lbl_address.string = this._address.address
        }
    }

    onOpen(item:BagItemItem)
    {
        this._item=item
        this.lbl_itemcount.string = this._count+""
        let itemM = GItemCfg.getItemById(this._item.item_id)
        this.lbl_itemname.string = itemM.name
        GHelper.createSpriteFrame(itemM.icon,(sf)=>
        {
            if(cc.isValid(this.sp_icon))
            {
                this.sp_icon.spriteFrame=sf
            }
        })
    }

    Btn_Address_OnClick(event) {
        GPageMgr.openPage("Page_Address")
    }

    Btn_Minus_OnClick(event)
    {
        if(this._count<=1)
        {
            return
        }
        --this._count
        this.lbl_itemcount.string = this._count+""
    }

    Btn_Add_OnClick(event)
    {
        if(this._count>=this._item.count)
        {
            return
        }
        ++this._count
        this.lbl_itemcount.string = this._count+""
    }

    Btn_Return_OnClick(event) {
        GPageMgr.closePage("Page_Exchange")
    }

    Btn_Exchange_OnClick(event)
    {
        if(!this._address)
        {
            GHelper.showTip("请先选择地址")
            return
        }
        GHallHttp.request("useitem",{addressinfo:this._address,bag_id:this._item.id,count:this._count}).then((rs)=>
        {
            //this._item.state=EBagItemState.Using
            GMsgCenter.send("item_changed")
            GPageMgr.closePage("Page_Exchange")
        })
    }
}