import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { EBagItemState, EItemType, GItemCfg } from "../../Service/ItemService";
import { GMsgCenter } from "../../utils/MessageCenter";
import Widget_ChangeItem from "../Widget/Widget_ChangeItem";

import tableView from "../../Framework/TableView/tableView"
import { GUserMgr } from "../../Logic/UserManager";
import { ykUtils } from "../../utils/YkUtils";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_MyPrize extends Page {

    @property(tableView)
    sv_content:tableView=null

    @property(cc.Prefab)
    pf_item:cc.Prefab=null

    private groupList = []
    private groupDetailList = {}
    private _checkIndex = "1";

    refreshGroups(){
        this.groupList = []
        this.groupDetailList = {}
    }

    onLoad() {
        GMsgCenter.register("update_myprize", this.update_myprize.bind(this), this.node);
    }

    protected onDestroy(): void {
        GMsgCenter.unregister("update_myprize", this.node)
    }


    async start() {
        await GItemCfg.init();
        this.init()
    }

    async update_myprize(refreshData = null){
        // await GUserMgr.refreshSelfUser()
        await GItemCfg.init()

        this.Tg_Exchange_OnChanged(null,this._checkIndex ,refreshData)
    }

    async resh()
    {
        // await GUserMgr.refreshSelfUser()
        await GItemCfg.init();
        
        GMsgCenter.send("updateUserInfo");
        this.Tg_Exchange_OnChanged(null,this._checkIndex)
    }

    init(refreshData=null)
    {
        this.Tg_Exchange_OnChanged(null,'1',refreshData)
    }
 
    Tg_Exchange_OnChanged(event, eventdata,refreshData=null)
    {
        this.sv_content.clear()
        var dataList  = []
        var resList = []

        this._checkIndex = eventdata;

        // this.sv_content.content.removeAllChildren()
        if (eventdata=="1") {
        //     //表示未兑换
            let bagItems = GItemCfg.bagItems
            bagItems.forEach(element => {
                if(element.state==EBagItemState.None || element.state==EBagItemState.TransOk){
                    dataList.push(element)
                }
            });
        }
        else if (eventdata=="2") {
        //     //表示已兑换
            let bagItems = GItemCfg.exItems
            bagItems.sort((a,b)=>a.state-b.state)
            bagItems.forEach(element => {
                if(element.state!=EBagItemState.None){
                    dataList.push(element)
                }
            });
        }
        this.refreshGroups()

        for(var i=0;i<dataList.length;i++){
            resList.unshift(dataList[i])
        }

        // 对未兑换的进行分组
        if(eventdata=="1"){
            let groupRes = {}
            for(var v of resList){
                if(v.give_check ==0){
                    this.groupList.push(v)
                }else{
                    if(!groupRes[v.item_id]){
                        groupRes[v.item_id] = []
                    }
                    groupRes[v.item_id].push(v)
                }
            }
            
            this.groupDetailList = groupRes
            for(var k in groupRes){
                if(groupRes[k].length == 1){
                    this.groupList.push(groupRes[k][0])
                }else{
                    let listData = ykUtils.clone(groupRes[k][0])
                    listData.count = groupRes[k].length
                    this.groupList.push(listData)
                }
            }

            resList =  this.groupList
        }

        //===========================
        console.log(resList)


        // 将可进行操作的卡 放到前排
        if(eventdata == 2){
            var firstList = []
            var threeList = []
            var oldList = []
            resList.forEach((element)=>{
                if(element.give_check == 0){
                    firstList.push(element)
                }else if(element.state == EBagItemState.Exchanging){
                    threeList.push(element)
                }else{
                    oldList.push(element)
                }
            })

            resList = firstList.concat(threeList).concat(oldList)
        }


        this.sv_content.delegate = this
        this.sv_content.init(resList)

        if(refreshData && refreshData.tag == 'Page_MyPrizeDetail'){
            GMsgCenter.send("update_myprizeDetail",this.groupDetailList[refreshData.item_id])
        }

    }
    // 当详细被点击
    onPageDetailClick(item_id){
        GPageMgr.openPage("Page_MyPrizeDetail",this.groupDetailList[item_id])
    }

    Btn_Close_OnClick(event)
    {
        GPageMgr.closePage("Page_MyPrize")
    }
}
