import { GAppDM } from "../../../DataModel/AppDataModel";
import { Page } from "../../../Framework/View/Page/Page";
import { GPageMgr } from "../../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../../Logic/UserManager";
import { GHallHttp } from "../../../Net/HallHttp";
import { GHelper } from "../../../Framework/Logic/Helper";
import { GMsgCenter } from "../../../utils/MessageCenter";
import { resMgr } from "../../../manager/ResourceManager";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_Person extends Page {
    
    @property(cc.Sprite)
    imgAvatar: cc.Sprite = null;

    @property(cc.EditBox)
    editboxNickname: cc.EditBox = null;

    @property(cc.Label)
    labelId: cc.Label = null;

    @property(cc.Label)
    labelPhone: cc.Label = null;

    initTable() {
        this.editboxNickname.string = GUserMgr.self_user.nickname
        this.labelId.string = String(GUserMgr.self_user.id)
        this.labelPhone.string = GAppDM.account.phone
    }

    onLoad(): void {
        this.editboxNickname.string = GUserMgr.self_user.nickname
        this.labelId.string = String(GUserMgr.self_user.id)
        this.labelPhone.string = GAppDM.account.phone
        let suffix: string = "image/head/" + GUserMgr.self_user.logo;
        let path = `${suffix}`;

        resMgr.loadImage(path, (res) => {
            cc.isValid(this.imgAvatar) && (this.imgAvatar.getComponent(cc.Sprite).spriteFrame = res);
        })
    }

    onEditNickname() {
        this.editboxNickname.string = this.editboxNickname.string.trim();
    }

    onClickSave() {
        let nickname = this.editboxNickname.string;
        GHallHttp.request("changename", {nickname: nickname}).then((res)=>
        {
            if (res.errcode==null) {
                GHelper.showTip("修改成功");
                GUserMgr.self_user.nickname = nickname
                GMsgCenter.send("updateUserInfo");
            }
        })
        // GNetGameTexas.changename(nickname, (msg) => {
        //     if (msg && !msg.error) {
        //         eventMgr.dispatchEvent(consts.EVENT.REFRESH_UI);
        //     }
        // })
    }

    onClickAuth() {
        GPageMgr.openPage("Page_IDCard")
    }

    onClickBack() {
        GPageMgr.closePage("Page_Person")
    }
}
