import { GAppDM } from "../../DataModel/AppDataModel";
import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_ChangePhone extends Page {

    @property(cc.EditBox)
    editPhone: cc.EditBox = null;

    @property(cc.EditBox)
    editCode: cc.EditBox = null;

    @property(cc.Label)
    btnCode: cc.Label = null;

    onReady() {
        this.editPhone.node.parent.getChildByName("tips").active = false;
    }

    initTable() {
    }

    updateLabel() {
        let remain = GAppDM.last_get_code ? GAppDM.last_get_code + 60000 - Date.now() : 0;
        if (remain < 0) {
            remain = 0;
        }
        remain = Math.floor(remain / 1000);
        this.btnCode.string = "获取验证码" + (remain ? `(${remain})` : "");
    }

    onClickGetCode() {
        let phone = this.editPhone.string;
        if (phone.length != 11) {
            GHelper.showTip("请输入11位手机号");
        }

        // loginCtrl.getVerificationCode(phone, "change_phone", (msg) => {
        //     if (msg && msg.code) {
        //         // this.editCode.string = msg.code;
        //     }
        // })
    }

    onEditPhone() {
        this.editPhone.string = this.editPhone.string.replace(/[^0-9]/ig, "");
        if (this.editPhone.string) {
            // loginCtrl.checkPhoneNumber(this.editPhone.string, (msg) => {
            //     if (msg && !msg.error) {
            //         this.editPhone.node.parent.getChildByName("tips").active = msg.count > 0;
            //     }
            // })
        }
    }

    onEditCode() {
        this.editCode.string = this.editCode.string.replace(/[^0-9]/ig, "");
    }

    onClickChangePhoneNumber() {
        let phone = this.editPhone.string;
        let code = this.editCode.string;

        if (phone.length != 11) {
            GHelper.showTip("请输入11位手机号");
            return;
        }

        if (!code) {
            GHelper.showTip("请输入验证码");
            return;
        }

        // loginCtrl.changePhoneNumber(phone, code, (msg) => {
        //     if (msg && !msg.error) {
        //         GHelper.showTip("更换手机号成功");
        //         this.hide();
        //     }
        // })
    }
    Btn_Close_OnClick(event)
    {
        GPageMgr.closePage("Page_ChangePhone")
    }
}
