import { GAppDM } from "../../DataModel/AppDataModel";
import { core } from "../../Framework/Core/Base";
import { GHelper } from "../../Framework/Logic/Helper";
import tableView from "../../Framework/TableView/tableView";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../Logic/UserManager";
import { GHallHttp } from "../../Net/HallHttp";
import { BagItemItem, EBagItemState, GItemCfg } from "../../Service/ItemService";
import { GMatchSer } from "../../Service/MatchService";
import { GMsgCenter, MessageCenter } from "../../utils/MessageCenter";
import { ykUtils } from "../../utils/YkUtils";
import Widget_NumberFrame from "../Widget/Widget_NumberFrame";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_MyPrizeDetail extends Page {
    @property(tableView)
    sv_content:tableView=null

    private items:BagItemItem[] = null
    private itemsId = null

    onLoad(){
        GMsgCenter.register("update_myprizeDetail", this.doRefresh.bind(this),this.node)
    }

    // 刷新被点击 需要重新加载
    onRefresh(newItems){
        // 通知刷新
        GMsgCenter.send('update_myprize',{tag:'Page_MyPrizeDetail',item_id:this.itemsId})
    }

    onBackClick(){
        GPageMgr.closePage("Page_MyPrizeDetail")
    }

    doRefresh(items:BagItemItem[]){
        this.items = items || []
        this.refreshView()
    }
    onOpen(items:BagItemItem[]){
        this.items = items
        this.refreshView()
        this.itemsId = items[0].item_id
    }

    refreshView(){
        this.sv_content.delegate = this
        this.sv_content.init(this.items)
    }

    protected onDestroy(): void {
        GMsgCenter.unregister("update_myprizeDetail", this.node)
    }
}