 
const BRIDGE_CLASS = "org/cocos2dx/javascript/AppActivity";
const Bridge_android_functionName = "nativeCall";

export default class Bridge_android {
    constructor() 
    {
    }
    tsCallNative(callData:any)
    {
        var sigs = "(Ljava/lang/String;)Ljava/lang/String;"
        var result = jsb.reflection.callStaticMethod(BRIDGE_CLASS, Bridge_android_functionName, sigs,JSON.stringify(callData))
        return result;
    }
    saveImgToSystemGallery(sendData:any) 
    {
        var result = this.tsCallNative(sendData);
        var resultData = JSON.parse(result);
        return resultData;
    }
    crashPush(msg:any)
    {
        var result = this.tsCallNative(msg);
        var resultData = JSON.parse(result);
        return resultData;
    }
    

}
