import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../Logic/UserManager";
import { GMsgCenter, MessageCenter } from "../../utils/MessageCenter";
import Widget_AddressItem from "../Widget/Widget_AddressItem";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_Address extends Page {

    @property(cc.Prefab)
    pf_address: cc.Prefab = null;

    @property(cc.ScrollView)
    sv_content: cc.ScrollView = null;

    protected _wgt_adress:Widget_AddressItem[]=[]



    @property(cc.Button)
    chooiseBt: cc.Button = null;


    onOpen(isChooise)
    {
        this.chooiseBt.node.active = isChooise;
    }
    start() {
        this.refresh()
    }
    refresh()
    {
        this.sv_content.content.removeAllChildren()
        let addresses = GUserMgr.self_user.addresses||[]
        for(let i=0;i<addresses.length;++i)
        {
            let ai = addresses[i]
            let item = cc.instantiate(this.pf_address)
            let wai = item.getComponent(Widget_AddressItem)
            this.sv_content.content.addChild(item)
            this._wgt_adress.push(wai)
            wai.init(ai)
        }
    }
    btn_chooise()
    {
        let addresses = GUserMgr.self_user.addresses||[]
        for(let i=0;i<addresses.length;++i)
        {
            let ai = addresses[i]
             if(ai.create_time==GUserMgr.self_user.address_time)
             {
                GMsgCenter.send("getOneAddress",ai)
                break;
             }
        }

        GPageMgr.closePage("Page_Address")
    }

    Btn_Return_OnClick(event)
    {
        GPageMgr.closePage("Page_Address")
    }
    Btn_Add_OnClick(event)
    {
        GPageMgr.openPage("Page_EditAddress")
    }
}
