export class Pool<T extends Object> {
    private func: {new (): T;};
    private pool: T[];

    constructor(type: {new (): T;}) {
        this.func = type;
        this.pool = [];
    }

    clear() {
        this.pool = [];
    }

    size() {
        return this.pool.length;
    }

    get(): T {
        if (this.pool.length) {
            return this.pool.shift();
        } else {
            return new this.func();
        }
    }

    put(obj: T) {
        if (obj && this.pool.indexOf(obj) < 0) {
            this.pool.push(obj);
        }
    }
}