import { GAppDM } from "../../DataModel/AppDataModel";
import { core } from "../../Framework/Core/Base";
import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../Logic/UserManager";
import { GHallHttp } from "../../Net/HallHttp";
import { BagItemItem, EBagItemState, GItemCfg } from "../../Service/ItemService";
import { GMatchSer } from "../../Service/MatchService";
import { GMsgCenter, MessageCenter } from "../../utils/MessageCenter";
import { ykUtils } from "../../utils/YkUtils";
import Widget_NumberFrame from "../Widget/Widget_NumberFrame";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_Change extends Page {
    @property(cc.RichText)
    lb_wx: cc.RichText = null;

    @property(cc.Node)
    qr: cc.Node = null;

    @property(cc.EditBox)
    editCode: cc.EditBox = null;

    @property(cc.Node)
    btnCode: cc.Node = null;

    @property(cc.Label)
    lb_entity: cc.Label = null;


    @property(cc.Node)
    kefu: cc.Node = null;


    @property(cc.Node)
    commentEdit:cc.Node = null;


    @property(cc.Label)
    commentLab: cc.Label = null;

    @property(cc.Label)
    addressLab: cc.Label = null;

    @property(Widget_NumberFrame)
    numberFrame = null

    commentEidtboxChange()
    {
        if(this.commentEdit.getComponent(cc.EditBox).string=="")
        {
            this.commentLab.string = "无备注"
        }else
        {
            this.commentLab.string = this.commentEdit.getComponent(cc.EditBox).string.slice(0,10)+".."
        }
    }
    checkCommentEidtBt()
    {
        this.commentEdit.active = !this.commentEdit.active;
        this.commentEidtboxChange();

    }
    checkAddressChooiseBt()
    {
        GPageMgr.openPage('Page_Address',true);
    }

    protected _item:BagItemItem=null

    protected onDestroy(): void {
       
        MessageCenter.getInstance().unregister("getOneAddress", this.node);
    }


    onLoad() {
        MessageCenter.getInstance().register("getOneAddress", this.getOneAddress.bind(this), this.node);
        this.commentEdit.active = false;
        this.initKefu();
        this.qr.getComponent('CQRCode').string='str';
        this.lb_wx.string = this.initTemplate("小妹")
    }

    _chooiseAddress:any  = null;

    getOneAddress(mmAddressItem)
    {
        this._chooiseAddress = mmAddressItem;
        cc.error("兑换页面获得一个地址",JSON.stringify(this._chooiseAddress)   )
        this.addressLab.string = this._chooiseAddress.name;

    }

    async initKefu(){
        let sp = await ykUtils.loadNetSpriteFrame(ykUtils.getKefu())
        if(sp){
            this.kefu.getComponent(cc.Sprite).spriteFrame = sp
        }
    }

    initTemplate(str) {
        let text = '<color=#00ff00>微信号</c>' + '<color=#EC9630>' + str + '</c>';
        return text
    }
    
    onClickClose() {
        GPageMgr.closePage("Page_Change")
    }

    onEditCode() {
        this.editCode.string = this.editCode.string.replace(/[^0-9]/ig, "");
    }

    async onGetCode() {
        let remain = GAppDM.last_get_code ? GAppDM.last_get_code + 60000 - Date.now() : 0
        if (remain > 0) {
            return
        }
        let res = await GHallHttp.request("getphonecode")
        if(res.errcode)
        {
            GHelper.showTip(JSON.stringify(res.errcode))
        }
        else
        {
            GHelper.showTip("验证码获取成功，请注意查收")
            GAppDM.last_get_code=Date.now()
        }
    }

    async onClickOk() {
        // let code = this.editCode.string
        // if (code.length <= 0) {
        //     GHelper.showTip("请输入正确的验证码")
        //     return
        // }

        // let count = this.numberFrame.getCurNum()

        if(this._item.count == 1){
            let commentStr = this.commentEdit.getComponent(cc.EditBox).string;
            let addressStr = this._chooiseAddress?JSON.stringify(this._chooiseAddress):"";
            let res = await GHallHttp.request("exchangeitem", {id: this._item.id,comment:commentStr,address:addressStr});
            if(res.errcode)
            {
                GHelper.showTip(JSON.stringify(res.errcode))
            }
            else
            {
                GHelper.showTip("兑换成功，请注意查收")
                GItemCfg.removeById(this._item.id)
    
                GItemCfg.exItems.push(res.exitem)
    
                
                GAppDM.last_get_code=Date.now()
                GMsgCenter.send('update_myprize',{tag:'Page_MyPrizeDetail',item_id:this._item.item_id})
                GPageMgr.closePage("Page_Change")
            }
        }else{
            let num = this.numberFrame.getCurNum()
            let res = await GHallHttp.request("exchangeItemMore", {item_id:this._item.item_id,num:num});

            if(res.errcode)
            {
                GHelper.showTip(JSON.stringify(res.errcode))
                GPageMgr.closePage("Page_Change")
            }
            else
            {
                // 兑换n张 成功n张 请注意查收

                let item_id = this._item.item_id
                GPageMgr.openPage("Page_Tips",{
                    tips:"兑换"+res.allNum+"张,"+"成功"+res.successNum+'张',
                    buttons:[
                        {
                            label:"确定",
                            callback:async ()=>
                            {
                                // 刷新
                                GMsgCenter.send('update_myprize',{tag:'Page_MyPrizeDetail',item_id:item_id})
                            }
                        }
                    ]
                })

                GPageMgr.closePage("Page_Change")
            }
        }
    }

    protected update(dt: number){
        let remain = GAppDM.last_get_code ? GAppDM.last_get_code + 60000 - Date.now() : 0
        if (remain < 0) {
            remain = 0
        }
        remain = Math.floor(remain / 1000)
        this.btnCode.getComponent(cc.Label).string = "获取验证码" + (remain ? `(${remain})` : "")
    }

    onOpen(item:BagItemItem)
    {
        console.log("test")
        this._item=item
        let itemscfg = GItemCfg.getItemById(this._item.item_id)
        if(itemscfg){
            this.lb_entity.string =  itemscfg.name;
        }
        // this.lb_entity.string =  GItemCfg.getItemById(this._item.item_id).name;
        let count = this._item.count
        this.numberFrame.setMax(count)
    }
}
