import { core } from "../../Framework/Core/Base"
import { GHelper } from "../../Framework/Logic/Helper"
import { resMgr } from "../../manager/ResourceManager"

export class FollowerItem
{
    id=0
    nickname=""
    logo=""
    create_time=0
    ticket=0
    isNew = false
}
const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_InviteeItem extends cc.Component {

    @property(cc.Sprite)
    imgAvatar: cc.Sprite = null;

    @property(cc.Label)
    labelNickname: cc.Label = null;

    @property(cc.Label)
    labelTime: cc.Label = null;

    @property(cc.Label)
    labelMoney: cc.Label = null;

    init(follower:FollowerItem) {
        this.labelNickname.string = follower.nickname
        this.labelTime.string = core.toSimpleTime(follower.create_time)
        this.labelMoney.string = follower.ticket+""
        // if(follower.logo)
        // {
        //     GHelper.createSpriteFrame(follower.logo,(sf)=>
        //     {
        //         this.imgAvatar.spriteFrame=sf
        //     })
        // }

        let suffix: string = "image/head/" + follower.logo;
        let path = `${suffix}`;
        resMgr.loadImage(path, (res) => {
            cc.isValid(this.imgAvatar) && (this.imgAvatar.getComponent(cc.Sprite).spriteFrame = res);
            this.imgAvatar.node.setContentSize(90,90)
        })

        if(follower.isNew){
            cc.find('new_title',this.node).active = true
        }else{
            cc.find('new_title',this.node).active = false
        }
      
    }
}
