
import Bridge_android from "./Bridge_android"
import Bridge_ios from "./Bridge_ios"
import Bridge_h5 from "./Bridge_h5"


export enum NATIVE_CALL_TYPE {
    ImgToSystemGallery = 1001,                //保存图片
    CrashPush = 1002,                         // 错误上传bugly
   
}

export default class MultiPlatform {
    private static _instance: MultiPlatform ;
    private _platform:any = null;
    constructor() 
    {
        if (cc.sys.isNative) 
        {
            if (cc.sys.OS_ANDROID == cc.sys.os) 
            {
                this._platform = new Bridge_android();
            }
            else if (cc.sys.OS_IOS == cc.sys.os) 
            {
                this._platform = new Bridge_ios();
            }
        }
        else if (cc.sys.isBrowser) 
        {
            this._platform = new Bridge_h5();
        }
    }


 
    capCamera = null;
    initCamera(_capCamera)
    {
       this.capCamera = _capCamera;
    }
    flipCapImageData(data:any,_width:any,_height:any)
    {
        let mtd_arr = [];
        for(let i=0;i<_width*_height;i++)
        {
            mtd_arr[mtd_arr.length] = 
            [
                data[i*4+0],
                data[i*4+1],
                data[i*4+2],
                data[i*4+3]
            ]
        }
        mtd_arr.reverse();
        let width_array = [];
        for(var i=0;i<_height;i++)
        {
            let smd = [];
             for(var d=0;d<_width;d++)
             {
                smd[smd.length] = mtd_arr[i*_width+d]
             }
             smd.reverse()
             width_array[width_array.length] = smd;
        }
        let jds = new Uint8Array(_width*_height*4);
        let index=  0;
        for(let i=0;i<_height;i++)
        {
            for(let d=0;d<_width;d++)
            {
                for(let m=0;m<4;m++)
                {
                    jds[index] = width_array[i][d][m];
                    index++;
                }
            }
        }
        return jds;
    }
    filpYImage(data, width, height) 
    {
        let picData = new Uint8Array(width * height * 4);
        let rowBytes = width * 4;
        for (let row = 0; row < height; row++) {
            let srow = height - 1 - row;
            let start = srow * width * 4;
            let reStart = row * width * 4;
            // save the piexls data
            for (let i = 0; i < rowBytes; i++) {
                picData[reStart + i] = data[start + i];
            }
        }
        return picData;
    }


    catScreenNodeSave(capNode: cc.Node ,callFunc:any)
    {
        if(!cc.sys.isNative)
        {
            callFunc(false)
            return
        }
        let _texture = new cc.RenderTexture();
        _texture.initWithSize(cc.visibleRect.width, cc.visibleRect.height,cc.Texture2D.PIXEL_FORMAT_RGBA8888);
        this.capCamera.targetTexture = _texture;
        let _width = _texture.width;
        let _height = _texture.height; 
        let self = this;
        setTimeout(() => 
        {
            let size = capNode.getContentSize();
            let pixels = new Uint8Array(size.width * size.height * 4);
            let x = Math.floor(_texture.width / 2 - capNode.width / 2)  ;
            let y = Math.floor(_texture.height / 2 - capNode.height / 2);
            _width = Math.floor(capNode.width);
            _height = Math.floor(capNode.height);
            let data = _texture.readPixels(pixels, x, y, _width, _height);
            let tagData = self.flipCapImageData(data,_width,_height);
            let write_path = jsb.fileUtils.getWritablePath()+"shares_img.png";
            if (jsb.fileUtils.isFileExist(write_path)) 
            {
                jsb.fileUtils.removeFile(write_path);
            }
            if(jsb.saveImageData(tagData,_width,_height,write_path))
            {
                callFunc && callFunc(true,write_path);
            }else
            {
                callFunc && callFunc(false,"");
            }
        }
        ,50);
    }

    // 通过截图保存图片 + 分享微信
    capScreenShareWx(sType: number, capNode: cc.Node ,callFunc:any)
    {
        MultiPlatform.getInstance().catScreenNodeSave(capNode,(isSucces,imgPath)=>
        {
            if(isSucces)
            {
                if(cc.sys.os == cc.sys.OS_ANDROID)
                {  
                   jsb.reflection.callStaticMethod("org/cocos2dx/javascript/AppActivity", "shareScreenShot","(Ljava/lang/String;I)V",imgPath,sType)
                }
                else if (cc.sys.os == cc.sys.OS_IOS) 
                {
                  jsb.reflection.callStaticMethod("AppController", "shareScreenShot:andType:" , imgPath, sType);
                }
            }else
            {
                callFunc && callFunc(false);
            }
        })
    }

    
    /**
     * 通过截图保存图片 + 保存到手机相册
     * @param capCamera 
     * @param capNode 
     * @param callFunc 
     */
    capScreen(capNode,callFunc)
    {
        let self = this;
        MultiPlatform.getInstance().catScreenNodeSave(capNode,(isSucces,imgPath)=>
        {
            if(isSucces)
            {
                callFunc && callFunc(self.saveImgToSystemGallery(imgPath));
                 
            }else
            {
                callFunc && callFunc(false);
            }
        })
    }
    /**
     * 下载远程图片到本地
     */
    getRemoteImgSaveToPhone(remote_url)
    {
        let write_path = jsb.fileUtils.getWritablePath()+"downimg.png";
        const xhr = new XMLHttpRequest();
        xhr.onreadystatechange = ()=>
        {
            if(xhr.readyState===4 && xhr.status===200)
            {
                if(xhr.response && cc.sys.isNative)
                {
                    const u8a = new Uint8Array(xhr.response);
                    jsb.fileUtils.writeDataToFile(u8a,write_path);
                    // cc.error("保存成功 路径:"+write_path);
                    return this.saveImgToSystemGallery(write_path);
                }else
                {
                    return false;
                }
            }else if(xhr.readyState===4 && xhr.status!=200)
            {
                return false;
            }
        };
        xhr.responseType = "arraybuffer";
        xhr.open("GET",remote_url,true);
        xhr.send();
    }
    handleNativeResultData(callData:any)
    {
        let mdData ;
        if(callData instanceof Object)
        {
            mdData = JSON.parse(JSON.stringify(callData));
        }
        else
        {
             mdData = JSON.parse(callData);
        }
        return callData;
    }
    /**
     * 原生回调 ts
     * @param callData   json 格式的 字符串
     */ 
     nativeCallTs(callData:any)
     {
         cc.error("TS-------------------------------------------------------")
        // error("走起=",callData)
        // error("type=",typeof(callData));
        let mdData ;
        if(callData instanceof Object)
        {
            mdData = JSON.parse(JSON.stringify(callData));
        }
        else
        {
             mdData = JSON.parse(callData);
        }
       if(mdData.callId==NATIVE_CALL_TYPE.ImgToSystemGallery) 
        {
             
        }
        else
        {
            cc.error("原生回调js 数据字符串=",callData);
            cc.error("原生回调js callId=",mdData.callId);
        }
     }
     /**
      * ts调用原生
      * @param callData 
      */
     tsCallNative(callData:any,callback: Function | null = null)
     {
         if(this._platform)
         {
             let backData = this._platform.tsCallNative(callData);
             callback && callback(backData);
         }
     }
     crashBind()
     {
        if (cc.sys.isNative) 
        {
            window.__errorHandler = function (file: any, line: any, msg:any ,error: any) 
            {
                cc.error("js 上传结果："+MultiPlatform.getInstance().crashPush(error+"++++++"+msg));
            }
        }
            // if (cc.sys.isBrowser) {
            // let __handler;
            // if (window.onerror) {
            // __handler = window.onerror
            // }
            // window.onerror = function (…args) {
            // console.log(‘游戏报错,浏览器’);
            // handleError(…args);
            // if (__handler) {
            // __handler(…args)
            // }
            // }
            // }
     }
     crashPush(msg)
     {
        if(!cc.sys.isNative)
        {
            return
        }
        let resultData = null;
        if (null != this._platform && null != this._platform.crashPush) 
        {
            let  sendData = 
            {
                callId: NATIVE_CALL_TYPE.CrashPush, 
                callData:
                {
                    name:"js错误上传",
                    errorMsgTag:msg
                }
            }
            resultData = this._platform.crashPush(sendData); 
            if(resultData.callId == NATIVE_CALL_TYPE.CrashPush)
            {
                return resultData.callData.result?true:false;
            }
        }
         return false;
     }
     saveImgToSystemGallery(imgUrl): boolean 
     { 
         let resultData = null;
        if (null != this._platform && null != this._platform.saveImgToSystemGallery) 
        {
            let  sendData = 
            {
                callId: NATIVE_CALL_TYPE.ImgToSystemGallery, 
                callData:
                {
                    name:"图片保存",
                    url:imgUrl
                }
            }
            resultData = this._platform.saveImgToSystemGallery(sendData); 
            if(resultData.callId == NATIVE_CALL_TYPE.ImgToSystemGallery)
            {
                return resultData.callData.result?true:false;
            }

    
        }
         return false;
     }
    setUpdateCall()
    {
        if (null != this._platform) 
        {
            this._platform.setUpdateCall(); 
          
        }
    }

    

    
    public static getInstance(): MultiPlatform {
        if (MultiPlatform._instance == null) {
            MultiPlatform._instance = new MultiPlatform();
        }
        
        window["ToTs"] = MultiPlatform._instance;
        return MultiPlatform._instance;
    }


}