import { GTexasTableDM } from "../../DataModel/TexasTableDataModel";
import { GHelper } from "../../Framework/Logic/Helper";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GMatchSer, MatchPlayer } from "../../Service/MatchService";
import Page_Game from "../Page/Page_Game";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_Time extends cc.Component {
    onLoad()
    {        
        this.onRefresh()
    }
    
    onRefresh()
    {
        this.schedule(this.onUpdate.bind(this),0.5)
        this.onUpdate()
    }
    onUpdate()
    {
        let page = GPageMgr.getPage("Page_Game") as Page_Game
        if(!page)
        {
            return
        }
        let table_id = page.tableId
        let tableinfo = GTexasTableDM.getTableInfo(table_id)
        let matchInsM = GMatchSer.getMatchInsById(tableinfo.match_insid)
        let matchM = matchInsM.match
        let now = new Date();
        let start = new Date(matchInsM.start_time)
        let bm = GMatchSer.getBlindModel(matchM.blind_id)

        let dttime = start.getTime()-GHelper.getServerTime()
        if(dttime<0)
        {
            this.node.active = false
            dttime=0
        }
        else
        {
            this.node.active = true;
            this.node.parent.getChildByName("Widget_MatchInfo").active = false
        }
        dttime=Math.floor(dttime/1000)
        let m = Math.floor(dttime/60)
        let s = dttime%60
        let str=m+":"
        let tm=m+""
        if(m<10)
        {
            tm="0"+tm
            str="0"+str
        }
        if(s>10)
        {
            tm+=s
            str+=s
        }
        else
        {
            tm+="0"+s
            str+="0"+s
        }

        var digits = tm.toString().split('');

        var realDigits = digits.map(Number)

        console.log(realDigits);
        if (realDigits.length>4) {
            this.node.getChildByName("sp_1").getChildByName("lb_1").getComponent(cc.Label).string = realDigits[1].toString()
            this.node.getChildByName("sp_2").getChildByName("lb_2").getComponent(cc.Label).string = realDigits[2].toString()
            this.node.getChildByName("sp_3").getChildByName("lb_3").getComponent(cc.Label).string = realDigits[3].toString()
            this.node.getChildByName("sp_4").getChildByName("lb_4").getComponent(cc.Label).string = realDigits[4].toString()
        }
        else
        {
            this.node.getChildByName("sp_1").getChildByName("lb_1").getComponent(cc.Label).string = realDigits[0].toString()
            this.node.getChildByName("sp_2").getChildByName("lb_2").getComponent(cc.Label).string = realDigits[1].toString()
            this.node.getChildByName("sp_3").getChildByName("lb_3").getComponent(cc.Label).string = realDigits[2].toString()
            this.node.getChildByName("sp_4").getChildByName("lb_4").getComponent(cc.Label).string = realDigits[3].toString()
        }
    }
}
