import { core } from "../../Framework/Core/Base";
import { GNetGameTexas } from "../../Net/NetGameTexas";
import { ExtUserMatchInfo } from "../../Service/MatchService";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_MatchListItemIng extends cc.Component {

    @property(cc.Sprite)
    sp_icon: cc.Sprite = null

    @property(cc.Label)
    lbl_name: cc.Label = null

    @property(cc.Label)
    lbl_signup: cc.Label = null

    @property(cc.Label)
    lbl_time: cc.Label = null

    protected _usermatchinfo: ExtUserMatchInfo = null

    init(eumi: ExtUserMatchInfo) {
        this._usermatchinfo=eumi
        this.lbl_name.string = this._usermatchinfo.match.name
        this.lbl_time.string = core.toSimpleTime(this._usermatchinfo.match.start_time)
    }

    Btn_Self_Click() {
        if(!this._usermatchinfo.match)
        {
            return
        }
        GNetGameTexas.send_texas_enter_match(this._usermatchinfo.match.id,this._usermatchinfo.ins_id)
    }
}
