import { GUserMgr } from "../../../Logic/UserManager";
import { GHallHttp } from "../../../Net/HallHttp";
import { GMatchSer } from "../../../Service/MatchService";
import { GOrderSer } from "../../../Service/OrderService";
import { GMsgCenter } from "../../../utils/MessageCenter";
import { GHelper } from "../../Logic/Helper";
//支付宝支付接口
class Alipay {
    async alipayEvent(produceid) {
        var self = this;
        console.log("-------------------");
        //let order = await GHallHttp.request("getorder", { product_id: produceid }, "order")

        let orderstr = await GOrderSer.getOrderInfo(produceid)
        console.log("<<<<<<<<<<<<<<<<<<<<<<<<<<<<<");
        if (cc.sys.os === cc.sys.OS_ANDROID) {
            //如果响应部位空的话
            console.log("response is " + orderstr)
            var payResult = jsb.reflection.callStaticMethod("org/cocos2dx/javascript/AppActivity", "payV2", "(Ljava/lang/String;)Ljava/lang/String;", orderstr);
            console.log("payResult is " + payResult);
            //当返回的支付结果不是空字符串的时候
            if (payResult === "") {
                //每0.5秒请求一次
                let handler = setInterval(()=> {
                    var isPay = jsb.reflection.callStaticMethod("org/cocos2dx/javascript/AppActivity", "isPaySuccess", "()Z");
                    if (isPay) {
                        console.log("is pay is " + isPay);
                        //如果支付状态为true的话获得支付信息
                        payResult = jsb.reflection.callStaticMethod("org/cocos2dx/javascript/AppActivity", "getPayResult", "()Ljava/lang/String;");
                        console.log("payResult is " + payResult);
                        //打印payResult
                        console.log("---------->>>>>>>>>>>>>>>payResult is " + payResult);
                        //将支付宝返回的数据传给服务器
                        self.payResultFun(payResult);
                        clearInterval(handler)
                    }
                }, 500)
            }
            else {
                //如果刚开始请求的时候是成功的话将支付信息返回给服务器端
                self.payResultFun(payResult);
            }
        }
        else if (cc.sys.os == cc.sys.OS_IOS)
        {
            //console.log("该平台暂时不支持支付功能");
            jsb.reflection.callStaticMethod("AppController", "CallAlipay:", orderstr.toString());
        }
    }
    //向服务器发送支付结果
    async payResultFun(payResult) {
        console.log("<<<<<<<<<<<<<<<<<<<<< payResult is " + payResult);
        console.log("type of payResult is " + typeof (payResult));
        // let rs = await GHallHttp.request("alipaycomplete",{result:payResult},"order")
        // if (rs.errcode&&rs.errcode.id!=20012) {
        //     GHelper.showTip(JSON.stringify(rs.errcode));
        // }
        GHallHttp.request("getuser").then((rs)=>
        {
            GUserMgr.addOneUser(rs.user);
            GMsgCenter.send("updateuserinfo");
            GMsgCenter.send("coinchanged")
        })
    }
}
export let GAlipay = new Alipay()
window["payResultFun"] = (obj)=> {
    GAlipay.payResultFun(obj);
}