import { GAlipay } from "../../Framework/View/Page/Alipay";
import { GOrderSer, ProductItem } from "../../Service/OrderService";
import { ykUtils } from "../../utils/YkUtils";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_NumberFrame extends cc.Component {
    @property(cc.Label)
    lbNum: cc.Label = null
    @property(cc.EditBox)
    editbox = null
    
    private maxNum = 1
    private cb:any = null
    onLoad(){
        // 初始化
        this.setMax(1)
        this.lbNum.string = '1'

        this.editbox.node.on('editing-did-ended',()=>{
            console.log(this.editbox.string)
            let edSts = this.editbox.string

            if(edSts.indexOf('.')!=-1){
                edSts = edSts.slice(0,edSts.indexOf('.'))
                this.editbox.string = edSts
            } 
            if(parseInt(edSts)<=1){
                this.editbox.string = 1
            }else if(parseInt(edSts)>this.maxNum){
                this.editbox.string = this.maxNum
            }
            
            this.cb&&this.cb()
        })
    }


    setMax(maxNum){
        this.maxNum = maxNum
        this.lbNum.string = maxNum.toString() 
    }

    // 添加
    onAdd(){
        let st =  parseInt(this.editbox.string)
        if(st+1<=this.maxNum){
            this.editbox.string = (st+1).toString()
            this.cb&&this.cb()
        }
    }
    //减小
    onSub(){
        let st =  parseInt(this.editbox.string)
        if(st-1>0){
            this.editbox.string = (st-1).toString()
            this.cb&&this.cb()
        }
    }

    getCurNum(){
        if(ykUtils.isEmpty(this.editbox.string)){
            return 1
        }else{
            return parseInt(this.editbox.string)
        }
    }

    setOnEditEndCb(cb){
        this.cb = cb
    }
}
