import { GGameDM } from './../../DataModel/GameDataModel';
import {core} from "../../Core/Base"
import {IProtoFilter} from "./IProtoFilter"
//import * as protobufjs from "./protobuf"

export class GoogleProtoFilter extends IProtoFilter
{
    _proto=null
    init(callback:Function)
    {
        let _init=()=>
        {
            let path = "Proto/hallmsg"
            // protobuf.load(path, (err, data)=>{
            //     this._proto = data
            //     if(callback) 
            //     {
            //         callback(!err)
            //     }
            // })
        }
        if(cc.sys.isBrowser)
        {
            _init()
            return
        }
        _init()
    }
    encode(data)
    { 
        let cmd = data.cmd
        data.cmd = undefined
        //消息公共头
        let ClientMsgHead = this._proto.lookupType("ClientMsgHead")
        let clientHead_content = {
            msgname : cmd+"Req",
            svr_id  : "",
            service_address : 0,
            msgtype : 1,
        }

        let message = ClientMsgHead.create(clientHead_content)
        let clientHead = ClientMsgHead.encode(message).finish()

        let msg_class = this._proto.lookupType(cmd+"Req")
        message = msg_class.create(data)
        let body = msg_class.encode(message).finish();

        //拼接消息
        let headLen = clientHead.length
        let c = core.convertIntToUInt8Array(headLen)
        clientHead = core.arraybuffer2array(clientHead.buffer)
        body = core.arraybuffer2array(body.buffer)
        message = c.concat(clientHead, body)
        message = core.array2arraybuffer(message)
        return message
    }
    decode(data,callback) 
    {
        core.blobToArrayBuffer(data,(d)=>
        {
            let dd = this._decode(d)
            if(dd.errcode == undefined) 
            {
                // 做个默认处理
                dd.errcode = 0
            }

            if(callback) 
            {
                callback(dd)
            }
        })
    }

    _decode(arrayBuffer) 
    {
        var data = core.arraybuffer2array(arrayBuffer)
        var len = data.length
        if(len<2) {
            cc.error("proto is too short!")
            return null
        }

        var headLenArray = core.getArrayFromArray(data,0,2)
        var headLen = core.convertUInt8ArrayToInt(headLenArray)
        if(headLen<=0) 
        {
            cc.error("proto head len is too short!")
            return 
        }

        var ClientMsgHead = this._proto.lookupType("ClientMsgHead");
        var clientHeadArray = core.getArrayFromArray(data,2,headLen)
        var clientHead = ClientMsgHead.decode(clientHeadArray)
        if(!clientHead) {
            cc.error("proto head decode wrong!")
            return null
        }

        var name = clientHead.msgname
        cc.log("clientHead.msgname",clientHead.msgname)
        if(!name) {
            cc.error("proto head decode null name!")
            return null
        }

        var builder = this._proto.lookupType(name);
        if(!builder) {
            cc.error("proto head name not in proto(!"+name+")")
            return null
        }

        var bodyArray = core.getArrayFromArray(data,2+headLen)
        var body = builder.decode(bodyArray)//builder.builder.deserializeBinary(bodyArray)
        if(!body) {
            cc.error("proto body decode wrong!")
            return null
        }

        var suffix = name.substring(name.length-3)
        var name = name.substring(0,name.length-3)
        if(suffix=="Ntc") {
            name = name + "_" + suffix.toLowerCase()
        }
        body.builder = builder.builder_name
        body.cmd = name
        return body
    }
}