import { GUserMgr } from "../../Logic/UserManager";
import { GNativeTool } from "../Logic/NativeTool";
import { GQiNiuTool } from "../Logic/QiNiuTool";

const {ccclass, property} = cc._decorator;

export let GTipLabel:TipLabel=null

@ccclass
export class TipLabel extends cc.Component
{
    Btn_Voice_OnStart()
    {
        let time = new Date().getTime()
        GNativeTool.beginVoiceRecord((path)=>
        {
            if(!path)
            {
                //失败之类的
                return
            }
            let dt = new Date().getTime() - time
            if(dt<1500)//小于1.5秒退出
            {
                //时间太短放弃发送
                return
            }
            GQiNiuTool.uploadFile("voice_"+GUserMgr.self_user_id+".wav",path, (status,url)=>
            {
                if (status == 1)
                {
                    let chat =
                    {
                        //type:EChatType.Voice,
                        content:url
                    }
                    //GNetGameOX.send_ox_chat(chat)
                }
                else 
                {
                    // 失败
                }
            })
        })
    }
    Btn_Voice_OnCancel()
    {
        GNativeTool.cancelRecord()
    }
    Btn_Voice_OnEnd()
    {
        GNativeTool.endVoiceRecord()
    }
}