import {core} from "../Core/Base"
import {GHelper} from "../Logic/Helper"

export class RollingNode extends cc.Layout
{
    private _temp_items:any[]
    private _root_layout:cc.Layout
    private _total_width:number=0
    public width:number=0
    static create(width)
    {
        let rn = <RollingNode>GHelper.createNode(RollingNode)
        rn.width = width
        return rn
    }
    addText(str)
    {
        //传进来的本来就是Label的节点了，就直接那啥了
        if(core.isObject(str))
        {
            this.addNode(str)
            return
        }
        let lbl = <cc.Label>GHelper.createNode(cc.Label)
        lbl.string = str || ""
        this.addNode(lbl)
    }
    addSprite(path)
    {
        let self = this
        //传进来的本来就是sprite的节点了，就直接那啥了
        if(core.isObject(path))
        {
            this.addNode(path)
            return
        }
        GHelper.createSprite(path,function(sp)
        {
            if(sp)
            {
                self.addNode(sp)
            }
        })
    }
    addNode(node_comp)
    {
        if( this._isOnLoadCalled )
        {
            node_comp.node.anchorX = 0
            node_comp.node.parent = this._root_layout.node
            this._total_width+=node_comp.node.width
            this._check_move()
        }
        else
        {
            this._temp_items.push(node_comp)
        }
    }
    _check_move()
    {
        let width = this.width
        let dt = this._total_width - width
        if(dt<=0)//无需移动
        {
            return
        }
        let action = cc.sequence(
            cc.moveTo(3,cc.v2(-dt,0)),
            cc.delayTime(1),
            cc.moveTo(3,cc.v2(0,0)),
            cc.delayTime(1),
        )
        action = cc.repeatForever(action)
        this._root_layout.node.stopAllActions()
        this._root_layout.node.runAction(action)
    }
    // use this for initialization
    onLoad () 
    {
        this.node.anchorX = 0
        let self = this
        this._root_layout = <cc.Layout>GHelper.createNode(cc.Layout)
        this._root_layout.node.anchorX = 0
        this._root_layout.node.parent = this.node
        core.foreach(this._temp_items,function(index,node_comp)
        {
            node_comp.node.anchorX = 0
            node_comp.node.parent = self._root_layout.node
            self._total_width+=node_comp.node.width
        })

        let mask = this.addComponent(cc.Mask)
        this._check_move()
    }
}