import {core} from "../../Core/Base"
import { GHelper } from "../../Logic/Helper";

const {ccclass, property} = cc._decorator

export let GPageMgr:PageManager=null

@ccclass
export class PageManager extends cc.Component
{
    protected _key_path={}
    protected _pages:{[page_name:string]:cc.Component}={}

    private maxZindex = 1000


    // use this for initialization
    onLoad() 
    {
        GPageMgr = this
        this.registerKeyPath("Page","Platform/Viea/Page/Layout/")
    }
    cachePage(pageName)
    {
        let prefix = pageName.substring(0,pageName.indexOf("_"))
        cc.loader.loadRes(this._key_path[prefix]+pageName, (err, prefab) =>{})
    }
    registerKeyPath(key,path)
    {
        this._key_path[key] = path
    }
    getPage(pageName)
    {
        return this._pages[pageName]
    }
    isPageOpened(pageName)
    {
        let page = this.getPage(pageName)
        if(page)
        {
            return true
        }
        return false
    }
    protected _reopening=false
    reOpenPage(pg,param?)
    {
        if(this._reopening)
        {
            return
        }
        this._reopening=true
        let pageName=pg
        if(!core.isString(pg))
        {
            pageName=pg.name
        }
        let page = this._pages[pageName]
        delete this._pages[pageName]
        this.openPage(pg,param).then((newone)=>
        {
            if(newone&&page)
            {
                page.node.removeFromParent()
            }
            this._reopening=false
        })
    }
    openPageHide(pg,param?):Promise<cc.Component|null>
    {
        return new Promise((resolve,reject)=>
        {
            let pageName=pg
            if(!core.isString(pg))
            {
                pageName=pg.name
            }
            let page = this.getPage(pageName) 
            if(page)
            {
                if(page.node.active)
                {
                    page.node.active = false
                }
                resolve(page)
                return
            }
            let prefix = pageName.substring(0,pageName.indexOf("_Page"))
            if(!prefix)
            {
                prefix = "Page"
            }
            if(!this._key_path[prefix])
            {
                this._key_path[prefix] = this._key_path["default"]+prefix+"/View/Page/Layout/"
            }
            let page_path = this._key_path[prefix]+pageName
            cc.loader.loadRes(page_path, (err, prefab) =>
            {
                if(!err)
                {
                    if(this._pages[pageName])
                    {
                        return
                    }
                    let page_node:cc.Node = cc.instantiate(prefab)
                    page_node.parent=this.node
                    let dty_ipad = (1-GPageMgr.node.scale)*1334/2
                    if(pageName!="Page_Game")
                    {
                        page_node.y-=dty_ipad
                    }
                    this._pages[pageName] = page_node.getComponent(pageName)
                    if (typeof(this._pages[pageName]["onOpen"]) != "undefined")
                    {
                        this._pages[pageName]["onOpen"](param)
                    }
                    let rs = GHelper.getAnimationClip(page_node,"open")
                    if(rs.clip)
                    {
                        rs.ani.play("open")
                    }
                    cc.log("open page success + "+pageName)
                    resolve(this._pages[pageName])
                }
                else
                {
                    cc.error(err)
                    resolve(null)
                }
            })
        })
    }





    openPage(pg,param?):Promise<cc.Component|null>
    {
        return new Promise((resolve,reject)=>
        {
            let pageName=pg
            if(!core.isString(pg))
            {
                pageName=pg.name
            }
            let page = this.getPage(pageName) 
            if(page)
            {
                if(!page.node.active)
                {
                    page.node.active = true
                }
                resolve(page)
                return
            }
            let prefix = pageName.substring(0,pageName.indexOf("_Page"))
            if(!prefix)
            {
                prefix = "Page"
            }
            if(!this._key_path[prefix])
            {
                this._key_path[prefix] = this._key_path["default"]+prefix+"/View/Page/Layout/"
            }
            let page_path = this._key_path[prefix]+pageName
            cc.loader.loadRes(page_path, (err, prefab) =>
            {
                if(!err)
                {
                    if(this._pages[pageName])
                    {
                        return
                    }
                    let page_node:cc.Node = cc.instantiate(prefab)
                    page_node.parent=this.node

                
                    let dty_ipad = (1-GPageMgr.node.scale)*1334/2
                    if(pageName!="Page_Game")
                    {
                        page_node.y-=dty_ipad
                    }
                    this._pages[pageName] = page_node.getComponent(pageName)
                    if (typeof(this._pages[pageName]["onOpen"]) != "undefined")
                    {
                        this._pages[pageName]["onOpen"](param)
                    }
                    let rs = GHelper.getAnimationClip(page_node,"open")
                    if(rs.clip)
                    {
                        rs.ani.play("open")
                    }
                    cc.log("open page success + "+pageName+page_node.zIndex)
                    resolve(this._pages[pageName])
                }
                else
                {
                    cc.error(err)
                    resolve(null)
                }
            })
        })
    }
    hidePage(pg)
    {
        let pageName=pg
        if(!core.isString(pg))
        {
            pageName=pg.name
        }
        let page = this.getPage(pageName) 
        if(page)
        {
            page.node.active = false
        }
    }
    closePage(pageName)
    {
        let page = this._pages[pageName]
        this._pages[pageName] = null
        delete this._pages[pageName]
        if(!page
            ||!page.node)
        {
            return
        }
        if(page["onClose"])
        {
            page["onClose"]()
        }
        let rs = GHelper.getAnimationClip(page.node,"close")
        if(rs.clip)
        {
            rs.ani.play("close")
            page["onCloseCompleted"]=()=>
            {
                page.node.destroy()
            }
        }
        else
        {
            page.node.destroy()
        }
    }
    refreshPage(pageName,params?)
    {
        let page = this.getPage(pageName)
        if(page&&page["refresh"])
        {
            page["refresh"](params)
        }
    }
    closeAll()
    {
        core.foreach(this._pages,function(pageName,page)
        {
            if(!page)
            {
                return
            }
            page["onClose"]&& page["onClose"]()
            page.node.destroy()
        })
        this._pages = {}
    }
}
