import {GLocalizationDataModel} from "./LocalizationDataModel"
import {GHelper} from "../Logic/Helper"

export enum ENetState
{
    None=-1,//游戏开始后还没有开始链接任何网络，否则只能是下面的其中之一
    Connectting,
    ReConnectting,
    Ready,
    Closed,
}
export let GNetSer:NetService = null
class NetService
{
    protected _ping : number = 0            //默认ping值
    protected _tip_dt : number = 10         //ping值提示间隔
    protected _tip_last_time : number = 0   //上次提示时间
    protected _tip_num : number = 0         //提示次数

    protected _dt_server_time : number = 0  //和服务器得时间差
    set dt_server_time(time)
    {
        this._dt_server_time = time
    }
    get dt_server_time()
    {
        return this._dt_server_time
    }

    protected _ping_cb:Function=null
    protected _tip:string =""

    protected _curCfg = {host:"",port:-1,state:ENetState.None}
    get curCfg()
    {
        return this._curCfg
    }
    set ping(value)
    {
        this._ping = value
        if(this._ping_cb)
        {
            this._ping_cb(this.ping)
        }
        let time = Date.now()
        if( this._ping>=500 && time-this._tip_last_time>=this._tip_dt && this._tip_num<5)//大于500延迟，间隔10秒，连续提示5次
        {
            this._tip_last_time = time
            this._tip_num = this._tip_num + 1
            GHelper.showTip("Net_Tips",3)
        }
        else if(this._ping>=500 && this._tip_num>=5 && time-this._tip_last_time>2*60*60)//两个小时以后重置提醒
        {
            this._tip_dt = 10
            this._tip_last_time = 0
            this._tip_num = 0
            this.ping = this._ping
        }
    }
    set pingCallBack(cb)
    {
        this._ping_cb = cb
    }
    showPing()//显示ping值
    {

    }
    tip(tip)
    {
        this._tip = tip || ""
    }
    //获取网络状态的提示字符串
    get stateTip()
    {
        //当前没有网络状态,仅仅当玩家进入游戏还没有开始连接任何网络的情况下才能出现这种情况
        if(this._curCfg.host=="" || this._curCfg.state==ENetState.None)
        {
            return
        }
        let str = ""
        if(this._curCfg.state==ENetState.Connectting)
        {
            str += GLocalizationDataModel.getStringByKey("ConServerIs")
        }
        else if(this._curCfg.state==ENetState.Ready)
        {
            str += GLocalizationDataModel.getStringByKey("NetIsInReadySerIs")
        }
        else if(this._curCfg.state==ENetState.Closed)
        {
            str += GLocalizationDataModel.getStringByKey("NetHasClosedSerIs")
        }
        str += this._curCfg.host+" "+this._curCfg.port
        return str
    }
}

GNetSer = new NetService()