import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { ClubModel ,GClubSer} from "../../Service/ClubService";
import { GMsgCenter } from "../../utils/MessageCenter";
import { GUserMgr } from "../../Logic/UserManager"
import ListView from "../../utils/ListView";

const {ccclass, property} = cc._decorator;



@ccclass
export default class Page_AddClub extends Page {
    @property(cc.Node)
    nodeJoin: cc.Node = null;

    @property(cc.Node)
    nodeJoined: cc.Node = null;

    @property(cc.Label)
    memberNumber: cc.Label = null;

    private clubData:ClubModel = null

    start() {

    }

    onLoad() {
       
    }

    onOpen(data:ClubModel){
        this.clubData = data
        for(var k in GUserMgr.self_user.clubs){
            console.log(k,this.clubData.id.toString())
            if(k == this.clubData.id.toString()){
                this.nodeJoined.active = true
                this.nodeJoin.active = false
                break
            }
        }
        this.refreshIsJoined()

        cc.find('Page_WebView',this.node).getComponent('Page').onOpen(data.url)
    }

    refreshIsJoined(){
        this.nodeJoined.active = false
        this.nodeJoin.active = true
        for(var k in GUserMgr.self_user.clubs){
            if(k == this.clubData.id.toString()){
                this.nodeJoined.active = true
                this.nodeJoin.active = false
                break
            }
        }

        this.memberNumber.string = this.clubData.user_count.toString()
    }


    async onJoninClubClick(){
        if(!this.clubData){
            return
        }

        let res = await GClubSer.joinClub(this.clubData.id)
        if(res.errcode){
            this.refreshIsJoined()
        }else{
           GUserMgr.self_user.clubs[this.clubData.id.toString()] = this.clubData.service
           this.clubData.user_count+=1
           this.refreshIsJoined()
        }
    }

    async onQuitClub(){
        if(!this.clubData){
            return
        }
        let res = await GClubSer.quitClub(this.clubData.id)
        if(res.errcode){
            this.refreshIsJoined()
        }else{
            delete(GUserMgr.self_user.clubs[this.clubData.id.toString()])
            // = this.clubData.service
            this.clubData.user_count-=1
            this.refreshIsJoined()
        }
    }
    protected onDestroy(): void {
    
    }
    onClose(){
        this.delegate.nodeClub.getComponent(ListView).notifyDataRefresh()
    }
    doClose(){
        GPageMgr.closePage("Page_AddClub")
    }
}
