import { GSimpleLogDataModel } from './../DataModel/SimpleLogDataModel';
export let GShare:Share = null
class Share
{
    _agent=null
    _share_plugin=null
    _callback=null
    init()
    {
        if(typeof anysdk == "undefined")
        {
            GSimpleLogDataModel.log("share:no anysdk")
            return
        }
        this._agent = anysdk.agentManager
        if(!this._agent)
        {
            GSimpleLogDataModel.log("share:no _agent")
            return
        }
        this._share_plugin = this._agent.getSharePlugin()
        if(!this._share_plugin)
        {
            GSimpleLogDataModel.log("share:no _share_plugin")
            return
        }
        this._share_plugin.setListener(this.onShareCallBack,this)
    }
    onShareCallBack(code, msg)
    {
        if(this._callback)
        {
            this._callback(code,msg)
        }
        GSimpleLogDataModel.log("share:share result, resultcode:"+code+", msg: "+msg)
        switch ( code ) 
        {
            case anysdk.ShareResultCode.kShareSuccess:
                break
            case anysdk.ShareResultCode.kShareFail:
                //do something
                break
            case anysdk.ShareResultCode.kShareCancel:
                //do something
                break
            case anysdk.ShareResultCode.kShareNetworkError:
                //do something
                break
        }
    }
    share(info,callback?)
    {
        this._callback = callback
        if(!this._share_plugin)
        {
            return
        }
        this._share_plugin.share(info)
    }
}

GShare = new Share()