let BRIDGE_CLASS = "NativeOcClass"
const Bridge_ios_functionName = "nativeCall:";
export default class Bridge_ios {

    constructor() 
    {
        
    }
    tsCallNative(callData:any)
    {
        var result = jsb.reflection.callStaticMethod(BRIDGE_CLASS, Bridge_ios_functionName,JSON.stringify(callData))
        return result;
    }
    saveImgToSystemGallery(sendData:any) 
    {
        var result = this.tsCallNative(sendData);
        var resultData = JSON.parse(result);
        return resultData;
    }
    crashPush(sendData:any) 
    {
        var result = this.tsCallNative(sendData);
        var resultData = JSON.parse(result);
        return resultData;
    }

  
}