const {ccclass, property} = cc._decorator;
import {cityDatas} from '../../Config/zdpkDatas'
import { GMsgCenter } from "../../utils/MessageCenter";


@ccclass
export default class Widget_ClubShaiXuanItem extends cc.Component {
    @property([cc.Prefab])
    pfb: cc.Prefab = null;

    private lastChoiceNode:cc.Node = null
    private lastChoiceCity:cc.Node = null
    onLoad()
    {
        this.initData()
    }

    initData(){
        for(var k in cityDatas){
            let node =  cc.instantiate(this.pfb);
            console.log(node.getContentSize())
            node.parent = this.getComponent(cc.ScrollView).content
            node.getComponent(this.pfb.name).setDelegate(this)
            node.getComponent(this.pfb.name).setData(k,cityDatas[k])
            node.getComponent(this.pfb.name)['setIsOpen'](false)
            node['_SCname'] = this.pfb.name
            node.x = 0
        }
    }


    onDirectChoice(node){
        if(this.lastChoiceNode == node){
            return
        }
        this.lastChoiceNode = node
        var allChildren =  this.getComponent(cc.ScrollView).content.children
        allChildren.forEach((nn)=>{
            if(node == nn){
                nn.getComponent(nn['_SCname'])['setIsOpen'](true)
            }else{
                nn.getComponent(nn['_SCname'])['setIsOpen'](false)
            }
        })

    }

    onCityChoice(node,city){
        if(this.lastChoiceCity == city){
            return
        }
        this.lastChoiceCity = city
        // 请求网络 进行查找
        
        GMsgCenter.send('filtrate_club_search',city)
    }
    
}