import { GAppDM } from "../../DataModel/AppDataModel";
import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GHallHttp } from "../../Net/HallHttp";
import { BagItemItem, GItemCfg } from "../../Service/ItemService";
import { GMsgCenter } from "../../utils/MessageCenter";
import Widget_NumberFrame from "../Widget/Widget_NumberFrame";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_Donate extends Page {
    @property(cc.EditBox)
    dfname: cc.EditBox = null;

    @property(cc.EditBox)
    dfid: cc.EditBox = null;

    @property(cc.EditBox)
    editCode: cc.EditBox = null;

    @property(cc.Node)
    btnCode: cc.Node = null;

    @property(cc.Label)
    lb_entity: cc.Label = null;

    @property(Widget_NumberFrame)
    numberFrame = null

    protected _item:BagItemItem=null

    onClickCancel() {
        GPageMgr.closePage("Page_Donate")
    }

    onEditCode() {
        this.editCode.string = this.editCode.string.replace(/[^0-9]/ig, "");
    }

    async onGetCode() {
        let remain = GAppDM.last_get_code ? GAppDM.last_get_code + 60000 - Date.now() : 0
        if (remain > 0) {
            return
        }
        let res = await GHallHttp.request("getphonecode")
        if(res.errcode)
        {
            GHelper.showTip(JSON.stringify(res.errcode))
        }
        else
        {
            GHelper.showTip("验证码获取成功，请注意查收")
            GAppDM.last_get_code=Date.now()
        }
    }

    async onClickOk() {
        let name = this.dfname.string
        if (name.length == 0) {
            GHelper.showTip("请输入姓名")
            return
        }

        let id = this.dfid.string
        if (id.length == 0) {
            GHelper.showTip("请输入ID")
            return
        }

        // let code = this.editCode.string
        // if (code.length == 0) {
        //     GHelper.showTip("请输入正确的验证码")
        //     return
        // }

        if(this._item.count == 1){
            let res = await GHallHttp.request("giveitem", {id: this._item.id, user_id: id, nickname: name});
            if(res.errcode)
            {
                GHelper.showTip(JSON.stringify(res.errcode))
            }
            else
            {
                GHelper.showTip("转赠成功，请注意查收")
    
                GItemCfg.removeById(id)
                GItemCfg.exItems.push(res.giveitem)
    
                GMsgCenter.send('update_myprize',{tag:'Page_MyPrizeDetail',item_id:this._item.item_id})
                GAppDM.last_get_code=Date.now()
                GPageMgr.closePage("Page_Donate")
            }
        }else{
            let num = this.numberFrame.getCurNum()

            let res = await GHallHttp.request("giveItemMore", {item_id:this._item.item_id,num:num,user_id:id, nickname: name});
            if(res.errcode)
            {
                GHelper.showTip(JSON.stringify(res.errcode))
            }
            else
            {
                let item_id = this._item.item_id
                GPageMgr.openPage("Page_Tips",{
                    tips:"转赠"+res.allNum+"张,"+"成功"+res.successNum+'张',
                    buttons:[
                        {
                            label:"确定",
                            callback:async ()=>
                            {
                                // 刷新
                                GMsgCenter.send('update_myprize',{tag:'Page_MyPrizeDetail',item_id:item_id})
                            }
                        }
                    ]
                })

                GPageMgr.closePage("Page_Donate")
            }
        }
       
    }

    protected update(dt: number){
        let remain = GAppDM.last_get_code ? GAppDM.last_get_code + 60000 - Date.now() : 0
        if (remain < 0) {
            remain = 0
        }
        remain = Math.floor(remain / 1000)
        this.btnCode.getComponent(cc.Label).string = "获取验证码" + (remain ? `(${remain})` : "")
    }

    onOpen(item:BagItemItem)
    {
        console.log("test")
        this._item=item
        let itemscfg = GItemCfg.getItemById(this._item.item_id)
        if(itemscfg){
            this.lb_entity.string =  itemscfg.name;
        }
      
        // this.lbl_itemcount.string = this._count+""
        // let itemM = GItemCfg.getItemById(this._item.item_id)
        // this.lbl_itemname.string = itemM.name
        // GHelper.createSpriteFrame(itemM.icon,(sf)=>
        // {
        //     if(cc.isValid(this.sp_icon))
        //     {
        //         this.sp_icon.spriteFrame=sf
        //     }
        // })
        this.numberFrame.setMax(this._item.count)
    }
}
