const { ccclass, property } = cc._decorator;

@ccclass
export default class LoginScene extends cc.Component {

    @property(cc.Node)
    loginLayer: cc.Node = null;

    @property(cc.Prefab)
    prefabRegister: cc.Prefab = null;

    @property(cc.Prefab)
    prefabAccountLogin: cc.Prefab = null;

    @property(cc.Prefab)
    prefabCodeLogin: cc.Prefab = null;

    @property(cc.Prefab)
    prefabResetPassword: cc.Prefab = null;

    private registerLayer: cc.Node = null;
    private accountLoginLayer: cc.Node = null;
    private codeLoginLayer: cc.Node = null;
    private resetPasswordLayer: cc.Node = null;

    onReady() {
        this.registerLayer = cc.instantiate(this.prefabRegister);
        this.node.addChild(this.registerLayer);
        this.registerLayer.active = false;
        
        this.accountLoginLayer = cc.instantiate(this.prefabAccountLogin);
        this.node.addChild(this.accountLoginLayer);
        this.accountLoginLayer.active = false;
        
        this.codeLoginLayer = cc.instantiate(this.prefabCodeLogin);
        this.node.addChild(this.codeLoginLayer);
        this.codeLoginLayer.active = true;
        
        this.resetPasswordLayer = cc.instantiate(this.prefabResetPassword);
        this.node.addChild(this.resetPasswordLayer);
        this.resetPasswordLayer.active = false;
    }
    
    onShow() {

    }

    onHide() {
    }

    onShowRegisterLayer() {
        this.registerLayer.active = true;
    }

    onShowAccountLoginLayer() {
        this.accountLoginLayer.active = true;
    }

    onShowCodeLoginLayer() {
        this.codeLoginLayer.active = true;
    }

    onShowResetPasswordLayer() {
        this.resetPasswordLayer.active = true;
    }
}
