import { EMatchState } from "../../Config/_ini_";
import { GHelper } from "../../Framework/Logic/Helper";
import { GMatchSer, MatchInsModel } from "../../Service/MatchService";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_TableMatchTip extends cc.Component {
    @property(cc.Label)
    lbl_main: cc.Label = null
    protected _matchIns:MatchInsModel=null
    onLoad()
    {
        this.node.active=false
    }
    init(ins_id:string)
    {
        this._matchIns = GMatchSer.getMatchInsById(ins_id)
        if((!this._matchIns)
            ||this._matchIns.state!=EMatchState.Waiting)
        {
            this.node.active=false
            this.unscheduleAllCallbacks()
            return false
        }

        let serverTime = GHelper.getServerTime()
        if(this._matchIns.start_time>serverTime) // 比赛已经不在 等待状态 但是比赛时间未到 显示开赛倒计时
        {
            this.node.active=true
            this.unscheduleAllCallbacks()
            this._refresh()
            this.schedule(this._refresh.bind(this),0.5)
            return true
        }
        else
        {
            this.node.active=false
            this.unscheduleAllCallbacks()

            return false
        }
    }
    protected _refresh()
    {
        let serverTime = GHelper.getServerTime()
        let dt = Math.floor((this._matchIns.start_time-serverTime)/1000)
        if(dt<0)
        {
            dt=0
        }
        let m = Math.floor(dt/60)
        if(m>=60)
        {
            if(this.lbl_main.string=="即将开赛")
            {
                return
            }
            this.lbl_main.string="即将开赛"
            this.lbl_main.fontSize=90
            return
        }
        if(this.lbl_main.string=="即将开赛")
        {
            this.lbl_main.fontSize=140
        }
        let str = m+":"
        if(m<0)
        {
            str="0"+m
        }
        let s = dt%60
        if(s<10)
        {
            str+="0"+s
        }
        else
        {
            str+=s
        }
        this.lbl_main.string=str
    }
    onIdle(deadline:number)
    {
        this.schedule(()=>
        {
            let serverTime = GHelper.getServerTime()
            let dt = Math.floor((deadline-serverTime)/1000)
            if(dt<0)
            {
                dt=0
            }
            let m = Math.floor(dt/60)
            if(m>=60)
            {
                this.lbl_main.string="即将开赛"
                return
            }
            let str = m+":"
            if(m<0)
            {
                str="0"+m
            }
            let s = dt%60
            if(s<10)
            {
                str+="0"+s
            }
            else
            {
                str+=s
            }
            this.lbl_main.string=str
        },0.5)
    }
}
