import { TimeLabel } from './TimeLabel';
import {GHelper} from "../Logic/Helper"

const {ccclass, property} = cc._decorator

@ccclass
export class ReverseCountLabel extends cc.Label
{
    protected _deadline:number = 0
    get deadline()
    {
        return this._deadline
    }
    set deadline(value)
    {
        this._deadline = value
    }

    protected _pre_text:string = ""
    get pre_text()
    {
        return this._pre_text
    }
    set pre_text(value)
    {
        this._pre_text = value
    }

    end_event:Function=null
    protected _interval_handler:number = null
    
    protected _pre_count:number = -1
    protected _left_time = -1
    onLoad() 
    {

    }
    pause()
    {
        this._left_time = this._deadline - GHelper.getServerTime()
        if(this._interval_handler)
        {
            clearInterval(this._interval_handler)
            this._interval_handler = null
        }
    }
    resume()
    {
        if(this._left_time>=0)
        {
            this._deadline = GHelper.getServerTime()+this._left_time
            this.run()
        }
    }
    run()
    {
        let self = this
        if(this._interval_handler)
        {
            return false
        }
        this._interval_handler = setInterval(function()
        {
            self._refresh()
        },1000)
        return this._refresh()
    }
    _refresh()
    {
        if(this._pre_count==0)
        {
            if(this._interval_handler)
            {
                clearInterval(this._interval_handler)
                this._interval_handler = null
            }
            this._pre_count = -1
            if(this.end_event)
            {
                this.end_event()
            }
            return true
        }
        let time = GHelper.getServerTime()
        let dt_time = this.deadline - time
        let count = Math.floor(dt_time/1000+0.1)
        if(count<0)
        {
            count = 0
        }
        this._pre_count = count
        this.string = this._pre_text+this._pre_count
        return false
    }
    onDestroy()
    {
        if(this._interval_handler)
        {
            clearInterval(this._interval_handler)
            this._interval_handler = null
        }
    }
}