import { GGameDM } from '../Framework/DataModel/GameDataModel';
import { GHelper } from '../Framework/Logic/Helper';
import {core} from "../Framework/Core/Base"
class LoginService
{
    async getGameConfig():Promise<any>
    {
        console.log('getGameConfig ====')
        let url="http://dzgame.obs.cn-east-2.myhuaweicloud.com/texasres/cfg.json"
        var promise = new Promise<any>(resolve => {
            GHelper.httpRequest(url,(xhr)=>
            {
                
                let json = xhr.response
                GGameDM.review_version = json.review_version
                GGameDM.auth = json.auth
                GGameDM.resUrl = json.res_url||""
                GGameDM.update = json.update
                GGameDM.androidMaxVersion = json.androidMaxVersion
                GGameDM.androidLoadUrl = json.androidLoadUrl
    
                GGameDM.iosMaxVersion = json.iosMaxVersion
                GGameDM.iosLoadUrl = json.iosLoadUrl

                GGameDM.arrayInvite = json.arrayInvite
                GGameDM.arrayInviteNei = json.arrayInviteNei

                GGameDM.isServerUpdate = json.isServerUpdate
                resolve(true)
            },(err)=>
            {
                resolve(false)
            })
        })

        return promise
    }

    async getServerConfig():Promise<any>
    {
        console.log('getServerConfig ====')
        let url="http://dzgame.obs.cn-east-2.myhuaweicloud.com/texasres/config.json"
        var promise = new Promise<any>(resolve => {
            GHelper.httpRequest(url,(xhr)=>
            {
                let json = xhr.response;
                if(json && json.links)
                {
                    GGameDM.server_list = json.links;
                    GGameDM.pass_word = json.password;
                    GGameDM.match_is_verification = json.verification;
                }else
                {
                    GGameDM.server_list = [];
                    GGameDM.pass_word = "";
                }
                resolve(true)
            },(err)=>
            {
                GGameDM.server_list = [];
                GGameDM.pass_word = "";
                resolve(false)
            })
        })

        return promise
    }
}

export let loginService=new LoginService()