import { EMatchState } from "../../Config/_ini_";
import { GHelper } from "../../Framework/Logic/Helper";
import { GUserMgr } from "../../Logic/UserManager";
import { resMgr } from "../../manager/ResourceManager";
import { GNetGameTexas } from "../../Net/NetGameTexas";
import { GItemCfg } from "../../Service/ItemService";
import { GMatchSer, MatchInsModel } from "../../Service/MatchService";

import { layerMgr } from "../../manager/LayerManager"
const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_MatchListItem extends cc.Component {

    @property(cc.Sprite)
    imgIcon: cc.Sprite = null;

    @property(cc.Label)
    labelName: cc.Label = null;

    @property(cc.Label)
    labelDesc: cc.Label = null;

    @property(cc.Label)
    labelStart: cc.Label = null;

    @property(cc.Node)
    nodeRemain: cc.Node = null;

    @property(cc.Label)
    labelRemainTitle: cc.Label = null;

    @property(cc.Label)
    labelRemainDay: cc.Label = null;

    @property(cc.Label)
    labelRemainTime: cc.Label = null;

    @property(cc.Label)
    labelCount: cc.Label = null;

    @property(cc.Label)
    labelReward: cc.Label = null;

    @property(cc.Node)
    imgMarkSignin: cc.Node = null;

    @property(cc.Node)
    imgMarkEnd: cc.Node = null;

    @property(cc.Label)
    labelDead: cc.Label = null;

    @property(cc.Node)
    nodeDead: cc.Node = null;

    @property(cc.Label)
    labelTime: cc.Label = null;

    public matchInsM: MatchInsModel = null;

    init(info: MatchInsModel) {
        if (this.matchInsM != info) {
            this.matchInsM = info;
            this.matchInsM = GMatchSer.getMatchInsById(info.id)||this.matchInsM
            this.reloadUI();
        } else {
            this.initTable();
        }
    }

    reloadUI() {
        if (!this.matchInsM) {
            return;
        }
        GHelper.createSpriteFrame(this.matchInsM.match.icon,(sf)=>
        {
            cc.isValid(this.imgIcon) && sf && (this.imgIcon.spriteFrame = sf);
        })

        let func=(info)=>
        {
            if(info.id==-99)
            {
                return info.count+"游戏点"
            }
            let item = GItemCfg.getItemById(info.id)
            if(!item)
            {
                return ""
            }
            return item.name+"x"+info.count
        }
        let cost = this.matchInsM.match.signup_cost[0]
        let cost_str=func(cost)
        let reward = this.matchInsM.match.reward[0]
        let reward_str = func(reward)
        let start = new Date(this.matchInsM.start_time)
        this.labelName.string = this.matchInsM.match.name
        this.labelDesc.string = `${cost_str}参赛`
        this.labelStart.string = start.getHours().toString().padStart(2, '0') + ":" + start.getMinutes().toString().padStart(2, '0')
        this.labelReward.string = "奖励：" + reward_str

        this.initTable();
    }

    initTable() {
        if (!this.matchInsM) {
            return;
        }


        this.updateLabel()
        this.schedule(this.updateLabel.bind(this),0.5)
    }

    updateLabel() {
        // 报名过的比赛 显示已参赛
        // 报名了没开始的显示已报名
        // 报名了正在打的显示参赛中
        if (!this.matchInsM) {
            return;
        }
        
        let now = new Date();
        let start = new Date(this.matchInsM.start_time)
        let entt=this.matchInsM.start_time
        this.nodeDead.active = false
        this.labelDead.node.active = false;

        if(this.matchInsM.match.reenter_blind_index>0)
        {
            let bm = GMatchSer.getBlindModel(this.matchInsM.match.blind_id)
            let totalBlind = 0
            for (let index = 0; index < this.matchInsM.match.reenter_blind_index; index++) {
                totalBlind += bm.items[index].seconds*1000;
            }

            if (this.matchInsM.state!=EMatchState.Waiting) {

                let remain = start.getTime() + totalBlind - now.getTime()
                if (remain > 0) {
                    //
                    this.nodeDead.active = true;
                    let dayday = new Date(start.getTime() + totalBlind)
                    let minute = (dayday.getMinutes()==0) ? "00" : dayday.getMinutes();
                    this.labelTime.string = dayday.getHours() + ":" + minute;
                }
                else
                {
                    //已截止
                    this.labelDead.node.active = true;
                    this.labelStart.node.active = true
                    this.labelStart.node.parent.getChildByName("bg").active = true;
                    this.labelDead.string = "已截止"
                }
            }
            entt+=this.matchInsM.match.reenter_blind_index*6*60*1000
        }
        this.labelCount.string = this.matchInsM.player_num+"/"+this.matchInsM.player_num
        this.labelCount.node.setPosition(this.labelDesc.node.getPosition().x + this.labelDesc.node.width + 10, this.labelDesc.node.getPosition().y);
        this.imgMarkSignin.active = false;
        if (this.matchInsM.state!=EMatchState.End&&this.matchInsM.state!=EMatchState.Cancel) {
            this.nodeRemain.active = true
            this.imgMarkEnd.active = false
            if(this.matchInsM.state==EMatchState.Waiting)
            {
                if (GUserMgr.self_user.matchinfos[this.matchInsM.id]!=null) {
                    //表示已报名
                    this.imgMarkSignin.active = true;
                    resMgr.loadImage("View/Page/Res/main/match/img_yibaoming", (res) => {
                        cc.isValid(this.imgMarkSignin) && (this.imgMarkSignin.getComponent(cc.Sprite).spriteFrame = res);
                    })
                }
                this.labelRemainTitle.string = "距离开始";
                let remain = start.getTime() - now.getTime()
                if(remain>=60*60*1000)
                {

                    remain = Math.floor(remain / 1000)
                    let day = Math.floor(remain / 86400)
                    let hour = Math.floor((remain % 86400) / 3600)
                    let minute = Math.floor((remain % 3600) / 60)
                    let second = (remain % 60)
                    this.labelRemainDay.node.active = false
                    //this.labelRemainDay.node.active = day > 0
                    //this.labelRemainDay.string = day + "天"
                    let total_hour = day*24 + Number(hour.toString().padStart(2, '0'))
                    this.labelRemainTime.string = total_hour + ":" + minute.toString().padStart(2, '0') + ":" + second.toString().padStart(2, '0')
                }
                else
                {
                    if(remain<0)
                    {
                        if (GUserMgr.self_user.matchinfos[this.matchInsM.id]!=null) {
                            //表示已参赛
                            this.imgMarkSignin.active = true;
                            resMgr.loadImage("View/Page/Res/main/match/img_yicansai", (res) => {
                                cc.isValid(this.imgMarkSignin) && (this.imgMarkSignin.getComponent(cc.Sprite).spriteFrame = res);
                            })
                        }

                        this.matchInsM.state=EMatchState.Playing
                        this.updateLabel()
                        this.unscheduleAllCallbacks()
                        return
                    }
                    this.labelRemainDay.node.active = false
                    remain=Math.floor(remain/1000)
                    let minute = Math.floor(remain / 60)
                    let second = (remain % 60)
                    this.labelRemainTime.string = minute.toString().padStart(2, '0') + ":" + second.toString().padStart(2, '0')
                }
            }
            else
            {
                if (GUserMgr.self_user.matchinfos[this.matchInsM.id]!=null) {
                    //表示参赛中
                    this.imgMarkSignin.active = true;
                    resMgr.loadImage("View/Page/Res/main/match/img_mark_signup", (res) => {
                        cc.isValid(this.imgMarkSignin) && (this.imgMarkSignin.getComponent(cc.Sprite).spriteFrame = res);
                    })
                }

                this.labelRemainDay.node.active=false
                this.labelRemainTitle.string = ""
                this.labelRemainTime.string = ""
                this.labelCount.string = (this.matchInsM["player_left_num"]||0)+"/"+this.matchInsM.player_num
                this.labelCount.node.setPosition(this.labelDesc.node.getPosition().x + this.labelDesc.node.width + 10, this.labelDesc.node.getPosition().y);
                
            }
        } 
        else {
            this.nodeRemain.active = false
            //this.imgMarkEnd.active = true
        }
        // let signupinfos = GUserMgr.self_user.matchinfos
        // if(!signupinfos||!signupinfos[this.matchInsM.id])
        // {
        //     this.imgMarkSignin.active=false
        // }
        // else
        // {
        //     this.imgMarkSignin.active=true
        // }
    }

    Btn_Match_Click() {
        if(this.matchInsM.state==EMatchState.End||this.matchInsM.state==EMatchState.Cancel)
        {
            return
        }
        GNetGameTexas.send_texas_enter_match(this.matchInsM.match_id,this.matchInsM.id)
    }
}
