import {GHelper} from "../Logic/Helper"

const {ccclass, property} = cc._decorator;

export let GTipLabel:TipLabel=null

@ccclass
export class TipLabel extends cc.Component{
    protected _time:number = -1
    protected _bg:cc.Sprite = null
    protected _lbl_tip:cc.Label = null
    // use this for initialization
    onLoad() 
    {
        GTipLabel = this
        //this.node.setPosition(cc.winSize.width/2,cc.winSize.height/2)
        this._bg = GHelper.createSprite("Common/Image/Other/tipBg")
        this._bg.node.setAnchorPoint(cc.v2(0.5,0.5))
        this._bg.node.parent = this.node

        this._lbl_tip = <cc.Label>GHelper.createNode(cc.Label)
        this._lbl_tip.node.setAnchorPoint(cc.v2(0.5,0.5))
        this._lbl_tip.node.parent = this.node
        this._lbl_tip.fontSize = 30
        this._lbl_tip.node.y=-this._lbl_tip.fontSize/2
        this._lbl_tip.node.color=cc.Color.WHITE
        this.node.active = false
    }
    show(str:string,time?,showPos?)
    {
        if(showPos)
        {
            this._bg.node.setPosition(showPos)
            this._lbl_tip.node.setPosition(cc.v2(showPos.x,showPos.y -this._lbl_tip.fontSize/2))
        }else
        {
            this._bg.node.setPosition(cc.v2(0,0))
            this._lbl_tip.node.setPosition(cc.v2(0,-this._lbl_tip.fontSize/2))
        }
        this._lbl_tip.string = str || "no string tip!"
        this._time = time || 2
        setTimeout(()=>
        {
            if(this._time>0)
            {
                this.node.active = true
            }
        }, 500)
    }
    refershTip(str:string)
    {
        this._lbl_tip.string = str
    }
    update(dt)
    {
        this._time -= dt
        if(this._time<=0)
        {
            this.node.active = false
        }
    }
    clear()
    {
        this._time = 0
        this.node.active = false
    }
}