const {ccclass, property} = cc._decorator;
import { GMsgCenter } from "../../utils/MessageCenter";
import { ClubModel ,GClubSer} from "../../Service/ClubService";
import { GUserMgr } from "../../Logic/UserManager"

@ccclass
export default class Widget_ClubItem extends cc.Component {

    @property(cc.Sprite)
    sp_icon: cc.Sprite = null

    @property(cc.Label)
    lbl_clubname: cc.Label = null

    @property(cc.Label)
    lbl_areaname: cc.Label = null

    @property(cc.Button)
    btn_join: cc.Button = null

    @property(cc.Node)
    im_joined: cc.Node = null


    private clubData:ClubModel = null 
    
    onLoad()
    {
        
    }
    protected onDestroy(): void {
    }
    Btn_Join_Click(event)
    {
        this.onJoninClubClick()
    }

    btn_Detail_click(event)
    {
        GMsgCenter.send('club_detail',this.clubData)
    }

    refreshData(){
        console.log('refreshData')
        this.refreshIsJoined()
    }
    
    setdata(jsonData) {
        cc.log("ab")
        let data:ClubModel = jsonData.clubdata
        this.clubData = data
        this.lbl_clubname.string = data.title
        this.lbl_areaname.string = data.city



        this.clubData = data

        this.refreshIsJoined()
    }

    refreshIsJoined(){
        this.im_joined.active = false
        this.btn_join.node.active = true
        for(var k in GUserMgr.self_user.clubs){
            // console.log(k,this.clubData.id.toString())
            if(k == this.clubData.id.toString()){
                // this.nodeJoined.active = true
                // this.nodeJoin.active = false
                this.im_joined.active = true
                this.btn_join.node.active = false
                break
            }
        }
    }

    async onJoninClubClick(){
        if(!this.clubData){
            return
        }
        let res = await GClubSer.joinClub(this.clubData.id)
        if(res.errcode){
            this.refreshIsJoined()
        }else{
           GUserMgr.self_user.clubs[this.clubData.id.toString()] = this.clubData.service
           this.clubData.user_count+=1
           this.refreshIsJoined()
        }
    }
}
