import { GHallHttp } from "../Net/HallHttp"

export interface ClubModel
{
    _id:string
    id:number
    invite_code:string
    title:string
    url:string
    icon:string
    ads:string[]
    notice:string
    region:string
    provience:string
    city:string
    address:string
    address_detail:string
    service:string
    user_count:number
    create_time:number
    open:number
}

class ClubService
{
    private static _instance: ClubService = null
    protected _clubs:ClubModel[]=null
    get clubs()
    {
        return this._clubs
    }
    async init()
    {
        if( this._clubs){
            return this._clubs
        }
        let res = await GHallHttp.request("GetClubList")
        this._clubs=res.clublist||[]
        return this._clubs
    }

    async getClubDataById(clubId){
        let allClubDatas = await this.init()

        for(var v of allClubDatas){
            if(v.id.toString() == clubId.toString()){
                return v
            }
        }

    }

    async joinClub(clubId)
    {
        let res = await GHallHttp.request("joinclub",{club_id:clubId})
        return res
    }

    async quitClub(clubId)
    {
        let res = await GHallHttp.request("QuitClub",{club_id:clubId})
        return res
    }

    async searchClub(message)
    {
        let res = await GHallHttp.request("Searchclub",message)
        return res
    }

    static  instance(){
        if(this._instance === null) {
          this._instance = new ClubService()
        }
        return this._instance
    }
    
}
export let GClubSer:ClubService =  ClubService.instance()