import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { EBagItemState, EItemType, GItemCfg } from "../../Service/ItemService";
import { GMsgCenter } from "../../utils/MessageCenter";
import Widget_ChangeItem from "../Widget/Widget_ChangeItem";

import tableView from "../../Framework/TableView/tableView"

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_MyPrizeInfo extends Page {

    @property(cc.Node)
    comment_server_Nd:cc.Node = null;

    
    @property(cc.Node)
    comment_server_line:cc.Node = null;

    @property(cc.Label)
    name_item_lab:cc.Label = null;

    @property(cc.Label)
    name_phone_lab:cc.Label = null;

    @property(cc.Label)
    address_lab:cc.Label = null;

    @property(cc.Label)
    comment_client_lab:cc.Label = null;

    @property(cc.Label)
    comment_server_lab:cc.Label = null;



    _itemData:any = null;

    onOpen(mData)
    {
        this._itemData = mData;
     
        let itemM = GItemCfg.getItemById(mData.item_id)

        this.name_item_lab.string = itemM.name;

        if(this._itemData.address)
        {
            this.address_lab.node.active = true;
            let addressStr = this._itemData.address;
          
            let addressData = addressStr.replace("\\","");

           
            let infoData = JSON.parse(addressData);

            this.name_phone_lab.string = infoData.name+"  "+infoData.phone
            this.address_lab.string = infoData.address
        }else
        {
            this.name_phone_lab.string = "无"
            this.address_lab.node.active = false;
            this.address_lab.string = "";
        }

        this.comment_client_lab.string = this._itemData.comment_client?this._itemData.comment_client:"无"

        this.comment_server_Nd.active = this.comment_server_line.active = this._itemData.comment_server;

        this.comment_server_lab.string =  this._itemData.comment_server? this._itemData.comment_server:"无"

 


    }

    onLoad() {
       
    }

    protected onDestroy(): void {
        
    }

    
    
    Btn_Close_OnClick(event)
    {
        GPageMgr.closePage("Page_MyPrizeInfo")
    }
}
