import {GHelper} from "../Logic/Helper"

const {ccclass, property} = cc._decorator

@ccclass
export class SpriteNumLabel extends cc.Component
{

    @property({type:cc.SpriteAtlas})
    spriteAtlas:cc.SpriteAtlas=null
    _string:string="0"
    _width:number=0
    get width()
    {
        return this._width
    }
    _height:number=0
    get height()
    {
        return this._height
    }
    get string() 
    {
        return this._string
    }
    set string(value) 
    {
        if(this._string===value)
        {
            return
        }
        this._string = value
        this._updateShow()
    }
    _numType:number=1
    get numType() 
    {
        return this._numType
    }
    set numType(value) 
    {
        if(this._numType===value)
        {
            return
        }
        this._numType = value
        this._updateShow()
    }
    _forceNum:number=-1
    get forceNum()
    {
        return this._forceNum
    }
    set forceNum(value)
    {
        this._forceNum = value
    }
    static create(string:string="0",numType:number=1,forceNum:number=-1)
    {
        let snl = <SpriteNumLabel>GHelper.createNode(SpriteNumLabel)
        snl._numType = numType
        snl._forceNum = forceNum
        snl._string = string
        return snl
    }
    // use this for initialization
    onLoad() 
    {
        this._updateShow()
    }
    _updateShow()
    {
        let self = this
        let numStr = this._string+""
        let length = numStr.length
        if(this._forceNum>0 && length < this._forceNum)
        {
            for(let i = 0;i<this._forceNum - length;++i)
            {
                numStr = "0" + numStr
            }
            length = numStr.length
        }
        this._init(numStr)
    }
    _init(numStr)
    {
        this.node.removeAllChildren()
        let offsetX = 0
        if(this._numType==1)
        {
            offsetX = -2
        }
        let width = 0
        let height = 0
        let length= numStr.length
        for(let i = 0; i<length;++i)
        {
            let s = numStr[i]
            let sf = this.spriteAtlas.getSpriteFrame(this._numType + "_" + s)
            let sp = <cc.Sprite>GHelper.createNode(cc.Sprite)
            sp.spriteFrame = sf
            sp.node.parent = this.node
            sp.node.setPosition(width, 0)
            let rect = sf.getRect()
            width += rect.width + 2 * offsetX
            let h = rect.height
            if(height < h)
            {
                height = h
            }
        }
        this._width = width
        this._height = height
    }
}
