import { GTexasTableDM } from "../../../DataModel/TexasTableDataModel";
import tableView from "../../../Framework/TableView/tableView";
import { GPageMgr } from "../../../Framework/View/Page/PageManager";
import { GMatchSer } from "../../../Service/MatchService";
import { GMsgCenter } from "../../../utils/MessageCenter";
import { utils } from "../../../utils/Utils";
import Page_Game from "../../Page/Page_Game";

const {ccclass, property} = cc._decorator;

@ccclass
export default class NodeMatchLevel extends cc.Component {

    @property(cc.ScrollView)
    scrollview: cc.ScrollView = null;

    @property(cc.Node)
    layout: cc.Node = null;

    @property(cc.Node)
    item: cc.Node = null;

    @property(tableView)
    tableView: tableView = null;

    onLoad()
    {
        GMsgCenter.register("upgradeblind",this.init.bind(this),this)
    }
    protected onDestroy(): void {
        GMsgCenter.unregister("upgradeblind",this)
    }
    protected onEnable(): void {
        this.init()
    }

    public matchInsM = null
    public cur_index = null
    init() {
        // this.layout.removeAllChildren();
        this.tableView.clear()
        let page = GPageMgr.getPage("Page_Game") as Page_Game
        if(!page)
        {
            return
        }
        let table_id = page.tableId
        let tableinfo = GTexasTableDM.getTableInfo(table_id)
        let matchInsM = GMatchSer.getMatchInsById(tableinfo.match_insid)
        this.matchInsM = matchInsM

        let bm = GMatchSer.getBlindModel(matchInsM.match.blind_id)
        let cur_index = matchInsM.blind_index
        this.cur_index = cur_index
        this.layout.height = bm.items.length*60
        // for(let i=0;i<bm.items.length;++i)
        // {
        //     let node = cc.instantiate(this.item)
        //     node.setPosition(0, -30 - i*60)
        //     this.layout.addChild(node)
        //     let bi = bm.items[i]
           
        // }
        // this.scrollview.scrollToOffset(new cc.Vec2(0, 60*cur_index))
        this.tableView.delegate = this
        this.tableView.init(bm.items)
        setTimeout(()=>{
            this.tableView.scrollToOffset(new cc.Vec2(0, 60*cur_index))
        },100)
    }
}
