export class SkillWheel extends cc.Component
{

    _fun_callback:Function
    // use this for initialization
    onLoad() 
    {

    }

    Btn_Skill_OnClick(event)
    {
        if(this._fun_callback)
        {
            this._fun_callback()
        }
    }

    registerCallBack(callback)
    {
        this._fun_callback = callback
    }
}
