import { BaseAd } from "./BaseAd";

class VivoAd extends BaseAd{
    private bannerAd = null;
    private videoAd = null;
    private videoSuccess: Function = null;
    private videoFail: Function = null;
    private nativeAd = null;
    private nativeSuccess: Function = null;
    private nativeFail: Function = null;

    constructor() {
        super("vivo");
    }

    login(success?: Function, fail?: Function) {
        qg.login().then((res) => {
            let code = res.data.token;
            qg.getUserInfo().then((res) => {
                if (res.data) {
                    success && success({
                        nickname: res.data.nickName,
                        avatar: res.data.smallAvatar,
                        gender: res.data.gender,
                        platform: this.platform,
                        code: code
                    })
                } else {
                    fail && fail();
                }
            }, (err) => {
                console.log("获取用户信息失败" + JSON.stringify(err));
                fail && fail();
            })
        }, (err) => {
            console.log("登录失败" + JSON.stringify(err));
            fail && fail();
        })
    }

    share() {
        qg.share();
    }

    createBannerAd() {
        if (!this.banner_id) {
            return;
        }

        if (!this.bannerAd) {
            let sysInfo = qg.getSystemInfoSync();

            this.bannerAd = qg.createBannerAd({
                posId: this.banner_id,
                style: {}
            });

            if (!this.bannerAd) {
                return;
            }

            this.bannerAd.onResize((size) => {
                if (this.bannerAd && this.bannerAd.style) {
                    this.bannerAd.style.top = sysInfo.screenHeight - size.height;
                    this.bannerAd.style.left = sysInfo.screenWidth / 2 - size.width / 2;
                }
            })

            this.bannerAd.onLoad(() => {
                console.log("Banner广告加载成功");
            });
            this.bannerAd.onError(() => {
                console.log("Banner广告加载失败");
            });

            this.bannerAd.show()
            .then(() => {
                console.log("Banner广告展示完成");
            })
            .catch((err) => {
                console.log("Banner广告加载失败 ", JSON.stringify(err));
            })
        }
    }

    destroyBannerAd() {
        if (this.bannerAd) {
            this.bannerAd.hide();
            this.bannerAd.destroy();
            this.bannerAd = null;
        }
    }

    createVideoAd(success?: Function, fail?: Function) {
        if (!this.video_id) {
            success && success();
            return;
        }

        if (!this.videoAd) {
            this.videoAd = qg.createRewardedVideoAd({
                posId: this.video_id
            });

            this.videoAd.onLoad(() => {
                console.log("激励视频广告加载成功");
            });

            this.videoAd.onError(() => {
                console.log("激励视频广告加载失败");
                this.videoFail && this.videoFail();
            })

            this.videoAd.onClose((res) => {
                if (res && res.isEnded) {
                    console.log("激励视频正常播放结束");
                    this.videoSuccess && this.videoSuccess();
                } else {
                    console.log("激励视频播放中途推出");
                    this.videoFail && this.videoFail();
                }
            })
        }

        this.videoSuccess = success;
        this.videoFail = fail;
        
        this.videoAd.show()
        .catch(() => {
            this.videoAd.load()
            .then(() => {
                this.videoAd.show();
            })
        })
    }

    createInterstitialAd(callback?: Function) {
        if (!this.interstitial_id) {
            callback && callback();
            return;
        }

        let interstitialAd = qg.createInterstitialAd({
            posId: this.interstitial_id
        });
        interstitialAd.show();
        interstitialAd.onLoad(() => {
            console.log('插屏广告加载成功');
        });
        interstitialAd.onError(() => {
            console.log('插屏广告加载失败');
        });
        interstitialAd.onClose(() => {
            console.log('插屏广告关闭');
            callback && callback();
        })
    }

    createNativeAd(success?: Function, fail?: Function) {
        if (!this.native_id) {
            success && success();
            return;
        }
        
        if (!this.nativeAd) {
            this.nativeAd = qg.createNativeAd({
                posId: this.native_id
            });

            this.nativeAd.onLoad((res) => {
                console.log("原生广告加载成功");
                if (res && res.adList && res.adList.length) {
                    let ad = res.adList[Math.floor(Math.random() * res.adList.length)];
                    this.nativeSuccess && this.nativeSuccess({
                        id: ad.adId,
                        title: ad.title,
                        desc: ad.desc,
                        icon: ad.icon,
                        image: ad.imgUrlList[Math.floor(Math.random() * ad.imgUrlList.length)],
                        logo: ad.logoUrl,
                        tips: ad.clickBtnTxt,
                        mode: 0
                    })
                } else {
                    this.nativeFail && this.nativeFail();
                }
            })

            this.nativeAd.onError(() => {
                console.log("原生广告加载失败");
                this.nativeFail && this.nativeFail();
            })
        }

        this.nativeSuccess = success;
        this.nativeFail = fail;

        this.nativeAd.load();
    }

    reportAdShow(id: string) {
        if (this.nativeAd) {
            this.nativeAd.reportAdShow({
                adId: id
            })
        }
    }

    reportAdClick(id: string) {
        if (this.nativeAd) {
            this.nativeAd.reportAdClick({
                adId: id
            })
        }
    }
}

export const vivoAd: VivoAd = new VivoAd();