import { ETexasSeatState, GTexasTableDM, TexasRecordTableInfo, TexasSeatInfo, TexasTableInfo } from '../DataModel/TexasTableDataModel';
import { GUserMgr } from '../Logic/UserManager';
import { SimpleWebSocket } from '../Framework/Net/SimpleWebSocket';
import { EMatchState, ETableState, ETexasAction } from '../Config/_ini_';
import { GAppDM } from '../DataModel/AppDataModel';
import { BlindItem, GMatchSer, MatchPlayer } from '../Service/MatchService';
import { GPageMgr } from '../Framework/View/Page/PageManager';
import { GHelper } from '../Framework/Logic/Helper';
import { GMsgCenter } from '../utils/MessageCenter';
import { GaoDeMapTool } from '../Framework/Logic/GaoDeMapTool';

import { layerMgr } from "../manager/LayerManager"
import { Global } from "../view/game/Global";

export let GNetGameTexas:NetGameTexas = null
class NetGameTexas extends SimpleWebSocket
{
    private _ins_id=""
    private _entered=false
    
    constructor()
    {
        super()
        //this._protocol="slots"
    }
    onOpen()
    {
        this._is_open = true
        this.send_texas_login()
        this.openHeartBeat()
    }

   
    _onCacheMsg()
    {
        if(!this._entered)
        {
            return
        }
        super._onCacheMsg()
    }
    send_texas_login()
    {
        let msg=
        {
            cmd:"texas_login",
            account_id:GAppDM.account.id,
            token:GAppDM.token
        }
        this.send(msg)
    }
    close(): void {
        this._need_close = true
        this.pageTable = null
        super.close()
    }

    receive_texas_login(jsonData)
    {
        super.onOpen()
        this._entered=true
        GUserMgr.init(jsonData.user.id) // 记录自己的userid
        GUserMgr.addOneUser(jsonData.user)
        this._onCacheMsg()
        let msg=
        {
            cmd:"texas_enter_match",
            ins_id:this._ins_id
        }
        this.send(msg)
    }
    receive_texas_kickout(jsonData)
    {
        GPageMgr.closePage("Page_Game")
        this.close()
        GHelper.showTip("账号在其他地方上线！")
    }
    send_texas_enter_match(match_id:number,ins_id:string)
    {
        this._ins_id=ins_id
        if(this.isOpen)
        {
            this.close()
        }
        let id = match_id%2
        let server = GAppDM.texas_servers[id]
        if(!server)
        {
            id=0
            server=GAppDM.texas_servers[id]
        }
        this.init(server.host,server.port)
        this.connect()
    }
    receive_texas_enter_match(jsonData)
    {
        // cc.error("进入比赛 比赛信息=",jsonData)
        GMatchSer.setMatchInsM(jsonData.matchInsM)
        GMatchSer.initRecords(jsonData.matchInsM.id)
        this.pageTable?.onEnterMatch()


        GMsgCenter.send('refresh_score')
    }
    // 废弃
    receive_texas_enter_match_failed(jsonData)
    {
        if(jsonData.errcode){
            GHelper.showTip(jsonData.errcode.des)
        }
       this.close()
       GPageMgr.closePage("Page_Game")
    }
    /**
     * 同步gps
     */
    async send_texas_gps()
    {
        let lm = await GaoDeMapTool.getLocation(true)
        if(!lm||!lm.latitude||!lm.longitude)
        {
            return
        }
        let msg=
        {
            cmd:"texas_gps",
            ins_id:this._ins_id,
            gps:[lm.longitude,lm.latitude]
        }
        this.send(msg)
    }
    receive_texas_enter_table(jsonData)
    {
        let ti:TexasTableInfo = jsonData.table_info
        GTexasTableDM.init(ti)
        if(this._ins_id!=ti.match_insid)
        {
            return
        }
        // if(GPageTable&&GPageTable.tableInfo.match_insid==ti.match_insid)
        // {
        //     console.log("reopen cause of enter_table")
        //     GPageMgr.reOpenPage("Page_Game",ti.id)
        //     return
        // }
        Global.tableData.tableId = ti.id;
        // 刷新桌子与座位的信息
        if(this.pageTable){
            // 刷新桌子信息
            let game = this.pageTable['node'].parent.getComponent('Page')
            game.initGame(ti.id)
            // this.pageTable.initTable(ti.match_insid)
        }else{
            this.onGetPage(ti)
        }

    }

    private pageTable
    async onGetPage(ti){
        let page =  await GPageMgr.openPage("Page_Game",ti.id)
        let pageTable = cc.find('Page_Table',page['node']).getComponent('Page')
        this.pageTable = pageTable
    }


    receive_texas_enter_table_bc(jsonData)
    {
        let seat:TexasSeatInfo = jsonData.seat
    }
    // 接收到坐下的信息
    receive_texas_sitdown_bc(jsonData)
    {
        GTexasTableDM.onSitdown(jsonData.table_id,jsonData.seat)
        this.pageTable?.onSit(jsonData.table_id,jsonData.seat)
    }
    receive_texas_start_game_bc(jsonData)
    {
        let ti = jsonData.table_info as TexasTableInfo
        GTexasTableDM.init(ti)
        let mins = GMatchSer.getMatchInsById(ti.match_insid)
        if(mins)
        {
            mins.state=EMatchState.Playing
        }
        this.pageTable?.onGameStart(ti.id)
    }
    receive_texas_refresh_table_rank(jsonData)
    {
        if(Global.tableData.tableId == jsonData.table_id && jsonData.tableRankInfo)
        {
            for(var i=0;i<jsonData.tableRankInfo.length;i++)
            {
                let temp = jsonData.tableRankInfo[i];
                if(temp.id==GUserMgr.self_user_id)
                {
                    this.pageTable?.refreshTableRank(temp.cur_rank);
                }
            }
        }
    }
    async receive_texas_beginbet_bc(jsonData)
    {
        let tableinfo = GTexasTableDM.getTableInfo(jsonData.table_id)
        if(!tableinfo)
        {
            return
        }

        tableinfo.curmaxbet = jsonData.curmaxbet
        tableinfo.dtbet = jsonData.dtbet
        tableinfo.timeout = jsonData.timeout
        tableinfo.turn_user_id = jsonData.turn_user_id
        tableinfo.min_raise_bet = jsonData.min_raise_bet
        this.pageTable?.onBeginBet(tableinfo.id)
    }
    send_texas_bet(table_id:number,bet:number)
    {
        let msg=
        {
            cmd:"texas_bet",
            table_id:table_id,
            bet:bet
        }
        this.send(msg)
    }
    receive_texas_bet_bc(jsonData)
    {
        GTexasTableDM.onbet(jsonData.table_id,jsonData.user_id,jsonData.bet,jsonData.curmaxbet,jsonData.dtbet,jsonData.action)
        this.pageTable?.onBet(jsonData.table_id,jsonData.user_id,jsonData.bet)
    }
    receive_texas_dealcard(jsonData)
    {
        GTexasTableDM.onDealCard(jsonData.table_id,jsonData.cards,jsonData.cardtype)
        this.pageTable?.onDealCard(jsonData.table_id)
    }
    receive_texas_dealcard_bc(jsonData)
    {
        GTexasTableDM.onDealCard(jsonData.table_id,jsonData.cards,jsonData.cardtype)
        this.pageTable?.onDealCard(jsonData.table_id)
    }
    receive_texas_game_result_bc(jsonData)
    {
        GTexasTableDM.onResult(jsonData.record)
        this.pageTable?.onGameResult(jsonData.record.table_id)

      
    }
    receive_texas_upgradeblind_bc(jsonData)
    {
        let table_id=jsonData.table_id
        if(this.pageTable?.tableInfo?.id!=table_id)
        {
            return
        }
        GMatchSer.onUpgradeBlind(table_id,jsonData.blind_index,jsonData.deadline)
        this.pageTable?.onUpgradeBlind()
        let blinditem:BlindItem=jsonData.blinditem
        if(blinditem.big_blind+blinditem.small_blind+blinditem.ante>0)
        {
            GHelper.showTip(`下局升级至,半基础分:${blinditem.small_blind},基础分:${blinditem.big_blind},前置分:${blinditem.ante}`,5)
        }
        else
        {
            GHelper.showTip(`本局结束后将进入休息阶段`,5)
        }
        GMsgCenter.send("upgradeblind")
    }
    receive_texas_idle_bc(jsonData)
    {
        let table_id:number = jsonData.table_id
        let deadline:number = jsonData.deadline
        GTexasTableDM.onIdle(table_id,deadline)
        this.pageTable?.onIdle()
    }
    receive_texas_standup_bc(jsonData)
    {
        GTexasTableDM.onStandup(jsonData.table_id,jsonData.user_id)
        this.pageTable?.onStandUp(jsonData.table_id,jsonData.user_id)
    }
    receive_texas_match_end(jsonData)
    {
        if(jsonData.ins_id==this._ins_id)
        {
            GHelper.showTip("比赛已经结束，谢谢参与！",2,cc.v2(0,630))
            this.close()
        }
    }
    send_texas_auto(table_id:number,auto:boolean)
    {
        let msg=
        {
            cmd:"texas_auto",
            table_id:table_id,
            auto:auto
        }
        this.send(msg)
    }
    receive_texas_auto_bc(jsonData)
    {
        let table_id:number = jsonData.table_id
        let user_id:number = jsonData.user_id 
        let auto:boolean = jsonData.auto 
        GTexasTableDM.onAuto(table_id,user_id,auto)
        this.pageTable?.onAuto(table_id,user_id,auto)
    }
    receive_texas_matchresult_nt(jsonData)
    {
        let ti = GTexasTableDM.getTableInfo(jsonData.table_id)
        if(ti)
        {
            ti.cur_player_num=jsonData.cur_player_num
            ti.total_player_num=jsonData.total_player_num
            let mins = GMatchSer.getMatchInsById(ti.match_insid)
            if(mins)
            {
                mins.players[GUserMgr.self_user_id]=mins.players[GUserMgr.self_user_id]||<any>{}
                mins.players[GUserMgr.self_user_id].rank=jsonData.rank
                mins.players[GUserMgr.self_user_id].cur_rank=jsonData.rank
            }
        }
        GMsgCenter.send("match_result", jsonData)
    }
    /**
     * 
     * @param table_id 
     * @param showcards 1左，2右，3全部，其他无 
     */
    send_texas_showcard(table_id:number,showcards:number)
    {
        let msg=
        {
            cmd:"texas_showcard",
            table_id:table_id,
            showcards:showcards
        }
        this.send(msg)
    }
    receive_texas_showcard_bc(jsonData)
    {
        let table_id=jsonData.table_id
        let user_id=jsonData.user_id
        let showcards=jsonData.showcards
        GTexasTableDM.onShowCards(table_id,user_id,showcards)
        this.pageTable?.onShowCards(table_id,user_id,showcards)
    }
    receive_texas_allallinshow_bc(jsonData)
    {
        let table_id=jsonData.table_id
        let all=jsonData.all
        let ti = GTexasTableDM.getTableInfo(table_id)
        if(ti)
        {
            ti.pots=jsonData.pots
        }
        this.pageTable?.onAllAllInShow(table_id,all)
    }
    send_texas_identified(ins_id:string)
    {
        let msg=
        {
            cmd:"texas_identified",
            nickname:GUserMgr.self_user.nickname,
            idcard_no:"@"+GUserMgr.self_user.idcard_no,
            ins_id:ins_id
        }
        this.send(msg)
    }
    receive_texas_identified(jsonData)
    {
        let player = GMatchSer.getSelfPlayer(jsonData.ins_id)
        player.identified = true;
        let tabl_id = this.pageTable?.tableInfo.id
        let si = GTexasTableDM.getSeatByUserId(tabl_id,GUserMgr.self_user_id)
        if(si)
        {
            let pi = si.playerinfo as MatchPlayer
            pi.identified=true
        }
    }
    send_texas_watch(ins_id:string,action:number){
        let msg = 
        {
            cmd:"texas_watch",
            ins_id:ins_id,
            action:action
        }
        this.send(msg)
    }
    // 拆桌通知
    receive_texas_waitcombine_bc(jsonData){
        let table_id=jsonData.table_id
        let ti = GTexasTableDM.getTableInfo(table_id)
        ti.state = ETableState.WaintingCombine

        // 刷新桌子
        this.pageTable?.clear()
        GHelper.showTip('正在拆桌',2)
    }
    // 等待发牌
    receive_texas_waiting_start_bc(jsonData){
        let table_id=jsonData.table_id
        let ti = GTexasTableDM.getTableInfo(table_id)
        ti.state = ETableState.WaitingStart


        this.pageTable?.clear()
        GHelper.showTip('等待其他桌发牌',2)
    }
}
GNetGameTexas = new NetGameTexas()