    updateCardStyle() {
        this.m_publiccards.forEach(element => {
            if (element.getComponent(Card).info) {
                element.getComponent(Card).setContent(element.getComponent(Card).info.id);
            }
        });
        if (this.seats[0].seatInfo && this.seats[0].seatInfo.playerinfo.id==GUserMgr.self_user_id) {
            this.seats[0].getCard(0).setContent(this.seats[0].getCard(0).getComponent(Card).info.id);
            this.seats[0].getCard(1).setContent(this.seats[0].getCard(1).getComponent(Card).info.id);
        }
    }

    updateCardBgStyle(index) {
        if(this.seats == null){
            return
        }
        
        for(let i=0;i<this.seats.length;++i)
        {
            this.seats[i].getCard(0).setCardBg(index)
            this.seats[i].getCard(1).setCardBg(index)

            this.m_publiccards.forEach(element => {
                if (element.getComponent(Card)) {
                    element.getComponent(Card).setCardBg(index);
                }
            });
        }
    }

