    async Btn_Focus_OnClick(event)
    {
        let insid = this.tableInfo.match_insid
        let focusmatches = GUserMgr.self_user.focusmatches||[]
        if(focusmatches.indexOf(insid)>0)
        {
            return
        }
        
        let res = await GHallHttp.request("focus",{insid:insid})
        if(res.errcode)
        {
            GHelper.showTip(res.errcode.des)
            return
        }
        focusmatches.push(insid)
        GUserMgr.self_user.focusmatches=focusmatches
        let mim = GMatchSer.getMatchInsById(this.tableInfo.match_insid)
        GMatchSer.focusmatchins.push(mim)
        this.btn_focus.interactable =false
        this.btn_focus.node.getChildByName("label").getComponent(cc.Label).string = "已关注赛事"
    }
    onAuto(table_id: number, user_id: number, auto: boolean)
    {
        if(table_id!=this._table_id)
        {
            return
        }
        this.wgt_tableaction.onAuto(table_id, user_id, auto)
        let seat = this.getSeat(user_id)
        if(!seat)
        {
            return
        }
        seat.onAuto()
    }
    onShowCards(table_id:number,user_id:number,showcards:number)
    {
        if(this.tableInfo.state!=ETableState.End)
        {
            return
        }
        if(this._table_id!=table_id)
        {
            return
        }
        let seat = this.getSeat(user_id)
        if(!seat||!seat.node.active)
        {
            return
        }
        seat.onShowCards(showcards)
    }
