    // 底部观战切换 按钮显示 是否
    setBootomChangeShow(isShow){
        if(this.tableInfo.cur_table_num<=1) // 桌子数量小于等于1 不显示切换
        {
            isShow = false
        }else
        {
            let tableinfo = GTexasTableDM.getTableInfo(this._table_id)
            if(!tableinfo)
            {
                isShow = true;  // 显示
            }
            else
            {
                isShow = false;  // 不显示
                let matchInsM = GMatchSer.getMatchInsById(this.tableInfo.match_insid)
                let matchM:MatchModel = matchInsM.match
                if( matchInsM.state==EMatchState.Waiting)
                {
                    isShow = false;
                }else
                {
                    
                    let seatinfo = GTexasTableDM.getSelfSeatInfo(tableinfo.id)
                    if(seatinfo)
                    {
                        isShow = false;
                    }else
                    {
                        isShow = true;
                    }
                }
            }

        }

         
       
        cc.find('changeLeftTable',this.node).active = isShow
        cc.find('changeRightTable',this.node).active = isShow
    }


    protected _toPot(pot:number)
    {
        pot=pot||0
        if(pot<1000000)
        {
            return pot+""
        }
        let str = pot/1000000+""
        if(str.length>5)
        {
            str = str.substring(0,5)
        }
        str+="M"
        return str
    }

    
    

