import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../Logic/UserManager";
import MultiPlatform from '../../platform/MultiPlatform';
import { ykUtils } from "../../utils/YkUtils";
const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_Invite extends Page {
    @property(cc.Label)
    labelInvite: cc.Label = null;

    @property(cc.Node)
    qrcodeNode: cc.Node;
    
    @property(cc.Node)
    content: cc.Node = null;

    @property(cc.Node)
    invite_er_wei_ma:cc.Node =null;

    start() {
        this.labelInvite.string = GUserMgr.self_user_id.toString();
        this.qrcodeNode = this.node.getChildByName("qrcode") as cc.Node;

        this.loadInviteCode();
    }

    async loadInviteCode(){
        let code =  await ykUtils.loadNetSpriteFrame(ykUtils.PATH.png_kefuCode)
        if(code){
            this.invite_er_wei_ma.getComponent(cc.Sprite).spriteFrame = code
        }
    }

    /**
     * 改变二维码显示内容
     * @param str 
     */
    changeQRCodeUI(str:String){
        //写入数据
        this.qrcodeNode.getComponent('CQRCode').string=str;
    }

    
    onClickShare(event: cc.Event.EventCustom, data: string) 
    {
        let tempNode = this.node.getChildByName("content");
        if(tempNode==null)
        {
            return;
        }
        if (data=="wechat") 
        {
            MultiPlatform.getInstance().capScreenShareWx(1,tempNode,null);
        }
        else if (data=="friend")
        {
            MultiPlatform.getInstance().capScreenShareWx(2,tempNode,null);
        }else if(data=="save")
        {
            MultiPlatform.getInstance().capScreen(tempNode,(isSucces)=>
            {
                GHelper.showTip(isSucces?'保存成功':'保存失败',2,cc.v2(0,630));
            });
        }
    }

    

    

   

    Btn_Close_OnClick(event)
    {
        GPageMgr.closePage("Page_Invite")
    }
}
