import { GUserMgr } from "../../Logic/UserManager";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_SignUpBar extends cc.Component {
    @property(cc.Label)
    itemName: cc.Label = null

    @property(cc.Node)
    left_kuang: cc.Node = null
    @property(cc.Node)
    left_label: cc.Node = null

    @property(cc.Label)
    right_label: cc.Label = null

    private itemCfg

    onLoad()
    {        
      
    }

    // 设置物品
    setItem(itemCfg:any){
        let user = GUserMgr.self_user
        this.itemCfg = itemCfg
        if(itemCfg == 'coin'){
            this.itemName.string = '游戏点'
            this.left_kuang.active = false
            this.left_label.active = true
            this.left_label.getComponent(cc.Label).string = '(剩余'+user.coin.toString()+'游戏点)'
        }else if(itemCfg == 'rewardCoin'){
            this.itemName.string = '欢乐点'
            this.left_kuang.active = false
            this.left_label.active = true
            this.left_label.getComponent(cc.Label).string = '(剩余'+user.reward_coin.toString()+'欢乐点)'
        }else{
            this.left_kuang.active = true
            this.left_label.active = false
            this.itemName.string = itemCfg.name
        }
    }

    setNum(haveNum,useNum){
        if(this.itemCfg=='coin' || this.itemCfg=='rewardCoin'){
           
            this.right_label.string = '已选'+useNum+'点'
        }else{
            this.right_label.string = '已选'+useNum+'张'
            this.left_kuang.children[0].getComponent(cc.Label).string = '剩余'+haveNum+'张'
        }
        if(useNum>0){
            cc.find('back_juan/checkmark',this.node).active = true
        }else{
            cc.find('back_juan/checkmark',this.node).active = false
        }
    }
}