import {core} from "../Core/Base"
import {GHelper} from "../Logic/Helper"

const {ccclass, property} = cc._decorator

@ccclass
export class LoopScrollList extends cc.Mask
{
    protected _items:any
    protected _width:number=0
    protected _isRecyle:boolean=true
    protected _scrollToIndex:number=0
    protected _item_list : any[]
    protected _item_height:number=36
    protected _item_num: number=0
    protected _item_show_num: number=7
    protected _root:cc.Node
    protected _offsetx:number=0
    protected _offsety:number=0
    protected _next_root:cc.Node
    protected _next_item_list:any[]
    protected _touch_began_y:number=0
    protected _need_update:boolean=false
    protected _prey:number=0
    protected _motion_a:number=0
    protected _motion_v:number=0
    protected _motion_s:number=0
    protected _motion_time:number=0
    protected _touch_state:number=-1 
    protected _touch_moved_y:number=0 
    protected _touch_ended_y:number=0 
    protected _touch_cancelled_y:number=0
    protected _touch_begin_time:number=0

    static create(items,width:number=0,showNum:number=7,item_height:number=36,isRecyle:boolean=true,scrollToIndex:number=0)
    {
        let lsl = <LoopScrollList>GHelper.createNode(LoopScrollList)
        lsl._items = items
        lsl._width = width
        
        lsl._isRecyle = isRecyle
        lsl._scrollToIndex = scrollToIndex

        lsl.node.width = width
        lsl._item_show_num = showNum
        lsl._item_height = item_height
        lsl.node.height = lsl._item_height * lsl._item_show_num

        return lsl
    }
    onLoad()
    {
        this.node.on(cc.Node.EventType.TOUCH_START, this._onTouchEvent, this)
        this.node.on(cc.Node.EventType.TOUCH_MOVE,this._onTouchEvent,this)
        this.node.on(cc.Node.EventType.TOUCH_END, this._onTouchEvent, this)
        this.node.on(cc.Node.EventType.TOUCH_CANCEL, this._onTouchEvent, this)
        //根据需要显示的数目来计算layer应该拥有的高度
        
        this._root = new cc.Node()
        this._root.parent = this.node

        //this.testInit()
        this._init_items(this._items)
        this._gotoFixedPos()
    }

    getHeight()                                                                                                                                                                                                                                                                    
    {
        return this._item_show_num * this._item_height
    }
    testInit()
    {
        let items = []
        core.foreach(24,function(i)
        {
            items.push({
                text : i + "点",
                value : i
            })
        })
        this._init_items(items)
    }
    protected _init_items(items)
    {
        let self = this
        if(!items)
        {
            return
        }
        let num = items.length
        if(num <= 0)
        {
            return
        }
        if(this._isRecyle == true)
        {
            this._next_root = new cc.Node()
            this._next_root.parent = this.node
            this._next_item_list = []
        }
        this._item_list = []
        core.foreach(num,function(i)
        {
            self._addItem(self._root, self._item_list, items[i].text, items[i].value)
            if(self._next_root)
            {
                self._addItem(self._next_root, self._next_item_list, items[i].text, items[i].value)
            }
        })
        this.initPos()
    }
    protected _addItem(node, list, text, value)
    {
        if(!text)
        {
            return
        }
        let ttfConfig = 
        { 
            fontFilePath:"fonts/FMBChina.ttf",
            fontSize:24
        }
        let text_item = <any>GHelper.createNode(cc.Label)
        text_item.node.color = cc.color(255, 255, 255,255)
        text_item.index = list.length
        text_item.value = value
        text_item.string = text
        list.push(text_item)
        text_item.node.parent = node
        text_item.node.x = this._offsetx
        text_item.node.y = - this._item_height * text_item.index + this._offsety
    }

    initPos()
    {
        let self = this
        let num = this._item_list.length
        let centerIndex = Math.ceil(num / 2)
        core.foreach(this._item_list,function(index, item){
            item.node.setPosition(cc.v2(self._offsetx, - self._item_height *(index - centerIndex) + self._offsety))
        })
        this._root.x = 0
        this._root.y = 0
        if(this._next_item_list)
        {
            core.foreach(this._next_item_list,function(index, item)
            {
                item.node.x = self._offsetx
                item.node.y = - self._item_height *(index - centerIndex) + self._offsety
            })
            this._next_root.x = 0
            //this._next_root.y = - this._item_height * num
            // 默认向下对齐
        }
        if(this._scrollToIndex>=0)
        {
            this._touch_began_y = 0
            this._prey = this._root.y
            this.onTouchEvent("touchmove",0,-(centerIndex-this._scrollToIndex)*this._item_height,true)
        }
    }

    protected _autoScroll(v)
    {
        // 速度，秒为单位
        this._motion_a = Math.abs(v / 1.5)
        this._need_update = true
        this._motion_v = v
        this._motion_time = GHelper.getServerTime()
        this._motion_s = 0
    }

    protected _gotoFixedPos()
    {
        let y = this._root.y
        let abs_y = Math.abs(y)
        abs_y = Math.floor(abs_y / this._item_height + 0.49) * this._item_height
        this._touch_began_y = y
        this._prey = y
        if(y < 0)
        {
            y = - abs_y
        }
        else
        {
            y = abs_y
        }
        this._prey = this._root.y
        this.onTouchEvent("touchmove", 0, y,true)
    }

    getSelectedValue()
    {
        let y = this._root.y
        let dtnum = Math.floor(Math.abs(y) / this._item_height + 0.49)
        if(y < 0)
        {
            dtnum = - dtnum
        }
        let num = this._item_list.length
        let centerIndex = Math.ceil(num / 2)
        let index = centerIndex + dtnum
        if(index >= num)
        {
            index = index - num
        }
        else if(index < 0)
        {
            index = index + num
        }
        return this._item_list[index].value
    }

// S=vt-(at^2)/2  匀减速运动的距离计算公式
    update(dt)
    {
        if(!this._need_update)
        {
            return
        }
        let v = Math.abs(this._motion_v)
        let t =(GHelper.getServerTime() - this._motion_time) / 1000
        let a = this._motion_a

        if(t <= 3)
        {
            a = this._motion_a
        }
        else if(t < 4)
        {
            a = this._motion_a / 4
        }
        else
        {
            a = this._motion_a / 8
        }

        let s = v * t - a * t * t / 2
        this._motion_s = s
        if(this._motion_v < 0)
        {
            // 向下减速运动
            s = - s
        }
        this._motion_time = GHelper.getServerTime()
        this._motion_s = 0
        this._touch_began_y = 0

        v = v - a * t
        if(v < 0)
        {
            this._gotoFixedPos()
            this._need_update = false
            return
        }
        if(this._motion_v > 0)
        {
            this._motion_v = v
        }
        else
        {
            this._motion_v = - v
        }
        this._prey = this._root.y
        this.onTouchEvent("touchmove", 0, s,true)
    }
    protected _onTouchEvent(event:cc.Event.EventTouch)
    {
        let pos = event.getLocation()
        this.onTouchEvent(event.type,pos.x,pos.y,false)
    }
    onTouchEvent(eventType, x?:number, y?:number,custom?:boolean)
    {
        let isCustom = custom || false
        if(!isCustom)
        {
            let pos = eventType.getLocation()
            y = pos.y
            x = pos.x
        }
        else
        {
            eventType = {type:eventType}
        }
        let posx = 0
        let posy = 0
        if(eventType == "touchstart")
        {
            this._need_update = false
            this._touch_state = 0
            this._touch_began_y = y
            this._prey = this._root.y
            this._touch_begin_time = GHelper.getServerTime()
            return true
        }
        else if(eventType == "touchmove")
        {
            this._touch_state = 1
            this._touch_moved_y = y
        }
        else if(eventType == "touchend")
        {
            this._touch_state = 2
            this._touch_ended_y = y
        }
        else if(eventType == "touchcancel")
        {
            this._touch_state = 3
            this._touch_cancelled_y = y
        }

        let dty = y - this._touch_began_y
        let rooty = this._prey + dty
        this._root.y = rooty
        if(this._next_root)
        {
            let num = this._item_list.length
            let centerIndex = Math.ceil(num / 2)
            let next_root_y = 0
            if(rooty >= 0)
            {
                // 向下对齐
                next_root_y = - this._item_height * num + rooty
            }
            else
            {
                // 向上对齐
                next_root_y = this._item_height * num + rooty
            }
            this._next_root.y = next_root_y
            // 纠正循环--产生交换操作
            if(rooty >= 0 && next_root_y >= 0// 都在上方
                || rooty < 0 && next_root_y < 0)
            {
                // 都在下方
                let temp_item_list = this._item_list
                this._item_list = this._next_item_list
                this._next_item_list = temp_item_list

                let temp_root = this._root
                this._root = this._next_root
                this._next_root = temp_root
                // 初始化数据，因为新的root已经产生
                this._touch_began_y = y
                this._prey = this._root.y
                // 重复刚才的事件操作，目的是实现真正的交换
                this.onTouchEvent(eventType, x, y,true)
            }
        }
        if(this._touch_state == 2 || this._touch_state == 3)
        {
            let dt = GHelper.getServerTime() - this._touch_begin_time
            let s = y - this._touch_began_y
            if(Math.abs(s) <= 5 || dt <= 0)
            {
                this._gotoFixedPos()
                return true
            }
            let v = s/(dt / 1000)
            // 计算速度用秒为单位
            if(v > 100)
            {
                this._autoScroll(v)
            }
            else if(v<-100)
            {
                this._autoScroll(v)
            }
            else if(v>0)
            {
                this._autoScroll(this._item_height-s)
            }
            else
            {
                this._autoScroll(-(this._item_height+s))
            }
        }
        if(!this._isRecyle)
        {
            let num = this._item_list.length
            let centerIndex = Math.ceil(num / 2)
            let minY = -(num - (centerIndex+1)) * this._item_height
            let maxY = centerIndex * this._item_height
            let curY = this._root.y
            if(curY >= minY && curY <= maxY)
            {
                return true
            }
            if(curY < minY)
            {
                curY = minY
            }
            if(curY > maxY)
            {
                curY = maxY
            }
            this._root.y = curY
            this._need_update = false
        }
        return true
    }
}