import { core } from "../../Framework/Core/Base"
import { GHelper } from "../../Framework/Logic/Helper"
import { GPageMgr } from "../../Framework/View/Page/PageManager"
import { AddressItem, GUserMgr } from "../../Logic/UserManager"
import { GHallHttp } from "../../Net/HallHttp"
import { BagItemItem, EBagItemState, GItemCfg, ItemItem } from "../../Service/ItemService"
import { GMsgCenter } from "../../utils/MessageCenter"
import { GAccountHttp } from '../../Net/AccountHttp';
import { GAppDM } from '../../DataModel/AppDataModel';

const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_serverListItem extends cc.Component {

    

    @property(cc.Label)
    lbl_title: cc.Label = null

    @property(cc.Label)
    lbl_host: cc.Label = null

    @property(cc.Label)
    lbl_port: cc.Label = null

   


    item_data = null;
    item_index = -1;

   
    onLoad()
    {
       
    }
    protected onDestroy(): void {
        
    }
    init(m_item_data:any,mIndex) 
    {
        this.item_index = mIndex;
        this.item_data = m_item_data;
        this.lbl_title.string = this.item_data.name;
        this.lbl_host.string = "host : " + this.item_data.host
        this.lbl_port.string = "port : " + this.item_data.port;
    }
    Btn_Connect_OnClick(event)
    {
        GAppDM.account_server={host:this.item_data.host,port:this.item_data.port};
         // 获取地址
         GAccountHttp.init("auth",GAppDM.account_server.host,GAppDM.account_server.port)
         GPageMgr.openPage("Page_Start")
         GPageMgr.closePage('Page_ServerList');
    }
}
