import { ECardType, ETableState, ETexasAction, GCardTypeStr } from "../../../Config/_ini_";
import { ECardSeatState } from "../../../DataModel/CardTableDataModel";
import { ETexasSeatState, GTexasTableDM, TexasRecordSeatInfo } from "../../../DataModel/TexasTableDataModel";
import { GHelper } from "../../../Framework/Logic/Helper";
import { GUserMgr } from "../../../Logic/UserManager";
import { resMgr } from "../../../manager/ResourceManager";
import { GMatchSer } from "../../../Service/MatchService";
import { HashMap } from "../../../utils/HashMap";
import Card from "./Card";
import { GameChipsMove } from "./GameChipsMove";

import { GSettingDM } from "../../../Framework/DataModel/SettingDataModel";
import { GAudioTool } from "../../../Framework/Logic/AudioTool";

const {ccclass, property} = cc._decorator;

const SEAT_POS = [
    cc.v3(0, -480),
    cc.v3(-300, -250), cc.v3(-300, 0), cc.v3(-300, 250),
    cc.v3(-130, 500), cc.v3(130, 500),
    cc.v3(300, 250), cc.v3(300, 0), cc.v3(300, -250)
]

export class CardInfo {
    id: number = 0;
    x: number = 0;
    y: number = 0;
    scale: number = 1;
    mark: number = 0;
    select: boolean = false;
    delay: number = 0;
}

export class SeatCardInfo {
    scale: number = 1;
    angle: number = 0;
    position: cc.Vec3 = cc.Vec3.ZERO;
}

@ccclass
export default class Seat extends cc.Component {

    /**
     * 仅仅自己的才用这个，别人的直接用筹码显示
     */
    @property(cc.Label)
    lbl_cardtype: cc.Label = null;

    @property(cc.Button)
    btnSit: cc.Button = null;

    @property(cc.Sprite)
    imgAvatar: cc.Sprite = null;

    @property(cc.Label)
    labelNickname: cc.Label = null;

    @property(cc.Node)
    nodeAllin: cc.Node = null;

    @property(cc.Node)
    nodeMark: cc.Node = null;

    @property(cc.Label)
    labelBet: cc.Label = null;

    @property(cc.Label)
    labelChip: cc.Label = null;

    @property(cc.Node)
    card_panel: cc.Node = null;

    @property(cc.Prefab)
    prefab_card: cc.Prefab = null;

    @property(cc.Node)
    bar_timer: cc.Node = null;

    @property(cc.Node)
    node_wait: cc.Node = null

    @property(cc.Node)
    node_fold: cc.Node = null

    @property(cc.Node)
    node_raise: cc.Node = null

    @property(cc.Node)
    node_call: cc.Node = null

    @property(cc.Node)
    node_allin: cc.Node = null

    @property(cc.Node)
    node_check: cc.Node = null

    @property(cc.Prefab)
    chipsMovePrefab: cc.Prefab = null

    @property(cc.Node)
    node_dealer_tip: cc.Node = null

    @property(cc.Node)
    node_gray: cc.Node = null

    @property(cc.Node)
    node_auto_tip: cc.Node = null

    @property(cc.Node)
    node_win_tip: cc.Node = null

    @property(Number)
    tableIdx: Number = 0

    public m_chipsMove: GameChipsMove = null

    public m_pkCards: Card[] = []
    public m_card_panel: cc.Node = null;

    protected _cardsInfoMap: HashMap<number, SeatCardInfo[]> = new HashMap();
    public realIndex: number;

    onLoad() {
        this.m_chipsMove = cc.instantiate(this.chipsMovePrefab).getComponent('GameChipsMove')
        this.node.addChild(this.m_chipsMove.node)
        this.m_chipsMove.setSeat(this)

        this.bar_timer.active = false;
        let sp_blink = this.bar_timer.parent.getChildByName("person").getChildByName("sp_blink")
        if (sp_blink!=null) {
            sp_blink.active = false;
        }
        if(this.lbl_cardtype)
        {
            this.lbl_cardtype.string=""
        }
        this.node_gray.active=false
        this.labelNickname.node.color = new cc.Color().fromHEX("#FFFFFF")
        this.node_auto_tip.active=false
        this.node_win_tip.active=false
        this._initCardsInfo()
        // this._initHandCards()
    }
    
    protected _table_id=-1
    protected _user_id=-1
    get userId()
    {
        return this._user_id
    }
    get seatIndex()
    {
        if(this.seatInfo)
        {
            return this.seatInfo.index
        }
        return -1
    }

    get seatInfo()
    {
        return GTexasTableDM.getSeatByUserId(this._table_id,this._user_id)
    }
    protected _initCardsInfo(): void {
        let card_panel: cc.Node = this.node.getChildByName("node_card");
        this.m_card_panel = card_panel
        this.m_card_panel.active = true
    
        let cardsInfo: SeatCardInfo[] = [];
        let childrenCount: number = card_panel.childrenCount;
        for (let i = 1; i < childrenCount + 1; i++) {
            let card: cc.Node = card_panel.getChildByName(`cardBg${i}`);
            if (card) {
                let t: SeatCardInfo = new SeatCardInfo();
                t.scale = card.scale;
                t.angle = card.angle;
                t.position.set(card.position);
                cardsInfo.push(t);
            }
        }
        this._cardsInfoMap.add(1, cardsInfo);
        // card_panel.removeFromParent(true);
        // card_panel.destroy();

        card_panel.removeAllChildren(true);
        // let cardsInfos: SeatCardInfo[] = this._cardsInfoMap.get(1);
        // if (!cardsInfos) return;
        for (let i = 0; i < cardsInfo.length; i++) {
            let t: Card = cc.instantiate(this.prefab_card).getComponent(Card);
            t.node.active = false;
            t.node.scale = cardsInfo[i].scale;
           
            t.node.angle = cardsInfo[i].angle;
            t.node.setPosition(cardsInfo[i].position);

            this.m_pkCards.push(t);
            card_panel.addChild(t.node, null, "pkcard" + i);
        }

      

        for (let i = 0; i < cardsInfo.length; i++) {
            card_panel.children[i].scale = cardsInfo[i].scale;
            card_panel.children[i].angle = cardsInfo[i].angle;
            card_panel.children[i].setPosition(cardsInfo[i].position);
            let t: Card =card_panel.children[i].getComponent(Card);
            t.clearContent()
            t.setFace(false)
        }
    }



    init(table_id:number,user_id:number, index:number) {
        // console.log('seat init ',table_id,user_id,index)
        this.realIndex = index;
        this.node_dealer_tip.active=false
        this._user_id=user_id
        this._table_id=table_id
        let seatinfo = this.seatInfo
        if(!seatinfo)
        {
            return null
        }

        let tableinfo = GTexasTableDM.getTableInfo(this._table_id)
        if (!tableinfo)
        {
            return null
        }

        if(seatinfo.action==ETexasAction.Fold || seatinfo.state==ETexasSeatState.Waiting || this._user_id==GUserMgr.self_user_id) {
            this.m_pkCards[0].node.active = false;
            this.m_pkCards[1].node.active = false;
        }
        else
        {
            this.m_pkCards[0].node.active = true;
            this.m_pkCards[1].node.active = true;
        }
        this.m_pkCards[0].node.parent.getComponent(cc.Mask).enabled = false;
        this.m_pkCards[1].node.parent.getComponent(cc.Mask).enabled = false;
        this.resetCardPos()
        this.resetCardSize()
        this.resetCardStye()
        this.btnSit.node.active=false
        this.labelNickname.node.active=true
        this.labelNickname.string=seatinfo.playerinfo.nickname

        if (seatinfo.playerinfo.logo=="") {
            let num = Math.floor(Math.random()*(16 - 1)) + 1
            let suffix: string = "image/head/" + num;
            let path = `${suffix}`;
            resMgr.loadImage(path, (res) => {
                cc.isValid(this.imgAvatar) && (this.imgAvatar.getComponent(cc.Sprite).spriteFrame = res);
            })
        }
        else
        {
            let suffix: string = "image/head/" + seatinfo.playerinfo.logo;
            let path = `${suffix}`;
            resMgr.loadImage(path, (res) => {
                cc.isValid(this.imgAvatar) && (this.imgAvatar.getComponent(cc.Sprite).spriteFrame = res);
            })
        }

        if(seatinfo.bet>0)
        {
            this.labelBet.node.parent.active=true
            this.labelBet.node.parent.getChildByName("bg").active = true;
            // this.labelBet.string = seatinfo.bet+""
            this.setLabelBetNum(seatinfo.bet)
        }
        else
        {
            this.labelBet.node.parent.active=false
            this.labelBet.node.parent.getChildByName("bg").active = false;
            this.labelBet.string = ""
        }
        this.node_auto_tip.active=seatinfo.auto
        this.labelChip.node.parent.active=true
        this.setLabelChipNum(seatinfo.playerinfo.point)
        this.refreshTip(true)
        this.showTime()

        if (seatinfo.cards&&seatinfo.cards.length>0) {
            if (tableinfo.turn_user_id==GUserMgr.self_user_id&&this._user_id==tableinfo.turn_user_id) {
                this.showSeatCard(true)      
            }
            else
            {
                if (!this.getCard(0).img_num.node.active) {
                    this.showSeatCard(false)
                }
                else
                {
                    this.getCard(0).node.active=true
                    this.getCard(1).node.active=true

                    let bet = tableinfo.curmaxbet-seatinfo.bet
                    if (seatinfo.state==ETexasSeatState.AllIn) {
                        //小盲位 大盲位 筹码不够 自动allin 翻牌
                        //自己的翻牌
                        // this.getCard(0).setContent(seatinfo.cards[0])
                        // this.getCard(1).setContent(seatinfo.cards[1])
                        
                        // this.resetCardSize()
                        // this.resetCardPos()
                        this.showSeatCard(true)
                    }
                    
                    else if (tableinfo.cards.length < 3) {
                        let matchInsM = GMatchSer.getMatchInsById(tableinfo.match_insid)
                        let bm = GMatchSer.getBlindItem(matchInsM.match.blind_id,tableinfo.blind_index)

                        console.log('blind check 1',seatinfo.index,tableinfo.big_blind_index,seatinfo.action,seatinfo.bet,bm.big_blind)
                        console.log('blind check 2',seatinfo.index,tableinfo.small_blind_index,seatinfo.action,seatinfo.bet,bm.small_blind)


                        if(seatinfo.index==tableinfo.big_blind_index&&seatinfo.action==ETexasAction.Call&&seatinfo.bet==bm.big_blind)
                        {
                            this.showSeatCard(false)
                        }else if(seatinfo.index==tableinfo.small_blind_index&&seatinfo.action==ETexasAction.Call&&seatinfo.bet==bm.small_blind)
                        {
                            this.showSeatCard(false)
                        }
                        else if (seatinfo.action!=0) {
                            this.showSeatCard(true)
                        }
                        else
                        {
                            this.showSeatCard(false)
                        }
                    }
                    else
                    {
                      this.showSeatCard(true)
                    }
                }
            }
        }

        else if (seatinfo.state==ETexasSeatState.Waiting || seatinfo.state==ETexasSeatState.Preparing || seatinfo.state==ETexasSeatState.Fold) {
            this.m_pkCards[0].node.active = false;
            this.m_pkCards[1].node.active = false;
        }
        else
        {
            this.m_pkCards[0].node.active = true;
            this.m_pkCards[0].updateCardBack()
            this.m_pkCards[0].setFace(false)

            this.m_pkCards[1].node.active = true;
            this.m_pkCards[1].updateCardBack()
            this.m_pkCards[1].setFace(false)
        }

        this.showCardType()
        if(seatinfo.action==ETexasAction.Fold)
        {
            this.labelNickname.node.color = new cc.Color().fromHEX("#C1AFAF")
            this.node_gray.active=true
        }
    }
    
    showTime()
    {
        let seatinfo = this.seatInfo
        let tableinfo = GTexasTableDM.getTableInfo(this._table_id)
        if (tableinfo.turn_user_id == seatinfo.playerinfo.id) {
            let lefttime = Math.floor((tableinfo.timeout-GHelper.getServerTime())/1000)
            if (lefttime<=0)
            {
                return
            }
            this.startTimeCutDown(lefttime);
        }
    }

    refreshTip(force=false,clear=false)
    {
        let seatinfo = this.seatInfo
        if(!seatinfo)
        {
            return null
        }
        this.node_wait.active=false
        this.node_fold.active=false
        this.node_fold.stopAllActions()
        this.node_raise.active=false
        this.node_call.active=false
        this.node_call.stopAllActions()
        this.node_allin.active=false
        this.node_check.active=false
        this.node_check.stopAllActions()
        this.node_gray.active = false
        if(seatinfo.action==ETexasAction.Allin)
        {
            this.node_allin.active=true
        }
        if(seatinfo.state==ETexasSeatState.Waiting)
        {
            this.node_gray.active = true;
            this.node_wait.active=true
            return
        }
        if(clear)
        {
            return
        }
        if(seatinfo.action==ETexasAction.Call&&!force)
        {
            this.node_call.active=true
            this.node_call.runAction(cc.sequence(cc.delayTime(3),cc.callFunc(()=>
            {
                this.node_call.active=false
            })))
        }
        else if(seatinfo.action==ETexasAction.Check&&!force)
        {
            this.node_check.active=true
            this.node_check.runAction(cc.sequence(cc.delayTime(3),cc.callFunc(()=>
            {
                this.node_check.active=false
            })))
        }
        else if(seatinfo.action==ETexasAction.Fold&&!force)
        {
            this.node_fold.active=true
            this.node_fold.runAction(cc.sequence(cc.delayTime(3),cc.callFunc(()=>
            {
                this.node_fold.active=false
            })))
        }
        else if(seatinfo.action==ETexasAction.Raise&&!force)
        {
            this.node_raise.active=true
        }
    }

    onGameStart()
    {
        this.clearTable()
        let seatinfo = this.seatInfo
        if(!seatinfo)
        {
            this.node.active = false
            return
        }

        if(seatinfo.bet>0)
        {
            this.labelBet.node.parent.active=true
            this.labelBet.node.parent.getChildByName("bg").active = true;
            // this.labelBet.string = seatinfo.bet+""
            this.setLabelBetNum(seatinfo.bet)
        }
        else
        {
            this.labelBet.node.parent.active=false
            this.labelBet.node.parent.getChildByName("bg").active = false;
        }

    }
    /**
     * 清理 座位 对于的 桌面显示
     */
    clearTable() {
        this.stopTimeCutDown()
        if(this.lbl_cardtype)     //  牌型展示 移除
        {
            this.lbl_cardtype.string=""
        }
        this.node_allin.active=false // all in 展示 移除
        this.node_win_tip.stopAllActions()
        this.node_win_tip.active=false
        this.node_gray.active=false
        this.labelNickname.node.color = new cc.Color().fromHEX("#FFFFFF")
        this.node_dealer_tip.active=false   // 发牌人 d 标志 移除
        let seatinfo = this.seatInfo
        cc.find('showCard',this.node).active = false

        if(!seatinfo)
        {
            return null
        }
        this.m_pkCards[0].setFace(false)
        this.m_pkCards[1].setFace(false)
        

        this.m_pkCards[0].node.active = false;
        this.m_pkCards[1].node.active = false;

        this.m_pkCards[0].node.parent.getComponent(cc.Mask).enabled = false;
        this.m_pkCards[1].node.parent.getComponent(cc.Mask).enabled = false;

        this.resetCardSize()
        this.resetCardPos()

        this.m_pkCards[0].setCardBg(cc.sys.localStorage.getItem("setting_cardbg_style") || "1")
        this.m_pkCards[1].setCardBg(cc.sys.localStorage.getItem("setting_cardbg_style") || "1")

        this.m_pkCards[0].clearContent();
        this.m_pkCards[1].clearContent();

        this.btnSit.node.active=false
        this.labelNickname.node.active=true
        this.labelNickname.string=seatinfo.playerinfo.nickname
        if(seatinfo.bet>0)
        {
            this.labelBet.node.parent.active=true
            this.labelBet.node.parent.getChildByName("bg").active = true;
            // this.labelBet.string = seatinfo.bet+""
            this.setLabelBetNum(seatinfo.bet)
        }
        else
        {
            this.labelBet.node.parent.active = false
            this.labelBet.node.parent.getChildByName("bg").active = false;
        }
        this.labelChip.node.parent.active=true
        this.setLabelChipNum( seatinfo.playerinfo.point)// 刷新用户目前持有的点数
        this.refreshTip(true,true)  // 清理桌面座位状态 显示 

   
    }

    onBeginBet()
    {
        this.showTime()
        this.node_raise.active=false
    }

    onAuto()
    {
        this.node_auto_tip.active=this.seatInfo.auto
    }

    onBet(bet:number)
    {
        this.stopTimeCutDown();
        let seatinfo = this.seatInfo
        // this.labelBet.string = seatinfo.bet+""
        this.setLabelBetNum(seatinfo.bet)
        
     
       
        // this.labelChip.string = seatinfo.playerinfo.point+""
        this.setLabelChipNum( seatinfo.playerinfo.point)
        this.refreshTip()

        if (bet > 0) {
            let tempPos = new cc.Vec2(0, 0);
            this.labelBet.node.parent.convertToWorldSpaceAR(this.labelBet.node.parent.getChildByName("bg").getPosition(), tempPos)
            this.m_chipsMove.MoveOut(false, tempPos, new cc.Vec2(0, 0), 0);
        }

        if (bet == 0) {
            this.labelBet.node.parent.active=false
            this.labelBet.string = ""
        }

        if(bet<0)
        {
            this.node_gray.active=true
            this.labelNickname.node.color = new cc.Color().fromHEX("#C1AFAF")
        }
    }

    onShow() {
        //eventMgr.addListener(consts.EVENT.SHOW_MOVE_SEAT, this.showMove, this);
    }

    public getCard(cardIndex: number): Card {
        let t: Card = null;
        if (cardIndex >= 0 && cardIndex < 2) {
            t = this.m_pkCards[cardIndex];
        }
        return t;
    }

    updateLabel() {
        
    }

    onClickSeat() {
    }

    showMove(time: number = 0.5, worldPos = null) {
        if (this._user_id==-1) {
            return;
        }

        let index = (this.seatIndex + 9 - 0) % 9;
        let pos = SEAT_POS[index];

        cc.Tween.stopAllByTarget(this.node);
        cc.tween(this.node)
        .to(time, {x: pos.x, y: pos.y})
        .call(() => {
            this.updatePosition(worldPos);
        })
        .start();
    }

    updatePosition(worldPos) {
    }

    startTimeCutDown(time) {
        let sp_blink = this.bar_timer.parent.getChildByName("person").getChildByName("sp_blink")
        if (sp_blink!=null) {
            sp_blink.active = true;
            sp_blink?.runAction(cc.repeatForever(cc.blink(1, 1)))
        }
        let allTime = time;
        let startOsTime = Date.now();
        this.bar_timer.getChildByName("label_timer").getComponent(cc.Label).string = ""//allTime;
        let scheduleCallback = ()=>{
            let nowOsTime = Date.now();
            let nowTime = allTime - Math.floor((nowOsTime - startOsTime)/1000);
            if (nowTime <= 0) {
                this.stopTimeCutDown();
            }
            this.bar_timer.getChildByName("label_timer").getComponent(cc.Label).string = ""//nowTime+""
            this.bar_timer.getComponent(cc.ProgressBar).progress = nowTime/allTime;
        }
        scheduleCallback()
        this.bar_timer.active = true;
        this.bar_timer.zIndex=9999
        this.bar_timer.getChildByName("label_timer").getComponent(cc.Label).schedule(scheduleCallback, 1);
    }
    /**
     * 移除座位倒计时
     */
    stopTimeCutDown() {
        let sp_blink = this.bar_timer.parent.getChildByName("person").getChildByName("sp_blink")
        if (sp_blink!=null) {
            sp_blink.stopAllActions();
            sp_blink.active = false;
        }
        this.bar_timer.getChildByName("label_timer").getComponent(cc.Label).unscheduleAllCallbacks();
        this.bar_timer.active = false;
    }

    showChips() {

    }

    ShakeSeat() {

    }
    showCardType(cardtype?:ECardType)
    {
        if(this.lbl_cardtype)
        {
            this.lbl_cardtype.string=""
        }
        let si = this.seatInfo
        if(!si)
        {
            return
        }
        cardtype=cardtype||si.cardtype||ECardType.None
        if(cardtype<=0)
        {
            return
        }
        let str = GCardTypeStr[cardtype]||""
        if(this.lbl_cardtype)
        {
            this.lbl_cardtype.string=str
        }
        else if(str)
        {
            // this.labelChip.string=str
             this.setLabelChipNum(str)
        }
    }

    hideChips() {
        this.labelBet.node.parent.active = true
        this.labelBet.node.parent.getChildByName("bg").active = true;
    }


    
    protected _recordseatinfo:TexasRecordSeatInfo=null
    onGameResult(recordseatinfo:TexasRecordSeatInfo)
    {

        if(recordseatinfo.dtpoint>0 && this.seatInfo.action==ETexasAction.Allin && GUserMgr.self_user_id==recordseatinfo.playerinfo.id)
        {
            GAudioTool.playEffect("Common/Audio/Effect/zdpk_win2")
        }
        // 修复 牌桌上下注筹码不消失问题 
        this.seatInfo.bet = 0;
        this.seatInfo.action = ETexasAction.None;
        //面前筹码清空
        this.labelBet.node.parent.active=false
        this.labelBet.string = ""

        this._recordseatinfo=recordseatinfo
        if(!recordseatinfo)
        {
            return
        }
        this.refreshTip(false,true)
        if(recordseatinfo.dtpoint>0)
        {
            // this.labelChip.string = (recordseatinfo.dtpoint + this.seatInfo.playerinfo.point).toString()
            this.setLabelChipNum(recordseatinfo.dtpoint + this.seatInfo.playerinfo.point)
            this.node_win_tip.active=true
            this.node_allin.active = false
            this.node_win_tip.runAction(cc.sequence(cc.delayTime(3.5),cc.callFunc(()=>
            {
                this.node_win_tip.active=false
            })))
            
        }

        if (GUserMgr.self_user_id==recordseatinfo.playerinfo.id) {
            if (this.getCard(0).imgBack.active) {
                this.m_pkCards[0].img_num.node.setContentSize(new cc.Size(105, 159));
                this.m_pkCards[1].img_num.node.setContentSize(new cc.Size(105, 159));

                this.getCard(0).setContent(recordseatinfo.cards[0])
                this.getCard(1).setContent(recordseatinfo.cards[1])
                this.getCard(0).turn(0, false)
                this.getCard(1).turn(0, false)
            }
            return
        }

        this.getCard(0).node.active = false
        this.getCard(1).node.active = false

        if (!recordseatinfo.cards) {
            return
        }

        if(recordseatinfo.showcards==3)
        {
            this.showCardType(recordseatinfo.cardtype)
        }
        else
        {
            return
        }
        

        this.m_card_panel.active = true
        this.m_card_panel.setPosition(cc.Vec2.ZERO)
        this.getCard(0).node.active = true
        this.getCard(1).node.active = true
        this.getCard(0).setContent(recordseatinfo.cards[0])
        this.getCard(1).setContent(recordseatinfo.cards[1])
        this.getCard(0).turn(0, true)
        this.getCard(1).turn(0, true)
        this._preshowcards=0
    }
    protected _preshowcards=0
    onShowCards(showcards:number)
    {
        if(!this._recordseatinfo)
        {
            return
        }
        var showCardNode =  cc.find('showCard',this.node)
        showCardNode.active = true
        
        if(this._user_id==GUserMgr.self_user_id)
        {
            if(showcards==1)
            {
                this.getCard(0).setColor(cc.Color.WHITE)
            }
            if(showcards==2)
            {
                this.getCard(1).setColor(cc.Color.WHITE)
            }
            if(showcards==3)
            {
                this.getCard(0).setColor(cc.Color.WHITE)
                this.getCard(1).setColor(cc.Color.WHITE)
            }
            return
        }
        if(this._preshowcards>0)
        {
            showcards=3
        }

        this.getCard(0).node.parent.getComponent(cc.Mask).enabled = false
        this.getCard(0).node.setPosition(cc.Vec2.ZERO)
        this.getCard(1).node.setPosition(cc.Vec2.ZERO)

        this.resetCardPos()
        this.resetCardSize()

        cc.error("shownum=",showcards)

        GAudioTool.playEffect("Common/Audio/Effect/zdpk_show_card")
        
        if(showcards==1)
        {
            this.getCard(0).node.active = true
            this.getCard(0).setContent(this._recordseatinfo.cards[0])
            this.getCard(0).turn(0, true)
            this.getCard(1).node.active = true
            this.getCard(1).updateCardBack()
        }
        if(showcards==2)
        {
            
            this.getCard(0).node.active = true
            this.getCard(0).updateCardBack()
            this.getCard(1).node.active = true
            this.getCard(1).setContent(this._recordseatinfo.cards[1])
            this.getCard(1).turn(0, true)
        }
        if(showcards==3)
        {
            
            if(this._preshowcards!=1)
            {
                this.getCard(0).node.active = true
                this.getCard(0).setContent(this._recordseatinfo.cards[0])
                this.getCard(0).turn(0, true)
            }
            if(this._preshowcards!=2)
            {
                this.getCard(1).node.active = true
                this.getCard(1).setContent(this._recordseatinfo.cards[1])
                this.getCard(1).turn(0, true)
            }
            this.showCardType(this._recordseatinfo.cardtype)
        }
    }

    // 显示座位上的卡牌
    showSeatCard(isShow){
        if(isShow){
            this.resetCardSize()
            this.resetCardPos()
            this.getCard(0).node.active=true
            this.getCard(1).node.active=true


            this.getCard(0).setContent(this.seatInfo.cards[0])
            this.getCard(1).setContent(this.seatInfo.cards[1])
            this.getCard(0).setFace(true)
            this.getCard(1).setFace(true)
        }else{
            this.getCard(0).setFace(false)
            this.getCard(1).setFace(false)
        }
    }

    resetCardPos(){
        if (this.realIndex==0) {
            this.m_pkCards[0].node.setPosition(new cc.Vec2(-29.736, -3.691));
            this.m_pkCards[1].node.setPosition(new cc.Vec2(39.721, -3.05));
        }
        else
        {
            this.m_pkCards[0].node.setPosition(new cc.Vec2(-13.496, -13.161));
            this.m_pkCards[1].node.setPosition(new cc.Vec2(11.586, -14.879));
        }
    }
    resetCardSize(){
        if (this.realIndex==0) {
            this.m_pkCards[0].imgBack.setContentSize(new cc.Size(105, 159));
            this.m_pkCards[1].imgBack.setContentSize(new cc.Size(105, 159));
            this.m_pkCards[0].imgBack.setContentSize(new cc.Size(105, 159));
            this.m_pkCards[1].imgBack.setContentSize(new cc.Size(105, 159));
            this.m_pkCards[0].img_num.node.setContentSize(new cc.Size(105, 159));
            this.m_pkCards[1].img_num.node.setContentSize(new cc.Size(105, 159));
        }
        else
        {
            this.m_pkCards[0].imgBack.setContentSize(new cc.Size(66, 100));
            this.m_pkCards[1].imgBack.setContentSize(new cc.Size(66, 100));
            this.m_pkCards[0].imgBack.setContentSize(new cc.Size(66, 100));
            this.m_pkCards[1].imgBack.setContentSize(new cc.Size(66, 100));
            this.m_pkCards[0].img_num.node.setContentSize(new cc.Size(66, 100));
            this.m_pkCards[1].img_num.node.setContentSize(new cc.Size(66, 100));
        }
    }
    resetCardStye(){
        this.m_pkCards[0].setCardBg(cc.sys.localStorage.getItem("setting_cardbg_style") || "1")
        this.m_pkCards[1].setCardBg(cc.sys.localStorage.getItem("setting_cardbg_style") || "1")
    }





    //所有人都allin了，就提前show牌
    onAllAllInShow(cards:number[])
    {
        if(this._user_id==GUserMgr.self_user_id)
        {
            this.showSeatCard(true)
            return
        }
        this.refreshTip(false,true)
        this.seatInfo.cards=cards
        this.resetCardPos()
        this.getCard(0).node.active = true
        this.getCard(0).setContent(cards[0])
        this.getCard(0).setFace(false)
        this.getCard(0).turn(0, true)

        this.getCard(1).node.active = true
        this.getCard(1).setContent(cards[1])
        this.getCard(1).setFace(false)
        this.getCard(1).turn(0, true)
    }
    onIdle()
    {
        this.clearTable()
        this.labelBet.node.parent.active = false
    }
    onStandUp()
    {
        this.node.active = false
        this.clearTable()
        this._user_id = -1
    }


    private lastChipNum = 0
    private lastBetNum = 0

    setLabelChipNum(chipNum){
        if(chipNum==''){
            chipNum = 0
        }
        chipNum = Number(chipNum)
        // console.log(chipNum,this.getFormatNum(chipNum))
        this.labelChip.string = this.getFormatNum(chipNum)
        this.lastChipNum = chipNum
    }

    setLabelBetNum(betNum){
        if(betNum==''){
            betNum = 0
        }
        betNum = Number(betNum)
        this.lastBetNum = betNum

        this.labelBet.string = this.getFormatNum(betNum)
    }


    getFormatNum(num):string{
        // bb保留两位小数
        let bigBland = this.node.parent.parent.getComponent('Page_Table')['curBig_blind']
        // console.log('getFormatNum',bigBland)
        if(!GSettingDM.bbOn || bigBland == -1){
            return num.toString()
        }
        

        let bb = Number((num/bigBland).toFixed(2))+' BB'
        return bb
    }


    refreshNumber(){
        this.setLabelChipNum(this.lastChipNum)
        this.setLabelBetNum(this.lastBetNum)
    }


  

}
