import {utils} from "../utils/Utils";
import { BaseAd } from "./BaseAd";

class TTAd extends BaseAd {
    private bannerAd: wx.BannerAd = null;

    constructor() {
        super("byte");
    }

    private getUserInfo(success?: Function, fail?: Function) {
        tt.getSetting({
            success: () => {
                tt.getUserInfo({
                    success: (res) => {
                        success && success(res.userInfo);
                    },
                    fail: () => {
                        fail && fail();
                    }
                })
            },
            fail: () => {
                tt.authorize({
                    scope: "userInfo",
                    success: (data) => {
                        tt.getUserInfo({
                            success: (res) => {
                                success && success(res.userInfo);
                            },
                            fail: () => {
                                fail && fail();
                            }
                        })
                    },
                    fail: () => {
                        fail && fail();
                    }
                })
            }
        });
    }

    login(success?: Function, fail?: Function) {
        tt.login({
            force: true,
            success: (res) => {
                let code = res.code;
                tt.getUserInfo({
                    success: (res) => {
                        if (res.userInfo) {
                            success && success({
                                nickname: res.userInfo.nickName,
                                avatar: res.userInfo.avatarUrl,
                                gender: res.userInfo.gender,
                                platform: this.platform,
                                code: code
                            })
                        }
                        success && success({

                        })
                    },
                    fail: (err) => {
                        fail && fail();
                    }
                })
            },
            fail: () => {
                fail && fail();
                console.log(`login调用失败`);
            }
        })
    }

    share() {

    }

    createBannerAd() {
        if (!this.banner_id) {
            return;
        }

        if (!this.bannerAd) {
            let sysInfo = tt.getSystemInfoSync();
            let width = 150;
            this.bannerAd = tt.createBannerAd({
                adUnitId: this.banner_id,
                style: {
                    width: width,
                    top: sysInfo.screenHeight - width * 0.4,
                    left: sysInfo.screenWidth / 2 - width / 2
                }
            });

            if (!this.bannerAd) {
                return;
            }

            this.bannerAd.onResize((size) => {
                this.bannerAd.style.top = sysInfo.screenHeight - size.height;
                this.bannerAd.style.left = sysInfo.screenWidth / 2 - size.width / 2;
            })

            this.bannerAd.show();
            this.bannerAd.onLoad(() => {
                console.log("Banner广告加载成功");
            });
            this.bannerAd.onError(() => {
                console.log("Banner广告加载失败");
            });
        }
    }

    destroyBannerAd() {
        if (this.bannerAd) {
            this.bannerAd.hide();
            this.bannerAd.destroy();
            this.bannerAd = null;
        }
    }

    createVideoAd(success?: Function, fail?: Function) {
        if (!this.video_id) {
            success && success();
            return;
        }

        let videoAd = tt.createRewardedVideoAd({
            adUnitId: this.video_id
        });

        if (!videoAd) {
            success && success();
            return;
        }

        videoAd.show()
        .then()
        .catch(() => {
            //重试
            videoAd.load()
            .then(() => {
                videoAd.show();
            })
            .catch((res) => {
                console.error(`激励视频广告播放失败！` + JSON.stringify(res));
            });
        });

        let on_error = (res) => {
            console.error(`激励视频广告播放失败！` + JSON.stringify(res));
            videoAd && videoAd.offError(on_error);
        }
        let on_close = (res) => {
            if (res && res.isEnded) {
                success && success();
            } else {
                fail && fail();
            }
            
            videoAd && videoAd.offClose(on_close);
        }
        
        videoAd.onError(on_error);
        videoAd.onClose(on_close);
    }

    createInterstitialAd(callback?: Function) {
        if (!this.interstitial_id) {
            callback && callback();
            return;
        }
        
        let interstitialAd = tt.createInterstitialAd({
            adUnitId: this.interstitial_id
        });

        if (!interstitialAd) {
            callback && callback();
            return;
        }

        interstitialAd.show();
        interstitialAd.onLoad(() => {
            console.log('插屏广告加载成功');
        });
        interstitialAd.onError(() => {
            console.log('插屏广告加载失败');
        });
        interstitialAd.onClose(() => {
            console.log('插屏广告关闭');
            callback && callback();
        })
    }
    
    createNativeAd(success?: Function, fail?: Function) {
        if (!this.native_id) {
            success && success();
            return;
        }
    }
    
    reportAdShow(id: string) {

    }
    
    reportAdClick(id: string) {

    }
}

export const ttAd: TTAd = new TTAd();