import { GTexasTableDM, TexasRecordTableInfo } from "../../DataModel/TexasTableDataModel";
import { GMatchSer } from "../../Service/MatchService";
import ListView from "../../utils/ListView";
import ReViewItem from "./ReViewItem";
const {ccclass, property} = cc._decorator;
@ccclass
export default class ReView extends cc.Component {
    @property(cc.ScrollView) data_scrollview: cc.ScrollView = null;
    @property(cc.Button) frontBtn: cc.Button = null;
    @property(cc.Button) backBtn: cc.Button = null;
    @property(cc.Button) prevBtn: cc.Button = null;
    @property(cc.Button) nextBtn: cc.Button = null;
    @property(cc.Label) cur: cc.Label = null;
    @property(cc.Label) number: cc.Label = null;
    @property(cc.Label) time: cc.Label = null;
    private index = -1;
    
    /**
     * 适配 widget(当前帧立即生效)
     * @param node          要适配的节点
     * @param bTransChild   是否遍历适配子节点(默认 false)
     */
    adaptWidget(node: cc.Node, bTransChild?: boolean): void {
        if (!node) return;

        let widget: cc.Widget = node.getComponent(cc.Widget);
        if (widget && cc.isValid(widget, true)) {
            widget.enabled = true;
            widget.updateAlignment();
            widget.enabled = false;
        }

        if (bTransChild) {
            for (let i = 0; i < node.children.length; ++i) {
                this.adaptWidget(node.children[i], bTransChild);
            }
        }
    }

    onLoad() {
        this.adaptWidget(this.node, true)
    }

    convertReviewTime(time)
    {
        time = parseInt(time)
        let dt = new Date(time)
        let now = new Date()
        return dt.getFullYear()+"-"+(dt.getMonth()+1)+"-"+dt.getDate()+" "+dt.getHours()+":"+dt.getMinutes()
    }

    onReviewData() {
        //拿最新的
        let records:TexasRecordTableInfo[] = GMatchSer.getMatchRecords(this.node.parent.getComponent('Page').tableInfo.match_insid)
        if(records==undefined || records.length==0)
        {
            return
        }
        this.index = records.length - 1;
        this.cur.string = records.length + "/" + records.length;
        let data = records[records.length-1]
        this.number.string = "牌局编号:" + data.id;
        this.time.string = this.convertReviewTime(data.end_time);
        let dataList = []
        for (const key in data.userrecords) {
            dataList.push({type: 0, publiccards: data.cards, userrecord: data.userrecords[key]})
        }
        // this.cur.string = ""
        // let dataList = []
        // for (let index = 0; index < 30; index++) {
        //     dataList.push({type: 0, data: {name: "abc", card0: 2, card1: 3, card2: 5, card3: 12, card4: 15, card5: 18, card6: 23}})   
        // }
        this.data_scrollview.getComponent(ListView).init(this.bindcallfunc.bind(this), this.getItemType.bind(this));
        this.data_scrollview.getComponent(ListView).notifyDataSetChanged(dataList)
    }

    public getItemType(data) {
        return data.type
    }

    public bindcallfunc(node: cc.Node, info, i) {
        node.getComponent(ReViewItem).setdata(info)
    }

    onClickFirstPage() {
        this.index = 0;
        let records:TexasRecordTableInfo[] = GMatchSer.getMatchRecords(this.node.parent.getComponent('Page').tableInfo.match_insid)
        let data = records[0]
        if(data == null){
            return
        }

        this.number.string = "牌局编号:" + data.id;
        this.time.string = this.convertReviewTime(data.end_time);

        let dataList = []
        this.cur.string = "1/" + records.length;
        for (const key in data.userrecords) {
            dataList.push({type: 0, publiccards: data.cards, userrecord: data.userrecords[key]})
        }
        this.data_scrollview.getComponent(ListView).notifyDataSetChanged(dataList)
    }

    onClickEndPage() {
        let records:TexasRecordTableInfo[] = GMatchSer.getMatchRecords(this.node.parent.getComponent('Page').tableInfo.match_insid)
        let data = records[records.length-1]
        if(data == null){
            return
        }
        
        this.number.string = "牌局编号:" + data.id;
        this.time.string = this.convertReviewTime(data.end_time);

        this.index = records.length-1
        this.cur.string = records.length + "/" + records.length;
        let dataList = []
        for (const key in data.userrecords) {
            dataList.push({type: 0, publiccards: data.cards, userrecord: data.userrecords[key]})
        }
        this.data_scrollview.getComponent(ListView).notifyDataSetChanged(dataList)
    }

    onClickPrevPage() {
        let records:TexasRecordTableInfo[] = GMatchSer.getMatchRecords(this.node.parent.getComponent('Page').tableInfo.match_insid)
        let curIndex = this.index - 1
        if (curIndex >= 0 && records[curIndex]!=null) {
            this.index = curIndex;
            let data = records[curIndex];
            
            this.number.string = "牌局编号:" + data.id;
            this.time.string = this.convertReviewTime(data.end_time);

            let cur = curIndex + 1;
            this.cur.string = cur + "/" + records.length;
            let dataList = []
            for (const key in data.userrecords) {
                dataList.push({type: 0, publiccards: data.cards, userrecord: data.userrecords[key]})
            }
            this.data_scrollview.getComponent(ListView).notifyDataSetChanged(dataList)
        }
    }

    onClickNextPage() {
        let records:TexasRecordTableInfo[] = GMatchSer.getMatchRecords(this.node.parent.getComponent('Page').tableInfo.match_insid)
        let curIndex = this.index + 1;
        if (curIndex >= 0 && records[curIndex]!=null) {
            this.index = curIndex;
            let data = records[curIndex];

            this.number.string = "牌局编号:" + data.id;
            this.time.string = this.convertReviewTime(data.end_time);

            let dataList = []
            let cur = curIndex + 1;
            this.cur.string = cur + "/" + records.length;
            for (const key in data.userrecords) {
                dataList.push({type: 0, publiccards: data.cards, userrecord: data.userrecords[key]})
            }
            this.data_scrollview.getComponent(ListView).notifyDataSetChanged(dataList)
        }
    }

    onClickClose() {
        //this.node.active = false;

        cc.Tween.stopAllByTarget(this.node);
        cc.tween(this.node)
        .to(0.2, {x: cc.winSize.width})
        .call(() => {
            
        })
        .start();
    }
}
