import { GGameDM } from "../../Framework/DataModel/GameDataModel";
import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import ListView from "../../utils/ListView";
import { GMsgCenter } from "../../utils/MessageCenter";
import Widget_CupDetailItem from "../Widget/Widget_CupDetailItem";

const {ccclass, property} = cc._decorator;

enum fialterRule{
    'week'=0,
    'day'=1
}

@ccclass
export default class Page_CupDetail extends Page {
    @property(cc.Node)
    nodeDetail: cc.Node = null;


    @property(cc.Label)
    titleDetail: cc.Label = null;

    
    @property(cc.Node)
    bootomChoice: cc.Node = null;

    private isBootomShowIng = false
    private hideY = null
    private isBootonAnimalRuning = false
    private mFialterRule = fialterRule.week
    private allBootomBts = []
    onLoad() {
    }

    detailInfo() {
    }

    public getItemType1(data) {
        return 0
    }

    public bindcallfunc1(node: cc.Node, info, i) {
        node.getComponent(Widget_CupDetailItem).setdata(info)
    }

    start() {
        // let dataList = []
        // dataList.push({type: 0, clubdata: {}})
        // dataList.push({type: 0, clubdata: {}})
        // dataList.push({type: 0, clubdata: {}})
       
        this.allBootomBts= [
            cc.find('choice1',this.bootomChoice),
            cc.find('choice2',this.bootomChoice)
        ]
        this.setChoice(this.allBootomBts,this.allBootomBts[0])
    }
    initDetailData(detailData,clubName){
        this.titleDetail.string = clubName+"赛事";
        this.nodeDetail.getComponent(ListView).init(this.bindcallfunc1.bind(this), this.getItemType1.bind(this));
        this.nodeDetail.getComponent(ListView).notifyDataSetChanged(detailData)
    }

    protected onDestroy(): void {
    }

    onClickBack() {
        GMsgCenter.send("detailBack");
    }

    
    showBootomChoiceBtn(event,eventData){
        if(this.isBootonAnimalRuning){
            return
        }
        this.isBootonAnimalRuning = true

        var height = 294
        if(!this.hideY){
            this.hideY =  this.bootomChoice.y
        }
        let showY = this.hideY+height
      
        // 
        if(eventData=="hide" || this.isBootomShowIng == true){
            this.isBootomShowIng = false
            this.bootomChoice.runAction(cc.sequence(
                cc.moveTo(0.2,cc.v2(this.bootomChoice.x,this.hideY)),
                cc.callFunc(()=>{
                    this.bootomChoice.y = this.hideY
                    this.isBootonAnimalRuning = false
                })
            ))
        }else{
            this.isBootomShowIng = true
            this.bootomChoice.runAction(cc.sequence(
                cc.moveTo(0.2,cc.v2(this.bootomChoice.x,showY)),
                cc.callFunc(()=>{
                    this.bootomChoice.y = showY
                    this.isBootonAnimalRuning = false
                })
            ))
        }
    }

    protected onDisable(): void {
        this.showBootomChoiceBtn(null,'hide')
    }

    onDateChoice(event){
        let choiceIdx =  this.allBootomBts.indexOf(event.target)
        this.mFialterRule = choiceIdx

        this.setChoice(this.allBootomBts,event.target)
    }

    setChoice(btNodes,choiceNode){
        console.log(choiceNode.name)
        btNodes.forEach(element => {
            if(element == choiceNode){
                element.children[0].color = cc.color(224,153,71)
            }else{
                element.children[0].color = cc.color(0,0,0)
            }
        });
    }
}
