const { ccclass, property } = cc._decorator;

const BYTE_LABEL: string[] = ["B", "KB", "MB", "GB", "TB", "PB"];

function getFormatLabel(bytes: number, fix: number = 1) {
    if (bytes < 1000) {
        return String(Math.round(bytes));
    }

    let count = 0;
    while(bytes >= 1000 && count < BYTE_LABEL.length - 1) {
        count += 1;
        bytes /= 1000;
    }

    return bytes.toFixed(fix) + BYTE_LABEL[count];
}

