
import viewCell from "../../Framework/TableView/viewCell"
import { utils } from "../../utils/Utils";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_mathinfo_level_cell extends viewCell {
    protected onLoad(): void {
        
    }
    init(index: number, data?: any): void {
        this.refreshData(index,data)
    }
    refreshData(index,data){
        let bi = data
        let node = this.node
        let i = index
        let cur_index = this.delegate.delegate.cur_index
        let matchInsM =  this.delegate.delegate.matchInsM
        
        node.getChildByName('img_curlevel').active = true;
        node.getChildByName('node_deadline').active = true;
        node.getChildByName('label_rest').active = true;

        if(bi.small_blind+bi.big_blind+bi.ante>0)
        {
            node.getChildByName('label_level').getComponent(cc.Label).string = bi.level+""
            node.getChildByName('label_blind').getComponent(cc.Label).string = utils.formatNumber(bi.small_blind, 0) + '/' + utils.formatNumber(bi.big_blind, 0)
            node.getChildByName('label_ante').getComponent(cc.Label).string = utils.formatNumber(bi.ante, 0)
            node.getChildByName('label_duration').getComponent(cc.Label).string = Math.floor(bi.seconds / 60) + '分钟'
            let shadow = node.getChildByName('img_shadow')
            shadow.active = false;
            // if(cur_index==i)
            // {
            //     shadow.active=true
            // }
            // else
            // {
            //     shadow.active=false
            // }

            node.getChildByName('img_curlevel').active = false;
            node.getChildByName('node_deadline').active = false;
            node.getChildByName('label_rest').active = false;
            if (i < cur_index) {
                node.getChildByName('label_level').color = new cc.Color().fromHEX("#CCCCCC");
                node.getChildByName('label_blind').color = new cc.Color().fromHEX("#CCCCCC");
                node.getChildByName('label_ante').color = new cc.Color().fromHEX("#CCCCCC");
                node.getChildByName('label_duration').color = new cc.Color().fromHEX("#CCCCCC");
            }
            else if (i == cur_index) {
                node.getChildByName('label_level').color = new cc.Color().fromHEX("#EC9630");
                node.getChildByName('label_blind').color = new cc.Color().fromHEX("#EC9630");
                node.getChildByName('label_ante').color = new cc.Color().fromHEX("#EC9630");
                node.getChildByName('label_duration').color = new cc.Color().fromHEX("#EC9630");
                node.getChildByName('img_curlevel').active = true;
            }
            else
            {
                node.getChildByName('label_level').color = new cc.Color().fromHEX("#666666");
                node.getChildByName('label_blind').color = new cc.Color().fromHEX("#666666");
                node.getChildByName('label_ante').color = new cc.Color().fromHEX("#666666");
                node.getChildByName('label_duration').color = new cc.Color().fromHEX("#666666");
            }
        }
        else
        {
            node.getChildByName('img_curlevel').active = false;
            node.getChildByName('node_deadline').active = false;
            node.getChildByName('label_level').getComponent(cc.Label).string = ""
            node.getChildByName('label_blind').getComponent(cc.Label).string = ""
            node.getChildByName('label_ante').getComponent(cc.Label).string = ""
            node.getChildByName('label_duration').getComponent(cc.Label).string = ""
            // let lbl_ante = node.getChildByName('label_ante').getComponent(cc.Label)
            // lbl_ante.node.color=cc.Color.RED
            // node.getChildByName('label_duration').getComponent(cc.Label).string = Math.floor(bi.seconds / 60) + '分钟'
            node.getChildByName('label_rest').active = true
            node.getChildByName('label_rest').getComponent(cc.Label).string = '休息' + Math.floor(bi.seconds / 60) + '分钟'
            // let shadow = node.getChildByName('img_shadow')
            // if(cur_index==i)
            // {
            //     shadow.active=true
            // }
            // else
            // {
            //     shadow.active=false
            // }
        }
        if(matchInsM.match.reenter_blind_index==i)
        {
            //lbl_ante.string = "截止报名"
            node.getChildByName('node_deadline').active = true
        }
    }
}   