import { GAppDM } from "../../DataModel/AppDataModel";
import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../Logic/UserManager";
import { resMgr } from "../../manager/ResourceManager";
import { GHallHttp } from "../../Net/HallHttp";
import ItemTask from "../main/layer/ItemTask";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_Sign extends Page {

    @property(cc.Label)
    labelSignCount: cc.Label = null;
    
    @property(cc.Label)
    labelTips: cc.Label = null;

    @property(cc.Label)
    labelScore: cc.Label = null;

    @property(cc.ScrollView)
    scrollviewTask: cc.ScrollView = null;

    @property(cc.Node)
    layoutTask: cc.Node = null;

    @property([cc.Toggle])
    tg_signs:cc.Toggle[]=[]

    @property([ItemTask])
    wgt_tasks:ItemTask[]=[]

    @property(cc.Sprite)
    imgAvatar: cc.Sprite = null;

    start() {
        this.scrollviewTask.scrollToTop();
        this.init()
    }

    init() {
        let user = GUserMgr.self_user
        let suffix: string = "image/head/" + user.logo;
        let path = `${suffix}`;
        resMgr.loadImage(path, (res) => {
            cc.isValid(this.imgAvatar) && (this.imgAvatar.getComponent(cc.Sprite).spriteFrame = res);
        })

        let sign_count=user.sign_count||0
        let next = sign_count % 7
        let next_mii = GAppDM.signs[next]
        
        let today_sign=false
        if(user.sign_time)
        {
            let today = new Date()
            let signday = new Date(user.sign_time)
            if(today.getFullYear()==signday.getFullYear()
                &&today.getMonth()==signday.getMonth()
                &&today.getDate()==signday.getDate())
            {
                today_sign=true
            }
        }
        this.labelTips.string = (today_sign ? "今日" : "明日") + "签到可获得" + GAppDM.getItemDesc(next_mii)
        this.labelSignCount.string = sign_count+""
        this.labelScore.string = user.coin+""

        for (let i = 0; i < this.tg_signs.length; i++) {
            this.tg_signs[i].isChecked = i < (sign_count%7)
            this.tg_signs[i].node.parent.getChildByName("label_day").color = i < sign_count ? cc.color(255,255,255) : cc.Color.WHITE
            let node = this.tg_signs[i].node.parent.getChildByName("label_reward")
            node.color = i < sign_count ? cc.color(255,255,255) : cc.Color.WHITE
            let mii = GAppDM.signs[i]
            node.getComponent(cc.Label).string = GAppDM.getItemDesc(mii)
        }
        GHallHttp.request("getuser").then((res)=>
        {
            if(!res.user)
            {
                return
            }
            GUserMgr.addOneUser(res.user)
            for(let i=0;i<this.wgt_tasks.length;++i)
            {
                let ti = this.wgt_tasks[i]
                ti.refresh()
            }
        })
    }

    async onClickSign() {
        let today_sign=false
        if(GUserMgr.self_user.sign_time)
        {
            let today = new Date()
            let signday = new Date(GUserMgr.self_user.sign_time)
            if(today.getFullYear()==signday.getFullYear()
                &&today.getMonth()==signday.getMonth()
                &&today.getDate()==signday.getDate())
            {
                today_sign=true
            }
        }
        if(today_sign)
        {
            return
        }

        let res = await GHallHttp.request("sign")
        if(res.errcode)
        {
            GHelper.showTip(res.errcode.des)
            return
        }
        GUserMgr.addOneUser(res.user)
        
        this.init()
        GPageMgr.openPage("Page_SignTip")
    }

    Btn_Close_OnClick(event)
    {
        GPageMgr.closePage("Page_Sign")
    }
}
