import { EAccountFrom, GAppDM } from "../../DataModel/AppDataModel";
import { core } from "../../Framework/Core/Base";
import { GaoDeMapTool } from "../../Framework/Logic/GaoDeMapTool";
import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GAccountHttp } from "../../Net/AccountHttp";

import {layerMgr} from "../../manager/LayerManager"

import {ykUtils} from "../../utils/YkUtils"
import { GGameDM } from "../../Framework/DataModel/GameDataModel";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_CodeLogin extends Page {

    @property(cc.EditBox)
    editPhone: cc.EditBox = null;

    @property(cc.EditBox)
    editCode: cc.EditBox = null;

    @property(cc.Label)
    btnCode: cc.Label = null;

    @property(cc.Toggle)
    toggleAgree: cc.Toggle = null;

    @property(cc.Button)
    btn_getCode: cc.Button = null;


    @property(cc.Button)
    btn_login: cc.Button = null;

    developLimit = [2,3,2]
    developData =[0,0,0];


    onDevelopChick(event: cc.Event.EventCustom, data: string)
    {
        let touchIndex = parseInt(data);
        if(touchIndex==3)
        {
            this.developData=[0,0,0]
        }
        else
        {
            if(touchIndex-1>=0)
            {
                if(this.developData[touchIndex-1]==this.developLimit[touchIndex-1])
                {
                    this.developData[touchIndex]++;
                    if(this.developData[2]==this.developLimit[2])
                    {
                        // let develpNd =  this.node.getChildByName("develop");
                        // develpNd.active = false;
                        this.developData=[0,0,0]
                        GPageMgr.closePage("Page_Start")
                        GPageMgr.closePage("Page_CodeLogin")
                        GPageMgr.openPage("Page_ServerList")
                        
                    }
                }
            }else
            {
                this.developData[0]++;
            }
        }
    }

    start() {
        GPageMgr.cachePage("Page_Game")
        GPageMgr.closePage("Page_Setting")
        GPageMgr.closePage("Page_Update")
        GPageMgr.closePage("Page_Main")
        GPageMgr.closePage("Page_MatchInfo")

        this.btnCode.node.active = false;
        this.btn_getCode.interactable = false;
        this.btn_login.interactable = false;

  

        if(GGameDM.isReviewVersion && cc.sys.os==cc.sys.OS_ANDROID){
            this.toggleAgree.uncheck();
        }else{
            this.toggleAgree.check();
        }
       
        try
        {
            if(GAppDM.quick_test)
            {
                this.editPhone.string="13808173531"
            }
            let str = cc.sys.localStorage.getItem("login_data")
            console.log('login_data',str)
            let data = JSON.parse(str)
            if(data)
            {
                if(GAppDM.auto_login)
                {
                    this._onLogin(data.unionid,data.openid)
                }
            }
        }catch(e)
        {

        }

        // 
        if(GGameDM.isReviewVersion){
            cc.find('content/btn_register',this.node).active = false
        }

        let develpNd =  this.node.getChildByName("develop");
        develpNd.active = true;
        develpNd.width = cc.winSize.width;
        develpNd.height = cc.winSize.height;
        this.developData = [0,0,0]
    }

    
    
    phone_code_Change()
    {
        let phone = this.editPhone.string;
        let code = this.editCode.string;
        if(core.isPhoneNo(phone))
        {
            this.btn_getCode.interactable = true;
            this.btn_login.interactable =  !!code;
        }else
        {
            this.btn_getCode.interactable = false;
            this.btn_login.interactable = false;
        } 
    }
    

    initTable() {
    }

    updateLabel() {
        let remain = GAppDM.last_get_code ? GAppDM.last_get_code + 60000 - Date.now() : 0;
        if (remain < 0) {
            remain = 0;
        }
        remain = Math.floor(remain / 1000);
        this.btnCode.string = "获取验证码" + (remain ? `(${remain})` : "");
    }

    async onClickLogin() {
        if (!this.toggleAgree.isChecked) {
            GHelper.showTip("请勾选同意用户协议和隐私条款");
            return
        }

        let phone = this.editPhone.string;
        let code = this.editCode.string;
        if (!core.isPhoneNo(phone)) {
            GHelper.showTip("请输入正确的手机号");
            return
        }
        if(GAppDM.quick_test&&!code)
        {
            code="123456"
        }
        if (!code) {
            GHelper.showTip("请输入验证码");
            return
        }
        this._onLogin(phone,code)
        return


        
    }
    protected async _onLogin(phone:string,code:string)
    {
        layerMgr.showNetLoading(true)
        let ret = await GAppDM.onLogin(phone,code,EAccountFrom.QuickPhone)
        layerMgr.showNetLoading(false)
        if(ret)
        {
            GPageMgr.openPage("Page_Main")
        }
    }

    onClickGetCode() {
        let phone = this.editPhone.string;
        if (!core.isPhoneNo(phone)) {
            GHelper.showTip("请输入正确的手机号");
            return
        }

        var codeTime = 60;
        this.btnCode.getComponent(cc.Label).string = '(' +  codeTime + 's' + ')'
        this.schedule(function() {
            codeTime--
            this.btn_getCode.node.active = false;
            this.btnCode.node.active  = true;
            this.btnCode.getComponent(cc.Label).string = '(' +  codeTime + 's' + ')'
            if ( codeTime<=0) {
                codeTime = 0
                this.unscheduleAllCallbacks();
                this.btn_getCode.node.active = true;
                this.btnCode.node.active  = false;
                this.btnCode.getComponent(cc.Label).string = "获取验证码";
            }
        }, 1);

        this.getCode(phone)
    }

    async getCode(phone){
        let res = await GAccountHttp.request("getphonecode",{phone:phone})
        if(res.errcode)
        {
            GHelper.showTip(JSON.stringify(res.errcode))
        }
    }




    onClickRegister() {
        GPageMgr.openPage("Page_Register")
    }

    onClickAccount() {
        GPageMgr.openPage("Page_AccountLogin")
    }

    onClickResetPassword() {
        GPageMgr.openPage("Page_ResetPassword")
    }

    onEditPhone() {
        this.editPhone.string = this.editPhone.string.replace(/[^0-9]/ig, "");
    }

    onEditCode() {
        this.editCode.string = this.editCode.string.replace(/[^0-9]/ig, "");
    }
    Btn_UserAgree_OnClick()
    {
        GPageMgr.openPage("Page_WebView","http://www.zdpoker.com/protocol/user")
    }
    Btn_Privacy_OnClick()
    {
        GPageMgr.openPage("Page_WebView","http://www.zdpoker.com/protocol/privacy")
    }

     // 开始倒计时
    //  startTimeCutDown(time){
    //     let allTime = time;
    //     let startOsTime = util.os.time();
    //     this.timeNode.getComponent(cc.Label).string = allTime;
    //     let scheduleCallback = function(){
    //         let nowOsTime = util.os.time();
    //         let nowTime = allTime - Math.floor((nowOsTime - startOsTime));
    //         if(nowTime <= 0){
    //             this.stopTimeCutDown();
    //         }
    //         this.timeNode.getComponent(cc.Label).string = nowTime;
    //     }.bind(this);
    //     this.timeNode.active = true;
    //     this.sprShaoDeng.active = true;
    //     this.timeNode.getComponent(cc.Label).schedule(scheduleCallback, 1);
    // }

    // // 停止倒计时
    // stopTimeCutDown(){
    //     this.timeNode.getComponent(cc.Label).unscheduleAllCallbacks();
    //     this.timeNode.active = false;
    //     this.sprShaoDeng.active = false;
    // }
}
