
import {GHelper} from "../Logic/Helper"

export class TimeLabel extends cc.Label
{
    _time_handler:number=0
    onLoad () 
    {
        let self = this
        this._refresh()
        this._time_handler = setInterval(function()
        {
            self._refresh()
        },10000)
    }
    _refresh()
    {
        this.string = GHelper.getLocalTime("hh:mm")
    }
    onDestroy()
    {
        clearInterval(this._time_handler)
        this._time_handler = 0
    }
}
