import { GHelper } from "../../../Framework/Logic/Helper";
import { Page } from "../../../Framework/View/Page/Page";

const {ccclass, property} = cc._decorator;

@ccclass
export default class NodeLive extends Page {

    @property(cc.ToggleContainer)
    toggles: cc.ToggleContainer = null;

    @property(cc.EditBox)
    editSearch: cc.EditBox = null;


    start() {
        this.toggles.toggleItems[0].check()
        this.onToggleAll()
    }

    initTable() {
        for (let item of this.toggles.toggleItems) {
            item.node.getChildByName("bg").active = !item.isChecked;
        }
    }

    onToggleAll() {
        this.initTable()
    }

    onToggleLive() {
        this.initTable()
    }

    onToggleVideo() {
        this.initTable()
    }

    onClickSearch() {
        let search = this.editSearch.string;
        if (!search) {
            GHelper.showTip("请输入搜索关键字");
            return;
        }

        GHelper.showTip("未搜索到相关内容");
    }
}
