import { GHelper } from "../../Framework/Logic/Helper";
import { GUserMgr } from "../../Logic/UserManager";
import { resMgr } from "../../manager/ResourceManager";
import { GHallHttp } from "../../Net/HallHttp";
import { GMatchSer } from "../../Service/MatchService";
import { GMsgCenter } from "../../utils/MessageCenter";

const {ccclass, property} = cc._decorator;

@ccclass
export default class changeHead extends cc.Component {
    @property(cc.Sprite)
    imgAvatar: cc.Sprite = null;

    @property([cc.Node])
    heads = new Array<cc.Node>()

    random: number = 0;

    onLoad() {
        this.onClickHead()
        let suffix: string = "image/head/" + GUserMgr.self_user.logo;
        let path = `${suffix}`;

        resMgr.loadImage(path, (res) => {
            cc.isValid(this.imgAvatar) && (this.imgAvatar.getComponent(cc.Sprite).spriteFrame = res);
        })
    }

    start() {
    }

    clickHeadCallback(selfTg) {
        let name = selfTg.node.name
        let random = Number(name.replace("head", ""))
        for (let index = 0; index < this.heads.length; index++) {
            const element = this.heads[index]
            element.getComponent(cc.Toggle).uncheck()
        }

        selfTg.check()

        GHallHttp.request("changelogo", {logo: random}).then((res)=>
        {
            if (res.errcode==null) {
                GHelper.showTip("修改成功");
                GUserMgr.self_user.logo = random.toString();
                GMsgCenter.send("changelogo", random)
            }
        })
    }

    // onClickClose() {
    //     this.headNode.active = false
    //     let suffix: string = "image/head/" + this.random;
    //     let path = `${suffix}`;

    //     resMgr.loadImage(path, (res) => {
    //         cc.isValid(this.imgAvatar) && (this.imgAvatar.getComponent(cc.Sprite).spriteFrame = res);
    //     })
    // }
    onClickClose() {
        this.node.active = false
    }

    onClickHead() {
        //之前随机的头像带上勾选框
        for (let index = 0; index < this.heads.length; index++) {
            const element = this.heads[index]
            element.on('toggle', this.clickHeadCallback.bind(this), this);
            if (Number(GUserMgr.self_user.logo) == (index + 1)) {
                element.getComponent(cc.Toggle).check()
            }
            else
            {
                element.getComponent(cc.Toggle).uncheck()
            }
        }
    }

    getRandomInt(min, max) {
        return Math.floor(Math.random()*(max - min)) + min
    }
}
