import { core } from "../../Framework/Core/Base"
import { GHelper } from "../../Framework/Logic/Helper"
import { GPageMgr } from "../../Framework/View/Page/PageManager"
import { AddressItem, GUserMgr } from "../../Logic/UserManager"
import { GHallHttp } from "../../Net/HallHttp"
import { BagItemItem, EBagItemState, GItemCfg, ItemItem } from "../../Service/ItemService"
import { GMsgCenter } from "../../utils/MessageCenter"

import viewCell from "../../Framework/TableView/viewCell"
import tableView from "../../Framework/TableView/tableView"

const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_ChangeItem extends viewCell {

    @property(cc.Sprite)
    sp_icon: cc.Sprite = null

    @property(cc.Label)
    lbl_title: cc.Label = null

    @property(cc.Label)
    lbl_subtitle: cc.Label = null

    @property(cc.Label)
    lbl_count: cc.Label = null

    @property(cc.Label)
    lbl_time: cc.Label = null;

    @property(cc.Label)
    lbl_time_end: cc.Label = null;


    @property(cc.Node)
    layout1: cc.Node = null

    @property(cc.Node)
    layout2: cc.Node = null

    @property(cc.Node)
    layout3: cc.Node = null

    @property(cc.Node)
    layout4: cc.Node = null

    @property(cc.Node)
    node_btnexchange:cc.Node=null

    @property(cc.Node)
    node_btnexchange_reward:cc.Node=null
    

    @property(cc.Node)
    node_btndonate:cc.Node=null

    @property(cc.Node)
    node_btnuse:cc.Node=null

    @property(cc.Node)
    node_btnGiveCheck:cc.Node=null


    protected _item:BagItemItem=null
    protected itemM = null

    infoBt()
    {
        if(this.delegate.delegate._checkIndex == '2')
        {
            GPageMgr.openPage("Page_MyPrizeInfo",this._item)
        }else if(this.delegate.delegate._checkIndex == '1'){
            if(this._item.count>1){
                this.delegate.delegate.onPageDetailClick
                &&this.delegate.delegate.onPageDetailClick(this._item.item_id)
            }
        }
    }
    onLoad()
    {
        GMsgCenter.register("item_changed",()=>
        {
            this.initBagItem(this._item)
        },this)
    }
    protected onDestroy(): void {
        GMsgCenter.unregister("item_changed",this)
    }
    init(index: number, data?: any, tv?: tableView): void {
        console.log("tb init",index,data)
        this.initBagItem(data)
    }

    reload(data?: any) {
        this.initBagItem(data)
    }

    initBagItem(item:BagItemItem) {
        this._item=item

        let needHideBtn = false
        let idex = this.delegate.delegate._checkIndex
        if(this._item.give_check == 0 && parseInt(idex) == 1){
            // 这里要显示使用按钮
            needHideBtn = true
        }

        this.initlayout()
        if (this._item.state==EBagItemState.None) {
            this.layout1.active = true;
            if (this._item.give_nickname=="") {
                //表示自己获得
                this.layout1.getChildByName("label_desc").getComponent(cc.Label).string = "奖品获得者:"
                this.layout1.getChildByName("label_winner").getComponent(cc.Label).string = this._item.start_nickname + " " + this._item.start_userid
                this.lbl_time.string = "获得时间:"+GHelper.convertTime(this._item.create_time)
            }
            else
            {
                this.layout1.getChildByName("label_desc").getComponent(cc.Label).string = "转赠人:"
                this.layout1.getChildByName("label_winner").getComponent(cc.Label).string = this._item.give_nickname + " " + this._item.give_userid
                this.lbl_time.string = "转赠时间:"+ GHelper.convertTime(this._item.give_time)
            }
        }
        else if (this._item.state==EBagItemState.Exchanging)
        {
            this.lbl_time.string = "申请时间:"+GHelper.convertTime(this._item.apply_time)
            this.layout2.active = true;
        }
        else if (this._item.state==EBagItemState.TransOk)
        {
            this.lbl_time.string = "转赠时间:"+ GHelper.convertTime(this._item.give_time)
            this.layout3.active = true;
            this.layout3.getChildByName("label_desc").getComponent(cc.Label).string = "被转赠人:"
            this.layout3.getChildByName("label_winner").getComponent(cc.Label).string = this._item.to_nickname + " " + this._item.to_userid
        }
        else if (this._item.state==EBagItemState.ExOk)
        {
            this.lbl_time.string = "兑换成功:"+GHelper.convertTime(this._item.ok_time)
            this.layout4.active = true;

            if (this._item.give_nickname=="") {
                this.layout4.getChildByName("label_winner").getComponent(cc.Label).string = this._item.start_nickname + " " + this._item.start_userid
            }else{
                this.layout4.getChildByName("label_winner").getComponent(cc.Label).string = this._item.to_nickname + " " + this._item.to_userid
            }
        }
        else if (this._item.state==EBagItemState.ExRewardCoin)
        {
            this.lbl_time.string = "兑换时间:"+GHelper.convertTime(this._item.apply_time)

            this.layout4.active = true;
            if (this._item.give_nickname=="") {
                this.layout4.getChildByName("label_winner").getComponent(cc.Label).string = this._item.start_nickname + " " + this._item.start_userid
            }else{
                this.layout4.getChildByName("label_winner").getComponent(cc.Label).string = this._item.to_nickname + " " + this._item.to_userid
            }
          

            this.layout4.getChildByName("btn_exchange").getChildByName("label").getComponent(cc.Label).string = "已兑换欢乐点"

        }
        else if (this._item.state==EBagItemState.ExClub || this._item.state==EBagItemState.ExClubOk)
        {
            this.lbl_time.string = "兑换时间:"+GHelper.convertTime(this._item.apply_time)

            this.layout4.active = true;
            if (this._item.give_nickname=="") {
                this.layout4.getChildByName("label_winner").getComponent(cc.Label).string = this._item.start_nickname + " " + this._item.start_userid
            }else{
                this.layout4.getChildByName("label_winner").getComponent(cc.Label).string = this._item.to_nickname + " " + this._item.to_userid
            }
            // this.layout4.getChildByName("label_winner").getComponent(cc.Label).string = this._item.start_nickname + " " + this._item.start_userid
            this.layout4.getChildByName("btn_exchange").getChildByName("label").getComponent(cc.Label).string = "已扫码兑换"
        }
 
        // if(this._item.state==EBagItemState.Used)
        // {
        //     this.btn_state.interactable=false
        //     this.lbl_state.string="已使用"
        // }
        // else if(this._item.state==EBagItemState.Using)
        // {
        //     this.lbl_state.string="使用中"
        // }
        // else
        // {
        //     this.lbl_state.string="使用"
        // }
        this.lbl_count.string = "x"+this._item.count
        let itemM = GItemCfg.getItemById(this._item.item_id)
        this.itemM = itemM


   
        cc.find('give_check',this.node).active = (this._item.give_check == 0)
        // 是否是exitem

        


        if(!itemM)
        {
            this.lbl_title.string = '过期数据'
            this.lbl_subtitle.string = '过期数据'
            this.lbl_time.string = "获得时间:"+GHelper.convertTime(this._item.create_time)
            return
        }
        
        if(this._item.state == EBagItemState.None && itemM['past_day'] && itemM['past_day']>0)
        {
            this.lbl_time_end.node.active   = true;
            let endTime = this._item.create_time+Number(itemM['past_day'])*24*60*60*1000;
            this.lbl_time_end.string ="过期时间  :  " + GHelper.convertTime(endTime)
            if(GHelper.getServerTime()>=endTime)
            {
                this.lbl_time_end.node.color = cc.Color.RED;
            }else
            {
                this.lbl_time_end.node.color  = cc.Color.WHITE;
            }
        }
        else
        {
            this.lbl_time_end.node.active   =  false;

        }

        if(itemM.verifi_club && (itemM.verifi_club>0 || itemM.verifi_club<-1)){
            this.node_btnuse.active = true
        }else{
            this.node_btnuse.active = false
        }

        
        if(itemM.can_exchange)
        {
            this.node_btnexchange.active=true
        }
        else
        {
            this.node_btnexchange.active=false
        }

        if(itemM['can_exchange_rewardcoin'])
        {
            this.node_btnexchange_reward.active = true;
        }
        else
        {
            this.node_btnexchange_reward.active = false;
        }

        if(itemM.can_give)
        {
            this.node_btndonate.active=true
        }
        else
        {
            this.node_btndonate.active=false
        }
        this.lbl_title.string=itemM.name
        this.lbl_subtitle.string=itemM.des

        if(this._item.give_check == 0 && parseInt(idex) == 2){
            // 这里要显示使用按钮
            // this.node_btnexchange.active = false
            this.node_btnGiveCheck.active = true
            cc.find('layout3/btn_exchange',this.node).active = false
        }else{
            // this.node_btnexchange.active = true
            this.node_btnGiveCheck.active = false
            cc.find('layout3/btn_exchange',this.node).active = true
        }

        if(needHideBtn){
            cc.find('layout1/New Layout',this.node).children.forEach((element)=>{
                element.active = false  
            })
        }
    }
    initlayout() {
        this.layout1.active = false;
        this.layout2.active = false;
        this.layout3.active = false;
        this.layout4.active = false;
    }



    Btn_copy() {
        let copyStr = "订单号"
		if (cc.sys.os == cc.sys.OS_ANDROID) {
			setTimeout(() => {
				jsb.reflection.callStaticMethod("org/cocos2dx/javascript/AppActivity", "Copy", "(Ljava/lang/String;)V", copyStr);
			}, 100);
		}
        else if (cc.sys.os == cc.sys.OS_IOS) {
            jsb.reflection.callStaticMethod("AppController", "Copy:" , copyStr);
        }
		else {
			const el = document.createElement('textarea');
			el.value = copyStr;
 
			// Prevent keyboard from showing on mobile
			el.setAttribute('readonly', '');
			//el.style.contain = 'strict';
			el.style.position = 'absolute';
			el.style.left = '-9999px';
			el.style.fontSize = '12pt'; // Prevent zooming on iOS
 
			const selection = getSelection()!;
			let originalRange;
			if (selection.rangeCount > 0) {
				originalRange = selection.getRangeAt(0);
			}
 
			document.body.appendChild(el);
			el.select();
 
			// Explicit selection workaround for iOS
			el.selectionStart = 0;
			el.selectionEnd = copyStr.length;
 
			let success = false;
			try {
				success = document.execCommand('copy');
			} catch (err) { }
 
			document.body.removeChild(el);
 
			if (originalRange) {
				selection.removeAllRanges();
				selection.addRange(originalRange);
			}
		}
    }

    Btn_Change_Reward_OnClick()
    {
        GPageMgr.openPage("Page_ChangePoint", this._item)
    }
    Btn_Change_OnClick() {
        GPageMgr.openPage("Page_Change", this._item)
    }

    Btn_donate_OnClick() {
        GPageMgr.openPage("Page_Donate", this._item)
    }
    // 出事票票二维码
    Btn_use_OnClick() {
        console.log(this._item)

        GPageMgr.openPage("Page_UseItemByCode", this._item)
    }

    Btn_Exchange_OnClick(event)
    {
        // if(this._item.state==EBagItemState.None)
        // {
        //     GPageMgr.openPage("Page_Exchange",this._item)
        // }
    }

    async Btn_Givecheck_OnClick(){
        console.log(this.itemM)
        // 弹窗提示
        GPageMgr.openPage("Page_Tips",{
            tips:"确定后,对方将立刻收到 ("+this.itemM.name+')',
            buttons:[
                {
                    label:"确定",
                    callback:async ()=>
                    {
                        let res = await GHallHttp.request("unLockItem",{user_id:GUserMgr.self_user.id,
                                id:this._item.id})
                            if(res.errcode){
                                GHelper.showTip(res.errcode)
                            }else{
                                // this.delegate.delegate.resh()
                                GMsgCenter.send('update_myprize')
                            }
                    }
                },
                {
                    label:"取消",
                    callback:()=>
                    {

                    }
                }
            ]
        })


    }
}
