import { GGameDM } from './DataModel/GameDataModel';
import { TipRunning } from './Extention/TipRunning';
import {GSettingDM} from "./DataModel/SettingDataModel"
import {GPageMgr, PageManager} from "./View/Page/PageManager"
import {TipLabel} from "./Extention/TipLabel"
import { GHelper } from './Logic/Helper';
import { loginService } from '../Service/LoginService';
import { layerMgr } from '../manager/LayerManager';
import { ykUtils } from '../utils/YkUtils';

export class IGame extends cc.Component {
    private reGetInterval = 0
    private reloadingIndex = 0
    onLoad() 
    {
        cc['yk'] = {}
        GSettingDM.init()
       
        let pageNode = new cc.Node()
        pageNode.addComponent(PageManager)
        pageNode.parent = this.node
        //初始化提示条
        let tipNode = new cc.Node()
        tipNode.addComponent(TipLabel)
        tipNode.parent = this.node

        //初始化提示条
        let tipRunningNode = new cc.Node()
        tipRunningNode.addComponent(TipRunning)
        tipRunningNode.parent = this.node

        GPageMgr.registerKeyPath("Page","View/Page/Layout/")

        layerMgr.showNetLoading(true,'正在加载配置中')
        this.getGameConfig()
        this.reGetInterval =  setInterval(()=>{
            this.reloadingIndex+=1
            this.getGameConfig()
        },2000)
    }


    async getGameConfig(){
        try{
            let isGet = await loginService.getGameConfig()
            if(isGet){
                layerMgr.showNetLoading(false)
                this.onPreInitDone()
            }else{
                layerMgr.showNetLoading(true,'正在加载配置中 尝试次数+'+this.reloadingIndex)
            }
        }catch(error){
            console.log('req error')
        }
    }

    onPreInitDone()
    {
        if(this.reGetInterval){
            clearInterval(this.reGetInterval)
            this.reGetInterval = null
        }
        layerMgr.showNetLoading(false)
    }
}