import { GUserMgr } from "../../Logic/UserManager"
import { GOrderHttp } from "../../Net/OrderHttp"
import { GPageMgr } from "../View/Page/PageManager"
import { GHelper } from "./Helper"

export let GGoogleTool:GoogleTool=null
window["google"]={}
class GoogleAccount
{
    nickname=""
    id=""
    token=""
    logo=""
    email=""
}

type FLoginCB=(account:GoogleAccount,err:string)=> void
type FEndRewardFSADCB=(rewardType:string,rewardAmount:number,err:string)=> void
type FPaySuccess=(order_id:string,purchase_token:string)=> void
type FAdNumCB=(num:number)=> void

export let google = window["google"]
class GoogleTool
{
    protected _native_plugin = "com/eryi/google/GoogleService"
    protected _empty_params = "()V"
    protected _one_string_param  = "(Ljava/lang/String;)V"
    protected _two_string_params = "(Ljava/lang/String;Ljava/lang/String;)V"
    protected _login_cb:FLoginCB=null
    protected _endrewardfsad_cb:FEndRewardFSADCB=null
    protected _pay_cb:FPaySuccess=null
    protected _onad_num:FAdNumCB=null
    init()
    {
        google.onLogin=this.onLogin.bind(this)
        google.onEndRewardFSAD=this.onEndRewardFSAD.bind(this)
        google.onPurchasesUpdated=this.onPurchasesUpdated.bind(this)
        google.onVerify=this.onVerify.bind(this)
        google.onLaunchBillingFlow=this.onLaunchBillingFlow.bind(this)
        google.onAdNum=this.onAdNum.bind(this)
    }
    login(cb:FLoginCB)
    {
        if(cc.sys.isBrowser || cc.sys.os != cc.sys.OS_ANDROID) 
        {
            return
        }
        this._login_cb=cb
        jsb.reflection.callStaticMethod(this._native_plugin, "login",this._empty_params)
    }
    onLogin(jsonStr:string)
    {
        if(!jsonStr)
        {
            if(this._login_cb)
            {
                this._login_cb(null,"error")
            }
            return
        }
        let account:GoogleAccount = JSON.parse(jsonStr)
        if(this._login_cb)
        {
            this._login_cb(account,account["err"])
        }
    }
    showRewardFSAD(cb:FEndRewardFSADCB)
    {
        if(cc.sys.isBrowser || cc.sys.os != cc.sys.OS_ANDROID) 
        {
            return
        }
        this._endrewardfsad_cb=cb
        jsb.reflection.callStaticMethod(this._native_plugin, "showRewardFSAD",this._empty_params)
    }
    onEndRewardFSAD(res:string)
    {
        let json = JSON.parse(res)
        if(this._endrewardfsad_cb)
        {
            this._endrewardfsad_cb(json.type,json.amount,json.err)
        }
    }
    /**
     * 购买商品
     * @param goods_id 商品的key
     * @param order_id 自家服务器的order_id
     * @returns 
     */
    buy(goods_id:string,order_id:string,cb?:FPaySuccess)
    {
        if(cc.sys.isBrowser || cc.sys.os != cc.sys.OS_ANDROID) 
        {
            return
        }
        this._pay_cb=cb
        jsb.reflection.callStaticMethod(this._native_plugin, "buy",this._two_string_params,goods_id,order_id)
    }
    consume(purchase_token:string)
    {
        if(cc.sys.isBrowser || cc.sys.os != cc.sys.OS_ANDROID) 
        {
            return
        }
        jsb.reflection.callStaticMethod(this._native_plugin, "consume",this._one_string_param,purchase_token)
    }
    onPurchasesUpdated(code:string)
    {

    }
    async onVerify(order_id:string,purchase_token:string)
    {
        let jsonData = await GOrderHttp.request("PayComplete",{
            order_id:order_id,
            money:-1
        })
        if(jsonData.errcode)
        {
            GHelper.showTip(JSON.stringify(jsonData.errcode))
            return
        }
        let crystal = jsonData.crystal
        let vip_exp = jsonData.vip_exp

        //let dt = crystal - GUserMgr.self_user.crystal
        //GUserMgr.self_user.crystal = crystal
        GUserMgr.self_user.vip_exp = vip_exp
        GPageMgr.refreshPage("Page_Main")
        //GHelper.showTip("充值成功，获得 "+dt+" 砖石!")
        this.consume(purchase_token)
        
        if(this._pay_cb)
        {
            this._pay_cb(order_id,purchase_token)
        }
    }
    onLaunchBillingFlow(code:string,goods_id:string,order_id:string)
    {

    }
    getAdNum(cb:FAdNumCB)
    {
        if(cc.sys.isBrowser || cc.sys.os != cc.sys.OS_ANDROID) 
        {
            return
        }
        this._onad_num=cb
        jsb.reflection.callStaticMethod(this._native_plugin, "adNum",this._empty_params)
    }
    onAdNum(res:string)
    {
        let json = JSON.parse(res)
        if(this._onad_num)
        {
            this._onad_num(parseInt(json.num))
        }
    }
}
GGoogleTool = new GoogleTool()