    protected _getRaise() {
        let min = this._getMinRaise()
        let max = this._getMaxRaise()
        if(min>max)
        {
            min = max
        }
        let seatinfo = this.selfSeatInfo
        let tableinfo=this.tableInfo
        let bet = min+Math.floor((max-min)*this.sd_raise.progress)
        //allin不管
        if(bet==seatinfo.bet+seatinfo.playerinfo.point)
        {

        }
        else
        {
            let dtbet = bet-seatinfo.bet
            if(tableinfo.min_raise_bet>0)
            {
                let num=Math.floor(dtbet/tableinfo.min_raise_bet)
                dtbet=num*tableinfo.min_raise_bet
                bet = seatinfo.bet+dtbet
            }
        }
        // let m = this.getBlindMod()
        // if(bet!=max)
        // {
        //     bet = bet-bet%m
        // }
        if(bet<min)
        {
            bet=min
        }
        if(bet>max)
        {
            bet=max
        }
        return bet
    }
    /**
     * 加注的最小值（加到）
     */
    protected _getMinRaise()
    {
        let tableinfo = this.tableInfo
        let seatinfo = this.selfSeatInfo
        if(tableinfo.min_raise_bet<=0)
        {
            tableinfo.min_raise_bet=tableinfo.big_blind
        }
        //1,翻牌前
        let min = tableinfo.curmaxbet
        if(!tableinfo.cards||tableinfo.cards.length<=0)
        {
            min += tableinfo.min_raise_bet
        }
        //2,翻牌后
        else
        {
            if(seatinfo.bet==0||min==0||min-seatinfo.bet>tableinfo.min_raise_bet)
            {
                min+=tableinfo.min_raise_bet
            }
        }
        if(min>seatinfo.bet+seatinfo.playerinfo.point)
        {
            min = seatinfo.bet+seatinfo.playerinfo.point
        }
        return min
    }
    /**nul
     * 加注的最大值（加到）
     */
     protected _getMaxRaise()
     {
        let tableinfo = this.tableInfo
        let seatinfo = this.selfSeatInfo
        if(tableinfo.min_raise_bet<=0)
        {
            tableinfo.min_raise_bet=tableinfo.big_blind
        }
        //1,翻牌前
        let max = seatinfo.bet+seatinfo.playerinfo.point
        if(!tableinfo.cards||tableinfo.cards.length<=0)
        {
            //最大值就是allin
        }
        //2,翻牌后
        else
        {
            //只能call
            if(tableinfo.curmaxbet-seatinfo.bet>0&&tableinfo.curmaxbet-seatinfo.bet<tableinfo.min_raise_bet)
            {
                max=tableinfo.curmaxbet
            }
        }
        if(max>seatinfo.bet+seatinfo.playerinfo.point)
        {
            max = seatinfo.bet+seatinfo.playerinfo.point
        }
        return max
     }
