    Btn_ShowLeft_OnClick()
    {
        let tableinfo = this.tableInfo
        let seatinfo = this.selfSeatInfo
        if (seatinfo==undefined) {
            return;
        }
        if (seatinfo.state==ETexasSeatState.Fold) {
            if (seatinfo != undefined && tableinfo.state != ETableState.Idle) {
                if (!this.node_showcards0.getChildByName("Background").active) {
                    this.node_showcards0.getChildByName("Background").active = true;
                    this.node.parent.getComponent("Page_Table").seats[0].getCard(0).setColor(cc.Color.WHITE)
                    this.showleft = 1;
                }
                else
                {
                    if (tableinfo.state == ETableState.End) {
                        return
                    }
                    else
                    {
                        this.node_showcards0.getChildByName("Background").active = false;
                        this.node.parent.getComponent("Page_Table").seats[0].getCard(0).setColor(cc.Color.BLACK.fromHEX("#4D4D4D"))
                        this.showleft = -1;
                    }
                }
                this.showCardEnd(this.showright)
            }
        }
        else if(tableinfo.state==ETableState.End)
        {
            let results = GTexasTableDM.battleRecords[tableinfo.id]
            if(!results||results.length<=0)
            {
                return
            }
            let rs = results[results.length-1]
            let rr = rs.userrecords[seatinfo.playerinfo.id]
            if(rr&&rr.showcards!=1&&rr.showcards!=3)
            {
                this.showleft=1
                GNetGameTexas.send_texas_showcard(this._table_id, 1)
                this.showCardEnd(rr.showcards|1)
            }
        }
    }

