    startTimeCut(time) {
        this.node_countdown.active = true;
        this.node_countdown.getChildByName("lbl_timer").getComponent(cc.Label).unscheduleAllCallbacks();
        let alltime = time;
        let lefttime = time;
        this.node_countdown.getChildByName("lbl_timer").getComponent(cc.Label).string = ""//allTime;
        let scheduleCallback = ()=>{
            lefttime = lefttime - 1;
            if (lefttime <= 0) {
                this.stopTimeCut();
            }
            this.node_countdown.getChildByName("lbl_timer").getComponent(cc.Label).string = lefttime+"s"
            this.node_countdown.getComponent(cc.Sprite).fillRange = lefttime/alltime;
        }

        scheduleCallback()
        this.node_countdown.zIndex=9999
        this.node_countdown.getChildByName("lbl_timer").getComponent(cc.Label).schedule(scheduleCallback, 1);
    }

    stopTimeCut() {
        this.btn_auto_any.node.getChildByName("lbl_auto_any").getComponent(cc.Label).string = "跟任何"
        this.node_countdown.getChildByName("lbl_timer").getComponent(cc.Label).unscheduleAllCallbacks();
        this.node_countdown.active = false;
    }
