export enum SUBSCRIBE { //订阅消息
    VERSION,            //版本更新
    SIGN,               //签到奖励
    GIFT,               //礼包领取
    OFFLINE,            //离线收益
    VITALITY,           //体力恢复
    ACTIVITY,           //活动开启、结束
    TASK,               //任务完成
}

export abstract class BaseAd {
    protected platform: string = "";
    protected banner_id: string = "";
    protected video_id: string = "";
    protected interstitial_id: string = "";
    protected native_id: string = "";
    protected subscribe_templates: string[] = [];

    constructor(platform: string) {
        this.platform = platform;
    }
    
    init(data?) {
        this.banner_id = data.banner_id || "";
        this.video_id = data.video_id || "";
        this.interstitial_id = data.interstitial_id || "";
        this.native_id = data.native_id || "";
    }

    setSubscribeTemplates(templates: string[]) {
        this.subscribe_templates = templates;
    }

    getPlatform() {
        return this.platform;
    }

    abstract login(success?: Function, fail?: Function);
    abstract share(success?: Function, fail?: Function);
    
    requestSubscribeMessage(success?: Function, fail?: Function) {
        fail && fail();
    }

    abstract createBannerAd();
    abstract destroyBannerAd();
    abstract createVideoAd(success?: Function, fail?: Function);
    abstract createInterstitialAd(callback?: Function);
    abstract createNativeAd(success?: Function, fail?: Function);
    abstract reportAdShow(id: string);
    abstract reportAdClick(id: string);

    vibrateShort() {
        window.navigator.vibrate(15);
    }

    vibrateLong() {
        window.navigator.vibrate(400);
    }
}

