import { GHelper } from "../../Framework/Logic/Helper";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { AdModel } from "../../Service/AdService";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_HomeAdsItem extends cc.Component {

    @property(cc.Label)
    lbl_title: cc.Label = null

    @property(cc.Sprite)
    sp_ad: cc.Sprite = null

    protected _ad:AdModel = null

    init(ad:AdModel) {
        this._ad=ad
        this.lbl_title.string = this._ad.title
        GHelper.createSpriteFrame(this._ad.icon,(sf)=>
        {
            this.sp_ad.spriteFrame=sf
        })
    }

    Btn_Self_OnClick(event) {
        if(this._ad.url.startsWith("http"))
        {
            GPageMgr.openPage("Page_WebView",this._ad.url)
        }
    }
}
