import { CardRecordSeatInfo, CardRecordTableInfo, CardSeatInfo, ETableType, _ECardSeatState } from './CardTableDataModel';
import { GUserMgr } from "../Logic/UserManager";
import { CardTableDataModel, CardTableInfo } from './CardTableDataModel';
import { ECardType, EMatchState, ETableState, ETexasAction } from '../Config/_ini_';
import { GMatchSer } from '../Service/MatchService';


class _ETexasSeatState extends _ECardSeatState
{
    AllIn=10
    Fold=11
}

export let ETexasSeatState=new _ETexasSeatState()

export class TexasSeatInfo extends CardSeatInfo
{
	//玩家手中的牌
    cards: number[] = []
    //玩家操作
    action=ETexasAction.None
    bet=0    // 下注
    total_bet=0 
    cardtype=ECardType.None
}

export class TexasTableInfo extends CardTableInfo<TexasSeatInfo>
{
    dealer_index = 0                        //发牌位
    small_blind_index = 0                   //小盲位
    big_blind_index = 0                     //大盲位
    small_blind=0
    big_blind=0
    cards: number[] = []                    //公共牌
    blind_index: number = 0                  //当前盲注等级
    pot: number = 0                         //底池
    turnpot:number = 0                      // 轮到谁说话
    //上次的dt值，方便加注使用（加注妖大于等于dtbet）
    dtbet=0
    /**
     * 加注单位量
     */
    min_raise_bet=0
    curmaxbet=0      //当前下注的最大量
    turn_user_id=-1 //轮到谁说话
    match_insid=""  // 当前比赛id
    blind_deadline=-1
    cur_player_num =0
    cur_table_num =0
    total_player_num =0
    match_total_point =0
    no=0
    allallinshow=false
    pots:number[]=[]      // 边池
}

class TexasResultPotItem
{
    pot=0
    wins:{[user_id:number]:number}={}
}

class TexasResult
{
    pots:TexasResultPotItem[]=[]
}

export class TexasRecordTableInfo extends CardRecordTableInfo<TexasRecordSeatInfo>
{
    id:string=""
    match_id=0
    blind_id=0
    blind_index=0
    dealer_index=0
    small_blind_index=0
    big_blind_index=0
    small_blind=0
    big_blind=0
    cards:number[]=[]
    tr:TexasResult=null
}
export class TexasRecordSeatInfo extends CardRecordSeatInfo
{
    bet=0
    cards:number[]=[]
    cardtype=ECardType.None
    state=ETexasSeatState.Waiting
    showcards=0
    win=0
    //消极行为
    negative=false
    //actions:ETexasAction[]=[]
    actions:{
        action:ETexasAction, bet:number
    }[]
}

export let GTexasTableDM:TexasTableDataModel=null
class TexasTableDataModel extends CardTableDataModel<TexasSeatInfo,TexasTableInfo,TexasRecordSeatInfo,TexasRecordTableInfo>
{
    onShowCards(table_id:number,user_id:number,showcards:number)
    {
        let results = this.battleRecords[table_id]
        if(!results||results.length<=0)
        {
            return
        }
        let rs = results[results.length-1]
        let rr = rs.userrecords[user_id]
        rr.showcards=rr.showcards|showcards
    }
    getTableInfoByMatchInsId(ins_id:string)
    {
        for(let key in this._table_infos)
        {
            let tb = this._table_infos[key]
            if(tb.match_insid==ins_id)
            {
                return tb
            }
        }
        return null
    }
    getSelfSeatInfo(table_id:number)
    {
        let si = this.getSeatByUserId(table_id,GUserMgr.self_user_id)
        return si
    }
    getSelfSeatIndex(table_id:number)
    {
        let self_seat = this.getSelfSeatInfo(table_id)
        return self_seat?.index||0
    }
    init(table_info:TexasTableInfo)
    {
        super.init(table_info)
        //开赛之后报名数据有点异常
        let matchInsM = GMatchSer.getMatchInsById(table_info.match_insid)
        if(table_info.state==ETableState.Playing&&matchInsM.state!=EMatchState.Playing)
        {
            matchInsM.state=EMatchState.Playing
        }
        let selfseatinfo=this.getSeatByUserId(table_info.id,GUserMgr.self_user_id)
        if(selfseatinfo)
        {
            matchInsM.state=EMatchState.Playing
        }
        if(GUserMgr.self_user.matchinfos[matchInsM.id])
        {
            return
        }
        if(this.getSelfSeatInfo(table_info.id))
        {
            GUserMgr.self_user.matchinfos[matchInsM.id]=
            <any>{
                ins_id:matchInsM.id
            }
        }
    }
    /**
     * 记录桌子休息状态 和截止时间
     * @param table_id 
     * @param deadline 
     * @returns 
     */
    onIdle(table_id:number,deadline:number)
    {
        let ti = GTexasTableDM.getTableInfo(table_id)
        if(!ti)
        {
            return
        }
        ti.state=ETableState.Idle
        ti.blind_deadline=deadline
    }
    onDealCard(table_id:number,cards:number[],cardtype:ECardType)
    {
        let ti = GTexasTableDM.getTableInfo(table_id)
        if(!ti)
        {
            return
        }
        ti.turnpot=ti.pot
        ti.cards=ti.cards.concat(cards)
        for(let key in ti.seats)
        {
            let seat = ti.seats[key] as TexasSeatInfo
            seat.bet=0
            if(seat.action!=ETexasAction.Fold
                &&seat.action!=ETexasAction.Allin)
            {
                seat.action=ETexasAction.None
            }
        }
        let self_seat = this.getSelfSeatInfo(table_id)
        if(self_seat)
        {
            self_seat.cardtype=cardtype
        }
    }
    onResult(record:TexasRecordTableInfo)
    {
        super.onResult(record)
        let ti = this._table_infos[record.table_id]
        if(!ti||!record.tr)
        {
            return
        }
        GMatchSer.addRecord(ti.match_insid,record)
        ti.pots=[]
        for(let i=0;i<record.tr.pots.length;++i)
        {
            let pot = record.tr.pots[i]
            ti.pots.push(pot.pot)
            for(let user_id in pot.wins)
            {
                let point = pot.wins[user_id]||0
                let ur = record.userrecords[user_id] as TexasRecordSeatInfo
                if(!ur)
                {
                    continue
                }
                ur.win=ur.win||0
                ur.win+=point
            }
        }
    }
    onbet(table_id:number,user_id:number,bet:number,curmaxbet:number,dtbet:number,action:ETexasAction)
    {
        let tableinfo=this.getTableInfo(table_id)
        if(!tableinfo)
        {
            return
        }
        tableinfo.curmaxbet = curmaxbet
        tableinfo.dtbet = dtbet
        tableinfo.turn_user_id=-1
        let seat = GTexasTableDM.getSeatByUserId(table_id,user_id) as TexasSeatInfo
        if(bet>=0)
        {
            seat.bet+=bet
            seat.total_bet+=bet
            seat.dt_point+=bet
            seat.playerinfo.point-=bet
            tableinfo.pot+=bet
            if(seat.playerinfo.point<=0)
            {
                seat.state=ETexasSeatState.AllIn
            }
        }
        else
        {
            seat.state=ETexasSeatState.Fold
        }
        seat.action = action
    }
}
GTexasTableDM = new TexasTableDataModel()