import { GUserMgr } from "../../Logic/UserManager";
import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GHallHttp } from "../../Net/HallHttp";
import { GPageMgr } from "../../Framework/View/Page/PageManager";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_Bind extends Page {
    @property(cc.EditBox)
    editbox: cc.EditBox = null;

    @property(cc.Node)
    btnConfirm: cc.Node = null;

    protected _cb:Function=null
    start() {
        this.init()
    }
    onOpen(cb?:Function)
    {
        this._cb=cb
    }
    init() {
         
        let pre_user_id = GUserMgr.self_user.pre_user_id||-1
        if(pre_user_id>0)
        {
            let user = GUserMgr.getUserByUserId(pre_user_id)

            if(user)
            {
                this.editbox.string=user.nickname+`(${pre_user_id})`    
            }
            else
            {
                this.editbox.string=pre_user_id+""
                GHallHttp.request("getsimpleuser",{user_id:pre_user_id}).then((res)=>
                {
                    if(res.user)
                    {
                        GUserMgr.addOneUser(res.user)
                        this.editbox.string=res.user.nickname+`(${pre_user_id})`   
                        // this.init()
                    }
                })
            }
            this.editbox.enabled=false
            this.btnConfirm.active = false
        }
        else
        {
            this.editbox.string=""
            this.editbox.enabled=true
            this.btnConfirm.active = true
        }
    }

    async Btn_Update_OnClick(event) {
        if (GUserMgr.self_user.pre_user_id>0) {
            GHelper.showTip("已绑定邀请码，请不要重复绑定！")
            return
        }
        let str = this.editbox.string.trim()
        // if(str.length!=7)
        // {
        //     GHelper.showTip("请输入7位的绑定码！")
        //     return
        // }
        let code = parseInt(str)

        let res = await GHallHttp.request("updatebind",{
            code:code
        })
        if(res.errcode)
        {
            GHelper.showTip(res.errcode.des)
            return
        }
        if(res.club_id)
        {
            GUserMgr.self_user.clubs[res.club_id]=Date.now()
        }
        GHelper.showTip("绑定成功")
        GUserMgr.addOneUser(res.user)
        GUserMgr.addOneUser(res.pre_user)
        this.init()
        if(this._cb)
        {
            this._cb()
        }
    }
    Btn_Close_OnClick(event)
    {
        GPageMgr.closePage("Page_Bind")
    }
}
