    /**
     * 获取本地时间，严格上尽量用下面的获取服务器时间
     * 毫秒
     * @param format 格式
     */
    static getLocalTime(format?:string)
    {
        let d = new Date()
        if(format===undefined)
        {
            return d.getTime()
        }
        return core.format(d,format)
    }

    static convertShortTime(time)
    {
        time = parseInt(time)
        let dt = new Date(time)
        let now = new Date()
        if(dt.getFullYear()!=now.getFullYear())
        {
            return dt.getFullYear()+"/"+(dt.getMonth()+1)+"/"+dt.getDate()+" "+dt.getHours()+":"+dt.getMinutes()
        }
        if(dt.getDate()!=now.getDate()
            ||dt.getMonth()!=now.getMonth())
        {
            return (dt.getMonth()+1)+"/"+dt.getDate()+" "+dt.getHours()+":"+dt.getMinutes()
        }
        return dt.getHours()+":"+dt.getMinutes()
    }
    static convertTime(time)
    {
        time = parseInt(time)
        let dt = new Date(time)
        return (dt.getMonth()+1)+"-"+dt.getDate()+" "+dt.getHours()+":"+(dt.getMinutes()<10?("0"+dt.getMinutes()):dt.getMinutes())
    }
    //获取服务器的时间
    static getServerTime(format?:string)
    {
        let mill = GGameDM.diffServerTime + new Date().getTime()
        if(typeof format==="undefined")
        {
            return mill
        }
        let d = new Date(mill)
        return core.format(d,format)
    }

    static random (min?:number, max?:number) 
    {
        if (min==undefined) 
        {
            return Math.random()
        }
        if (!max) {
            max = min
            min = 0
        }
        return Math.floor(Math.random() * (max - min)) + min
    }

