    static makeDir=function (path)
    {
        let folders=path.split("/")
        if (folders==null)
        {
            return
        }
        let dir=""
        for(let i=0;i<folders.length-1;i++)
        {
            dir=dir+folders[i]+"/"
            jsb.fileUtils.createDirectory(dir)
        }
    }

    //num代表多少汉字长度
    static getShortStr(str,num=5)
    {
        if(!str)
        {
            return ""
        }
        num*=2
        let len = 0
        for(let i=0;i<str.length;++i)
        {
            len = i + 1
            if(str.charCodeAt(i)>255)
            {
                num-=2
            }
            else
            {
                num-=1
            }
            if(num<=0)
            {
                break
            }
        }
        if(len==str.length)
        {
            return str
        }
        return str.substring(0,len)+"..."
    }
    static shuffle(v:Array<any>)
    {
        for(let j, x, i = v.length; i; j = Math.random() * i, x = v[--i], v[i] = v[j], v[j] = x)
        return v
    }
    static max(v:Array<number>)
    {
        if(v.length<=0)
        {
            return 0
        }
        let max = v[0]
        for(let i=1;i<v.length;++i)
        {
            if(max<v[i])
            {
                max = v[i]
            }
        }
        return max
    }
    static httpRequest(url:string,ok_cb?,error_cb?,responseType?:XMLHttpRequestResponseType)
    {
        let xhr = new XMLHttpRequest()
        xhr.onreadystatechange = ()=>
        {
            if (xhr.readyState == 4 && (xhr.status >= 200 && xhr.status < 400)) 
            {
                if(ok_cb) 
                {
                    ok_cb(xhr)
                }
            }
        }
        xhr.ontimeout = ()=>
        {
            if(error_cb)
            {
                error_cb("timeout")
            }
        }
        xhr.onerror = (err)=>
        {
            if(error_cb)
            {
                error_cb(err)
            }
            error_cb(err)
        }
        xhr.responseType =  responseType||"json"
        xhr.open("GET", url, true)
        xhr.send()
    }
    /**
     * 
     * @param url 请求地址
     * @param data 数据
     */
    static httpPost(url:string,data?:any):Promise<any>
    {
        return new Promise((resolve,reject)=>{
            try{
                let xhr = new XMLHttpRequest()
                //xhr.withCredentials=true
                xhr.onreadystatechange = (ev)=>
                {
                    if (xhr.readyState == 4 && (xhr.status >= 200 && xhr.status < 400))
                    {
                        let jsonData = null
                        try
                        {
                            if(xhr.response)
                            {
                                jsonData=JSON.parse(xhr.response)
                            }
                            else
                            {
                                jsonData={}
                            }
                        }
                        catch(err)
                        {
                            resolve({errcode:err})
                            return
                        }
                        resolve(jsonData)
                    }

                }
                xhr.onerror=(err)=>
                {
                    console.log("err url="+url)
                    resolve({errcode:err})
                }
                xhr.ontimeout=(err)=>
                {
                    console.log("timeout url="+url)
                    resolve({errcode:err})
                }
                xhr.open("post", url, true)
                xhr.setRequestHeader("Content-Type", "application/json")

                if(JSON.stringify(data) !='{}'){
                    GHelper.signature(data)
                }
                console.log("req:"+url)
                console.log("req msg:"+JSON.stringify(data))
                xhr.send(JSON.stringify(data||{}))
            }
            catch(err)
            {
                console.log("catch url="+url)
                console.error(err)
                resolve({errcode:err})
            }
        })
    }
