    static getAnimationClip(node:cc.Node,clipName?)
    {
        let rs = {ani:<cc.Animation>null,clip:<cc.AnimationClip>null}
        let ani = node.getComponent(cc.Animation)
        rs.ani = ani
        if(ani)
        {
            if(!clipName)
            {
                rs.clip = ani.defaultClip
                return rs
            }
            let clips = ani.getClips()
            for(let index in clips)
            {
                let cp = clips[index]
                if(cp.name==clipName)
                {
                    rs.clip = cp
                    return rs
                }
            }
        }
        return rs
    }
    /**
     * 抖动
     * @param node 抖动节点
     * @param shake_num 抖动次数
     */
    static shake(node:cc.Node,shake_num?)
    {
        shake_num=shake_num||5
        let base = node.position
        let shake_pos=
        [
            cc.v2(base.x+10,base.y),
            cc.v2(base.x+10,base.y+10),
            cc.v2(base.x,base.y+10),
            cc.v2(base.x,base.y)
        ]
        let one_shake_actions=[]
        for(let i=0;i<shake_pos.length;++i)
        {
            one_shake_actions.push(cc.moveTo(0.03,shake_pos[i]))
        }
        let one_shake_action = cc.sequence(one_shake_actions)
        let actions=[
            cc.repeat(one_shake_action,shake_num),
            cc.callFunc(()=>
            {
                node.position = base
            })
        ]
        node.runAction(cc.sequence(actions))
    }

