//        if (targetNode == null) {
            Width = cc.winSize.width;
            Height = cc.winSize.height;
        // } else {
        //     Width = targetNode.width;
        //     Height = targetNode.height;
        // }

        camera.targetTexture = texture;
        camera.render(null);
        camera.targetTexture = null;
        let data = texture.readPixels();
