export let GBase64:Base64=null
class Base64
{
    protected _b64 = new Array(64)
    protected _s64 = new Array(123)
    protected _invalidEncoding = "invalid encoding"
    constructor()
    {
        // 65..90, 97..122, 48..57, 43, 47
        for (let i = 0; i < 64;)
        {
            this._s64[this._b64[i] = i < 26 ? i + 65 : i < 52 ? i + 71 : i < 62 ? i - 4 : i - 59 | 43] = i++
        }
    }
    /**
     * Calculates the byte length of a base64 encoded string.
     * @param {string} string Base64 encoded string
     * @returns {number} Byte length
     */
    length(string) 
    {
        let p = string.length
        if (!p)
        {
            return 0
        }
        let n = 0
        while (--p % 4 > 1 && string.charAt(p) === "=")
        {
            ++n
        }
        return Math.ceil(string.length * 3) / 4 - n
    }

    /**
     * Encodes a buffer to a base64 encoded string.
     * @param {Uint8Array} buffer Source buffer
     * @param {number} start Source start
     * @param {number} end Source end
     * @returns {string} Base64 encoded string
     */
    encode(buffer, start, end) 
    {
        let parts = null
        let chunk = []
        let i = 0 // output index
        let j = 0 // goto index
        let t     // temporary
        while (start < end) 
        {
            let b = buffer[start++]
            switch (j) 
            {
                case 0:
                    chunk[i++] = this._b64[b >> 2]
                    t = (b & 3) << 4
                    j = 1
                    break
                case 1:
                    chunk[i++] = this._b64[t | b >> 4]
                    t = (b & 15) << 2
                    j = 2
                    break
                case 2:
                    chunk[i++] = this._b64[t | b >> 6]
                    chunk[i++] = this._b64[b & 63]
                    j = 0
                    break
            }
            if (i > 8191) 
            {
                (parts || (parts = [])).push(String.fromCharCode.apply(String, chunk))
                i = 0
            }
        }
        if (j) 
        {
            chunk[i++] = this._b64[t]
            chunk[i++] = 61
            if (j === 1)
            {
                chunk[i++] = 61
            }
        }
        if (parts) 
        {
            if (i)
            {
                parts.push(String.fromCharCode.apply(String, chunk.slice(0, i)))
            }
            return parts.join("")
        }
        return String.fromCharCode.apply(String, chunk.slice(0, i))
    }

    /**
     * Decodes a base64 encoded string to a buffer.
     * @param {string} string Source string
     * @param {Uint8Array} buffer Destination buffer
     * @param {number} offset Destination offset
     * @returns {number} Number of bytes written
     * @throws {Error} If encoding is invalid
     */
    decode(string, buffer, offset) 
    {
        let start = offset
        let j = 0 // goto index
        let t     // temporary
        for (let i = 0; i < string.length;) 
        {
            let c = string.charCodeAt(i++)
            if (c === 61 && j > 1)
            {
                break
            }
            if ((c = this._s64[c]) === undefined)
            {
                throw Error(this._invalidEncoding)
            }
            switch (j) 
            {
                case 0:
                {
                    t = c
                    j = 1
                    break
                }
                case 1:
                {
                    buffer[offset++] = t << 2 | (c & 48) >> 4
                    t = c
                    j = 2
                    break
                }
                case 2:
                {
                    buffer[offset++] = (t & 15) << 4 | (c & 60) >> 2
                    t = c
                    j = 3
                    break
                }
                case 3:
                {
                    buffer[offset++] = (t & 3) << 6 | c;
                    j = 0;
                    break;
                }
            }
        }
        if (j === 1)
        {
            throw Error(this._invalidEncoding)
        }
        return offset - start
    }
}

GBase64 = new Base64()