
import { GPageMgr, PageManager } from './Framework/View/Page/PageManager';
import {IGame} from "./Framework/IGame"
import { GGoogleTool } from './Framework/Logic/GoogleTool';
import { GGameDM } from './Framework/DataModel/GameDataModel';
import { GAppDM } from './DataModel/AppDataModel';
import { GAccountHttp } from './Net/AccountHttp';
import { GaoDeMapTool } from './Framework/Logic/GaoDeMapTool';
import { layerMgr } from "./manager/LayerManager"
import { loginService } from './Service/LoginService';
import { GMsgCenter } from './utils/MessageCenter';
import { GHelper } from './Framework/Logic/Helper';
import { GNativeTool } from './Framework/Logic/NativeTool';

import MultiPlatform from './platform/MultiPlatform';
import { ykUtils } from './utils/YkUtils';

const {ccclass, property} = cc._decorator;

@ccclass
export class Main extends IGame{

    @property(cc.Camera)
    nodeCaptureCamera:cc.Camera = null;
    onLoad() 
    {
        super.onLoad()
        MultiPlatform.getInstance().crashBind();
        this.initSceenState()
        MultiPlatform.getInstance().initCamera(this.nodeCaptureCamera);
    }
    async onPreInitDone()
    {
        super.onPreInitDone()

        GGoogleTool.init()
        GAppDM.account_server=GGameDM.auth
        //是否自动登陆
        GAppDM.auto_login=true
        //内网测试版本
        GAppDM.quick_test=false
        // 是否检测更新 发版一定改成true
        GAppDM.checkUpdate =  true


        // 服务器停机更新
        if(GGameDM.isServerUpdate){

            GPageMgr.openPage("Page_Maintain");
            return 
        }


        if(GAppDM.quick_test)
        {
            // GAppDM.account_server={host:"119.3.92.190",port:6001}
            //GAppDM.account_server={host:"192.168.1.102",port:6001}
            GAppDM.account_server={host:"123.60.12.239",port:6001}
        }
        
        // 设置back 监听
        cc['yk']['onBackPressed'] = this.onBackPressed.bind(this)
    
        if(this.serverListHandler())
        {
            GAppDM.isShowServerList = true;
            GPageMgr.openPage("Page_ServerList");
        }
        else
        {
             // 获取地址
            GAccountHttp.init("auth",GAppDM.account_server.host,GAppDM.account_server.port)
            GPageMgr.openPage("Page_Start")
        }
    }
    serverListHandler()
    {
        let isShowServerList =  cc.sys.localStorage.getItem("isShowServerList");
        if(isShowServerList==null)
        {
            isShowServerList = false;
            cc.sys.localStorage.setItem("isShowServerList",isShowServerList)
        }
        else
        {
            isShowServerList = JSON.parse(isShowServerList);
        }
        if(isShowServerList)
        {
           return this.getServerConfig();
        }
        this.getServerConfig();
        return   isShowServerList;
    }
    async getServerConfig()
    {
        let isget = await loginService.getServerConfig();
        if(isget && GGameDM.server_list.length>0)
        {
            return true;
        }else
        {
            return false;
        }
    }

    private lastBackTime = null
    onBackPressed(){
        console.log('onback pressed vvvvvvvvvvv')
        let curData =  Date.now()
        if(this.lastBackTime == null){
            this.lastBackTime =curData
        }

        
        this.checkBackApp(curData)
    }

    checkBackApp(curData){
           // 间隔小于1s 退出
        if(curData != this.lastBackTime&&(curData-this.lastBackTime)/1000<2){
            GNativeTool.doBackClose()
        }else{
            GHelper.showTip('再次返回退出应用')
            this.lastBackTime = curData
        }
    }

    //==================== 后台管理=======================
    private backTime = null
    // 需要重新登录的时间 秒
    private reLoginTime = 5*60
    // 屏幕进入前台后台
    initSceenState()
    {
        cc.game.on(cc.game.EVENT_HIDE, ()=>{
           cc.log("游戏进入后台");
           GMsgCenter.send('Game_Hide')
           this.backTime = Date.now()
              
        });
        cc.game.on(cc.game.EVENT_SHOW, ()=>{
            cc.log("重新返回游戏");
            if( this.backTime == null){
                console.log('未进入过后台 返回')
                return
            }
            var hideTime =  Date.now() - this.backTime
            GMsgCenter.send('Game_Show',hideTime)

            if(hideTime/1000 > this.reLoginTime){

                console.log('超时 登录重新加载')
                // 需要重新登录
                GPageMgr.closeAll()

                this.getGameConfig()
            }
            
            this.backTime = null

        });
    }

}