import { GHallHttp } from "../../Net/HallHttp"
import { core } from "../Core/Base"

export let GToSvrLog:ToServerLog=null
class ToServerLog
{
    protected _logs=[]
    log(obj)
    {
        if(GHallHttp.session_id)
        {
            GHallHttp.request("clientLog",{log:JSON.stringify(obj)})
        }
        else
        {
            this._logs.push(obj)
        }
    }
    sendAll()
    {
        if(GHallHttp.session_id)
        {
            if(this._logs.length>0)
            {
                GHallHttp.request("clientLog",{log:JSON.stringify(this._logs)})
                this._logs=[]
            }
            return true
        }
        return false
    }
}
GToSvrLog = new ToServerLog()