import { EOrderFrom } from "../Framework/Logic/ShopTool"
import { GOrderHttp } from "../Net/OrderHttp"

export class CoinItem
{
    id=""
    title=""
    icon=""
    crystal=0
    coin=0
    popular=false
    best=false
}
export enum ERechargeType
{
    /**
     * 今日首充
     */
    TodayFst=1,
    /**
     * 本周首充
     */
    WeekFst,
    /**
     * 月首充
     */
    MonthFst,
    /**
     * 月卡
     */
    MonthCard,
    /**
     * 终身卡
     */
    ForeverCard,
    /**
     * 普通充值
     */
    Common
}
export class ProductItem
{
    id:string
    key:string
    title:string
    icon:string
    money:number
    coin:number
    crystal:number
    give_coin:number
    give_crystal:number
    vip_exp:number
}
export enum EOrderState
{
    Init=0,//初始化
    Cancel,//用户取消
    Verifying,//验证中
    Failed,//验证失败
    Ok//验证成功，订单完成
}
export class OrderModel
{
    user_id:number=-1
    product_id:string=""//商品id(texas@product@0)
    money:number=-1
    from:EOrderFrom=-1//充值来源
    create_time:number=-1
    state:EOrderState=EOrderState.Init
}
export let GOrderSer:OrderService=null
class OrderService
{
    protected _config:{products:Array<ProductItem>,coins:Array<CoinItem>}=null
    get products()
    {
        return this._config.products
    }
    get coins():Array<CoinItem>
    {
        return this._config.coins
    }
    async init()
    {
        let jsonData = await GOrderHttp.request("getconfig")
        this._config=jsonData.config
        this._config.products.sort((a,b)=>
        {
            return a.money-b.money
        })
    }
    async getNewOrder()
    {
        let jsonData = await GOrderHttp.request("getneworder",{from:EOrderFrom.Alipay})
        return jsonData.order
    }
    async getOrderInfo(product_id)
    {
        let jsonData = await GOrderHttp.request("getOrderInfo",{from:EOrderFrom.Alipay, product_id:product_id})
        return jsonData.order_info
    }
    getProduct(product_id):ProductItem
    {
        if(!this._config.products)
        {
            return null
        }
        return this._config.products[product_id]
    }
    getCoin(coin_id):CoinItem
    {
        if(!this._config.coins)
        {
            return null
        }
        return this._config.coins[coin_id]
    }
}
GOrderSer = new OrderService()