import {GHelper} from "../Logic/Helper"

export class MFAnimation extends cc.Component
{
    //多个动画信息，一个角色有多个动画
    private _animations:any
    //动画，由多个部分组成，每个bone就是一个sprite
    private _widgetBones:cc.Sprite[]
    //当前的动画名称
    private _curAnimationName:string=""
    //是否循环
    private _isloop:boolean=false
    //当前时间
    private _curTime:number=0
    //帧间隔时间
    private _interval:number=0.2
    public set interval(value)//可以动态修改间隔时间
    {
        this._interval = value
    }
    //当前帧的索引
    private _curFrameIndex:number=-1
    //是否正在播放
    private _isPlaying:boolean=false
    private _lastFrameCallback:Function

    static create()
    {
        let mfa = <MFAnimation>GHelper.createNode(MFAnimation)
        return mfa
    }
    init(animationName,startIndex,endIndex)
    {
        this._animations[animationName] =
        {
            name:animationName,//动画名称
            startIndex:startIndex,//开始帧
            endIndex:endIndex,//结束帧
            widgets:[],//部件
            widgetInfos:[],//部件描述信息
            widgetIndex:[]//部件名称对应的索引号，方便按照部件名称去索引，算是优化手段
        }   
    }
    // use this for initialization
    onLoad() 
    {

    }

    play(name,loop)
    {
        this._curAnimationName = name
        this._isloop = loop || false
        this._curTime = 0.0
        this._curFrameIndex = -1
        this._isPlaying = true
    }
    stop()
    {
        this._isPlaying = false
    }
    // called every frame, uncomment this function to activate update callback
    update (dt) 
    {
        if(!this._isPlaying)
        {
            return
        }
        let animationInfo = this._animations[this._curAnimationName]
        if (!animationInfo)
        {
            return
        }
        this._curTime += dt
        let index = Math.floor(this._curTime / this._interval)
        if (this._curFrameIndex==index)
        {
            return
        }
        this._curFrameIndex = index
        let framesCount = animationInfo.widgets.length
        if (framesCount<=0)
        {
            this.stop()
            return
        }

        let size = this._widgetBones.length//身体的部位数量
        let frameCount = animationInfo.widgets[0].length//动画的帧长度，每个部件动画要求一样长所以就直接取第一个部件的帧数
        index = index % frameCount//计算出下一帧

        //
        if (index > 0 && this._curTime>this._interval*frameCount)
        {
            this._curTime -= this._interval*frameCount
            if (this._lastFrameCallback)
            {
                this._lastFrameCallback()
            }
            if (!this._isloop)
            {
                this.stop()
                return
            }
        }

        //初始化或者补充身体的部件bone
        while (size < framesCount)
        {
            let sprite = GHelper.createSprite()
            this._widgetBones.push(sprite)
            size = this._widgetBones.length
            sprite.node.parent = this.node
        }

        for (let i = 0; i < framesCount; ++i)
        {
            let sprite = this._widgetBones[i]//遍历每个部位

            let widgetFrames = animationInfo.widgets[i]
            if(!widgetFrames)
            {
                //可能已经被删除了毕竟有removeWidget函数存在
                continue
            }
            let spriteFrame = widgetFrames[index]
            if (spriteFrame)
            {
                sprite.node.active = true
                sprite.spriteFrame = spriteFrame
                sprite.node.setPosition(spriteFrame.getOffset())
            }
            else//当前帧的当前部件没有图
            {
                sprite.node.active = false
            }
        }
        //没有装扮的部分全部隐藏掉
        for (let i = framesCount; i < size;++i)
        {
            this._widgetBones[i].node.active = false
        }
    }
    isAnimationExist(animationName)
    {
        if(this._animations[animationName])
        {
            return true
        }
        return false
    }
    addWidget(animationName, widgetInfo)
    {
        let self = this
        let animationInfo = this._animations[animationName]
        if (!animationInfo)
        {
            return false
        }
        //单动画结构
        let widgetIndex = animationInfo.widgetIndex[widgetInfo.name]
        if (!widgetIndex)//之前这个部件不存在
        {
            animationInfo.widgetInfos.push(widgetInfo)
            animationInfo.widgets.push([])
            widgetIndex = animationInfo.widgets.length - 1
            animationInfo.widgetIndex[widgetInfo.name] = widgetIndex
        }
        animationInfo.widgets[widgetIndex] = []
        cc.loader.loadRes(widgetInfo.atlas, cc.SpriteAtlas, function(err, atlas){
            for (let i = 0; i <= (animationInfo.endIndex - animationInfo.startIndex); ++i)
            {
                let name = widgetInfo.path+self._formatNumName(i + animationInfo.startIndex)
                name = name.replace(/\//g,"-")
                let sf = atlas.getSpriteFrame(name)
                animationInfo.widgets[widgetIndex].push(sf)
            }
        })
        return true;
    }
    removeWidget(widgetName, animationName)
    {
        if (animationName)//指定删除
        {
            let animationInfo = this._animations[animationName];
            if (!animationInfo)
            {
                return
            }
            let widgetIndex = animationInfo.widgetIndex[widgetName]
            if (!widgetIndex)
            {
                return
            }
            animationInfo.widgets[widgetIndex] = null
        }
        else//全部删除
        {
            for(let animationName in this._animations)
            {
                let animationInfo = this._animations[animationName]
                if (!animationInfo.widgetIndex[widgetName])
                {
                    return
                }
                let index = animationInfo.widgetIndex[widgetName]
                animationInfo.framess[index] = null
            }
        }
    }
    setLastFrameCallFunc(callback)
    {
        this._lastFrameCallback = callback
    }
    clearFrameEventCallFunc()
    {
        this._lastFrameCallback = null
    }
    //1="001",13="013",123="123"
    _formatNumName(index)
    {
        if(index<10)
        {
            return "00"+index
        }
        if(index<100)
        {
            return "0"+index
        }
        return ""+index
    }
}