import { core } from "../../Framework/Core/Base"
import { GHelper } from "../../Framework/Logic/Helper"
import { GPageMgr } from "../../Framework/View/Page/PageManager"
import { AddressItem, GUserMgr } from "../../Logic/UserManager"
import { GHallHttp } from "../../Net/HallHttp"
import { GMsgCenter } from "../../utils/MessageCenter"

export class FollowerItem
{
    id=0
    nickname=""
    logo=""
    create_time=0
    money=0
}
const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_AddressItem extends cc.Component {

    @property(cc.Toggle)
    tg_choose: cc.Toggle = null

    @property(cc.Label)
    lbl_name_phone: cc.Label = null

    @property(cc.Label)
    lbl_address: cc.Label = null

    protected _address:AddressItem=null
    onLoad()
    {
        GMsgCenter.register("address_changed",this.onChanged.bind(this),this)
    }
    protected onDestroy(): void {
        GMsgCenter.unregister("address_changed",this)
    }
    init(address:AddressItem) {
        this._address=address
        this.lbl_name_phone.string=address.name+"  "+address.phone
        this.lbl_address.string=address.address
        let address_time=GUserMgr.self_user.address_time||0
        if(this._address.create_time==address_time)
        {
            this.tg_choose.check()
        }
        else
        {
            this.tg_choose.uncheck()
        }
    }
    Btn_Edit_OnClick(event)
    {
        GPageMgr.openPage("Page_EditAddress",this._address)
    }
    Btn_Choose_OnClick(event)
    {
        GMsgCenter.send("address_changed",this._address.create_time)
    }
    onChanged(create_time:number)
    {
        if(this._address.create_time==create_time)
        {
            this.tg_choose.check()
            GUserMgr.self_user.address_time=this._address.create_time
            GHallHttp.request("chooseaddress",{address_time:this._address.create_time})
        }
        else
        {
            this.tg_choose.uncheck()
        }
    }
}
