import { core } from './../Core/Base';
export class BaseDataModel
{
    protected _data=null
    protected _filename=""
    get data()
    {
        return this._data
    }
    constructor(filename:string)
    {
        this._filename = filename
    }
    init()
    {
        cc.loader.loadRes("Common/Data/"+this._filename,(err,data)=>
        {
            if(core.isString(data))
            {
                data = JSON.parse(data)
            }
            this._data = data.json
        })
    }
}