import { EMatchState } from "../../Config/_ini_";
import { GTexasTableDM } from "../../DataModel/TexasTableDataModel";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GMatchSer } from "../../Service/MatchService";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Page_MatchInfo extends Page {

    @property(cc.ToggleContainer)
    toggles: cc.ToggleContainer = null;


    @property(cc.Node)
    matchInfo: cc.Node = null;

    @property(cc.Node)
    nodeInfo: cc.Node = null;

    @property(cc.Node)
    nodeLevel: cc.Node = null;

    @property(cc.Node)
    nodeReward: cc.Node = null;

    @property(cc.Node)
    nodeScore: cc.Node = null;
    
    @property(cc.Node)
    nodeType: cc.Node = null;

    protected _init_y=0
    private outPos = -1500
    start() {

        this.node.zIndex=500
        this.toggles.toggleItems[0].check()
        this.onToggleInfo()
        this.doShow()
    }
 
    protected onEnable(): void {
     
        // this.node.runAction(cc.moveTo(0.2,0,this._init_y))
        this.doShow()
    }

    doShow(){
        cc.find('content/nns',this.node).stopAllActions()
        cc.find('content/nns',this.node).y = this.outPos
        cc.find('content/nns',this.node).runAction(cc.moveTo(0.2,0,0))
    }
    
    hide()
    {
        this.node.stopAllActions()
        if(!this.node.active)
        {
            return
        }
        // this.node.runAction(cc.sequence(
        //     cc.moveTo(0.2,0,-1200),
        //     cc.callFunc(()=>
        //     {
        //         this.node.active=false
        //     })
        // ))
        cc.find('content/nns',this.node).runAction(cc.sequence(
                cc.moveTo(0.2,0,-1500),
                cc.callFunc(()=>
                {
                    this.node.active=false
                })
            ))
    }
    onOpen(table_id:number)
    {
        this._init_y=this.node.y
        if(!table_id)
        {
            // this.node.y=-1200
            this.node.active=false
            return
        }
        let tableinfo = GTexasTableDM.getTableInfo(table_id)
        if(!tableinfo)
        {
            this.node.active=false
            return
        }
        let matchM = GMatchSer.getMatchInsById(tableinfo.match_insid)
        if(!matchM)
        {
            this.node.active=false
            return
        }
        if(matchM.state==EMatchState.Waiting)
        {
            this.node.active=true
            return
        }
        this.node.active=false
    }
    onToggleInfo() {
        this.nodeInfo.active = true;
        this.nodeLevel.active = false;
        this.nodeReward.active = false;
        this.nodeScore.active = false;
        this.nodeType.active = false;
    }

    onToggleLevel() {
        this.nodeInfo.active = false;
        this.nodeLevel.active = true;
        this.nodeReward.active = false;
        this.nodeScore.active = false;
        this.nodeType.active = false;
    }

    onToggleReward() {
        this.nodeInfo.active = false;
        this.nodeLevel.active = false;
        this.nodeReward.active = true;
        this.nodeScore.active = false;
        this.nodeType.active = false;
    }

    onToggleScore() {
        this.nodeInfo.active = false;
        this.nodeLevel.active = false;
        this.nodeReward.active = false;
        this.nodeScore.active = true;
        this.nodeType.active = false;
    }

    onToggleType() {
        this.nodeInfo.active = false;
        this.nodeLevel.active = false;
        this.nodeReward.active = false;
        this.nodeScore.active = false;
        this.nodeType.active = true;
    }

    Btn_Bg_Click(event)
    {
        this.hide()
    }
}
