import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../Logic/UserManager";
import MultiPlatform from '../../platform/MultiPlatform';
import { GGameDM } from '../../Framework/DataModel/GameDataModel';
import { GAccountHttp } from '../../Net/AccountHttp';
import { GAppDM } from '../../DataModel/AppDataModel';
const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_ServerList extends Page {
    @property(cc.Prefab)
    item_prefab:cc.Prefab = null;

    @property(cc.Node)
    contentNode:cc.Node = null;


    @property(cc.Node)
    chooiseNd:cc.Node = null;

    @property(cc.Node)
    pass_wordNd:cc.Node = null;

    @property(cc.EditBox)
    pass_word_editbox:cc.EditBox = null;

    serverData:any = null;

    start() {
        this.init(GGameDM.server_list);
    }
    init(mdata:any)
    {
        this.serverData = mdata;
        this.contentNode.removeAllChildren();
        for(var i=0;i<this.serverData.length;i++)
        {
            let temp = cc.instantiate(this.item_prefab);
            this.contentNode.addChild(temp);
            temp.getComponent('Widget_serverListItem').init(this.serverData[i],i)
        }
        if(JSON.parse(cc.sys.localStorage.getItem("isShowServerList")))
        {
            this.chooiseNd.active = true;
            this.pass_wordNd.active = false;
        }else
        {
            this.chooiseNd.active = false;
            this.pass_wordNd.active = true;
        }
    }
    checkPassWord()
    {
        if(GGameDM.pass_word=="")
        {
            GHelper.showTip("远处密码未配置！！！")
            return
        }
        if(this.pass_word_editbox.string==GGameDM.pass_word)
        {
            this.pass_wordNd.active = false;
            this.chooiseNd.active = true;
            cc.sys.localStorage.setItem("isShowServerList",true)
        }else
        {
            GHelper.showTip("请输入正确密码！！！")
        }
    }
    closeView()
    {
         // 获取地址
         GAccountHttp.init("auth",GAppDM.account_server.host,GAppDM.account_server.port)
         if(!GPageMgr.isPageOpened("Page_CodeLogin"))
         {
            GPageMgr.openPage("Page_Start")
         }
         GPageMgr.closePage('Page_ServerList');
    }
    btn_close_onClick(event)
    {
        this.closeView();
    }
    btn_closeShowIng_onClick(event)
    {
        cc.sys.localStorage.setItem("isShowServerList",false)
        GAppDM.isShowServerList = false;
        this.closeView();
        
    }
}
