import { ChargeConfig } from "../../data/data";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_ChargeConfirm extends cc.Component {
    
    @property(cc.Label)
    labelPrice: cc.Label = null;

    @property(cc.Label)
    labelName: cc.Label = null;

    @property(cc.Label)
    labelCount: cc.Label = null;

    private id: number = 1;
    private count: number = 1;

    init(id: number) {
        this.id = id;
    }

    onShow() {
        this.count = 1;
    }

    initTable() {
        // let config = configMgr.getConfig(ChargeConfig, this.id);
        // this.labelPrice.string = "¥" + config.price * this.count;
        // this.labelName.string = config.name;
        // this.labelCount.string = String(this.count);
    }

    onClickSub() {
        this.count -= 1;
        if (this.count < 1) {
            this.count = 1;
        }
        this.initTable();
    }

    onClickAdd() {
        this.count += 1;
        if (this.count > 10) {
            this.count = 10;
        }
        this.initTable();
    }

    onClickCharge() {
        // shopCtrl.createOrder(this.id, this.count, (msg) => {
        //     if (msg && msg.order_id) {
        //         this.hide();
        //         layerMgr.showLayer(LAYER.CHARGE_RESULT_LAYER, msg.order_id);
        //     }
        // })
    }
}
