import {GAudioTool} from "../Logic/AudioTool"
import {GHelper} from "../Logic/Helper"

export let GSettingDM:SettingDataModel = null
class SettingDataModel
{
    musicOn = true//音乐开
    soundOn = true//音效开
    voiceOn = true//声音开
    shakeOn = true//震动开
    bbOn = false//bb数 开
    music_value = 1
    sound_value = 1
    voice_value = 1
    select_bg=""
    card_style=""
    cardbg_style=""
    quick_raise_pre=[2,2.5,2.8,3]
    quick_raise_aft=[33,50,60,100]
    init()
    {
        this.musicOn = cc.sys.localStorage.getItem("setting_musicOn")
        this.soundOn = cc.sys.localStorage.getItem("setting_soundOn")
        this.voiceOn = cc.sys.localStorage.getItem("setting_voiceOn")
        this.shakeOn = cc.sys.localStorage.getItem("setting_shakeOn")
        this.bbOn = cc.sys.localStorage.getItem("setting_bbOn")

        this.music_value = cc.sys.localStorage.getItem("setting_music_value")||0
        this.sound_value = cc.sys.localStorage.getItem("setting_sound_value")||0
        this.voice_value = cc.sys.localStorage.getItem("setting_voice_value")||0
        this.select_bg = cc.sys.localStorage.getItem("setting_select_bg")||this.select_bg
        this.card_style = cc.sys.localStorage.getItem("setting_card_style")||this.card_style
        this.cardbg_style = cc.sys.localStorage.getItem("setting_cardbg_style")||this.cardbg_style
        if(cc.sys.localStorage.getItem("setting_quick_raise_pre")){
            this.quick_raise_pre = JSON.parse(cc.sys.localStorage.getItem("setting_quick_raise_pre"))
        }
        if(cc.sys.localStorage.getItem("setting_quick_raise_aft")){
            this.quick_raise_aft = JSON.parse(cc.sys.localStorage.getItem("setting_quick_raise_aft"))
        }

        this.quick_raise_pre.sort((a,b)=>a-b)
        this.quick_raise_aft.sort((a,b)=>a-b)
        
        try{
            this.music_value = parseFloat(this.music_value+"")
        }
        catch(e)
        {
            this.music_value = 1
        }
        try{
            this.sound_value = parseFloat(this.sound_value+"")
        }
        catch(e)
        {
            this.sound_value = 1
        }
        try{
            this.voice_value = parseFloat(this.voice_value+"")
        }
        catch(e)
        {
            this.voice_value = 1
        }
        GAudioTool.setMusicVolume(this.music_value)
        //GAudioTool.setSoundVolume(this.sound_value)
        GAudioTool.setVoiceVolume(this.voice_value)
        if(<any>this.musicOn=="false")
        {
            this.musicOn = false
        }
        else
        {
            this.musicOn = true
        }
        if(<any>this.bbOn=="false")
        {
            this.bbOn = false
        }else if(<any>this.bbOn=="true")
        {
            this.bbOn = true;
        }
        else
        {
            this.bbOn = false
        }
        if(<any>this.soundOn == "false")
        {
            this.soundOn = false
        }
        else
        {
            this.soundOn = true
        }
        if(<any>this.voiceOn == "false")
        {
            this.voiceOn = false
        }
        else
        {
            this.voiceOn = true
        }
        if(<any>this.shakeOn == "false")
        {
            this.shakeOn = false
        }
        else
        {
            this.shakeOn = true
        }
    }
    updateQuickRaisePre()
    {
        this.quick_raise_pre.sort((a,b)=>a-b)
        GHelper.saveData({"setting_quick_raise_pre":JSON.stringify(this.quick_raise_pre)})
    }
    updateQuickRaiseAft()
    {
        this.quick_raise_aft.sort((a,b)=>a-b)
        GHelper.saveData({"setting_quick_raise_aft":JSON.stringify(this.quick_raise_aft)})
    }
    setMusicValue(m_value)
    {
        this.music_value = m_value
        GAudioTool.setMusicVolume(this.music_value)
        GHelper.saveData({"setting_music_value":this.music_value})
    }
    setSoundValue(s_value)
    {
        this.sound_value = s_value
        GAudioTool.setSoundVolume(this.sound_value)
        GHelper.saveData({"setting_sound_value":this.sound_value})
    }
    setVoiceValue(s_value)
    {
        this.voice_value = s_value
        GAudioTool.setVoiceVolume(this.voice_value)
        GHelper.saveData({"setting_sound_value":this.voice_value})
    }
    setMusicOn(musicOn)
    {
        this.musicOn = musicOn
        if(!this.musicOn)
        {
            GAudioTool.stopMusic()
        }
        GHelper.saveData({"setting_musicOn":this.musicOn})
    }
    setSoundOn(soundOn)
    {
        this.soundOn = soundOn
        GHelper.saveData({"setting_soundOn":this.soundOn})
    }
    setVoiceOn(voiceOn)
    {
        this.voiceOn = voiceOn
        GHelper.saveData({"setting_voiceOn":this.voiceOn})
    }
    setShakeOn(shakeOn)
    {
        this.shakeOn = shakeOn
        GHelper.saveData({"setting_shakeOn":this.shakeOn})
    }

    setBbOn(isOn){
        this.bbOn = isOn
        GHelper.saveData({"setting_bbOn":this.bbOn})
    }

}
GSettingDM = new SettingDataModel()
