import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_ChangePassword extends Page {

    @property(cc.EditBox)
    editPasswordOld: cc.EditBox = null;

    @property(cc.EditBox)
    editPasswordNew: cc.EditBox = null;

    @property(cc.EditBox)
    editPasswordConfirm: cc.EditBox = null;

    onReady() {
        this.editPasswordNew.node.parent.getChildByName("tips").active = false;
        this.editPasswordConfirm.node.parent.getChildByName("tips").active = false;
    }

    initTable() {
    }

    onEditPasswordOld() {
        this.editPasswordOld.string = this.editPasswordOld.string.trim();
    }

    onEditPasswordNew() {
        this.editPasswordNew.string = this.editPasswordNew.string.trim();
        this.editPasswordNew.node.parent.getChildByName("tips").active = this.editPasswordNew.string && this.editPasswordNew.string.length < 6 || this.editPasswordNew.string.length > 20;
    }

    onEditPasswordConfirm() {
        this.editPasswordConfirm.string = this.editPasswordConfirm.string.trim();
        this.editPasswordConfirm.node.parent.getChildByName("tips").active = this.editPasswordConfirm.string && this.editPasswordNew.string != this.editPasswordConfirm.string;
    }

    onClickChangePassword() {
        let password_old = this.editPasswordOld.string;
        let password_new = this.editPasswordNew.string;
        let password_confirm = this.editPasswordConfirm.string;

        if (!password_old) {
            GHelper.showTip("请输入旧密码");
            return;
        }

        if (!password_new) {
            GHelper.showTip("请输入新密码");
            return;
        }

        if (password_new.length < 6) {
            GHelper.showTip("您输入的密码长度过短，请重新输入");
            return;
        }

        if (password_new != password_confirm) {
            GHelper.showTip("两次输入密码不一致");
            return;
        }

        // loginCtrl.changePassword(password_old, password_new, (msg) => {
        //     if (msg && !msg.error) {
        //         GHelper.showTip("密码修改成功");
        //         this.hide();
        //     }
        // })
    }
}
