import { Pool } from "../data/pool";

class CallbackInfo {
    callback: Function;
    target: Object;
    once: boolean;

    constructor() {
        this.reset();
    }

    reset() {
        this.callback = undefined;
        this.target = undefined;
        this.once = false;
    }
}

class EventManager {
    private pool: Pool<CallbackInfo>;
    private listeners: {[key: string]: CallbackInfo[]};
    constructor() {
        this.pool = new Pool(CallbackInfo);
        this.listeners = {};
    }

    private _on(id: string, callback: Function, target: Object) {
        let info = this.pool.get();
        info.callback = callback;
        info.target = target;
        info.once = false;

        this.listeners[id].push(info);
    }

    private _off(target: Object, id: string) {
        let infos = this.listeners[id];

        for (let i = infos.length - 1; i >= 0; i--) {
            if (infos[i].target === target) {
                let info = infos.splice(i, 1)[0];
                info.reset();
                this.pool.put(info);
            }
        }
    }

    private _once(id: string, callback: Function, target: Object) {
        let info = this.pool.get();
        info.callback = callback;
        info.target = target;
        info.once = true;

        this.listeners[id].push(info);
    }

    private _emit(id: string, ...args) {
        let infos = this.listeners[id].filter(info => info.callback && info.target);
        for (let info of infos) {
            info.callback.call(info.target, ...args);
            if (info.once) {
                this._off(info.target, id);
            }
        }
    }

    public addListener(id: number|string, callback: Function, target: Object) {
        if (!this.listeners[id]) {
            this.listeners[id] = [];
        }

        this._on(String(id), callback, target);
    }

    public removeListener(target: Object, id?: number|string) {
        if (id === undefined) {
            for (let id in this.listeners) {
                this._off(target, id);
            }
        } else {
            this._off(target, String(id))
        }
    }

    public eventOnce(id: number|string, callback: Function, target: Object) {
        if (!this.listeners[id]) {
            this.listeners[id] = [];
        }

        this._once(String(id), callback, target);
    }

    public dispatchEvent(id: number|string, ...args) {
        if (!id || !this.listeners[id]) {
            return;
        }

        this._emit(String(id), ...args);
    }
}

export const eventMgr: EventManager = new EventManager();