import { GHelper } from "../../Framework/Logic/Helper";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../Logic/UserManager";
import { GHallHttp } from "../../Net/HallHttp";
import { GItemCfg, ItemItem } from "../../Service/ItemService";
import { GMsgCenter } from "../../utils/MessageCenter";
import { GGameDM } from "../../Framework/DataModel/GameDataModel";
import { ykUtils } from "../../utils/YkUtils";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_ExchangeItem extends cc.Component {

    @property(cc.Sprite)
    imgIcon: cc.Sprite = null;

    @property(cc.Label)
    labelName: cc.Label = null;

    @property(cc.Label)
    labelDesc: cc.Label = null;

    @property(cc.Button)
    btnExchange: cc.Button = null;

    protected _item:ItemItem=null

    init(item:ItemItem)
    {
        this._item=item

        // if(GGameDM.isReviewVersion){

        // }
        this.labelName.string = this._item.name
 

        this.labelDesc.string = "需要"+this._item['coin']+"欢乐点兑换"
        if(GUserMgr.self_user.reward_coin<this._item['coin'])
        {
            //this.btnExchange.interactable=false
        }

        ykUtils.loadNetSpriteFrameWithNode(this.imgIcon,ykUtils.getItemIcon(this._item.icon))

        // GHelper.createSpriteFrame(this._item.icon,(sf)=>
        // {
        //     if(cc.isValid(this))
        //     {
        //         this.imgIcon.spriteFrame=sf
        //     }
        // })
    }

    Btn_Exchange_OnClick(event)
    {
        if(GUserMgr.self_user.reward_coin<this._item['coin'])
        {

            GHelper.showTip("您的欢乐点不足!")
            // GPageMgr.openPage("Page_Tips",{tips: "竞技点不足请前往充值", buttons: [
            //     {
            //         label: "确认",
            //         callback: async ()=>
            //         {
            //             GPageMgr.openPage("Page_Charge")
            //         }
            //     },
            //     {
            //         label: "取消"
            //     }
            // ]})
            return
        }
        GPageMgr.openPage("Page_Tips",{tips: "是否使用"+this._item['coin']+"欢乐点"+this._item.name, buttons: [
            {
                label: "确认",
                callback: async ()=>
                {
                    let rs = await GHallHttp.request("buyitem",{item_id:this._item.id})
                    if(rs.errcode)
                    {
                        GHelper.showTip(rs.errcode.des)
                        return
                    }
                    let bi = GItemCfg.getBagItemById(this._item.id)
                    if(!bi)
                    {
                        GItemCfg.bagItems.push(rs.bagitem)
                    }
                    else
                    {
                        bi.count=rs.bagitem.count
                    }
                    if(GUserMgr.self_user.coin<this._item.coin)
                    {
                        
                    }
                    GUserMgr.self_user.reward_coin=rs.reward_coin
                    GMsgCenter.send("coinchanged")
                    GHelper.showTip("兑换成功")
                }
            },
            {
                label: "取消"
            }
        ]})
    }
}
