export let GResTool:ResTool=null
class ResTool
{
    protected _items={}
    getRes(name)
    {
        return this._items[name]
    }
    cacheAll(dirs:Array<string>,callback)
    {
        let i=0
        let complete = ()=>
        {
            if(i==0)
            {
                if(callback)
                {
                    callback()
                }
            }
        }
        for(let k=0;k<dirs.length;++k)
        {
            ++i
            this._cacheDir(dirs[k],()=>
            {
                --i
                complete()
            })
        }
    }
    protected _cacheDir(dir,callback)
    {
        cc.loader.loadResDir(dir,cc.SpriteFrame,(error,resource,urls)=>
        {
            for(let i=0;i<urls.length;++i)
            {
                this._items[urls[i]] = resource[i]
            }
            if(callback)
            {
                callback()
            }
        })
    }
}
GResTool = new ResTool()
cc["GResTool"] = GResTool