import { GHallHttp } from "../../Net/HallHttp";
import { GBase64 } from "../Core/Base64";

export let GQiNiuTool:QiNiuTool = null
class QiNiuTool
{    
    protected _local_file_path:string=""
    protected _upload_callback:Function=null
    protected _download_host : ""

    // callback -> callback(status,response) 
    // status = 1为成功, 其他为失败  
    // 成功后response 为文件在七牛的外网地址，失败为错误原因
    async uploadFile(qiniu_filename,localfilepath:string,callback:Function)
    {
        this._upload_callback = callback
        this._local_file_path = localfilepath
        let jsonData = await GHallHttp.request("getQiniuUploadtoken",{filename:qiniu_filename})
        if(jsonData.errcode)
        {
            return
        }
        this.onGetUploadToken(jsonData.host,jsonData.upload_token)
    }

    onGetUploadToken (host,upload_token)
    {
        this._download_host = host
        let tmp_data = jsb.fileUtils.getStringFromFile(this._local_file_path)
        let str_b64 = GBase64.encode(tmp_data, 0, tmp_data.length)
        this._upload(str_b64, upload_token)
    }

    _upload(str_b64, token)
    {
        let data = str_b64.replace(/^.*?,/, '')
        let url = "http://upload.qiniup.com/putb64/-1"
        let xhr = new XMLHttpRequest()
        xhr.onreadystatechange = ()=>
        {
            let status = -1
            if (xhr.readyState == 4) 
            {
                let response = ""
                if (xhr.status == 200)
                {
                    status = 1
                    response = "http://" + this._download_host + "/" +  (JSON.parse(xhr.response).key)
                }
                else
                {
                    status = xhr.status
                    response = xhr.responseText
                }
                if (this._upload_callback)
                {
                    this._upload_callback(status,response)
                }
            }
        }
        xhr.open("POST", url, true)
        xhr.setRequestHeader("Content-Type", "application/octet-stream")
        xhr.setRequestHeader("Authorization", "UpToken " + token)
        xhr.send(data)
     }
}

GQiNiuTool = new QiNiuTool()