import { GMatchSer, MatchInsModel, MatchModel } from "../../Service/MatchService";
import { GMsgCenter } from "../../utils/MessageCenter";

import { ClubModel ,GClubSer} from "../../Service/ClubService";
const {ccclass, property} = cc._decorator;

@ccclass
export default class Widget_DemainCode extends cc.Component {

    private url = null

    protected onLoad(): void {
        this.generateDeminCode('http://www.baidu.com',this.node.width)
    }
    generateDeminCode(url,len?:number){
        var drawNode = this.node.children[0]
        if(len){
            this.node.width = len
            this.node.height = len
        }else{
            len = this.node.width
        }
        var margain = 10
        
        var drawNodelen = this.node.width - margain*2
        drawNode.width = drawNodelen
        drawNode.height = drawNodelen
        drawNode.x = -drawNodelen/2
        drawNode.y = -drawNodelen/2
        this.url = url

        var qrcode = new QRCode(-1, QRErrorCorrectLevel.H);
        qrcode.addData(url);
	    qrcode.make();

	    var ctx = drawNode.getComponent(cc.Graphics);

	    // compute tileW/tileH based on node width and height
	    var tileW = drawNode.width / qrcode.getModuleCount();
	    var tileH = drawNode.height / qrcode.getModuleCount();

	    // draw in the Graphics
	    for (var row = 0; row < qrcode.getModuleCount(); row++) {
	    	for (var col = 0; col < qrcode.getModuleCount(); col++) {
	    		// ctx.fillStyle = qrcode.isDark(row, col) ? options.foreground : options.background;
	    		if (qrcode.isDark(row, col)) {
	    			ctx.fillColor = cc.Color.BLACK;
	    		} else {
	    			ctx.fillColor = cc.Color.WHITE;
	    		}
	    		var w = (Math.ceil((col + 1) * tileW) - Math.floor(col * tileW));
	    		var h = (Math.ceil((row + 1) * tileW) - Math.floor(row * tileW));
	    		ctx.rect(Math.round(col * tileW), Math.round(row * tileH), w, h);
	    		ctx.fill();
	    	}
	    }
    }
}
