import { MD5 } from "./MD5"
import encryptjs = require("./encryptjs/encryptjs");
import { WeightInfo } from "../data/data";


const LABEL_NUM: string[] = ["", "K", "M"];

class Utils {
    private secretkey = "secret_key";
    constructor() {
    }

    public shuffle<T>(arr: T[]): T[] {
        if (arr.length <= 1) {
            return arr;
        }

        for (let i = arr.length - 1; i > 0; i--) {
            let j = Math.floor(Math.random() * (i + 1));
            [arr[i], arr[j]] = [arr[j], arr[i]];
        }

        return arr;
    }

    /**
     * 时间戳(ms)转为分秒时间(00:00)
     * @param time 
     */
    public formatTime(time: number) {
        let remain = Math.floor(time / 1000);
        let hour = Math.floor(remain / 3600);
        let minute = Math.floor(remain / 60) % 60;
        let second = remain % 60;
        return (hour ? (hour < 10 ? "0" + hour : hour) + ":" : "") + (minute < 10 ? "0" + minute : minute) + ":" + (second < 10 ? "0" + second : second);
    }

    public formatDate = function(date: Date, fmt: string = "yyyy-MM-dd hh:mm:ss"): string {
        const opt = {
            "y+": date.getFullYear().toString(),
            "M+": (date.getMonth() + 1).toString(),
            "d+": date.getDate().toString(),
            "h+": date.getHours().toString(),
            "m+": date.getMinutes().toString(),
            "s+": date.getSeconds().toString(),
        }
    
        let ret = /(y+)/.exec(fmt);
        if (ret) {
            fmt = fmt.replace(ret[1], (date.getFullYear() + "").substring(4 - ret[1].length));
        }
    
        for (let k in opt) {
            ret = new RegExp("(" + k + ")").exec(fmt);
            if (ret) {
                fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
            };
        };
    
        return fmt;
    }

    public getMD5(input: string): string {
        return new MD5().hex_md5(input);
    }

    public encrypt(text: string, secret?: string): string {
        return encryptjs.encrypt(text, secret || this.secretkey, 256);
    }

    public decrypt(text: string, secret?: string): string {
        try {
            return encryptjs.decrypt(text, secret || this.secretkey, 256);
        } catch (e) {
            console.error(e);
            return "";
        }
    }

    public parseCsv(data: string, delimiter = ',') {
        let pattern = "(\\" + delimiter + "|\\r?\\n|\\r|^)" + "(?:\"([^\"]*(?:\"\"[^\"]*)*)\"|" + "([^\"\\" + delimiter + "\\r\\n]*))";
        let objPattern = new RegExp(pattern, "g");

        let arrData = [[]];
        let arrMatches = null;
        while (arrMatches = objPattern.exec(data)) {
            let strMatchedDelimiter = arrMatches[1];
            if (
                strMatchedDelimiter.length &&
                (strMatchedDelimiter != delimiter)
            ) {
                arrData.push([]);
            }
            if (arrMatches[2]) {
                var strMatchedValue = arrMatches[2].replace(
                    new RegExp("\"\"", "g"),
                    "\""
                );
            } else {
                var strMatchedValue = arrMatches[3];
            }
            arrData[arrData.length - 1].push(strMatchedValue);
        }
        if (arrData.length > 0) {
            arrData.pop();
        }
        return arrData;
    }

    public csvToJSON(data: string[][], index_key: number = 0) {
        let keys: string[] = data[index_key];
        let results = [];
        for (let i = index_key + 1; i < data.length; i++) {
            let values = data[i];
            let result = {};
            for (let j = 0; j < keys.length; j++) {
                result[keys[j]] = values[j];
            }
            results.push(result);
        }
        return results;
    }

    //幂级数求和
    getPowerSum(k, n) {
        return (!k || !n || k == 1) ? k * n : k * (1 - Math.pow(k, n)) / (1 - k);
    }

    //平方和
    getSquaresSum(n: number) {
        return n * (n + 1) * (n * 2 + 1) / 6;
    }

    //斐波那契数列
    getFibonacci(n) {
        let value = (Math.pow(1 + Math.sqrt(5), n) - Math.pow(1 - Math.sqrt(5), n)) / Math.pow(2, n) / Math.sqrt(5);
        return Math.round(value);
    }

    getRoute(p0: cc.Vec2, p1: cc.Vec2, p2: cc.Vec2, count: number = 10) {
        if (count <= 1) {
            return [];
        }

        let points = [];
        for (let i = 0; i < count; i++) {
            let t = i / 10;
            let x = p0.x * (1 - t) * (1 - t) + p1.x * t * (1 - t) * 2 + p2.x * t * t;
            let y = p0.y * (1 - t) * (1 - t) + p1.y * t * (1 - t) * 2 + p2.y * t * t;
            points.push(cc.v2(x, y));
        }
        points.push(p2);
        
        return points;
    }
    
    formatNumber(num: number, fix: number = 1) {
        if (num < 10000) {
            return String(Math.round(num));
        }

        let count = 0;
        while(num >= 1000 && count < LABEL_NUM.length - 1) {
            count += 1;
            num /= 1000;
        }

        return num.toFixed(fix) + LABEL_NUM[count];
    }

    checkWeight(list: WeightInfo[]) {
        let weights = 0;
        for (let weight of list) {
            weights += weight.weight;
        }

        let temp = Math.random() * weights;
        for (let weight of list) {
            if (temp <= weight.weight) {
                return weight;
            }
            temp -= weight.weight;
        }

        return null;
    }

    /**
     * 
    自由加注本质是生成一个Step的数组  这样上滑的时候 根据进度取数组里面的值来生成下注筹码
        ChipsStep 为自由加注的数组  
        u32MiniRaise 是最小下注筹码
        u32Stake 是最大下注筹码
        isSmall 判断是否微局

        ChipsStep 的第一个值为 0 第二个值为最小下注 

        定义一个i32Stake  初始值为最小筹码u32MiniRaise
        通过调用 GetRaiseLevel 函数 传递i32Stake 获取一个i32Step值
        定义一个while循环  只要i32Stake  不等于 最大下注 就让 i32Stake累加一个i32Step值
        如果累加后的i32Stake 大于最大下注  就让它等于最大下注终止循环
        然后把得到的 i32Stake 值存入ChipsStep数组
        用累加后的 i32Stake 值 再调用 GetRaiseLevel 函数获取最新的i32Step值

        微局和非微局的区别在于调用 GetRaiseLevel 时  微局直接调用，可以得到小数 

        非微局需要先把 参数转换为客户端显示值 也就是除100  然后再把得到的值 转换成服务器值乘100  
        这样最终存入 ChipsStep 里的数据就是非小数的值

        最终客户端自由加注条 根据上滑的进度  对应 ChipsStep数组的值来显示和下注
        如果上滑到最上面就是allin
    */
    public SplitChipsLevel(u32Stake: number, u32MiniRaise: number, isSmall: number): number[] {
        let ChipsStep: number[] = [];
        ChipsStep.push(0);
        let i32Stake = u32MiniRaise;
        let i32Step = 0;
        //if (isSmall) {
            i32Step = this.GetRaiseLevel(i32Stake);
        //}
        // else {
        //     i32Step = cv.StringTools.serverGoldByClient(this.GetRaiseLevel(cv.StringTools.clientGoldByServer(i32Stake)));
        // }
        ChipsStep.push(u32MiniRaise);

        while (i32Stake != u32Stake) {
            if (i32Stake + i32Step <= u32Stake) {
                i32Stake += i32Step;
            }
            else {
                i32Stake = u32Stake;
            }
            ChipsStep.push(i32Stake);
            //if (isSmall) {
                i32Step = this.GetRaiseLevel(i32Stake);
            //}
            // else {
            //     i32Step = cv.StringTools.serverGoldByClient(this.GetRaiseLevel(cv.StringTools.clientGoldByServer(i32Stake)));
            // }

        }

        return ChipsStep;
    }
    
    public GetRaiseLevel(u32Stake: number): number {
        let i32Step: number = 1;
        if (u32Stake < 50) {
            i32Step = 1;
        }
        else if (u32Stake < 100) {
            i32Step = 5;
        }
        else if (u32Stake < 1000) {
            i32Step = 10;
        }
        else if (u32Stake < 10000) {
            i32Step = 100;
        }
        else if (u32Stake < 100000) {
            i32Step = 1000;
        }
        else if (u32Stake < 1000000) {
            i32Step = 2000;
        }
        else if (u32Stake < 10000000) {
            i32Step = 10000;
        }
        else if (u32Stake < 100000000) {
            i32Step = 20000;
        }
        else {
            i32Step = 100000;
        }
        return i32Step;
    }

    

}





export let utils: Utils = new Utils();