import { GHallHttp } from "../Net/HallHttp"

export class NewsModel
{
    id=0
    title=""
    icon=""
    url=""
    subtitle=""
    info=""
    create_time=0
    type=""//赛事公告
}
class NewsService
{
    protected _news:NewsModel[]=[]
    get news()
    {
        return this._news
    }
    async init()
    {
        let res = await GHallHttp.request("allnews")
        this._news=res.news||[]
        return this._news
    }
}
export let GNewsSer=new NewsService()