import { EMatchState } from "../../Config/_ini_";
import { core } from "../../Framework/Core/Base";
import { GGameDM } from "../../Framework/DataModel/GameDataModel";
import { GAlipay } from "../../Framework/View/Page/Alipay";
import { Page } from "../../Framework/View/Page/Page";
import { GUserMgr } from "../../Logic/UserManager";
import { GItemCfg } from "../../Service/ItemService";
import { GMatchSer, MatchInsModel, MatchModel } from "../../Service/MatchService";
import { GOrderSer } from "../../Service/OrderService";
import ListView from "../../utils/ListView";
import { GMsgCenter } from "../../utils/MessageCenter";
import Widget_ExchangeItem from "../Widget/Widget_ExchangeItem";
import Widget_MatchListItem from "../Widget/Widget_MatchListItem";
import Widget_ProductItem from "../Widget/Widget_ProductItem";

export let GPageMatchList:Page_MatchList=null
const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_MatchList extends Page {
    @property(cc.Label)
    lbl_score: cc.Label = null;
    @property(cc.Label)
    lbl_score1: cc.Label = null;

    @property(cc.Node)
    node_btn: cc.Node = null;

    @property(cc.Node)
    nodeExchange: cc.Node = null;

    @property(cc.ToggleContainer)
    toggles: cc.ToggleContainer = null;

    @property(cc.Label)
    labelToday: cc.Label = null;

    @property(cc.Label)
    labelTomorrow: cc.Label = null;

    @property(cc.ScrollView)
    sv_match: cc.ScrollView = null;

    @property(cc.ScrollView)
    sv_score: cc.ScrollView = null;

    @property(cc.Prefab)
    pf_matchitem: cc.Prefab = null

    @property(cc.ScrollView)
    scrollviewExchange: cc.ScrollView = null;

    @property(cc.Node)
    layoutExchange: cc.Node = null;

    @property(cc.Prefab)
    pf_exchangeitem:cc.Prefab=null

    protected _all:{matches:MatchModel[],matchins:MatchInsModel[]}={matches:null,matchins:null}

    @property(cc.ToggleGroup)
    title_toggle:cc.ToggleGroup = null;
    onLoad()
    {
        this.init()
        if(GGameDM.isReviewVersion)
        {
            this.node_btn.active=false
            this.scrollviewExchange.node.height+=this.node_btn.height
            cc.find('shenheEmpty',this.node).active = true
        }else{
            cc.find('shenheEmpty',this.node).active = false
        }
        GMatchSer.getAllMatches(false).then((all)=>
        {
            this._all = all
            this.initTable()
        })
        GMsgCenter.register("coinchanged",this.initExchange.bind(this),this)
        GMsgCenter.register("toexchange",this.toexchange.bind(this),this)
      
    }
    toexchange()
    {
        this.sv_match.node.parent.active = false
        this.sv_score.node.parent.active = false
        this.nodeExchange.active = true
    }
    protected onDestroy(): void {
        GMsgCenter.unregister("coinchanged",this)
        GMsgCenter.unregister("toexchange",this)
    }
    init() {
        this.lbl_score.string = "游戏点: "+GUserMgr.self_user.coin+"    欢乐点: "+GUserMgr.self_user.reward_coin+"    视频点: "+(GUserMgr.self_user.video_coin||0) +""
        this.lbl_score1.string = this.lbl_score.string;
        this.sv_match.node.parent.active = false
        this.sv_score.node.parent.active = false
        this.nodeExchange.active = false
        if(GGameDM.isReviewVersion)
        {
            this.nodeExchange.active = true
        }
        else
        {
            this.sv_match.node.parent.active = true
        }
        
        let today = new Date();
        let month = today.getMonth() + 1;
        let date = today.getDate();
        this.labelToday.string = `今日赛事（${month < 10 ? "0" + month : month}/${date < 10 ? "0" + date : date}）`;
        today.setHours(24);
        month = today.getMonth() + 1;
        date = today.getDate();
        this.labelTomorrow.string = `明日赛事（${month < 10 ? "0" + month : month}/${date < 10 ? "0" + date : date}）`;
    }
    onEnable()
    {
        this.refresh()
        this.initExchange()
    }
    async refresh()
    {
        this._all = await GMatchSer.getAllMatches()
        this.initTable()
        GPageMatchList=this
    }

    initTable() {
        let today = new Date()
        let month = today.getMonth() + 1
        let date = today.getDate()
        this.labelToday.string = `今日赛事（${month < 10 ? "0" + month : month}/${date < 10 ? "0" + date : date}）`
        today.setHours(24)
        month = today.getMonth() + 1
        date = today.getDate()
        this.labelTomorrow.string = `明日赛事（${month < 10 ? "0" + month : month}/${date < 10 ? "0" + date : date}）`
        
        let checkindex = 0
        for (let i=0;i<this.toggles.toggleItems.length;++i) {
            let toggle = this.toggles.toggleItems[i]
            if(toggle.isChecked)
            {
                checkindex=i
            }
            toggle.node.color = toggle.isChecked ? cc.color(255, 255, 255) : cc.color(102, 102, 102);
        }
        this.sv_match.content.removeAllChildren()
        if(this._handle)
        {
            clearInterval(this._handle)
            this._handle=null
        }
        if(GGameDM.isReviewVersion)
        {
            return
        }
        let today_start = core.getStartTime()+GGameDM.diffServerTime
        let tomorrow = today_start+24*60*60*1000
        this._all.matchins.sort((a,b)=>
        {
            return a.start_time-b.start_time
        })

        this._all.matchins.sort((a,b)=>
        {
            if (GUserMgr.self_user.matchinfos[a.id]!=null && GUserMgr.self_user.matchinfos[b.id]==null) {
                return -1
            }
            if (GUserMgr.self_user.matchinfos[a.id]==null && GUserMgr.self_user.matchinfos[b.id]!=null) {
                return 1
            }
        })
        let i=-1
        let createOne=()=>
        {
            ++i
            if(this._handle&&i>=this._all.matchins.length)
            {
                clearInterval(this._handle)
                this._handle=null
                return
            }
            if(this._all.matchins&&this._all.matchins[i]){

            }else{
                return
            }

            let mim = this._all.matchins[i]
            if(!mim||!mim.match)
            {
                return
            }
            if(checkindex==0)
            {
                if(mim.state==EMatchState.Playing)
                {

                }
                else if(mim.start_time<today_start||mim.start_time>tomorrow)
                {
                    return
                }
            }
            else if(checkindex==1)
            {
                if(mim.start_time<tomorrow)
                {
                    return
                }
            }
            else if(checkindex==2||checkindex==3)
            {
                return
            }
            let item = cc.instantiate(this.pf_matchitem)
            this.sv_match.content.addChild(item)
            let mli = item.getComponent(Widget_MatchListItem)
            mli.init(mim)
        }
        for(let i=0;i<this._all.matchins.length&&i<8;++i)
        {   
            let match = this._all.matchins[i].match
            if(!match||(match.club_id&&match.club_id>0))
            {
                continue
            }
            createOne()
        }



        this._handle=setInterval(createOne,10)
    }
    protected _handle=null
    initExchange()
    {
        if(!GGameDM.isReviewVersion){
            // this.lbl_score.string = GUserMgr.self_user.coin+""
 

         
            this.lbl_score.string = "游戏点: "+GUserMgr.self_user.coin+"  欢乐点: "+GUserMgr.self_user.reward_coin+"  视频点: "+(GUserMgr.self_user.video_coin||0) +""
            this.lbl_score1.string = this.lbl_score.string;
            this.scrollviewExchange.content.removeAllChildren()
            let items = GItemCfg.items
            for(let key in items)
            {
                let item = items[key]
                // if(!item.coin||!item.can_exchange)
                // {
                //     continue
                // }
                if(!item.coin || !item['can_buy'])
                {
                    continue
                }
                let node = cc.instantiate(this.pf_exchangeitem)
                this.scrollviewExchange.content.addChild(node)
                let wei = node.getComponent(Widget_ExchangeItem)
                wei.init(item)
            }
        }
    }
    Btn_Exchange_OnClick() {
        this.sv_match.node.parent.active = false
        this.sv_score.node.parent.active = false
        this.nodeExchange.active = true
    }

    Btn_Match_OnClick() {
        this.sv_match.node.parent.active = true
        this.sv_score.node.parent.active = false
        this.nodeExchange.active = false
        this.initTable()
    }
    Btn_Score_OnClick(event)
    {
        this.sv_match.node.parent.active = false
        this.sv_score.node.parent.active = true
        this.nodeExchange.active = false
        this.initTable()
        this.initProducts()
    }

    Tg_All_OnClick(event)
    {
        this.initTable()
    }

    payMoney(produce_id) {
        let productid = GOrderSer.products[0].id
        GAlipay.alipayEvent(productid);
    }

    initProducts() {
        let products = GOrderSer.products;
        let dataList = []
        for (const key in products) {
            dataList.push({type: 0, product: products[key]})
        }

        this.sv_score.getComponent(ListView).init(this.bindcallfunc.bind(this), this.getItemType.bind(this));
        this.sv_score.getComponent(ListView).notifyDataSetChanged(dataList)

    }

    public getItemType() {
        return 0
    }

    public bindcallfunc(node: cc.Node, info, i) {
        node.getComponent(Widget_ProductItem).setdata(info)
    }
}
